<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-switch v-model="value.use"></el-switch>
    <div class="fill-div"></div>
    <el-popover placement="right" width="350" trigger="click">
      <el-form size="mini" class="setting-form" label-position="left" label-width="60px">
        <el-form-item  style="margin-bottom: 10px;" label="指针颜色">
          <el-radio-group v-model="value.colorType">
            <el-radio-button label="custom">自定义颜色</el-radio-button>
            <el-radio-button label="progress">跟随进度条</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="选择颜色" v-if="value.colorType === 'custom'">
          <el-color-picker v-model="value.color"></el-color-picker>
        </el-form-item>
        <el-form-item label="指针宽度">
          <range-selector v-model="value.width" :options="widthOptions"/>
        </el-form-item>
        <el-form-item label="指针长度">
          <range-selector v-model="value.length" :options="lengthOptions"/>
        </el-form-item>
      </el-form>
      <i slot="reference" class="el-icon-s-tools"></i>
    </el-popover>
  </n-form-item>
</template>

<script>
import rangeSelector  from '@/components/rangeSelector'
import _ from 'lodash'
export default {
  components: {
    rangeSelector
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      widthOptions: { unit: [{ label: 'px', value: '' }] },
      lengthOptions: { unit: [{ label: 'px', value: '' }, { label: '%', value: '%' }] }
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    }
  },
  methods: {},
  mounted() {
  },
  created() {}
}
</script>

