<template>
  <div class="widget-config-item">
    <div class="name">{{ name }}</div>
    <div class="container">
      <el-button-group class="flex-button-group">
        <el-button
          size="mini"
          class="config-item-button"
          v-for="item in options"
          :key="item.value"
          :type="getType(item.value)"
          @click="value = item.value"
        >
          <span>
            {{ item.label }}
          </span>
          <el-tooltip
            v-if="item.tips"
            popper-class="tooltip-question"
            effect="dark"
            :content="item.tips"
            placement="top-start"
          >
            <i class="el-icon-question"></i>
          </el-tooltip>
        </el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
    },
    vue: {
      type: String,
    },
  },
  data() {
    return {
      options: [
        { label: '标准', value: 'default' },
        { label: '速度指标', value: 'speed' },
        { label: '进度指标', value: 'progress' }
      ],
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = this.addExtra(
          {
            ...this.elementProps,
            [this.propKey]: value,
          },
          value
        )
        this.$emit('update', res)
      },
    },
  },
  methods: {
    getType(value) {
      return this.elementProps[this.propKey] === value ? 'primary' : 'default'
    },
    // 这个地方产品要求帮助快速预设
    addExtra(obj, value) {
      const { customProp, yField_tpl } = require(`@/template/components/gauge-chart/${obj.elementVersion}/watch-props.js`)
      const defaultValues = Object.keys(customProp).reduce((result, key) => {
        // 过滤一些不需覆盖回的键
        if(!['showTitle', 'title', 'titleAlign', 'toolbox', 'type'].includes(key)) {
          result[key] = customProp[key]?.default instanceof Function ? customProp[key]?.default() : customProp[key]?.default
        }
        return result
      }, {})

      if (value === 'speed') {
        defaultValues.progress.use = true
        defaultValues.axisTick.use = false
        obj.dataVariables = obj.dataVariables.map((item) => {
          if (item.key === 'yAxis') {
            item.fields = item.fields.map((field) => {
              const preset = _.cloneDeep(yField_tpl)
              preset.titleShow = false
              preset.detailOffsetY = { unit: '%', value: 70 }
              preset.detailUseformatter = true
              preset.detailFormatter = '{value} km/s'
              return {
                ...preset,
                key: field.key,
                value: field.value
              }
            })
          }
          return item
        })
      } else if (value === 'default') {
        defaultValues.progress.use = false
        defaultValues.axisTick.use = true
        obj.dataVariables = obj.dataVariables.map((item) => {
          if (item.key === 'yAxis') {
            item.fields = item.fields.map((field) => {
              const preset = _.cloneDeep(yField_tpl)
              preset.titleShow = true
              preset.detailOffsetY = { unit: '%', value: 40 }
              preset.detailUseformatter = false
              return {
                ...preset,
                key: field.key,
                value: field.value
              }
            })
          }
          return item
        })
      } else if (value === 'progress') {
        defaultValues.startAngle.value = 180
        defaultValues.endAngle.value = 0
        defaultValues.centerY.value = 70
        defaultValues.axisTick.splitNumber = 2
        obj.dataVariables = obj.dataVariables.map((item) => {
          if (item.key === 'yAxis') {
            item.fields = item.fields.map((field) => {
              const preset = _.cloneDeep(yField_tpl)
              preset.titleShow = false
              preset.detailShow = false
              preset.detailOffsetY = { unit: '%', value: 40 }
              preset.detailUseformatter = false
              return {
                ...preset,
                key: field.key,
                value: field.value
              }
            })
          }
          return item
        })
      }
      return { ...obj, ...defaultValues }
    },
  },
}
</script>

