<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="选择数据集"
    :visible.sync="dialogVisible"
    width="1000px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <n-form
      class="data-select-form"
      label-position="top"
      :model="queryForm"
      size="mini"
      @submit.native.prevent
      v-loading.lock="loading"
    >
      <n-form-item>
        <div class="table-search">
          <el-select
            style='margin-right: 10px; width: 300px;'
            v-model="queryForm.condition.type"
            placeholder="请选择"
            @change='handleSearch(false)'
          >
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <el-input
            class="search-input"
            size="mini"
            v-model="queryForm.condition.name"
            placeholder="输入名称查询"
          >
          </el-input>
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-search"
            @click="handleSearch(false)"
          >
          </el-button>
        </div>
      </n-form-item>
      <n-form-item>
        <n-table
          :data="dataList"
          border
          highlight-current-row
          @row-click="handleRowClick"
        >
          <n-table-column width="50px" label="选择">
            <template slot-scope="scope">
              <el-radio
                v-if="selectedRow && selectedRow.id === scope.row.id"
              ></el-radio>
              <el-radio v-else :value='false'></el-radio>
            </template>
          </n-table-column>
          <n-table-column prop="name" label="名称"> </n-table-column>
          <n-table-column prop="description" label="描述"> </n-table-column>
          <n-table-column prop="typeLabel" label="类型"> </n-table-column>
        </n-table>
        <div class="pagination">
          <pagination
            :page="queryForm.pagination.pagenum"
            :limit="queryForm.pagination.pagesize"
            :total="totalCount"
            @pagination="handlePageChange"
          />
        </div>
      </n-form-item>
    </n-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import API from '@/api/application'
  import Pagination from '@/components/Pagination'

  export default {
    name: 'DataConfigSelect',
    components: {
      Pagination,
    },
    data() {
      return {
        dialogVisible: false,
        loading: false,
        totalCount: 0,
        queryForm: {
          pagination: {
            pagenum: 1,
            pagesize: 10,
          },
          condition: {
            name: '',
            type: 4,
            id: undefined,
          },
        },
        typeList: [
          {
            label: '静态数据',
            value: 1,
          },
          {
            label: '动态SQL',
            value: 2,
          },
          {
            label: '模板SQL',
            value: 3,
          },
          {
            label: '网关API',
            value: 4,
          },
          {
            label: '自定义API',
            value: 5,
          },
          {
            label: '外部API',
            value: 6,
          },
          {
            label: '自动化运维操作',
            value: 7,
          },
          {
            label: '自动化运维编排',
            value: 8,
          },
          {
            label: '自动化运维调度',
            value: 9,
          },
        ],
        dataList: [],
        selectedRow: null,
      }
    },
    methods: {
      open({ data, type }) {
        this.selectedRow = data
        this.queryForm.condition.type = this.getTypeNumber(type)
        this.queryForm.pagination.pagenum = 1
        if (type) {
          this.handleSearch(false)
        } else {
          this.handleSearch(true)
        }

        this.dialogVisible = true
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      confirm() {
        if (!this.selectedRow) {
          this.$message.error('请选择数据源')
          return
        }
        this.$emit('confirm', this.selectedRow)
        this.dialogVisible = false
      },
      handleSearch(reset) {
        this.loading = true
        if (reset) {
          this.queryForm = {
            pagination: {
              pagenum: 1,
              pagesize: 10,
            },
            condition: {
              name: '',
              type: 4,
              id: undefined,
            },
          }
        }
        this.$http({
          url: API.dataSetList,
          data: this.queryForm,
        })
          .then(({ data }) => {
            this.totalCount = data.rowCount
            this.dataList = data.records.map((item) => ({
              ...item,
              typeLabel: this.typeList.find((t) => t.value === item.type).label,
            }))
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleRowClick(row, column, event) {
        this.selectedRow = row
      },
      handlePageChange({ page, size }) {
        this.queryForm.pagination.pagenum = page
        this.queryForm.pagination.pagesize = size
        this.handleSearch(false)
      },
      getTypeNumber(type) {
        switch (type) {
          case 'script': return 7
          case 'orchestration': return 8
          case 'sncflower': return 9
          default:
            return type
        }
      }
    },
  }
</script>

<style lang="less" scoped>
  .data-select-form::v-deep {
    .table-search {
      flex: 1;
      display: flex;
      align-items: center;
      float: right;
      .search-input {
        margin-right: 10px;
        .el-input-group__prepend {
          min-width: 100px;
        }
      }
    }
    .pagination {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 10px;
    }
  }
</style>
