<template>
  <div class="variable-input">
    <div class="inner-content">
      <div class="title">
        <el-checkbox v-if="deleteMode" :indeterminate="isIndeterminate" v-model="isSelectAll" @change="handleCheckAllChange"></el-checkbox>
        <span class="col-title">子组件</span>
        <span class="key">渲染规则</span>
      </div>
      <div class="content" :style='{opacity: col.isHidden ? "0.5" : "1"}' v-for="(col, index) in value" :key="col.child">
        <el-checkbox v-if="deleteMode" v-model="selectedList" :label="col.child" @change="radioChange"></el-checkbox>
        <span class="label">{{ getNameById(col.child) }}</span>
        <span class="key">隐藏</span>
        <div class="caozuo">
          <el-button type="text" @click="handleEditColumn(index, col)"
            >编辑
          </el-button>
          <el-button type="text" @click="handleDeleteColumn(col.child)"
            >删除
          </el-button>
        </div>
      </div>
    </div>
    <template v-if="!deleteMode">
      <div style="display: flex; align-items: center;">
        <div
          class="add-btn-primary"
          @click="handleAddColumn"
        >
          +
        </div>
        <el-button style="margin-left: 20px;" size="mini" type="danger" @click="deleteMode = true">批量删除</el-button>
      </div>
    </template>
    <template v-else>
      <el-button size="mini" @click="deleteMode = false">取消</el-button>
      <el-button size="mini" type="danger" @click="doDelete">删除</el-button>
    </template>
    <el-dialog
      custom-class="low-code-dialog"
      :close-on-click-modal="false"
      :modal-append-to-body="true"
      :append-to-body="true"
      :title="variableForm.isAdd ? '新增' : '编辑'"
      :visible.sync="popoverVisible"
      width="1000px"
      top="5vh"
      :before-close="handleClose"
    >
      <div class="dialog-content">
        <n-form
          v-if="popoverVisible"
          ref="variableForm"
          :model="variableForm"
          label-width="95px"
          @submit.native.prevent
        >
          <n-form-item prop="child">
            <template slot='label'>
              <span>子组件</span>
              <el-tooltip content="选择一个子组件做条件渲染" popper-class="tooltip-question" placement="top">
                <i style='margin-left: 5px;' class="el-icon-question"></i>
              </el-tooltip>
            </template>
            <el-select v-model="variableForm.child">
              <el-option v-for="child in children" :key="child.elementId" :disabled="value.some(item => item.child === child.elementId)" :label="child.elementName" :value="child.elementId">
                <div style="float: left">{{ child.elementName }}</div>
                <div style="float: right; color: #8492a6; font-size: 12px">{{ child.elementId }}</div>
              </el-option>
            </el-select>
          </n-form-item>
          <n-form-item prop="rule">
            <template slot='label'>
              <span>渲染规则</span>
              <el-tooltip content="当条件通过时，当前选择的子组件会按照渲染规则进行渲染" popper-class="tooltip-question" placement="top">
                <i style='margin-left: 5px;' class="el-icon-question"></i>
              </el-tooltip>
            </template>
            <el-radio v-model="variableForm.rule" label="hidden">隐藏</el-radio>
          </n-form-item>
          <n-form-item prop="code">
            <template slot='label'>
              <span>条件配置</span>
              <el-tooltip content="可根据当前插槽注入的数据来判断" popper-class="tooltip-question" placement="top">
                <i style='margin-left: 5px;' class="el-icon-question"></i>
              </el-tooltip>
            </template>
          </n-form-item>
          <n-tabs v-model="variableForm.conditionType" type="card">
            <n-tab-pane disabled label="标准模式" name="normal">

            </n-tab-pane>
            <n-tab-pane lazy label="高级模式" name="function">
              <js-config ref='jsConfig' v-model='functionId' :arguments-addable="false" :pure-function='true' :default-arguments='defaultArguments'></js-config>
            </n-tab-pane>
          </n-tabs>
        </n-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="popoverVisible = false">取 消</el-button>
        <el-button type='primary' @click="handleConfirm">确 认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import _ from 'lodash'
  import { pageInject } from '@/renderer/tools/pageStore'
  import JsConfig from '@/components/JsConfig'
  import { FUNCTION_FLAG } from '@/config'
  import { getNodeByElementId, getParentNodeByElementId } from '@/utils/tree-utils'
  import args from './args'

  const columnData = {
    child: undefined,
    rule: 'hidden',
    conditionType: 'function',
    condition: [],
    functionId: ''
  }

  export default {
    components: {
      JsConfig
    },
    inject: ['dndOptions', ...pageInject],
    props: {
      elementId: {
        type: String,
      },
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        variableForm: {},
        popoverVisible: false,
        edtingIndex: undefined,
        deleteMode: false,
        isSelectAll: false,
        isIndeterminate: false,
        selectedList: [],
      }
    },
    computed: {
      currentElement() {
        return getNodeByElementId(this.getStore().layoutData, this.elementProps.elementId)
      },
      children() {
        return this.currentElement.children.map(child => this.getElementPropsById(child.elementId))
      },
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
        },
      },
      defaultArguments() {
        const parentNode = getParentNodeByElementId(this.getStore().layoutData, this.elementId)
        if (parentNode) {
          return args[parentNode.elementType] || []
        }
        return []
      },
      functionId: {
        get() { return this.variableForm.functionId.split(FUNCTION_FLAG)[1] },
        set(value) {
          this.variableForm.functionId = `${FUNCTION_FLAG}${value}`
        }
      }
    },
    methods: {
      doDelete() {
        this.value = this.value.filter(item => !this.selectedList.includes(item.child))
        this.handleCheckAllChange(false)
        this.deleteMode = false
      },
      handleCheckAllChange(val) {
        this.selectedList = val ? this.value.map(item => item.child) : []
        this.isIndeterminate = false;
      },
      radioChange() {
        let checkedCount = this.selectedList.length;
        this.isSelectAll = checkedCount === this.value.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.value.length;
      },
      handleEditColumn(index, value) {
        this.popoverVisible = true
        this.variableForm = _.cloneDeep({ ...value, isAdd: false })
        this.edtingIndex = index
      },
      handleDeleteColumn(child) {
        this.value = this.value.filter(item => item.child !== child)
      },
      handleAddColumn() {
        this.popoverVisible = true
        this.variableForm = _.cloneDeep(columnData)
        this.variableForm.isAdd = true
        this.edtingIndex = undefined
      },
      async handleConfirm() {
        if (!this.variableForm.child) {
          this.$message.error('请选择子组件！')
          return
        }
        if (this.variableForm.conditionType === 'function') {
          await this.$refs.jsConfig.confirm()
        }
        let tempValue = _.cloneDeep(this.value)
        const form = _.cloneDeep(this.variableForm)
        delete form.isAdd
        if (this.edtingIndex === undefined) {
          tempValue.push(form)
          const res = {
            ...this.elementProps,
            [this.propKey]: tempValue,
          }
          this.$emit('update', res)
        } else {
          tempValue[this.edtingIndex] = form
          const res = {
            ...this.elementProps,
            [this.propKey]: tempValue,
          }
          this.$emit('update', res)
        }
        this.popoverVisible = false
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      },
      getNameById(elementId) {
        return this.getElementPropsById(elementId).elementName
      }
    },
  }
</script>

