<template>
  <div ref="chart" class="chart-demo"></div>
</template>

<script>
import echarts from '@/lib/echarts'
export default {
  components: {},
  props: {
    type: {
      type: String,
      require: true,
    },
    data: {
      type: Object,
      require: true,
    },
    currentProps: {
      type: Object,
      require: true,
    },
    xField: {
      type: Array,
      require: true,
    },
    yField: {
      type: Array,
      require: true,
    },
  },
  data() {
    return {
      chartInstance: null,
    }
  },
  created() {
    this.init()
  },
  mounted() {},
  methods: {
    async init() {
      await this.$nextTick()
      this.chartInstance?.dispose()
      // 基于准备好的dom，初始化echarts实例
      // 未加载完依赖的话继续等待
      if(!echarts.instance) {
        await echarts.init()
      }
      this.chartInstance = echarts.instance.init(this.$refs.chart)
      const option = this.getOption()
      if(this.currentProps.type === 'horizontal') {
        option.xAxis.type = 'value'
        option.yAxis.type = 'category'
      }

      // 如果数据线第一项是数值的话, x坐标类型变数字, 为适应散点图四象限的模式
      if(option.dataset) {
        try {
          const {
            dimensions: [xKey],
            source: [firstDataItem],
          } = option.dataset
          if (typeof firstDataItem[xKey] === 'number') {
            option.xAxis.type = 'value'
          }
        } catch (err) {
          console.log(err)
        }
      }
      this.chartInstance.setOption(option)
    },
    getOption() {
      let {
        data,
        type,
        xField: [{ key: xFieldKey } = {}],
        yField,
        currentProps: { dataType, dynamicVariables }
      } = this
      // 接口配置的
      if (dataType === 'dynamic') {
        let legendName, legendData
        dynamicVariables.forEach(item => {
          switch (item.key) {
            case 'legendName':
              legendName = item.path[item.path.length - 1];
              break;
            case 'legendData':
              legendData = item.path[item.path.length - 1];
              break;
          }
        })
        xFieldKey = 'lowCode_xAxisFiledKey'
        let newYField = []
        data.forEach(item => {
          const name = item[legendName]
          // 没有图例名称
          if(!name) {
            return
          }
          // 相同key映射
          const mapField = yField.find(opt => opt.key === name) || {}
          newYField.push({
            ...mapField,
            key: name,
            value: mapField.value || name
          })
          // 构建长度为图例数据长度, key为图例名称, value为图例数据第二项 [{key: value}]的数组
          let i = 0;
          for (const key in item[legendData]) {
            const value = item[legendData][key]
              data[i] = data[i] || {}
            // 数组格式
            if(value instanceof Array) {
              data[i][xFieldKey] = value[0]
              data[i][name] = value[1]
            }
            // { key: value } 格式
            else {
              data[i][xFieldKey] = key
              data[i][name] = value
            }
            i++
          }
        })
        yField = newYField
      }
      // 有坐标轴的
      const hasCoord = ['line', 'bar', 'scatter'].includes(type)
      const option = {
        xAxis: hasCoord ? {
          name: 'X轴',
          type: 'category',
          axisLine: {
            show: true
          },
          axisTick: {
            show: true
          },
          splitLine: {
            show: true
          },
        } : undefined,
        yAxis: hasCoord ? {
          name: 'Y轴',
          type: 'value',
          axisLine: {
            show: true
          },
          axisTick: {
            show: true
          },
          splitLine: {
            show: true
          },
        } : undefined,
        series: yField.map(opt => ({
          ...opt,
          type: opt.type || type,
        })),
        legend: {
          show: true,
        },
      }
      if(['gauge'].includes(type)) {
        if(data.length) {
          option.series[0].data = yField.map(item => ({ name: item.value, value: data[0][item.key] }))
        }
      } else if(['scatter'].includes(type)) {
        // x坐标是数字时转为数字,因为散点图的四象限模式
        if (data[0] && typeof data[0][xFieldKey] === 'number') {
          option.xAxis.type = 'value'
        }
        // 数据
        option.series = option.series.map((item, index) => {
          const { key, value } = yField[index]
          return {
            ...item,
            // 图例名
            name: value || key,
            // 数据
            data: data?.slice(0, 20).map(opt => [opt[xFieldKey], opt[key]])
          }
        })
      } else if(['radar'].includes(type)) {
        option.series = [{
          data: yField.map(({ key,value, ...others }) => ({ value: data.map(item => item[key]), name: value || key, ...others })),
          type
        }]
        option.radar = {
          indicator: this.xField.map(({ name, max, useMax }) => {
            const result = { name }
            if(useMax) {
              result.max = max
            }
            return result
          })
        }
      } else {
        option.dataset = {
          dimensions: [
            xFieldKey,
            ...yField.map((item) => item.value || item.key),
          ],
          source: data?.slice(0, 20)?.map((item) => {
            const obj = {
              // 数据原本有的键
              ...item,
              [xFieldKey]: item[xFieldKey],
            }
            yField.forEach((opt) => {
              // 为了显示图例名称的键
              obj[opt.value || opt.key] = item[opt.key]
            })
            return obj
          }),
        }
      }
      return option
    },
  },

  computed: {
    watchField() {
      const { data, xField, yField } = this
      return { data, xField, yField }
    },
  },

  watch: {
    watchField: {
      deep: true,
      handler() {
        this.init()
      },
    },
  },
}
</script>
<style lang='less' scoped>
.chart-demo {
  width: 100%;
  height: 100%;
}
</style>