<template>
  <div>
    <n-form-item class="config-item">
      <template slot="label">
        <span>{{ name }}</span>
      </template>
      <el-select
        :value="value"
        filterable
        placeholder="请选择"
        @change="typeChange"
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="`${item.label} (${item.value})`"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </n-form-item>
    <template v-if="value">
      <n-form-item class="config-item">
        <template slot="label">
          <span>图表配置</span>
          <el-tooltip
            :content="'ANTV G2Plot 图表的配置, 详细配置请查阅文档 : \n' + documentUrl"
            popper-class="tooltip-question"
            placement="top"
          >
            <i style="margin-left: 5px" class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <div>
          <el-button type="primary" @click="chartConfigOpen">配置</el-button>
          <el-button type="info" @click="jumpUrl">查看文档</el-button>
          <jsFormat
            title="图表配置"
            ref="jsFormat"
            useType="chartConfig"
            @confirm="chartConfigConfirm"
          ></jsFormat>
        </div>
      </n-form-item>
      <n-form-item class="config-item" v-if="elementProps.chartConfigId">
        <template slot="label">
          <span>数据集</span>
        </template>
        <div>
          <el-button v-if="!dataInfo" type="primary" @click="dataOpen"
            >配置</el-button
          >
          <div v-else class="data-info-item">
            <el-button type="text" @click="dataOpen">{{ dataInfo }}</el-button>
          </div>
          <antvChartData
            :elementProps="elementProps"
            ref="antvChartData"
            @confirm="chartDataConfirm"
          ></antvChartData>
        </div>
      </n-form-item>
    </template>
  </div>
</template>

<script>
import antvChartData from '@/components/DataSelect/antvChartData'
import jsFormat from '@/components/jsFormat'
import { FUNCTION_FLAG, STATIC_PREFIX } from '@/config'
import { options } from './options'
import axios from 'axios'
import { pageInject } from '@/renderer/tools/pageStore'
import { getRandomHexadecimal } from '@/utils/tree-utils'
import { UPDATE_FUNCTIONS_DATA } from '@/config/storeKey'
import { copyText } from '@/utils'

export default {
  inject: [...pageInject],
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
    },
    vue: {
      type: String,
    },
  },
  data() {
    return {
      options
    }
  },
  components: {
    antvChartData,
    jsFormat
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
    },
    dataInfo() {
      const { apiRequest, sqlRequest, staticData } = this.elementProps
      switch (true) {
        case !!apiRequest:
          return 'API数据集'
        case !!sqlRequest:
          return 'SQL数据集'
        case !!staticData:
          return '静态数据集'
        default:
          return undefined
      }
    },
    documentUrl() {
      const { value } = this
      let str = ''
      // 驼峰命名转为小写, 以 '-' 分割
      Array.prototype.forEach.call(value, (item, index) => {
        if(/[A-Z]/.test(item)) {
          str += (index === 0 ? '' : '-') + item.toLocaleLowerCase()
        } else {
          str += item
        }
      })
      return `https://g2plot.antv.vision/zh/docs/api/plots/${str}`
    }
  },
  methods: {
    typeChange(value) {
      Promise.all([this.getConfig(value), this.getMockData(value)]).then(
        ([{ data: config }, { data: staticData }]) => {
          const functionId = getRandomHexadecimal(12)
          const res = {
            ...this.elementProps,
            [this.propKey]: value,
            chartConfigId: `${FUNCTION_FLAG}${functionId}`,
            apiRequest: undefined,
            sqlRequest: undefined,
            staticData,
            dataMapping: [
              {
                sourcePath: ['response', 'data'],
                mappingPath: ['Gauge', 'Liquid'].includes(value) ? ['percent'] : ['data'],
                formatCode: '',
              },
            ],
          }

          // 更新function
          const func = {
            id: functionId,
            name: 'func',
            code: config,
            paramsData: [
              {
                key: '',
              },
            ],
          }
          this._storeCommit(UPDATE_FUNCTIONS_DATA, {
            functionsData: [...this.getStore().functionsData, func],
          })

          this.$emit('update', res)
        }
      )
    },
    async getConfig(value) {
      try {
        return await axios({
          url: `${STATIC_PREFIX}/antv-chart-config/${value}.js`,
        })
      } catch (e) {
        return {}
      }
    },
    async getMockData(value) {
      try {
        return await axios({
          url: `${STATIC_PREFIX}/antv-chart-data/${value}.json`,
        })
      } catch (e) {
        return {}
      }
    },
    dataOpen() {
      this.$refs.antvChartData.open(this.elementProps)
    },
    chartConfigOpen() {
      this.$refs.jsFormat.open({
        id: this.elementProps.chartConfigId.replace(FUNCTION_FLAG, ''),
      })
    },
    chartConfigConfirm(id) {
      const res = {
        ...this.elementProps,
        chartConfigId: `${FUNCTION_FLAG}${id}`,
      }
      this.$emit('update', res)
    },
    chartDataConfirm(res) {
      this.$emit('update', {
        ...this.elementProps,
        ...res,
      })
    },
    jumpUrl() {
      window.open(this.documentUrl)
    }
  },
}
</script>

<style lang="less" scoped>
div.config-item::v-deep {
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 8px;
  margin-right: 0;
  .el-form-item__content {
    flex: 1;
    & > * {
      width: 100%;
    }
  }
  .el-form-item__label {
    min-width: 100px;
  }
}
</style>
