<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-select class="button-type-select" v-model="value">
      <el-option
        class="option-item"
        v-for="item in buttonTypes"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      >
        <el-button class="button-item" :type="item.value">{{
          item.label
        }}</el-button>
      </el-option>
    </el-select>
  </n-form-item>
</template>

<script>
  export default {
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        buttonTypes: [
          {
            label: '默认按钮',
            value: 'default',
          },
          {
            label: '文字按钮',
            value: 'text',
          },
          {
            label: '主要按钮',
            value: 'primary',
          },
          {
            label: '成功按钮',
            value: 'success',
          },
          {
            label: '信息按钮',
            value: 'info',
          },
          {
            label: '警告按钮',
            value: 'warning',
          },
          {
            label: '危险按钮',
            value: 'danger',
          },
        ],
      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
        },
      },
    },
  }
</script>

<style lang="less" scoped>
  div.config-item::v-deep {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    margin-right: 0;
    .el-form-item__label {
      min-width: 100px;
    }
    .el-form-item__content {
      flex: 1;
    }
    .button-type-select {
      width: 100%;
    }
  }
  .option-item {
    padding: 5px;
    height: auto;
    .button-item {
      width: 100%;
    }
  }
</style>
