import _ from  'lodash'
// import XLSX from 'xlsx'

export default {
  data() {
    return {
      rABS: false, // 是否 文件流转BinaryString
      excelVisible: false,
      excelData: [],
      uploadExcelDisable: true,
      excelLoading: false
    }
  },
  methods: {
    // excel导入
    excelFileUpload() {
      this.excelVisible = true
    },
    // 取消导入
    excelDataUploadCancel() {
      this.excelData = []
      this.excelVisible = false
    },
    async readExcel(file) {
      this.excelLoading = true
      const XLSX = await import(/* webpackChunkName: "xlsx" */ 'xlsx')
      let reader = new FileReader()
      reader.onload = (e) => {
        try {
          let data = e.target.result
          let workbook = null
          let sheetObj = {}
          if (this.rABS) {
            workbook = XLSX.read(btoa(this.fixdata(data)), {
              type: 'base64',
            })
          } else {
            workbook = XLSX.read(data, {
              type: 'binary',
            })
          }
          // for (let sheet in workbook.Sheets) {
          //   sheetObj[sheet] = XLSX.utils.sheet_to_json(workbook.Sheets[sheet])
          // }
          Object.keys(workbook.Sheets).forEach((key, index) => {
            if (index === 0) {
              const sheet = workbook.Sheets[key]
              sheetObj = XLSX.utils.sheet_to_json(sheet, { defval: '' })

              //取出第一行为头部的数据,把不为空的数据补全
              if (!_.isEmpty(sheetObj)) {
                let header = []
                let headerIndex = 0
                const addRange = XLSX.utils.decode_range(sheet['!ref'])
                for (let i = 0; i <= addRange.e.c; i++) {
                  header.push(XLSX.utils.format_cell(sheet[XLSX.utils.encode_col(i) + XLSX.utils.encode_row(headerIndex)]))
                }
                //
                let tempDataArray = []
                sheetObj.forEach((value) => {
                  let tempDataObject = {}
                  header.forEach((item) => {
                    tempDataObject[item] = value[item] !== undefined ? value[item] : ''
                  })
                  tempDataArray.push(tempDataObject)
                })
                sheetObj = tempDataArray
              }
              return true
            }
          })
          this.handleExcelData(sheetObj)
        } catch (e) {
          this.$message.warning('文件类型不正确或数据格式不正确,请重新修改上传')
          return false
        } finally {
          this.excelLoading = false
        }
      }
      if (this.rABS) {
        reader.readAsArrayBuffer(file.raw)
      } else {
        reader.readAsBinaryString(file.raw)
      }
    },
    fixdata(data) { //文件流转BinaryString
      let o = '',
        l = 0,
        w = 10240
      for (; l < data.byteLength / w; ++l) o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w, l * w + w)))
      o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)))
      return o
    },
  },
}
