<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="data-select-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="数据集配置"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="data-select" v-loading.lock='loading'>
      <data-set-config
        v-if='dialogVisible'
        ref='dataSetConfig'
        :request='{apiRequest, sqlRequest, staticData, optionsMode }'
        :type.sync='activeDataType'
        :is-hide-static="optionsMode"
        @response='handleResponse'
      ></data-set-config>
      <div class="data-select-item">
        <el-collapse class='config-collapse' v-model='activeCollapse'>
          <el-collapse-item name='config'>
            <template slot="title">
              <div class='config-collapse-title'>字段映射</div>
            </template>
            <div class="data-select-form data-content">
              <div class="data-config">
                <div class="data-table" :style='{width: activeDataType === "static" ? "100%" : "50%"}' v-loading='tableLoading'>
                  <el-collapse v-if='activeDataType !== "static"' class='config-collapse' v-model='activeDataCollapse'>
                    <el-collapse-item name='data'>
                      <template slot="title">
                        <div class='config-collapse-title'>变量映射</div>
                      </template>
                      <vxe-table
                        ref='dataVariablesTable'
                        size='mini'
                        :data="dataVariables"
                        border
                        style="width: 100%;"
                        header-cell-class-name="header-cell"
                      >
                        <vxe-table-column title="变量名">
                          <template #default='{ row }'>
                            <div class="variable-label">
                              <span class="label" :class="{ require: row.require }">{{ row.label }}</span>
                              <el-tooltip
                                v-if="row.desc"
                                effect="dark"
                                :content="row.desc"
                                placement="top"
                              >
                                <i class="el-icon-question"></i>
                              </el-tooltip>
                            </div>
                          </template>
                        </vxe-table-column>
                        <vxe-table-column field="key" title="编码"></vxe-table-column>
                        <vxe-table-column field="path" title="数据路径" min-width="150px">
                          <template #default='{ row }'>
                            <div class="drop-zone-path" :data-key="row.key">
                              <el-tooltip
                                v-if="row.path.length"
                                effect="dark"
                                :content="row.path.join('/')"
                                placement="top"
                              >
                                <el-tag
                                  closable
                                  @close="row.path = []"
                                  disable-transitions
                                  >{{ row.path[row.path.length - 1] }}</el-tag
                                >
                              </el-tooltip>
                              <span v-else>请从右侧拖拽节点</span>
                            </div>
                          </template>
                        </vxe-table-column>
                      </vxe-table>
                    </el-collapse-item>
                  </el-collapse>
                </div>
                <div class="data-result" v-if='activeDataType !== "static"'>
                  <div class="title">
                    <div class="name">
                      响应体
                      <el-button size='mini' type='primary' @click="showInputResponseDialog">手动录入</el-button>
                      <el-button size='mini' v-if='response' @click='handleClearResponse'>清除</el-button>
                    </div>
                  </div>
                  <div class="body">
                    <vue-json-pretty
                      v-show='response'
                      class="json-pretty no-select"
                      path="response"
                      :data="response"
                      :virtual='true'
                      :itemHeight='32'
                      v-model="selectedPath"
                      selectableType="single"
                      :showSelectController="true"
                      :selectOnClickNode="true"
                    >
                    </vue-json-pretty>
                    <div v-if='!response' class='empty-text'>暂无数据</div>
                    <input-dialog @confirm="handleInputResponse" ref="inputDialog"></input-dialog>
                  </div>
                </div>
              </div>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
      <div class="dnd-cover" v-show="dndCoverStyle" :style="dndCoverStyle"></div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" :loading='loading' @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import DataSetConfig from '@/components/DataSetConfig'
  import VueJsonPretty from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  import _ from 'lodash'
  import { isValidJsonString } from '@/utils/validateUtils'
  import {
    buildNewWidget,
    getNodeByElementId,
  } from '@/utils/tree-utils'
  import {
    DELETE_LAYOUT_DATA,
  } from '@/config/storeKey'
  import { getLastVersion } from '@/config/module'
  import API from '@/api/application'
  import { getObjectTree, getPathList, getPathString } from '@/utils/format'
  import dnd from '@/mixins/dragAndDrop'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    name: 'DataSelect',
    components: {
      VueJsonPretty,
      DataSetConfig,
    },
    mixins: [dnd],
    inject: ['dndOptions', ...pageInject],
    props: {
      useType: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        loading: false,
        activeCollapse: ['config'],
        activeDataCollapse: ['data', 'column'],
        dialogVisible: false,
        dataColumn: undefined,
        dataVariables: undefined,
        apiRequest: undefined,
        sqlRequest: undefined,
        staticData: undefined,
        selectedPath: undefined,
        responseOptions: [],
        response: undefined,
        dataListKeys: [],
        jsonLoading: false,
        tableLoading: false,
        activeDataType: ''
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      currentElement() {
        return getNodeByElementId(this.layoutData, this.dndOptions.selectedId)
      },
      currentProps() {
        return this.getElementPropsById(this.dndOptions.selectedId)
      },
      optionsMode() {
        return this.useType === 'options'
      },
    },
    methods: {
      open({ dataColumn, dataVariables, apiRequest, sqlRequest, staticData }) {
        this.dataColumn = dataColumn ? _.cloneDeep(dataColumn) : []
        this.dataVariables = dataVariables ? _.cloneDeep(dataVariables) : []
        this.staticData = _.cloneDeep(staticData) || undefined
        this.apiRequest = _.cloneDeep(apiRequest) || undefined
        this.sqlRequest = _.cloneDeep(sqlRequest) || undefined
        this.response = null
        this.selectedPath = undefined
        this.dialogVisible = true
        this.$nextTick(() => {
          this.handleInitDnD()
        })
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      handleDelete(elementId) {
        this._storeCommit(DELETE_LAYOUT_DATA, { elementId })
      },
      handleGenerateTableData() {
        const res = {
          dataColumn: this.dataColumn,
          dataVariables: this.dataVariables,
          apiRequest: this.apiRequest,
          sqlRequest: this.sqlRequest,
          staticData: this.staticData,
        }
        const preSlots = this.currentElement.children.filter(
          (item) =>
            ['slot'].includes(item.elementType) &&
            ['data'].includes(this.getElementPropsById(item.elementId).slotType)
        )
        const preSlotsKeys = preSlots.map(item => this.getElementPropsById(item.elementId).slotName)
        const dataColKeys = this.dataColumn.map(item => item.key)
        const deleteSlotKeys = preSlotsKeys.filter(key => !dataColKeys.includes(key))
        const deleteSlotIds = preSlots.filter(item => deleteSlotKeys.includes(this.getElementPropsById(item.elementId).slotName)).map(item => item.elementId)
        deleteSlotIds.forEach(elementId => this.handleDelete(elementId))
        const isUseSlot = !!this.currentProps.isUseSlot
        this.dataColumn.forEach((item) => {
          const slotName = item.key
          if (preSlotsKeys.includes(slotName)) {
            return
          }
          const widget = {
            elementId: '',
            elementType: 'slot',
            elementVersion: getLastVersion('slot'),
            elementName: item.label,
            defaultProps: {
              isVisible: isUseSlot,
              slotName,
              slotType: 'data',
              canDropInsideTypes: ['text', 'tag', 'href', 'button'],
            },
            children: [
              {
                elementId: '',
                elementType: 'text',
                elementVersion: getLastVersion('text'),
                elementName: '文本',
                children: [],
                defaultProps: {
                  useSlotProps: true,
                  prop: slotName,
                },
              },
            ],
          }
          buildNewWidget.call(this, widget, this.currentElement.elementId, 'center')
        })
        return res
      },
      async confirm() {
        const { apiRequest, sqlRequest, staticData } = this.$refs.dataSetConfig.getResult()
        this.apiRequest = apiRequest
        this.sqlRequest = sqlRequest
        this.staticData = staticData

        this.loading = true
        setTimeout(() => {
          const res = this.handleGenerateTableData()
          this.loading = false
          this.$emit('confirm', res)
          this.dialogVisible = false
        }, 100)
      },
      handleDataListPathChange(selectPath, key) {
        if (key === 'dataList') {
          const path = getPathString(selectPath)
          this.handleGenerateConfig(path)
        }
      },
      handleAutoGenerate() {
        this.jsonLoading = true
        const path = this.selectedPath.replace('response.', '')
        const dataList = this.dataVariables.find((d) => d.key === 'dataList')
        const pathList = getPathList(path)
        const parentPathList = pathList.slice(0, pathList.length - 1)
        const data = _.get(this.response, parentPathList) || {}
        this.dataVariables = this.dataVariables.map(item => {
          if (typeof data[item.key] !== 'undefined') {
            return {...item, path: parentPathList.concat(item.key)}
          }
          switch(true) {
            case item.key === 'totalCount':
              if (typeof data.rowCount !== 'undefined') {
                return {...item, path: parentPathList.concat('rowCount')}
              }
              if (typeof data.total !== 'undefined') {
                return {...item, path: parentPathList.concat('total')}
              }
          }
          return item
        })
        if (dataList) {
          dataList.path = pathList
        }
        this.handleGenerateConfig(path)
      },
      async handleGenerateConfig(path) {
        const target = _.get(this.response, path)
        let res
        switch (true) {
          case typeof target === 'string':
            if (isValidJsonString(target)) {
              try {
                res = JSON.parse(target.trim())[0]
              } catch (e) {
                console.log(e)
              }
            }
            break
          case Array.isArray(target) && _.isObject(target[0]):
            res = target[0]
            break
        }
        this.jsonLoading = false
        if (!res) {
          this.$confirm('数据解析失败，请手动映射', '提示', {
            showCancelButton: false,
          })
          return
        }
        const pathList = getPathList(path)
        this.dataListKeys = Object.keys(res)
        if (this.dataColumn.length) {
          await this.$confirm(
            '此操作将覆盖数据列映射的配置，是否继续？',
            '提示'
          )
        }
        this.tableLoading = true
        this.$http({
          url: API.appDesignJsonDataConfigSave,
          data: {
            appDesignId: Number(this.$route.query.vid),
            preferences: [
              {
                key: `${this.currentElement.elementId}.$currentRow`,
                value: JSON.stringify(res) || ''
              }
            ]
          }
        }).then(() => {
          this.dataColumn = this.dataListKeys.map((key) => ({
            label: `${key}`,
            key,
            align: 'left',
            width: undefined,
            minWidth: '160',
            sortable: false,
            isHidden: false,
            path: [...pathList, '0', key],
          }))
        }).finally(() => {
          this.tableLoading = false
        })
      },
      handleResponse({ response, responseOptions, isStatic }) {
        if (isStatic) {
          this.handleStaticResponse({response, responseOptions})
          return
        }
        this.response = response
        this.responseOptions = responseOptions
      },
      async handleStaticResponse({ response, responseOptions }) {
        if (!response) {
          return
        }
        if (!Array.isArray(response)) {
          this.$message.error('数据格式不正确')
          return
        }
        const res = response[0]
        if (!_.isObject(res)) {
          this.$message.error('数据格式不正确')
          return
        }
        if (this.dataColumn.length) {
          await this.$confirm(
            '此操作将覆盖数据列映射的配置，是否继续？',
            '提示'
          )
        }
        const dataList = this.dataVariables.find((d) => d.key === 'dataList')
        if (dataList) {
          dataList.path = []
          const pathList = []
          this.dataListKeys = Object.keys(res)
          this.dataColumn = this.dataListKeys.map((key) => ({
            label: `${key}`,
            key,
            align: 'left',
            width: undefined,
            minWidth: '160',
            sortable: false,
            isHidden: false,
            path: [...pathList, '0', key],
          }))
        }
        this.staticData = _.cloneDeep(response)
      },
      handleDrop(event) {
        const { key } = event.target.dataset
        const current = this.dataVariables.find((item) => item.key === key)
        const path = getPathList(this.selectedPath)
        current.path = path.slice(1)
      },
      handleInputResponse(value) {
        this.response = value
        this.responseOptions = getObjectTree(value)
      },
      showInputResponseDialog() {
        this.$refs.inputDialog.open({type: 'object', data: this, key: 'response'})
      },
      handleClearResponse() {
        this.response = null
        this.responseOptions = []
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      }
    },
  }
</script>

