<template>
  <div class='form-config'>
    <div class='config-item' v-if='!dataInfo'>
      <div class='label'>表单类型</div>
      <div class='value'>
        <el-button-group>
          <el-button size='mini' :type='formType === "static" ? "primary" : ""' @click='formType = "static"'>静态表单</el-button>
          <el-button size='mini' :type='formType === "dynamic" ? "primary" : ""' @click='formType = "dynamic"'>动态表单</el-button>
        </el-button-group>
      </div>
    </div>
    <div class='config-item' v-if='!dataInfo'>
      <div class='label'>{{ formType === 'static' ? '组件生成' : '动态数据' }}</div>
      <div class='value'>
        <el-button size='mini' type='primary' :loading='loading' @click='handleShowConfig(formType)'>{{ formType === 'static' ? '配置' : '数据集' }}</el-button>
      </div>
    </div>
    <div class='config-item' v-else>
      <div class='label'>动态数据</div>
      <div class='value'>
        <el-button type='text' :loading='loading' @click='handleShowConfig("dynamic")'>{{ dataInfo }}</el-button>
        <div class='data-tools' @click='handleDelete'>
          <i class='iconfont icon-delete'></i>
        </div>
      </div>
    </div>
    <static-form ref='staticForm'></static-form>
    <dynamic-form ref='dynamicForm' @confirm='handleConfirm'></dynamic-form>
  </div>
</template>

<script>
  import staticForm from '@/components/DataSelect/staticForm'
  import dynamicForm from '@/components/DataSelect/dynamicForm'
  export default {
    components: {
      staticForm,
      dynamicForm
    },
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      optionkey: {
        type: String,
      },
    },
    data() {
      return {
        loading: false,
        formType: 'static'
      }
    },
    computed: {
      dataInfo() {
        const { apiRequest, sqlRequest, staticData } = this.elementProps
        switch (true) {
          case !!apiRequest:
            return 'API数据集'
          case !!sqlRequest:
            return 'SQL数据集'
          case !!staticData:
            return '静态数据集'
          default:
            return undefined
        }
      }
    },
    methods: {
      handleShowConfig(type) {
        const refKey = type === 'static' ? 'staticForm' : 'dynamicForm'
        this.loading = true
        setTimeout(() => {
          this.$refs[refKey].open({
            dataVariables: this.elementProps.dataVariables,
            apiRequest: this.elementProps.apiRequest,
            sqlRequest: this.elementProps.sqlRequest,
            staticData: this.elementProps.staticData,
          })
          setTimeout(() => {
            this.loading = false
          }, 100)
        }, 100)

      },
      handleConfirm(res) {
        this.$emit('update', {
          ...this.elementProps,
          ...res,
        })
      },
      handleDelete() {
        const res = {
          apiRequest: undefined,
          sqlRequest: undefined,
          staticData: undefined,
          dataVariables: this.elementProps.dataVariables?.map(item => ({...item, path: []})),
        }
        this.$emit('update', {
          ...this.elementProps,
          ...res,
        })
      }
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .form-config::v-deep {
    div.config-item {
      display: flex;
      align-items: center;
      margin-bottom: 8px;
      .label {
        width: 100px;
        font-size: 12px;
        color: #48576a;
      }
      .value {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        .data-tools {
          margin-left: 10px;
          &:hover {
            cursor: pointer;
            color: red;
          }
        }
      }
    }
  }
</style>
