<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-switch v-model="isChecked" @change="switchChange"></el-switch>
    <div class="fill-div"></div>
    <el-popover v-if="value" placement="right" width="250" trigger="click">
      <el-form class="setting-form" label-position="left" label-width="100px">
        <el-form-item label="排布方式">
          <el-radio-group v-model="value.orient">
            <el-radio-button label="horizontal">横向</el-radio-button>
            <el-radio-button label="vertical">纵向</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="起止范围">
          <el-slider v-model="sliderValue" range :max="100" @change="sliderChange"></el-slider>
        </el-form-item>
      </el-form>
      <i slot="reference" class="el-icon-s-tools"></i>
    </el-popover>
  </n-form-item>
</template>

<script>
export default {
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      sliderValue: [0, 20]
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
    isChecked() {
      return !!this.value
    }
  },
  methods: {
    switchChange(value) {
      this.value = value
        ? {
            orient: 'horizontal',
            start: 0,
            end: 20
          }
        : false
    },
    sliderChange(val) {
      const [start, end] = val
      this.value.start = start
      this.value.end = end
    }
  },
  watch: {
    value(val) {
      if (val) {
        const { start, end } = val
        this.sliderValue = [start, end]
      } else {
        this.sliderValue = [0, 20]
      }
    }
  }
}
</script>

