<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <div class='content'>
      <el-tag class='tag'>{{ getElementName }}</el-tag>
      <el-tag class='tag'>{{ elementProps.elementVersion }}</el-tag>
    </div>
  </n-form-item>
</template>

<script>
import { getPropsValues } from '@/renderer/tools/config'
import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    inject: [...pageInject],
    props: {
      defaultValue: {
        type: [Object, String, Number],
      },
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    computed: {
      getElementName() {
        const { elementType, elementVersion } = this.elementProps
        const { elementName } = getPropsValues.call(this, { elementType, elementVersion })
        return elementName
      }
    }
  }
</script>

<style lang="less" scoped>
  div.config-item::v-deep {
    margin-bottom: 8px;

    .el-form-item__label {
      min-width: 100px;
    }

    .content {
      display: flex;
      align-items: center;
      .tag {
        margin-right: 5px;
      }
    }
  }
</style>
