<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-switch v-model="isChecked" @change="switchChange"></el-switch>
    <div class="fill-div"></div>
    <el-popover
      popper-class="setting-popover"
      v-if="value"
      placement="right"
      width="300"
      trigger="click"
    >
      <el-form class="setting-form" label-position="left" label-width="120px">
        <el-form-item  v-if="optionkey === 'showTrigger'" label="数据提示触发">
          <el-radio-group v-model="value.trigger">
            <el-radio-button label="item">数据线</el-radio-button>
            <el-radio-button label="axis">坐标轴</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="格式化">
          <el-switch v-model="useFormat" @change="formatChange"></el-switch>
        </el-form-item>
        <el-form-item v-if="useFormat" label="格式化内容">
          <el-tooltip popper-class="tooltip-question" effect="dark" :content="formatTips" placement="top-start">
            <i class="el-icon-question"></i>
          </el-tooltip>
          <el-input
            v-model="value.formatter"
            placeholder="输入格式化内容"
          ></el-input>
        </el-form-item>
      </el-form>
      <i slot="reference" class="el-icon-s-tools"></i>
    </el-popover>
  </n-form-item>
</template>

<script>
import { formatTips } from '@/config'
export default {
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
      default: 'showTrigger'
    },
  },
  data() {
    return {
      formatTips,
      useFormat: false
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
    isChecked() {
      return !!this.value
    },
  },
  watch: {
    'value.formatter'(val){
      this.useFormat = !!val
    }
  },
  methods: {
    switchChange(value) {
      this.value = value
        ? {
            trigger: 'item',
            formatter: '',
          }
        : false
    },
    formatChange() {
      this.value.formatter = ''
    },
  },
  mounted() {
  },
}
</script>

