<template>
  <div>
    <n-form-item class="config-item" label="使用指引">
      <div>
        <el-button size="mini" @click="download">下载模板和教程 <i class="el-icon-download"></i></el-button>
      </div>
    </n-form-item>
    <n-form-item class="config-item" label="加载模式">
      <el-switch
        v-model="type"
        active-text="外部"
        inactive-text="内部"
        active-value="outer"
        inactive-value="inner">
      </el-switch>
    </n-form-item>
    <n-form-item v-if="type === 'inner'" class="config-item">
      <template slot="label">
        {{name}}<n-tooltip :value="tips1"></n-tooltip>
      </template>
      <el-select v-model="value" filterable placeholder="请选择">
        <el-option
          v-for="item in appList"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </n-form-item>
    <n-form-item v-else-if="type === 'outer'" class="config-item" :error="appUrlError" :show-message="false">
      <template slot="label">
        组件url<n-tooltip :value="tips1" style="margin-left: 27px"></n-tooltip>
      </template>
      <el-select
        v-model.trim="appUrl"
        allow-create
        placeholder="请输入或选择url"
        filterable
        clearable
      >
        <el-option
          v-for="item in urlHistory"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
    </n-form-item>
    <n-form-item v-if="source" class="config-item">
      <template slot="label">
        模块名称<n-tooltip :value="tips2" style="margin-left: 17px"></n-tooltip>
      </template>
      <el-select
        v-model="moduleName"
        v-loading="componentsLoading"
        filterable
        placeholder="请选择"
        @change="initSetting"
      >
        <el-option
          v-for="item in componentsOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </n-form-item>
    <template v-if="source && moduleName">
      <n-form-item class="config-item"  label="参数配置">
        <div>
          <el-button
            v-if="propsData.length"
            type="primary"
            @click="setPropsInitVal"
            >设置初始值</el-button
          >
          <el-button
            icon="el-icon-refresh-right"
            @click="refreshSetting"
            >更新</el-button
          >
        </div>
      </n-form-item>
      <div class="props-wrap">
        <n-table border :data="propsData" style="width: 100%">
          <n-table-column
            v-for="column in columns1"
            :key="column.key"
            :prop="column.key"
            :label="column.name"
            width="auto"
          >
            <template slot-scope="scope">{{
              column.format
                ? column.format(scope.row, column.key)
                : scope.row[column.key] !== undefined
                ? scope.row[column.key]
                : '--'
            }}</template>
          </n-table-column>
        </n-table>
      </div>
    </template>
    <el-dialog
      custom-class="low-code-dialog"
      :close-on-click-modal="false"
      width="1000px"
      title="参数配置"
      class="props-setting-dialog"
      :visible.sync="paramsDialogVisible"
    >
      <n-table class="props-setting-table" border :data="dialogPropsData" style="width: 100%">
        <n-table-column
          v-for="column in columns2"
          :key="column.key"
          :prop="column.key"
          :label="column.name"
          :width="column.width || auto"
        >
          <template slot-scope="scope">
            <template v-if="column.key === 'defaultValue'">
              <value-input
                :value="scope.row.defaultValue.value"
                :type="scope.row.defaultValue.type"
                @showInputDialog="
                  currentIndex = scope.$index
                  showInputDialog(...$event)
                "
                @remove="scope.row.defaultValue.value = undefined"
              ></value-input>
            </template>
            <template v-else-if="column.key === 'desc'">
              <el-input
                v-model="scope.row.desc"
                placeholder="请输入参数描述"
              ></el-input>
            </template>
            <template v-else>
              {{
                column.format
                  ? column.format(scope.row, column.key)
                  : scope.row[column.key] !== undefined
                  ? scope.row[column.key]
                  : '--'
              }}
            </template>
          </template>
        </n-table-column>
      </n-table>
      <input-dialog
        @confirm="handleInputResponse"
        ref="inputDialog"
      ></input-dialog>
      <div slot="footer" class="dialog-footer">
        <el-button @click="paramsDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirm">确 认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { appList } from '@/lib/business'
import { resolveEvents, resolveProps } from '@/renderer/tools/config'
import valueInput from '@/components/valueInput'
import elementEditor from '@/mixins/elementEditor'
import EventBus from '@/renderer/tools/eventBus'
import { BIND_EVENTS } from '@/config/eventKey'
import _ from 'lodash'
import { debounce } from '@/utils'
import trigger from '@/renderer/tools/trigger'
import { STATIC_PREFIX } from '@/config'
export default {
  mixins: [elementEditor],
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
    },
    vue: {
      type: String,
    },
    tip: {
      type: String,
    },
  },
  data() {
    return {
      appList,
      componentsOptions: [],
      componentsLoading: false,
      appUrlError: false,
      columns1: [
        {
          name: '参数名',
          key: 'name',
        },
        {
          name: '参数描述',
          key: 'desc',
        },
        {
          name: '是否必传',
          key: 'required',
          format(row, key) {
            const value = row[key]
            return value !== undefined ? value : false
          },
        },
      ],
      columns2: [
        {
          name: '参数名',
          key: 'name',
        },
        {
          name: '参数描述',
          key: 'desc',
        },
        {
          name: '参数类型',
          key: 'type',
        },
        {
          name: '是否必传',
          key: 'required',
          format(row, key) {
            const value = row[key]
            return value !== undefined ? value : false
          },
        },
        {
          name: '默认值',
          key: 'default',
        },
        {
          name: '初始值',
          key: 'defaultValue',
          width: '260px',
        },
      ],
      paramsDialogVisible: false,
      currentIndex: -1,
      dialogPropsData: [],
      tips1: '按es module规范暴露组件的js脚本',
      tips2: '组件模块支持按需加载, 建议使用\n\nexport const xx = () => import(\'xxx\')',
    }
  },
  components: {
    valueInput,
  },
  computed: {
    type: {
      get() {
        return this.elementProps['type']
      },
      set(value) {
        const res = {
          ...this.elementProps,
          type: value,
          moduleName: '',
          propsData: [],
        }
        this.$emit('update', res)
      },
    },
    appUrl: {
      get() {
        return this.elementProps['appUrl']
      },
      set(value) {
        const urlHistory = _.cloneDeep(this.elementProps.urlHistory) || []
        if(value && !urlHistory.includes(value)) {
          urlHistory.unshift(value)
          if(urlHistory.length > 10) {
            urlHistory.pop()
          }
        }
        const res = {
          ...this.elementProps,
          appUrl: value,
          urlHistory
        }
        this.$emit('update', res)
      }
    },
    urlHistory() {
      return this.elementProps.urlHistory
    },
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = {
          ...this.elementProps,
          [this.propKey]: value,
          moduleName: '',
          propsData: [],
        }
        this.$emit('update', res)
      },
    },
    moduleName: {
      get() {
        return this.elementProps['moduleName']
      },
      set(value) {
        if (value !== this.moduleName) {
          const res = {
            ...this.elementProps,
            ['moduleName']: value,
            propsData: [],
            eventsData: [],
          }
          this.$emit('update', res)
        }
      },
    },
    propsData: {
      get() {
        return this.elementProps['propsData']
      },
    },
    componentInstance() {
      const { source, moduleName, componentsOptions } = this
      if (!source || !moduleName || !componentsOptions.length) {
        return {}
      }
      return componentsOptions.find((item) => item.value === moduleName)
        .instance
    },
    source() {
      return {
        inner: this.value,
        outer: this.appUrl
      }[this.type]
    }
  },
  watch: {
    source: {
      immediate: true,
      deep: true,
      handler: debounce(function (val) {
        if (val) {
          this.componentsLoading = true
          this.componentsOptions = []
          window.System.import(val).then((res) => {
            this.componentsLoading = false
            this.componentsOptions = Object.keys(res).map((key) => ({
              label: key,
              value: key,
              instance: res[key],
            }))
            this.appUrlError = false
          }).catch(err => {
            this.componentsLoading = false
            this.appUrlError = true
            trigger.recordBug({
              message: '加载自定义组件失败, 请确定“组件url”是否可访问并配置了跨域信息',
              error: err,
              target: this.elementProps,
              clickType: 'props'
            })
          })
        }
      }, 500),
    },
  },
  methods: {
    download() {
      window.open(`${STATIC_PREFIX}/business_example.zip`)
    },
    refreshSetting() {
      this.$confirm(
        '重新解析组件所有的参数和事件名,但会覆盖目前修改后的名字, 是否继续?',
        '提示',
        {
          closeOnClickModal: false,
          confirmButtonText: '继续',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).then(() => {
        this.initSetting(true)
        this.$message.success('已更新');
      })
    },
    setPropsInitVal() {
      this.dialogPropsData = _.cloneDeep(this.propsData)
      this.paramsDialogVisible = true
    },
    confirm() {
      const res = {
        ...this.elementProps,
        propsData: this.dialogPropsData,
        props: this.getProps(this.dialogPropsData),
      }
      this.$emit('update', res)
      this.paramsDialogVisible = false
    },
    async initSetting(pushHistory = false) {
      await this.$nextTick()

      // 解析参数表 和 解析events
      const [propsData, eventsData] = await Promise.all([resolveProps(this.componentInstance), resolveEvents(this.componentInstance)])

      const res = {
        ...this.elementProps,
        propsData,
        eventsData,
        props: this.getProps(propsData),
      }
      this.$emit('update', res, { pushHistory })
      this.$nextTick(() => {
        EventBus.$emit(BIND_EVENTS)
      })
    },
    // 根据propsData获取 props初始值
    getProps(propsData) {
      const result = propsData.reduce((obj, item) => {
        const { type, value } = item.defaultValue

        obj[item.name] =
          type === 'function'
            ? (() => {
                let val
                try {
                  // todo: js 沙箱机制
                  val = new Function(value)()
                } catch (error) {
                  val = value
                  console.warn(error)
                }
                return val
              })()
            : value
        return obj
      }, {})
      return result
    },
    handleInputResponse(value, type) {
      this.dialogPropsData[this.currentIndex].defaultValue = {
        value,
        type,
      }
    },
  },
}
</script>

<style lang="less" scoped>
div.config-item::v-deep {
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 8px;
  margin-right: 0;
  .el-form-item__content {
    flex: 1;
    & > * {
      width: 100%;
    }
  }
  .el-form-item__label {
    min-width: 100px;
    .el-tooltip.el-icon-question {
      margin-left: 5px;
      position: relative;
      z-index: 1;
    }
  }
  .props-wrap {
    overflow-x: auto;
    overflow-y: auto;
  }
}

.props-setting-dialog::v-deep {
  .el-table {
    user-select: text;
  }
}
</style>
