<template>
  <div class="widget-config-item">
    <div
      :class="{
        name: true,
        required: requiredArr.includes(name),
      }"
    >
      {{ name }}
    </div>
    <n-form-item class="config-item" label="" :prop="propKey">
      <Upload v-model='value' :action="uploadAction" title="点击上传图片"></Upload>
      <el-button style="margin-left: 10px;" @click="$refs.imageSelect.open()">图片库选择</el-button>
    </n-form-item>
    <ImageSelect ref="imageSelect" @confirm="v => value = v" />
  </div>
</template>

<script>
  import Upload from '@/components/Upload'
  import ImageSelect from '@/components/SourceManage/imageSelect.vue'
import { uploadPrefix } from '@/config/code-var'
  export default {
    components: { Upload, ImageSelect },
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        requiredArr: [],
        uploadAction: `${uploadPrefix}/${this.$route.query.appId}/images`
      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(n) {
          const res = { ...this.elementProps, [this.propKey]: n }
          this.$emit('update', res)
        }
      },
    },
  }
</script>

