<template>
  <div>
    <n-form-item class="config-item" :label="name" :prop="propKey">
      <el-radio-group v-model="value.type" @change="typeChange">
        <el-radio-button :label="0">纯色</el-radio-button>
        <el-radio-button :label="1">自定义颜色</el-radio-button>
      </el-radio-group>
    </n-form-item>
    <n-form-item class="config-item color-setting" label="颜色选择">
      <template v-if="value.type === 0">
        <el-color-picker v-model="value.color"></el-color-picker>
      </template>
      <template
        v-if="value.type === 1 && value.colorRange && value.colorRange.length"
      >
        <div
          class="color-select-list"
          v-for="(item, index) in value.colorRange"
          :key="index"
        >
          <el-input-number
            v-model="item[0]"
            :precision="1"
            :step="0.1"
            :max="getMax(index)"
            :min="getMin(index)"
          ></el-input-number>
          <el-color-picker v-model="item[1]"></el-color-picker>
          <i
            class="el-icon-remove"
            v-if="
              value.colorRange.length !== 1 &&
              index === value.colorRange.length - 1
            "
            @click="value.colorRange.splice(index, 1)"
          ></i>
          <el-popover
            v-if="
              value.colorRange.length - 1 === index &&
              !value.colorRange.some((item) => item[0] === 1)
            "
            placement="left-start"
            width="250"
          >
            <el-form label-position="left" label-width="80px">
              <el-form-item class="flex-item" label="颜色范围">
                <el-input-number
                  v-model="editItem[0]"
                  :precision="1"
                  :step="0.1"
                  :min="value.colorRange[value.colorRange.length - 1][0]"
                  :max="1"
                ></el-input-number>
              </el-form-item>
              <el-form-item class="flex-item" label="颜色取值">
                <el-color-picker v-model="editItem[1]"></el-color-picker>
              </el-form-item>
            </el-form>
            <div style="text-align: right; margin: 0">
              <el-button
                size="mini"
                type="text"
                @click="() => $refs['openBtn'].$el.click()"
                >取消</el-button
              >
              <el-button type="primary" size="mini" @click="confirm"
                >确定</el-button
              >
            </div>
            <el-button
              ref="openBtn"
              slot="reference"
              type="text"
              @click="initEditItem"
              ><i class="el-icon-circle-plus"></i
            ></el-button>
          </el-popover>
        </div>
      </template>
    </n-form-item>
  </div>
</template>

<script>
import rangeSelector from '@/components/rangeSelector'
import _ from 'lodash'
import { formatTips } from '@/config'
export default {
  components: {
    rangeSelector,
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      editItem: [],
      formatTips,
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
  },
  methods: {
    typeChange(type) {
      const { colorRange = [] } = this.value
      if (type === 1 && !colorRange.length) {
        this.value.colorRange = [[1, '#E6EBF8']]
      }
    },
    initEditItem() {
      this.editItem = [1, '#E6EBF8']
    },
    getMax(index) {
      const next = this.value.colorRange[index + 1]
      return next ? next[0] : 1
    },
    getMin(index) {
      const prev = this.value.colorRange[index - 1]
      return prev ? prev[0] : 0
    },
    confirm() {
      this.value.colorRange.push(_.cloneDeep(this.editItem))
      this.$nextTick(() => {
        const ref =  this.$refs['openBtn']
        ref && ref[0] && ref[0].$el.click()
      })
    },
  },
  mounted() {},
  created() {},
}
</script>

