<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <default-time-picker :value.sync="value" :placeholder="placeholder"></default-time-picker>
  </n-form-item>
</template>

<script>
import DefaultTimePicker from '@/components/defaultTimePicker'
export default {
  components: {
    DefaultTimePicker,
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
  },
}
</script>

