<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="data-select-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="数据集配置"
    :visible.sync="dialogVisible"
    width="1200px"
    top="30px"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="data-select" v-loading.lock="loading">
      <data-set-config
        v-if="dialogVisible"
        ref="dataSetConfig"
        :request="{ apiRequest, sqlRequest, staticData }"
        :type.sync="activeDataType"
        :clear-static="false"
        @response="handleResponse"
        @tab-click="handleTabClick"
      ></data-set-config>
      <div class="data-select-item">
        <el-collapse class="config-collapse" v-model="activeCollapse">
          <el-collapse-item name="config">
            <template slot="title">
              <div class="config-collapse-title">字段映射</div>
            </template>
            <chart-field-selector
              ref="chartFieldSelector"
              :data="responseData"
              :currentProps="elementProps"
              :dynamicVariables="dynamicVariables"
              :staticData="staticData"
              :activeDataType="activeDataType"
              :dataVariables="dataVariables"
            />
          </el-collapse-item>
        </el-collapse>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" :loading="loading" @click="confirm"
        >确 定</el-button
      >
    </div>
  </el-dialog>
</template>

<script>
import chartFieldSelector from '@/components/chartFieldSelector'
import DataSetConfig from '@/components/DataSetConfig'
import VueJsonPretty from '@/lib/vue-json-pretty'
import 'vue-json-pretty/lib/styles.css'
import _ from 'lodash'
import {
  getNodeByElementId,
} from '@/utils/tree-utils'
import { pageInject } from '@/renderer/tools/pageStore'
export default {
  name: 'DataSelect',
  components: {
    VueJsonPretty,
    chartFieldSelector,
    DataSetConfig,
  },
  inject: ['dndOptions', ...pageInject],
  props: {
    useType: {
      type: String,
      default: '',
    },
    elementProps: {
      type: Object
    },
  },
  data() {
    return {
      loading: false,
      activeCollapse: ['config'],
      dialogVisible: false,
      dataVariables: undefined,
      apiRequest: undefined,
      sqlRequest: undefined,
      staticData: undefined,
      dynamicVariables: [],
      responseOptions: [],
      response: undefined,
      activeDataType: 'api',
    }
  },
  computed: {
    responseData() {
      return this.elementProps.dataType === 'dynamic' && this.activeDataType === 'static' ? this.staticData : this.response
    },
    layoutData() {
      return this.getStore().layoutData
    },
    currentElement() {
      return getNodeByElementId(this.layoutData, this.dndOptions.selectedId)
    }
  },
  methods: {
    handleTabClick() {
      if(this.elementProps.dataType === 'dynamic') {
        this.dynamicVariables.forEach(item => {
          item.path = []
        })
      }
    },
    open({ dataVariables, apiRequest, sqlRequest, staticData, dynamicVariables }) {
      this.dataVariables = dataVariables ? _.cloneDeep(dataVariables) : []
      this.staticData = _.cloneDeep(staticData) || undefined
      this.apiRequest = _.cloneDeep(apiRequest) || undefined
      this.sqlRequest = _.cloneDeep(sqlRequest) || undefined
      this.dynamicVariables = _.cloneDeep(dynamicVariables) || undefined
      this.response = null
      this.dialogVisible = true
    },
    handleClose(done) {
      this.dialogVisible = false
      done()
    },
    cancel() {
      this.dialogVisible = false
    },
    async confirm() {
      const {
        apiRequest,
        sqlRequest,
        staticData,
      } = this.$refs.dataSetConfig.getResult()
      this.apiRequest = apiRequest
      this.sqlRequest = sqlRequest
      this.staticData = staticData
      let dataVariables = _.cloneDeep(this.dataVariables)
      if(this.elementProps.dataType !== 'dynamic') {
        dataVariables = await this.$refs.chartFieldSelector.getValue()
      }
      this.loading = true
      const res = {
        dataVariables,
        apiRequest,
        sqlRequest,
        staticData,
        dynamicVariables: this.dynamicVariables
      }
      this.loading = false
      this.$emit('confirm', res)
      this.dialogVisible = false
    },
    handleResponse({ response, responseOptions, isStatic }) {
      if (isStatic) {
        this.handleStaticResponse({ response, responseOptions })
        return
      }
      this.response = response
      this.responseOptions = responseOptions
      this.$refs.chartFieldSelector.init()
    },
    async handleStaticResponse({ response, responseOptions }) {
      if (!response) {
        return
      }
      if (!Array.isArray(response)) {
        this.$message.error('数据格式不正确')
        return
      }
      const res = response[0]
      if (!_.isObject(res)) {
        this.$message.error('数据格式不正确')
        return
      }
      this.staticData = _.cloneDeep(response)
      this.$refs.chartFieldSelector.init()
    },
  },
}
</script>

