<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-input
      style="width: 100%"
      type="textarea"
      :rows="3"
      :placeholder="placeholder"
      clearable
      v-model.trim="inputValue"
      @change='handleChange'
    ></el-input>
  </n-form-item>
</template>

<script>
  import _ from 'lodash'

  export default {
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        inputValue: undefined
      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
      },
    },
    watch: {
      value: {
        deep: true,
        immediate: true,
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.inputValue = n
        }
      }
    },
    methods: {
      handleChange(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      }
    },
  }
</script>

<style lang="less" scoped>
  div.config-item::v-deep {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 8px;

    .el-form-item__content {
      flex: 1;
    }
    .el-form-item__label {
      min-width: 100px;
    }
  }
</style>
