<template>
  <el-dialog
    :title="title"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="1000px"
    top='5vh'
    :before-close="handleClose"
    custom-class="handler-dialog low-code-dialog"
  >
    <el-tabs v-model="activeName">
      <el-tab-pane label="组件事件" name="normal">
        <n-form
          class="data-form"
          ref="dataForm"
          :model="dataForm"
          @submit.native.prevent
          label-width="80px"
          label-position="left"
        >
          <n-form-item label="条件查询">
            <div class="search-item">
              <tree-select
                v-if="dialogVisible"
                :tree="treeData"
                :width="600"
                :multiple="false"
                node-key="elementId"
                :default-selected-keys="defaultSelectedKeys"
                @change="handleTreeSelectChange(false, $event)"
              >
              </tree-select>
              <el-input style="margin: 0 10px;" v-model="keyword" placeholder="请输入名称查询" clearable @clear="handleSearch"></el-input>
              <el-button type="primary" icon="el-icon-search" @click="handleSearch">查询</el-button>
            </div>
          </n-form-item>
          <n-form-item label='程序列表'>
            <n-table
              :data="eventHandlers.filter(item => item.isShow)"
              border
              highlight-current-row
              @row-click="handleRowClick"
              max-height="550px"
              size="mini"
            >
              <n-table-column v-if="!isManage" width="50px" label="选择">
                <template slot-scope="scope">
                  <el-radio v-if="getIsSelected(scope.row)"></el-radio>
                  <el-radio v-else :value='false'></el-radio>
                </template>
              </n-table-column>
              <n-table-column prop="handlerName" label="处理程序名称">
              </n-table-column>
              <n-table-column prop="actionName" label="动作类型" width="140px">
              </n-table-column>
              <n-table-column prop="eventLabel" label="所属事件" width="140px">
              </n-table-column>
              <n-table-column prop="elementName" label="所属组件" width="140px">
              </n-table-column>
              <n-table-column prop="disabled" label="状态" width="80px">
                <template slot-scope="scope">
                  <span v-if="scope.row.disabled" style="color: #999;">禁用</span>
                  <span v-else style="color: green;">启用</span>
                </template>
              </n-table-column>
              <n-table-column v-if="isManage" prop="value" label="操作" width="80px">
                <template slot-scope="scope">
                  <el-button type="text" @click="handleEdit(scope.row)">编辑</el-button>
                </template>
              </n-table-column>
            </n-table>
          </n-form-item>
        </n-form>
      </el-tab-pane>
      <el-tab-pane label="公共事件" name="common">
        <div class="search-area">
          <el-input placeholder="处理程序名称查询" v-model="tempVal" class="input-with-select" clearable @clear="commonKeyword = tempVal = ''" @keydown.enter.native="commonKeyword = tempVal">
            <el-button slot="append" type="primary" icon="el-icon-search" @click="commonKeyword = tempVal"></el-button>
          </el-input>
          <el-button type="primary" icon="el-icon-plus" size="mini" @click="handleAdd">新建事件</el-button>
        </div>
        <n-table
          :data="commonEvents"
          border
          highlight-current-row
          @row-click="handleRowClick"
          max-height="550px"
          size="mini"
        >
          <n-table-column v-if="!isManage" width="50px" label="选择">
            <template slot-scope="scope">
              <el-radio v-if="getIsSelected(scope.row)"></el-radio>
              <el-radio v-else :value='false'></el-radio>
            </template>
          </n-table-column>
          <n-table-column prop="handlerName" label="处理程序名称">
          </n-table-column>
          <n-table-column prop="disabled" label="状态">
            <template slot-scope="scope">
              <span v-if="scope.row.disabled" style="color: #999;">禁用</span>
              <span v-else style="color: green;">启用</span>
            </template>
          </n-table-column>
          <n-table-column label="动作类型" prop="actionName"></n-table-column>
          <n-table-column v-if="isManage" prop="value" label="操作">
            <template slot-scope="scope">
              <el-button type="text" v-if="scope.row.disabled" @click="switchDisable(scope.row, false)">启用</el-button>
              <el-button type="text" v-else @click="switchDisable(scope.row, true)">禁用</el-button>
              <el-button type="text" @click="handleEdit(scope.row)">编辑</el-button>
              <el-button type="text" style="color: red;" @click="handleDelete(scope.row)">删除</el-button>
            </template>
          </n-table-column>
        </n-table>
      </el-tab-pane>
    </el-tabs>
    <div slot="footer" class="dialog-footer">
      <template v-if="!isManage">
        <el-button @click="clear">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </template>
      <el-button v-else @click="clear">关 闭</el-button>
    </div>
    <handler-dialog
      ref="handlerDialog"
      @confirm="handleUpdateEventData"
    ></handler-dialog>
  </el-dialog>
</template>

<script>
  import TreeSelect from '@/components/WidgetTreeSelect/index'
  import { UPDATE_EVENTS_DATA } from '@/config/storeKey'
  import { getNodeByElementId, getRandomHexadecimal, treeUtil } from '@/utils/tree-utils'
  import _ from 'lodash'
  import { MOUNT, DESTROY, PAGE_COMMON_EVENTS } from '@/config/eventName'
  import { getConfigByKey } from '@/renderer/tools/config'
  import treeTool from 'tree-tool'
  import { pageInject } from '@/renderer/tools/pageStore'
  import { getGlobalConfig, update } from '@/renderer/tools/history'
  export default {
    name: 'EventSelect',
    inject: ['dndOptions', ...pageInject],
    components: {
      TreeSelect,
    },
    data() {
      return {
        dialogVisible: false,
        dataForm: {},
        defaultSelectedKeys: [],
        rootNode: undefined,
        eventHandlers: [],
        selectedEventHandler: null,
        isFunction: false,
        isManage: false,
        keyword: '',
        selectedId: '',
        editId: '',
        activeName: 'normal',
        commonKeyword: '',
        tempVal: '',
        actionNameMap: {
          control: '组件操作',
          link: '跳转页面',
          open: '打开窗体',
          close: '关闭窗体',
          api: '调用API',
          sql: '调用SQL',
          js: '调用JS脚本',
          refresh: '刷新当前页',
          reset: '刷新组件',
          devops: '调用自动化运维操作',
          pageEmit: '触发页面事件',
          commonAction: '调用公共事件处理行为',
        }
      }
    },
    computed: {
      title() {
        return this.isManage ? '事件管理' : '选择事件'
      },
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      layoutData() {
        return this.getStore().layoutData
      },
      eventsData() {
        return this.getStore().eventsData
      },
      treeData() {
        const copy = this.rootNode ? _.cloneDeep(this.rootNode) : _.cloneDeep(this.layoutData)
        treeUtil.forEach([copy], (node) => {
          const hasEvent = !!this.eventsData[node.elementId]
          if (!hasEvent) {
            node.disabled = true
          }
          const showChildren = []
          node.children.forEach(item => {
            if (['slot'].includes(item.elementType) && (this.getElementPropsById(item.elementId).isVisible === false || ['data'].includes(this.getElementPropsById(item.elementId).slotType))) {
              return
            }
            if (['dialog-container', 'timer-container'].includes(item.elementType)) {
              if (!item.children.length) {
                return
              }
              item.disabled = true
            }
            showChildren.push(item)
          })
          node.children = showChildren
          node.elementName =
            this.getElementPropsById(node.elementId).elementName || node.elementName
        })
        return treeUtil.filter([copy], node => !node.disabled)
      },
      commonEvents() {
        const events = this.eventsData[this.layoutData.elementId]
        const result = events ? (events[PAGE_COMMON_EVENTS] || []) : []
        return result.filter(item => item.name.includes(this.commonKeyword.trim())).map(h => {
          const actionName = this.actionNameMap[h.action]
          return {
            ...h,
            handlerName: h.name,
            eventName: PAGE_COMMON_EVENTS,
            actionName,
            elementId: this.layoutData.elementId
          }
        })
      }
    },
    methods: {
      open(data = {}) {
        const { isFunction, isManage, selectedId, elementId } = data
        this.isFunction = !!isFunction
        this.isManage = !!isManage
        this.handleUpdateEventHandlers()
        this.dialogVisible = true
        this.selectedId = elementId + '-' + selectedId
        this.editId = selectedId
      },
      clear() {
        this.selectedEventHandler = null
        this.selectedId = ''
        this.eventHandlers = []
        this.defaultSelectedKeys = []
        this.keyword = ''
        this.dialogVisible = false
      },
      handleClose(done) {
        this.clear()
        done()
      },
      confirm() {
        if (!this.selectedEventHandler) {
          this.$message.error('请选择事件处理程序！')
          return
        }
        const handler = _.cloneDeep(this.selectedEventHandler)
        delete handler.handlerName
        delete handler.eventName
        delete handler.eventLabel
        delete handler.actionName
        delete handler.elementName
        delete handler.disabled
        delete handler.elementId
        delete handler.isShow
        this.$emit('confirm', handler, this.editId)
        this.clear()
      },
      handleTreeSelectChange(multiple, selection) {
        if (multiple) {
          return
        }
        const selected = selection[0]
        this.defaultSelectedKeys = selected ? [selected.elementId] : []
        this.handleUpdateEventHandlers(selected)
      },
      handleUpdateEventHandlers(selectedWidget) {
        const widgetList = selectedWidget ? [selectedWidget] : treeUtil.toList(this.treeData)
        let eventHandlers = []
        widgetList.forEach(widget => {
          const { elementId } = widget
          const events = this.eventsData[elementId] || {}
          const { elementName } = this.getElementPropsById(elementId)

          const currentNode = getNodeByElementId(this.layoutData, elementId)
          if (!currentNode) {
            return []
          }
          const { events: componentEvents } = getConfigByKey.call(this, { elementType: currentNode.elementType, elementVersion: currentNode.elementVersion, elementId })
          const eventNameMap = {
            ...Object.fromEntries(componentEvents.map(item => [[item.eventName], item.name])),
            [MOUNT]: '组件初始化',
            [DESTROY]: '组件销毁'
          }
          Object.entries(events).forEach(([key, value]) => {
            // 公共事件不在这里显示
            if(key === PAGE_COMMON_EVENTS) {
              return
            }
            const eventLabel = eventNameMap[key]
            const treeUtil = treeTool.createInstance({
              id: 'id',
              children: 'callback',
              pid: 'callbackId',
            })
            const handlers = []
            treeUtil.forEach(value, h => {
              const actionName = this.actionNameMap[h.action]
              const disabled = h.disabled
              const handler = {
                ...h,
                handlerName: h.name,
                eventLabel,
                eventName: key,
                actionName,
                elementName,
                disabled,
                elementId,
                isShow: true
              }
              handlers.push(handler)
            })
            eventHandlers.push(...handlers)
          })
          if (this.isFunction) {
            eventHandlers = eventHandlers.filter(item => !['js'].includes(item.action))
          }
        })

        this.eventHandlers = eventHandlers
      },
      handleRowClick(row, column, event) {
        this.selectedEventHandler = row
        this.selectedId = row.elementId + '-' + row.id
      },
      getIsSelected(row) {
        return this.selectedId === row.elementId + '-' + row.id
      },
      handleSearch() {
        this.eventHandlers = this.eventHandlers.map(item => ({...item, isShow: !this.keyword || item.handlerName.includes(this.keyword)}))
      },
      // 新增供页面暴露函数选择的事件处理行为
      handleAdd() {
        const eventName = PAGE_COMMON_EVENTS
        const elementId = this.getStore().layoutData.elementId
        this.selectedEventHandler = {
          eventName,
          elementId
        }
        const id = getRandomHexadecimal(12)
        this.$refs.handlerDialog.open({
          id,
          elementId,
          eventName,
          name: '',
          condition: [],
          emitEvent: {
            elementId,
            eventName,
            params: []
          }
        })
      },
      switchDisable(row, disabled) {
        const eventsData = _.cloneDeep(this.eventsData)
        eventsData[this.layoutData.elementId][PAGE_COMMON_EVENTS].forEach(item => {
          if(item.id === row.id) {
            item.disabled = disabled
          }
        })
        this.saveEventsData(eventsData)
      },
      handleEdit(row) {
        const handler = _.cloneDeep(row)
        delete handler.handlerName
        delete handler.actionName
        delete handler.elementName
        delete handler.isShow
        this.selectedEventHandler = row
        this.selectedId = row.id
        this.$refs.handlerDialog.open(handler)
      },
      async handleDelete(row) {
        await this.$confirm(
          `确认要删除“${row.name}”?`,
          '提示',
          {
            confirmButtonText: '确认',
            cancelButtonText: '取消',
            type: 'warning',
          }
        )
        const events = this.eventsData[this.layoutData.elementId][PAGE_COMMON_EVENTS].filter(item => item.id !== row.id)
        const eventsData = {
          ...this.eventsData,
          [this.layoutData.elementId]: {
            ...this.eventsData[this.layoutData.elementId],
            PAGE_COMMON_EVENTS: events
          }
        }
        this.saveEventsData(eventsData)
      },
      handleUpdateEventData(res) {
        const eventsData = this.getStore().eventsData
        const { elementId, eventName } = this.selectedEventHandler
        const events = eventsData[elementId] ? (eventsData[elementId][eventName] || []) : []
        const isAdd = !events.map((e) => e.id).includes(res.id)
        const newEventsData = {
          [elementId]: {
            ...eventsData[elementId],
            [eventName]: isAdd
              ? [
                  ...events,
                  {
                    id: res.id,
                    functionId: res.functionId,
                    name: res.name,
                    binds: res.binds,
                    condition: res.condition,
                    action: res.action,
                    linkOptions: res.linkOptions,
                    controlOptions: res.controlOptions,
                    apiRequest: res.apiRequest,
                    sqlRequest: res.sqlRequest,
                    callback: res.callback,
                    loadingConfig: res.loadingConfig,
                    responseConfig: res.responseConfig,
                    pageEmit: res.pageEmit,
                    commonActionId: res.commonActionId
                  },
                ]
              : events.map((e) => {
                  if (e.id === res.id) {
                    return {
                      id: res.id,
                      functionId: res.functionId,
                      name: res.name,
                      binds: res.binds,
                      condition: res.condition,
                      action: res.action,
                      linkOptions: res.linkOptions,
                      controlOptions: res.controlOptions,
                      apiRequest: res.apiRequest,
                      sqlRequest: res.sqlRequest,
                      callback: res.callback,
                      pageEmit: res.pageEmit,
                      commonActionId: res.commonActionId
                    }
                  }
                  return e
              })
          },
        }
        this.saveEventsData(newEventsData)
        this.handleUpdateEventHandlers(this.defaultSelectedKeys.length ? {elementId: this.defaultSelectedKeys[0]} : undefined)
        this.selectedEventHandler = null
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      },
      saveEventsData(newEventsData) {
        const oldVal = getGlobalConfig.call(this, 3)
        this._storeCommit(UPDATE_EVENTS_DATA, { eventsData: newEventsData })
        update({
          extra: {
            oldVal,
            val: getGlobalConfig.call(this, 3)
          }
        })
      }
    },
  }
</script>

<style lang="less" scoped>
  .data-form::v-deep {
    .search-item {
      display: flex;
      align-items: center;
    }
  }
  .search-area {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
    .el-input-group--append {
      width: 500px;
      .el-input-group__append {
        .el-button {
          color: #FFF;
          background-color: #409EFF;
          border-color: #409EFF;
          margin-top: -12px;
        }
      }
    }
  }
</style>
