<template>
  <el-popover
    v-model="popoverVisible"
    placement="bottom"
    title="选择组件"
    trigger="click"
  >
    <el-input
      class="tree-select-search"
      placeholder="输入组件名称筛选"
      clearable
      v-model="filterText"
    ></el-input>
    <n-tree
      :style="{
        minWidth: typeof width === 'string' ? width : `${width - 24}px`,
      }"
      class="tree-select-popper-content"
      :data="treeData"
      show-checkbox
      :default-expand-all='false'
      :default-expanded-keys='defaultExpandedKeys'
      :check-on-click-node="true"
      :expand-on-click-node="false"
      :node-key="nodeKey"
      :check-strictly="true"
      :filter-node-method="filterNode"
      ref="tree"
      icon-class="icon-right"
      :props="treeProps"
      :default-checked-keys='defaultSelectedKeys'
      @check="handleCheckClick"
      @check-change="handleCheckChange"
    >
      <div slot-scope="{ node, data }" class="widget-tree-node-name">
        <div class="widget-tree-node-icon" :style="`background-image: url(${$getImgPrefix(iconMap[data.elementType])})`"></div>
        <span>{{ `${data.elementName} ${data.elementId}` }}</span>
      </div>
    </n-tree>
    <el-select
      class="tree-select"
      popper-class="tree-select-popper-trigger"
      placeholder="请选择组件"
      :style="{ width: typeof width === 'string' ? width : `${width}px` }"
      slot="reference"
      ref="trigger"
      v-model="selected"
      :clearable="!multiple"
      :multiple="multiple"
      @clear="clearSelection"
      @remove-tag="handleRemoveSelection"
    >
      <el-option
        v-for="item in selectedNodes"
        :key="item.elementId"
        :label="item.elementName"
        :value="item"
      >
      </el-option>
    </el-select>
  </el-popover>
</template>

