<script>
  import _ from 'lodash'
  import { getParentNodeListByElementId, treeUtil } from '@/utils/tree-utils'
  import { iconMap } from '@/config/widgets'
  import { pageInject } from '@/renderer/tools/pageStore'

  export default {
    name: 'WidgetTreeSelect',
    inject: [...pageInject],
    props: {
      tree: {
        type: Array,
        default: () => [],
      },
      defaultSelectedKeys: {
        type: Array,
        default: () => [],
      },
      multiple: {
        type: Boolean,
        default: false,
      },
      showAppNode: {
        type: Boolean,
        default: false,
      },
      nodeKey: {
        type: String,
        default: 'id',
      },
      treeProps: {
        type: Object,
        default: () => ({
          children: 'children',
          label: 'elementName',
          disabled: (data, node) => data.disabled
        }),
      },
      width: {
        type: [String, Number],
        default: '180px',
      },
    },
    data() {
      return {
        iconMap,
        popoverVisible: false,
        filterText: '',
        selectedNodes: [],
        defaultExpandedKeys: [],
      }
    },
    computed: {
      treeData() {
        if (this.showAppNode) {
          const appNode = {
            elementId: 'app',
            elementType: 'page',
            elementName: '应用',
            elementVersion: this.tree[0]?.elementVersion || 'v1',
            children: this.tree
          }
          return [appNode]
        }
        return this.tree
      },
      selected: {
        get() {
          if (this.multiple) {
            return this.selectedNodes.map((node) => `${node.elementName} ${node.elementId}`)
          }
          const elementId = this.selectedNodes[0]?.elementId
          const parentNodeList = elementId ? getParentNodeListByElementId(this.treeData, elementId) || [] : []
          return parentNodeList.map(node => `${node.elementName}`).join(' / ')
        },
      },
    },
    watch: {
      defaultSelectedKeys: {
        immediate: true,
        deep: true,
        handler(n, o) {
          const arr = []
          treeUtil.forEach(this.treeData, (node) => {
            if (n.includes(node.elementId)) {
              arr.push(node)
            }
          })
          this.selectedNodes = arr
          const layoutData = this.getStore().layoutData
          const pageElementId = layoutData.elementId
          const childrenIds = layoutData.children.map(item => item.elementId)
          this.defaultExpandedKeys = arr.length ? arr.map(item => item.elementId) : _.uniq([this.treeData[0]?.elementId, pageElementId, ...childrenIds]).filter(id => id)
        },
      },
      filterText(val) {
        this.$refs.tree.filter(val)
      },
    },
    methods: {
      filterNode(value, data) {
        if (!value) return true
        return data[this.treeProps.label].indexOf(value) !== -1
      },
      handleCheckClick(data, node, tree) {
        if (this.multiple) {
          return
        }
        this.$refs.tree.setCheckedKeys([data[this.nodeKey]])
      },
      handleCheckChange: _.debounce(
        function() {
          this.selectedNodes = this.$refs.tree.getCheckedNodes()
          this.$emit('change', this.selectedNodes)
          if (!this.multiple) {
            this.$refs.trigger.$el.click()
          }
        }, 100),
      clearSelection() {
        this.$refs.tree.setCheckedKeys([])
      },
      handleRemoveSelection(t) {
        const id = _.last(t.split('_'))
        this.selectedNodes = this.selectedNodes.filter(
          (node) => node.elementId !== id
        )
        this.$refs.tree.setChecked(id, false)
      },
    },
  }
</script>

<style lang="less">
  .tree-select {
    width: 100%;
  }
  .tree-select-popper-trigger {
    display: none;
  }
  .tree-select-search {
    width: 100%;
    margin-bottom: 10px;
  }
  .tree-select-popper-content {
    max-height: 360px;
    overflow: auto;
  }
  .widget-tree-node-name {
    padding: 1px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    .widget-tree-node-icon {
      margin-right: 4px;
      width: 16px;
      height: 16px;
      background-size: 100% 100%;
    }
  }
</style>
