<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-switch v-model="value.use"></el-switch>
    <div class="fill-div"></div>
    <el-popover placement="right" width="300" trigger="click">
      <el-form
        size="mini"
        class="setting-form"
        label-position="left"
        label-width="90px"
      >
        <el-form-item label="分割段数">
          <el-input-number
            v-model="value.splitNumber"
            :min="0"
            :max="100"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="刻度标签">
          <el-switch v-model="value.axisLabelShow"></el-switch>
        </el-form-item>
        <template v-if="value.axisLabelShow">
          <el-form-item label="格式化">
            <el-switch v-model="value.axisLabelUseFormat"></el-switch>
          </el-form-item>
          <el-form-item v-if="value.axisLabelUseFormat" label="格式化内容">
            <el-tooltip
              popper-class="tooltip-question"
              effect="dark"
              :content="formatTips"
              placement="top-start"
            >
              <i class="el-icon-question"></i>
            </el-tooltip>
            <el-input v-model="value.axisLabelFormatter"></el-input>
          </el-form-item>
        </template>
      </el-form>
      <i slot="reference" class="el-icon-s-tools"></i>
    </el-popover>
  </n-form-item>
</template>

<script>
import rangeSelector from '@/components/rangeSelector'
import _ from 'lodash'
import { formatTips } from '@/config'
export default {
  components: {
    rangeSelector,
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      widthOptions: { unit: [{ label: 'px', value: '' }] },
      formatTips,
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
  },
  methods: {},
  mounted() {},
  created() {},
}
</script>

