<template>
  <div class="change-height" @mousedown="mouseDown"></div>
</template>

<script>
  export default {
    name: 'ChangeHeight',
    data() {
      return {
        lastY: 0,
        movedHeight: 0
      }
    },

    mounted() {
      window.addEventListener('mouseup', this.mouseUp)
    },

    beforeDestroy() {
      window.removeEventListener('mouseup', this.mouseUp)
    },

    methods: {
      mouseDown(e) {
        window.addEventListener('mousemove', this.mouseMove)
        this.lastY = e.screenY
      },
      mouseMove(e) {
        this.movedHeight = e.screenY - this.lastY
        this.$emit('heightChange', {
          height: this.movedHeight
        })
        e.stopPropagation()
        e.preventDefault()
      },
      mouseUp() {
        this.lastY = 0
        this.movedHeight = 0
        window.removeEventListener('mousemove', this.mouseMove)
        this.$emit('clearEvent')
      },
    },
  }
</script>
<style lang="less" scoped>
@import '~@/styles/index.less';
  .change-height {
    opacity: 1;
