<template>
  <div class="sql-config" v-loading="loading">
    <n-form
      class="data-select-form"
      ref="dataForm"
      label-position="top"
      :model="formData"
      :rules="rules"
      size="mini"
      @submit.native.prevent
    >
      <n-form-item v-if='pureFunction && !defaultArguments.length' class="sql-form-item inline" label="名称" prop="name">
        <div style="display: flex;">
          <el-input v-model='formData.name' placeholder='请输入名称'></el-input>
          <el-button style="margin-left: 20px;" type="primary" @click="handleShowJsCodeSelect">选择已存高级变量</el-button>
        </div>
      </n-form-item>
      <n-form-item class="sql-form-item" :class='{"is-hide-label": !pureFunction}' style='margin-bottom: 16px;' label="JS" prop="code">
        <el-button class='test-btn' size='mini' type='primary' @click='handleTest'>测试</el-button>
        <el-tooltip v-if="!isJsFormat" popper-class="tooltip-question" placement="left">
          <div slot="content" v-html='emptyTips'></div>
          <i class="el-icon-question test-btn tooltip"></i>
        </el-tooltip>
        <div class="empty-code" v-html="templateText"></div>
        <div class='code-wrapper' :class='{"is-show-log": isShowLog}'>
          <js-code
            class="sql-code"
            ref='jsCode'
            @focus-change="focusChange"
            @screenChange="isShowLog = false"
            :params-data='paramsData'
            :resizable="true"
            v-model="formData.code"
            :custom-class="isJsFormat"
            @heightChange="heightChange"
            :useType="useType"
          ></js-code>
          <div class='log-wrapper' :class="{ 'custom-class': isJsFormat }" v-if='isShowLog' :style="logWrapperHeight ? `height: ${logWrapperHeight}px` : ''">
            <div>
              <el-button class='clear-btn' size='mini' :disabled='!logList.length' @click='handleClearLog'>清空日志</el-button>
              <el-button class='clear-btn' size='mini' @click='isShowLog = false'>关闭</el-button>
            </div>
            <div class='log-list'>
              <div class='log-item' v-for='(log, index) in logList' :key='index'>{{log}}</div>
            </div>
          </div>
        </div>
      </n-form-item>
      <n-form-item label="">
        <div class="params-config">
          <!-- <el-button
            class="source-edit-button"
            type='primary'
            @click="
              showInputDialog(
                'object',
                paramsSource,
                'paramsData',
                'paramsSource'
              )
            "
          >转换器</el-button> -->
          <n-table
            :data="paramsData"
            border
            style="width: 100%;margin-top: 10px;"
          >
            <n-table-column prop="key" label="参数名">
              <template slot-scope="scope">
                <el-input v-model="scope.row.key" :readonly='isDefaultArgument(scope.row.key)'></el-input>
              </template>
            </n-table-column>
            <n-table-column prop="value" label="参数值">
              <template slot-scope="scope">
                <div class='value-input' v-if='isDefaultArgument(scope.row.key)'>
                  <el-tooltip
                    effect="dark"
                    placement="top"
                  >
                  <div slot="content" style="white-space: nowrap" v-html="defaultArgDesc(scope.row.key).desc"></div>
                    <el-tag
                      class="value-tag"
                      disable-transitions
                    >
                      {{ formatTag(defaultArgDesc(scope.row.key).name, 12) }}
                    </el-tag>
                  </el-tooltip>
                </div>
                <div class="value-input" v-if="!isDefaultArgument(scope.row.key)">
                  <el-tooltip
                    v-if="scope.row.value !== undefined"
                    effect="dark"
                    placement="top"
                  >
                    <div slot="content" style="white-space: nowrap" v-html="isInnerParams(scope.row) ? innerParamsDesc(scope.row.value) : formatTip(scope.row.value)"></div>
                    <el-tag
                      class="value-tag"
                      closable
                      disable-transitions
                      @click="handleTagClick(scope.row, 'value')"
                      @close="scope.row.value = undefined"
                      :type='getTagType(scope.row.value)'
                    >
                      {{ formatTag(getElementName(scope.row.value), 12) }}
                    </el-tag>
                  </el-tooltip>
                  <div v-else class="value-input-content">
                    <el-button-group>
                      <el-button
                        @click="handleShowVariableSelect(scope.row, 'value')"
                        >变量
                      </el-button>
                      <el-button
                        v-if='!pureFunction && !isJsFormat'
                        @click="handleShowFunctionSelect(scope.row, 'value')"
                        >动作
                      </el-button>
                      <el-button
                        @click="handleShowUtilsSelect(scope.row, 'value')"
                        >工具函数
                      </el-button>
                    </el-button-group>
                  </div>
                </div>
              </template>
            </n-table-column>
            <n-table-column prop="test" label="测试值">
              <template slot-scope="scope">
                <div class="value-input">
                  <el-tooltip
                    v-if="isShowTest(scope.row)"
                    effect="dark"
                    :content="formatTip(isInnerParams(scope.row) ? '内置参数，不可更改' : scope.row.test)"
                    placement="top"
                  >
                    <el-tag
                      class="value-tag"
                      :closable='!isInnerParams(scope.row)'
                      disable-transitions
                      @click="handleTagClick(scope.row, 'test')"
                      @close="scope.row.test = undefined"
                    >
                      {{ !isInnerParams(scope.row) ? formatTag(scope.row.test) : '内置参数，不可更改' }}
                    </el-tag>
                  </el-tooltip>
                  <div v-else class="value-input-content">
                    <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                      <el-button
                        @click="showInputDialog('text', scope.row, 'test')"
                        >文本
                      </el-button>
                      <el-button
                        @click="showInputDialog('number', scope.row, 'test')"
                        >数值
                      </el-button>
                      <el-button
                        @click="showInputDialog('object', scope.row, 'test')"
                        >对象
                      </el-button>
                    </el-button-group>
                    <MoreTypeDropdown @change="(v) => scope.row.test = v" />
                  </div>
                </div>
              </template>
            </n-table-column>
            <n-table-column v-if="argumentsAddable" width="180" label="操作">
              <template slot-scope="scope">
                <div class="btns">
                  <div class="add-btn-primary" @click="handleAddParams">+</div>
                  <div
                    v-if='!isDefaultArgument(scope.row.key)'
                    class="add-btn-primary"
                    @click="handleDeleteParams(scope.$index)"
                  >
                    -
                  </div>
                </div>
              </template>
            </n-table-column>
          </n-table>
        </div>
      </n-form-item>
    </n-form>
    <variable-select
      ref="variableSelect"
      @confirm="handleVariableConfirm"
    ></variable-select>
    <utils-select
      ref="utilsSelect"
      @confirm="handleVariableConfirm"
    ></utils-select>
    <function-select
      ref="functionSelect"
      @confirm="handleVariableConfirm"
    ></function-select>
    <input-dialog @confirm="handleUpdateValue" ref="inputDialog"></input-dialog>
    <JsCodeSelect ref='jsCodeSelect' @confirm='handleJsCodeSelectConfirm' />
  </div>
</template>

<script>
  import JsCode from '@/components/JsCode'
  import { getValidators, required } from '@/utils/validateUtils'
  import { getRandomHexadecimal } from '@/utils/tree-utils'
  import _ from 'lodash'
  import { UPDATE_FUNCTIONS_DATA } from '@/config/storeKey'
  import API from '@/api/application'
  import elementEditor from '@/mixins/elementEditor'
  import JsCodeSelect from '@/components/JsCodeSelect'
  import { UTILS_FUNCTION } from '@/config'
  import { utils } from '../FunctionSelect/utils'
  import { pageInject } from '@/renderer/tools/pageStore'

  const templateText1 = `支持JS原生语法，使用“参数名”进行取值, 使用return返回结果，示例JS：
var age = userInfo.age;
return age + 1;`
  const templateText2 = `支持JS原生语法，使用“参数名”进行取值, 当参数为函数时，使用“参数名()”执行函数，为参数赋值时，需调用setValue(参数路径，值) 示例JS：
var name = userInfo.name;
if (!name) {
setValue('userInfo.name', '张同学');
sayHello();
}`

  export default {
    name: 'JsConfig',
    inject: [...pageInject],
    components: {
      JsCode,
      JsCodeSelect,
    },
    mixins: [elementEditor],
    props: {
      value: {
        type: String
      },
      callbackId: {
        type: String,
      },
      pureFunction: {
        type: Boolean,
        default: false,
      },
      argumentsAddable: {
        type: Boolean,
        default: true,
      },
      defaultArguments: {
        type: Array,
        default: []
      },
      // 事件参数
      emitEvent: {
        required: false
      },
      tips: {
        type: String
      },
      useType: {
        type: String
      },
      isJsFormat: {
        type: Boolean
      }
    },
    data() {
      return {
        templateText1,
        templateText2,
        UTILS_FUNCTION,
        loading: false,
        rules: {
          name: getValidators([
            {
              message: '请输入名称',
              include: required,
              trigger: 'change',
            },
          ]),
        },
        formData: {
          id: '',
          code: '',
          name: ''
        },
        variableRowDetail: null,
        editorFocus: false,
        paramsData: [
          {
            key: '',
            value: undefined,
            test: undefined
          }
        ],
        currentFunction: null,
        logList: [],
        isShowLog: false,
        innerArguments: [
        ],
        logWrapperHeight: ''
      }
    },
    computed: {
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      functionsData() {
        return this.getStore().functionsData
      },
      templateText() {
        if (this.formData.code || this.editorFocus || this.isShowLog) {
          return ''
        }
        return this.emptyTips
      },
      emptyTips() {
        return this.tips || (this.pureFunction ? templateText1 : templateText2)
      },
      paramsSource: {
        get() {
          return {
            paramsData: Object.fromEntries(
              this.paramsData.map((item) => [[item.key], item.value])
            ),
          }
        },
        set(n) {
          this.paramsData = Object.entries(n).map(([key, value]) => ({
            key,
            value,
            test: value,
          }))
        },
      },
    },
    watch: {
      value: {
        immediate: true,
        handler(n) {
          this.currentFunction = this.functionsData.find(item => item.id === n)
          if (this.currentFunction) {
            const { name, paramsData, code } = this.currentFunction
            this.formData.id = n
            this.formData.name = name
            this.formData.code = code
            this.paramsData = _.cloneDeep([
              ...this.defaultArguments,
              ...this.innerArguments,
              ...paramsData.map(item => ({...item, value: item.value, test: item.test})),
            ])
            this.paramsData = _.uniqBy(this.paramsData, 'key')
          } else {
            this.formData.id = getRandomHexadecimal(12)
            this.formData.name = this.pureFunction ? '' : 'func'
            this.formData.code = ''
            this.paramsData = [
              ...this.defaultArguments,
              ...this.innerArguments,
            ]
            if (this.argumentsAddable) {
              this.paramsData.push({
                key: '',
                value: undefined,
                test: undefined
              })
            }
            this.$nextTick(() => {
              this.$refs.dataForm?.clearValidate()
            })
          }
        }
      }
    },
    methods: {
      heightChange(height) {
        const minHeight = this.isJsFormat ? 550 : 200
        this.logWrapperHeight = height < minHeight ? minHeight: height
      },
      focusChange(n) {
        this.editorFocus = n
        if (n) {
          this.isShowLog = false
        }
      },
      handleShowVariableSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.variableSelect.open({
          fullVariable: row[key],
          callbackId: this.callbackId,
          hideFunc: true,
          emitEvent: this.emitEvent
        })
      },
      handleShowUtilsSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.utilsSelect.open({
          fullVariable: row[key],
        })
      },
      handleShowFunctionSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.functionSelect.open({
          fullVariable: row[key],
          callbackId: this.callbackId,
          emitEvent: this.emitEvent
        })
      },
      handleVariableConfirm(selectedVariable) {
        const { row, key, cb } = this.variableRowDetail
        if (key) {
          this.$set(row, key, selectedVariable)
          if (selectedVariable.indexOf(UTILS_FUNCTION) === 0) {
            if (!row.key) {
              const utilName = selectedVariable.replace(UTILS_FUNCTION, '')
              const count = this.paramsData.filter(item => item.key === utilName).length
              this.$set(row, 'key', count ? `${utilName}${count + 1}` : utilName)
            }
          }
        }
        cb?.(selectedVariable)
      },
      handleAddParams() {
        this.paramsData.push({
          key: '',
          value: undefined,
          test: undefined,
        })
      },
      handleDeleteParams(index) {
        const paramsData = this.paramsData
        if (paramsData.length === 1) {
          paramsData[0].key = ''
          paramsData[0].value = undefined
          paramsData[0].test = undefined
          return
        }
        paramsData.splice(index, 1)
      },
      async handleTest() {
        this.isShowLog = true
        await this.$nextTick()
        if (!this.formData.code) {
          return
        }
        this.$refs.jsCode.handleTest(false)
        this.logList = this.$refs.jsCode.logList
      },
      handleClearLog() {
        this.$refs.jsCode.logList = []
        this.logList = []
      },
      async confirm() {
        await this.$refs.dataForm.validate()
        const isPass = await this.$refs.jsCode.handleTest(true)
        if (!isPass) {
          return Promise.reject(false)
        }
        return this.handleUpdateFunctionsData()
      },
      handleUpdateFunctionsData() {
        const func = {
          id: getRandomHexadecimal(12),
          name: this.formData.name,
          code: this.formData.code,
          paramsData: _.cloneDeep(_.uniqBy(this.paramsData, 'key'))
        }
        this.loading = true
        const result = [...this.functionsData, func]
        return this.$http({
          url: API.appPageDesignSavePageConfig,
          data: [
            {
              pageDesignId: Number(this.getStore().layoutData.pageDesignId),
              type: 5,
              config: JSON.stringify(result),
            },
          ],
        }).then(() => {
          this._storeCommit(UPDATE_FUNCTIONS_DATA, { functionsData: result })
          this.$nextTick(() => {
            this.$emit('input', func.id)
          })
        }).finally(() => {
          this.loading = false
        })
      },
      isDefaultArgument(key) {
        const allArguments = [...this.defaultArguments, ...this.innerArguments]
        return allArguments.some(item => item.key === key)
      },
      defaultArgDesc(key) {
        const allArguments = [...this.defaultArguments, ...this.innerArguments]
        const current = allArguments.find(item => item.key === key) || {}
        return {name: current.name || current.desc, desc: current.desc}
      },
      innerParamsDesc(value) {
        if (value?.indexOf(UTILS_FUNCTION) === 0) {
          const currentUtil = utils.find(item => item.id === value)
          return currentUtil?.desc || value
        }
        return value
      },
      updateCode(code) {
        this.formData.code = code
      },
      handleShowJsCodeSelect() {
        this.$refs.jsCodeSelect.open()
      },
      handleJsCodeSelectConfirm(res) {
        const { name, paramsData, code, id } = res
        this.formData.id = id
        this.formData.name = name
        this.formData.code = code
        this.paramsData = _.cloneDeep([...this.defaultArguments, ...paramsData.map(item => ({...item, value: item.value, test: item.test}))])
        this.paramsData = _.uniqBy(this.paramsData, 'key')
        this.$nextTick(() => {
          this.$refs.dataForm?.clearValidate()
        })
      },
      isShowTest(row) {
        return row.test !== undefined || this.isInnerParams(row)
      },
      isInnerParams(row) {
        return row.definedTest || row.value?.indexOf(UTILS_FUNCTION) === 0
      }
    },
  }
</script>

