<template>
  <div class="static-config">
    <n-form
      class="data-select-form"
      label-position="top"
      @submit.native.prevent
      size='mini'
    >
      <el-button-group class='api-button-group'>
        <el-button class='api-button' :type='type === 1 ? "primary" : ""' @click='changeType(1)'>手动录入</el-button>
        <el-button class='api-button' :type='type === 2 ? "primary" : ""' @click='changeType(2)'>外部导入</el-button>
      </el-button-group>
      <n-form-item class="sql-form-item" v-if='type === 1'>
        <span slot="label">
                  JSON
                  <el-tooltip popper-class="tooltip-question" placement="top">
                    <div slot="content">
JSON文件都是被包裹在一个大括号{}或者方括号[]中，通过key-value的方式来表达数据。JSON的Key必须包裹在一个双引号中，在实践中，编写 JSON 的时候，忘了给 Key 值加双引号或者是把双引号写成单引号是常见错误。

JSON的值只能是以下几种数据格式，其他任何格式都会触发报错，例如 JavaScript 中的 undefined。

1.数字，包含浮点数和整数
2.字符串，需要包裹在双引号中
3.Bool值，true 或者 false
4.数组，需要包裹在方括号中 []
5.对象，需要包裹在大括号中 {}
6.Null

还需要注意的是 JSON 文件中无法使用注释，试图添加注释将会引发报错
                    </div>
                    <i class="el-icon-question"></i>
                  </el-tooltip>
                  </span>
        <json-code class="sql-code" v-model="json"></json-code>
        <el-button size='mini' style='margin-top: 10px;' type='primary' @click='handleInput'>确定录入</el-button>
        <el-button v-if="showCancel" size='mini' style='margin-top: 10px;' @click='cancel'>取消</el-button>
      </n-form-item>
      <n-form-item class="sql-form-item" label="" v-if='type === 2'>
        <div class='upload-wrapper'>
          <el-upload
            v-loading='excelLoading'
            v-if='type === 2'
            class='uploader'
            ref="upload"
            drag
            action="/tamp/upload/"
            :show-file-list="true"
            :on-change="onChange"
            :on-remove="handleRemove"
            :auto-upload="false"
          >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div slot="tip" class="el-upload__tip">备注：支持导入excel文件</div>
          </el-upload>
        </div>
        <el-button type="primary" size='mini' @click='exportTemplate'>模板导出</el-button>
        <el-button v-if="showCancel" size='mini' @click='cancel'>取消</el-button>
      </n-form-item>
    </n-form>
  </div>
</template>

<script>
  import JsonCode from '@/components/JsonCode'
  import uploadExcel from '@/mixins/uploadExcel'
  import _ from 'lodash'
  import { getObjectTree } from '@/utils/format'
  import { isValidJsonString } from '@/utils/validateUtils'
  import { sleep, trimAll } from '@/utils'
  import { STATIC_PREFIX } from '@/config'

  export default {
    name: 'StaticConfig',
    components: {
      JsonCode,
    },
    mixins: [uploadExcel],
    props: {
      staticData: {
        type: Object,
        required: true,
        default: () => ({}),
      },
      showCancel: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        type: 1,
        json: '{}'
      }
    },
    watch: {
      staticData: {
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.json = JSON.stringify(n, function(k, v) { return v === undefined ? null : v }, 4) || '{}'
        },
        deep: true,
        immediate: true,
      },
    },
    methods: {
      exportTemplate() {
        const url = `${STATIC_PREFIX}/template/template.xlsx`
        window.open(url, '__slef')
      },
      changeType(type) {
        this.type = type
      },
      onChange(file) {
        const isAccept = ['application/vnd.openxmlformats-officedocument.spreadsheetml.sheet', 'application/vnd.ms-excel'].includes(file.raw.type)
        if (isAccept) {
          this.readExcel(file)
        } else {
          this.$message.error('文件类型不正确！')
        }
      },
      handleExcelData(data) {
        this.json = data
        this.handleInput()
      },
      handleRemove() {
        this.json = {}
        this.$emit('response', { response: undefined, responseOptions: undefined, isStatic: true })
      },
      async handleInput() {
        await this.$nextTick()
        await sleep(200)
        if (typeof this.json === 'string') {
          const res = trimAll(this.json)
          if (!isValidJsonString(res)) {
            this.$message.error('JSON格式不正确！')
            return
          }
          try {
            const response = JSON.parse(this.json)
            this.$emit('update:staticData', response)
            this.$emit('response', { response, responseOptions: getObjectTree(response), isStatic: true })
          } catch (e) {
            this.$message.error('JSON格式不正确！')
          }
        } else {
          this.$emit('update:staticData', this.json)
          this.$emit('response', { response: this.json, responseOptions: getObjectTree(this.json), isStatic: true })
        }
      },
      cancel() {
        this.$emit('cancel')
      }
    }
  }
</script>

<style lang="less" scoped>
  .static-config::v-deep {
    .api-button-group {
      display: flex;
      align-items: center;
      width: 240px;
      margin-bottom: 10px;
      margin-left: 10px;
      .api-button {
        flex: 1;
      }
    }
    .uploader {

    }

    .upload-wrapper {
      height: 300px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
</style>
