<template>
  <el-dialog
    custom-class="low-code-dialog"
    class='data-select-dialog'
    title="事件处理行为"
    :visible="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <n-form
      v-loading.lock='loading'
      size="mini"
      class="data-form"
      ref="dataForm"
      @submit.native.prevent
      label-width="80px"
      label-position="left"
    >
      <n-form-item class='event-name-item' label="处理标题" required>
        <div style='display: flex; align-items: center; padding-left: 10px;'>
          <el-input v-model="dataForm.name" placeholder="请输入处理标题" />
          <el-button style='margin-left: 10px;' type='primary' @click='handleShowEventSelect'>选择已存事件处理程序</el-button>
        </div>
      </n-form-item>
      <div class="condition-panel">
        <n-form-item class="panel-title" label="条件配置">
          <el-switch
            v-model="isEnableCondition"
          ></el-switch>
        </n-form-item>
        <div
          class="condition-panel-inner"
          v-if="isEnableCondition"
        >
          <template v-if="!dataForm.condition.length">
            <div class="condition-item">
              <div class="setting">
                <div class="btns">
                  <div class="add-btn-primary" @click="handleAddCondition">
                    +
                  </div>
                </div>
              </div>
            </div>
          </template>
          <div
            class="condition-item"
            v-for="(c, index) in dataForm.condition"
            :key="c"
          >
            <n-table :data="c.child" border header-cell-class-name='header-cell'>
              <n-table-column prop="variable" label="条件配置内容">
                <div class="value-input" slot-scope="scope">
                  <el-tooltip
                    v-if="scope.row.variable !== undefined"
                    effect="dark"
                    :content="formatTip(scope.row.variable)"
                    placement="top"
                  >
                    <el-tag
                      class="value-tag"
                      closable
                      disable-transitions
                      @click="handleTagClick(scope.row, 'variable')"
                      @close="scope.row.variable = undefined"
                      :type='getTagType(scope.row.variable)'
                    >
                      {{ formatTag(getElementName(scope.row.variable), 12) }}
                    </el-tag>
                  </el-tooltip>
                  <div v-else class="value-input-content">
                    <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                      <el-button
                        size="mini"
                        @click="showInputDialog('text', scope.row, 'variable')"
                        >文本
                      </el-button>
                      <el-button
                        size="mini"
                        @click="
                          showInputDialog('number', scope.row, 'variable')
                        "
                        >数值
                      </el-button>
                      <el-button
                        size="mini"
                        @click="
                          showInputDialog('object', scope.row, 'variable')
                        "
                        >对象
                      </el-button>
                      <el-button
                        size="mini"
                        @click="handleShowVariableSelect(scope.row, 'variable')"
                        >变量
                      </el-button>
                    </el-button-group>
                    <MoreTypeDropdown @change="(v) => scope.row.variable = v" />
                  </div>
                </div>
              </n-table-column>
              <n-table-column prop="rule" label="规则" width="120px">
                <template slot-scope="scope">
                  <el-select size="mini" v-model="scope.row.rule">
                    <el-option
                      v-for="item in ruleOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    >
                      <div style="float: left">{{ item.label }}</div>
                      <div style="float: right; color: #8492a6; font-size: 12px">{{ item.desc }}</div>
                    </el-option>
                  </el-select>
                </template>
              </n-table-column>
              <n-table-column prop="value" label="值">
                <template slot-scope="scope">
                  <div class="value-input" v-show="!['falsely', 'truly'].includes(scope.row.rule)">
                    <el-tooltip
                      v-if="scope.row.value !== undefined"
                      effect="dark"
                      :content="formatTip(scope.row.value)"
                      placement="top"
                    >
                      <el-tag
                        class="value-tag"
                        closable
                        disable-transitions
                        @click="handleTagClick(scope.row, 'value')"
                        @close="scope.row.value = undefined"
                        :type='getTagType(scope.row.value)'
                      >
                        {{ formatTag(getElementName(scope.row.value), 12) }}
                      </el-tag>
                    </el-tooltip>
                    <div v-else class="value-input-content">
                      <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                        <el-button
                          size="mini"
                          @click="showInputDialog('text', scope.row, 'value')"
                          >文本
                        </el-button>
                        <el-button
                          size="mini"
                          @click="showInputDialog('number', scope.row, 'value')"
                          >数值
                        </el-button>
                        <el-button
                          size="mini"
                          @click="showInputDialog('object', scope.row, 'value')"
                          >对象
                        </el-button>
                        <el-button
                          size="mini"
                          @click="handleShowVariableSelect(scope.row, 'value')"
                          >变量
                        </el-button>
                      </el-button-group>
                      <MoreTypeDropdown @change="(v) => scope.row.value = v" />
                    </div>
                  </div>
                  <span v-if="scope.row.rule === 'falsely'">false/空文本/null/undefined/0</span>
                  <span v-if="scope.row.rule === 'truly'">对象/数组/非空字符串/true/非零数值...</span>
                </template>
              </n-table-column>
              <n-table-column prop="" label="操作" width="200px">
                <template slot-scope="scope">
                  <div class="control-col">
                    <el-switch
                      v-if="scope.$index !== c.child.length - 1"
                      v-model="scope.row.unit"
                      active-value="||"
                      inactive-value="&&"
                      active-text="或"
                      inactive-text="且"
                    >
                    </el-switch>
                    <div class="btns">
                      <div
                        class="add-btn-primary"
                        @click="handleAddConditionChild(scope.$index, c.child)"
                      >
                        +
                      </div>
                      <div
                        class="add-btn-primary"
                        @click="
                          handleDeleteConditionChild(scope.$index, c.child)
                        "
                      >
                        -
                      </div>
                    </div>
                  </div>
                </template>
              </n-table-column>
            </n-table>
            <div class="setting">
              <el-switch
                v-if="index !== dataForm.condition.length - 1"
                v-model="c.unit"
                active-value="||"
                inactive-value="&&"
                active-text="或"
                inactive-text="且"
              >
              </el-switch>
              <div class="btns">
                <div
                  class="add-btn-primary"
                  v-if="index === dataForm.condition.length - 1"
                  @click="handleAddCondition"
                >
                  +
                </div>
                <div
                  class="add-btn-primary"
                  v-if="dataForm.condition.length > 1"
                  @click="handleDeleteCondition(index)"
                >
                  -
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          class="condition-panel-inner placeholder"
          v-if="!isEnableCondition"
        >
          无条件配置
        </div>
      </div>
      <div class="action-panel">
        <n-form-item class="panel-title" label="选择动作">
          <el-select
            size="mini"
            v-model="dataForm.action"
            placeholder="请选择动作"
            @change="handleActionChange"
          >
            <el-option-group
              v-for="group in actionOptions"
              :key="group.label"
              :label="group.label"
            >
              <el-option
                v-for="item in group.options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-option-group>
          </el-select>
        </n-form-item>
        <template v-if="dataForm.action">
          <template v-if="dataForm.action === 'control'">
            <div
              class="action-panel-inner"
              v-for="(bind, elementId) of dataForm.binds"
              :key="elementId"
            >
              <div
                class="action-delete-btn"
                @click="handleDeleteBind(elementId)"
              >
                <div class="add-btn-primary">-</div>
              </div>
              <div class="action-item">
                <n-form-item label="选择组件">
                  <tree-select
                    :tree="treeData"
                    :width="830"
                    :multiple="false"
                    node-key="elementId"
                    :default-selected-keys="[elementId]"
                    :show-app-node='true'
                    @change="handleBindChange($event, elementId)"
                  >
                  </tree-select>
                </n-form-item>
                <div class="fn" v-for="(fn, index) in bind.fns" :key="fn.name">
                  <n-form-item label="选择操作">
                    <div class="form-select">
                      <el-select v-model="fn.name">
                        <el-option
                          :disabled="
                            bind.fns.map((f) => f.name).includes(fnOption.value)
                          "
                          :label="fnOption.label"
                          :value="fnOption.value"
                          v-for="fnOption in getFnsByElementId(elementId)"
                          :key="fnOption.value"
                        ></el-option>
                      </el-select>
                      <div
                        class="btn-wrapper"
                        v-if="isShowAddFnsBtn(elementId, bind.fns)"
                        @click="handleAddFns(bind.fns)"
                      >
                        <div class="add-btn-primary">+</div>
                      </div>
                      <div
                        class="btn-wrapper"
                        v-if="bind.fns.length > 1"
                        @click="handleDeleteFns(bind.fns, index)"
                      >
                        <div class="add-btn-primary">-</div>
                      </div>
                    </div>
                  </n-form-item>
                  <n-table
                    v-if="fn.name === COMMON_FN_ASSIGN"
                    :data="getVariables(bind)"
                    border
                    header-cell-class-name='header-cell'
                  >
                    <n-table-column
                      prop="variable"
                      label="组件变量"
                      min-width="200px"
                    >
                      <div class="variable-column" slot-scope="scope">
                        <el-select
                          v-model="scope.row.selected"
                          @change="
                            handleUpdateVariable(
                              $event,
                              scope.row.variable,
                              bind
                            )
                          "
                        >
                          <el-option
                            :disabled="
                              Object.keys(bind.variable).includes(option.value)
                            "
                            :label="option.label"
                            :value="option.value"
                            v-for="option in getVariablesByElementId(elementId)"
                            :key="option.value"
                          >
                            <span style="font-size: 14px;text-decoration:line-through;" v-if="option.isNotSupported">{{ option.label }}</span>
                            <span style="font-size: 14px;" v-else>{{ option.label }}</span>
                            <span style="color: #8492a6;font-size: 12px;margin-left: 10px;" >{{ option.desc }}</span>
                          </el-option>
                        </el-select>
                      </div>
                    </n-table-column>
                    <n-table-column prop="desc" label="描述" min-width="200px">
                      <template slot-scope="scope">
                        {{ getConfig(scope.row.variable, elementId).desc }}
                      </template>
                    </n-table-column>
                    <n-table-column prop="value" label="值" min-width="300px">
                      <div
                        class="value-input"
                        slot-scope="scope"
                        v-if="scope.row.selected"
                      >
                        <el-tooltip
                          v-if="bind.variable[scope.row.variable] !== undefined"
                          effect="dark"
                          :content="
                            formatTip(bind.variable[scope.row.variable])
                          "
                          placement="top"
                        >
                          <el-tag
                            class="value-tag"
                            closable
                            disable-transitions
                            @click="
                              handleTagClick(bind.variable, scope.row.variable, getAttrOptions(elementId, getConfig(scope.row.variable, elementId)))
                            "
                            @close="bind.variable[scope.row.variable] = undefined"
                            :type='getTagType(bind.variable[scope.row.variable])'
                          >
                            {{ formatTag(getElementName(bind.variable[scope.row.variable]), 12) }}
                          </el-tag>
                        </el-tooltip>
                        <div v-else class="value-input-content">
                          <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                            <template v-if='!getConfig(scope.row.variable, elementId).attrVue'>
                              <el-button
                                size="mini"
                                @click="
                                showInputDialog(
                                  'text',
                                  bind.variable,
                                  scope.row.variable
                                )
                              "
                              >文本
                              </el-button>
                              <el-button
                                size="mini"
                                @click="
                                showInputDialog(
                                  'number',
                                  bind.variable,
                                  scope.row.variable
                                )
                              "
                              >数值
                              </el-button>
                              <el-button
                                size="mini"
                                @click="
                                showInputDialog(
                                  'object',
                                  bind.variable,
                                  scope.row.variable
                                )
                              "
                              >对象
                              </el-button>
                            </template>
                            <el-button
                              v-else
                              size="mini"
                              @click="
                                showInputDialog(
                                  'attrVue',
                                  bind.variable,
                                  scope.row.variable,
                                  undefined,
                                  getAttrOptions(elementId, getConfig(scope.row.variable, elementId))
                                )
                              "
                            >选择</el-button>
                            <el-button
                              size="mini"
                              @click="
                                handleShowVariableSelect(
                                  scope.row,
                                  '',
                                  handleUpdateVariableValue(
                                    bind.variable,
                                    scope.row.variable
                                  )
                                )
                              "
                              >变量
                            </el-button>
                          </el-button-group>
                          <MoreTypeDropdown @change="(v) => bind.variable[scope.row.variable] = v" />
                        </div>
                      </div>
                    </n-table-column>
                    <n-table-column prop="" label="操作" width="90px">
                      <template slot-scope="scope">
                        <div class="control-col">
                          <div class="btns">
                            <div
                              class="add-btn-primary"
                              @click="handleAddVariable(bind.variable)"
                            >
                              +
                            </div>
                            <div
                              class="add-btn-primary"
                              @click="
                                handleDeleteVariable(
                                  bind.variable,
                                  scope.row.variable
                                )
                              "
                            >
                              -
                            </div>
                          </div>
                        </div>
                      </template>
                    </n-table-column>
                  </n-table>
                  <n-form-item label="选择函数" v-if="fn.name === EXECUTE_CUSTOM_FN">
                    <div class="form-select" v-for="(opt, i) in fn.execute_fns" :key="i">
                      <el-select v-model="opt.id" @change="customFnsChange(opt, elementId)">
                        <el-option
                          :disabled="
                            fn.execute_fns.map((f) => f.id).includes(fnOption.value)
                          "
                          :label="fnOption.label"
                          :value="fnOption.value"
                          v-for="fnOption in getCustomFnsByElementId(elementId)"
                          :key="fnOption.value"
                        ></el-option>
                      </el-select>
                      <div
                        class="btn-wrapper"
                      >
                        <div class="add-btn-primary" @click="fn.execute_fns.push({ id: '', name: '' })">+</div>
                      </div>
                      <div
                        v-if="fn.execute_fns.length > 1"
                        class="btn-wrapper"
                      >
                        <div class="add-btn-primary" @click="fn.execute_fns.splice(i, 1)">-</div>
                      </div>
                    </div>
                  </n-form-item>
                  <TabsParams :elementId="elementId" :config="fn" :callbackId="dataForm.callbackId" :isChange="fn.name === CHANGE_TAB" v-if="[CHANGE_TAB, ADD_TAB, CHANGE_BADGE].includes(fn.name)" :isChangeBadge="fn.name === CHANGE_BADGE" />
                  <CollapseParams :elementId="elementId" :config="fn" :callbackId="dataForm.callbackId" v-if="[CHANGE_COLLAPSE, TOGGLE_COLLAPSE].includes(fn.name)" />
                  <StepsParams :elementId="elementId" :config="fn" :callbackId="dataForm.callbackId" v-if="[CHANGE_STEP].includes(fn.name)" />
                  <StyleConfigParams :elementId="elementId" :config="fn" :callbackId="dataForm.callbackId" v-if="[CHANGE_STYLES].includes(fn.name)" />
                  <AnimationConfigParams :elementId="elementId" :config="fn" :callbackId="dataForm.callbackId" v-if="[PLAY_ANIMATION].includes(fn.name)" />
                </div>
              </div>
            </div>
            <div class="action-add" @click="handleAddBind">
              <i class="iconfont icon-tuopugongju_fangda"></i>
            </div>
          </template>
          <template v-if="dataForm.action === 'link'">
            <div class="action-panel-inner" v-loading="linkLoading">
              <div class="action-item">
                <n-form-item label="跳转类型">
                  <el-select
                    v-model="dataForm.linkOptions.jumpType"
                    @change="handleJumpTypeChange"
                  >
                    <el-option label="应用内跳转" value="page"></el-option>
                    <el-option label="第三方链接跳转" value="link"></el-option>
                  </el-select>
                </n-form-item>
                <template v-if="dataForm.linkOptions.jumpType === 'link'">
                  <n-form-item>
                    <template slot='label'>
                      <span>链接</span>
                      <el-tooltip popper-class="tooltip-question" placement="top">
                        <div slot="content">1.动态路径参数需要在url路径前加冒号表示，动态参数在params中配置，如/user/:id/:name<br/>2.运行阶段url的参数由下表所填写的页面参数值决定</div>
                        <i class="el-icon-question"></i>
                      </el-tooltip>
                    </template>
                    <el-input v-model="linkUrl" placeholder="输入跳转链接"></el-input>
                  </n-form-item>
                  <n-form-item label="页面参数">
                    <n-table :data="dataForm.linkOptions.linkParams" border header-cell-class-name='header-cell'>
                      <n-table-column prop="name" label="变量名">
                        <template slot-scope="scope">
                          <el-input v-model="scope.row.name" :disabled='paramsInputDisabled(scope.row.name)' @input="handleParamsChange"></el-input>
                        </template>
                      </n-table-column>
                      <n-table-column prop="value" label="参数值">
                        <template slot-scope="scope">
                          <div class="value-input">
                            <el-tooltip
                              v-if="scope.row.value !== undefined"
                              effect="dark"
                              :content="formatTip(scope.row.value)"
                              placement="top"
                            >
                              <el-tag
                                class="value-tag"
                                closable
                                disable-transitions
                                @click="handleTagClick(scope.row, 'value')"
                                @close="scope.row.value = undefined"
                                :type='getTagType(scope.row.value)'
                              >
                                {{ formatTag(getElementName(scope.row.value), 12) }}
                              </el-tag>
                            </el-tooltip>
                            <div v-else class="value-input-content">
                              <el-button-group>
                                <el-button
                                  @click="
                                    showInputDialog('text', scope.row, 'value')
                                  "
                                  >文本
                                </el-button>
                                <el-button
                                  @click="
                                    handleShowVariableSelect(scope.row, 'value')
                                  "
                                  >变量
                                </el-button>
                              </el-button-group>
                            </div>
                          </div>
                        </template>
                      </n-table-column>
                      <n-table-column prop="value" label="操作">
                        <template slot-scope="scope">
                          <div class="add-btn-primary" @click="handleAddParams(scope.$index)">+</div>
                          <div v-if='!paramsInputDisabled(scope.row.name)' class="add-btn-primary" @click="handleDeleteParams(scope.$index)">-</div>
                        </template>
                      </n-table-column>
                    </n-table>
                  </n-form-item>
                </template>
                <template v-if="dataForm.linkOptions.jumpType === 'page'">
                  <n-form-item label="选择页面">
                    <el-select
                      v-model="dataForm.linkOptions.pageId"
                      @change="handleLinkPageChange"
                    >
                      <el-option
                        v-for="page in pageData"
                        :key="page.elementId"
                        :label="page.elementName"
                        :value="page.elementId"
                      ></el-option>
                    </el-select>
                  </n-form-item>
                  <n-form-item label="页面参数">
                    <n-table :data="dataForm.linkOptions.pageVariables" border header-cell-class-name='header-cell'>
                      <n-table-column prop="name" label="变量名">
                      </n-table-column>
                      <n-table-column prop="desc" label="描述"></n-table-column>
                      <n-table-column prop="value" label="参数值">
                        <template slot-scope="scope">
                          <div class="value-input">
                            <el-tooltip
                              v-if="scope.row.value !== undefined"
                              effect="dark"
                              :content="formatTip(scope.row.value)"
                              placement="top"
                            >
                              <el-tag
                                class="value-tag"
                                closable
                                disable-transitions
                                @click="handleTagClick(scope.row, 'value')"
                                @close="scope.row.value = undefined"
                                :type='getTagType(scope.row.value)'
                              >
                                {{ formatTag(getElementName(scope.row.value), 12) }}
                              </el-tag>
                            </el-tooltip>
                            <div v-else class="value-input-content">
                              <el-button-group>
                                <el-button
                                  @click="
                                    showInputDialog('text', scope.row, 'value')
                                  "
                                  >文本
                                </el-button>
                                <el-button
                                  @click="
                                    handleShowVariableSelect(scope.row, 'value')
                                  "
                                  >变量
                                </el-button>
                              </el-button-group>
                            </div>
                          </div>
                        </template>
                      </n-table-column>
                    </n-table>
                  </n-form-item>
                </template>
                <n-form-item label="跳转方式">
                  <el-select v-model="dataForm.linkOptions.type">
                    <el-option label="浏览器标签页" value="blank"></el-option>
                    <el-option label="应用内标签页（需开启多Tab功能）" value="tab" :disabled="dataForm.linkOptions.jumpType === 'page'"></el-option>
                    <el-option label="当前页" value="self"></el-option>
                  </el-select>
                </n-form-item>
              </div>
            </div>
          </template>
          <template v-if="dataForm.action === 'commonAction'">
            <div class="action-panel-inner">
              <div class="action-item">
                <n-form-item label="处理程序名称" style="white-space: nowrap;">
                  <el-select
                    v-model="dataForm.commonActionId"
                  >
                    <el-option v-for="item in commonActionsOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </n-form-item>
              </div>
            </div>
          </template>
          <template v-if="dataForm.action === 'pageEmit'">
            <div class="action-panel-inner">
              <div class="action-item">
                <n-form-item label="事件名称">
                  <el-select
                    v-model="dataForm.pageEmit.emitKey"
                    @change="initPageEmitParams"
                  >
                    <el-option v-for="item in pageEmitOptions" :key="item.emitKey" :label="item.name" :value="item.emitKey"></el-option>
                  </el-select>
                </n-form-item>
                <template >
                  <n-form-item label="页面参数" v-if="dataForm.pageEmit.emitKey">
                    <n-table :data="dataForm.pageEmit.params" border header-cell-class-name='header-cell'>
                      <n-table-column prop="name" label="参数名"></n-table-column>
