  import API from '@/api/application'
  import { CLOSE, OPEN, RESET, HTTP_REQUEST, ADD_TAB, CHANGE_TAB, CHANGE_COLLAPSE, TOGGLE_COLLAPSE, CHANGE_STEP, CHANGE_BADGE, CHANGE_STYLES, PLAY_ANIMATION, EXECUTE_CUSTOM_FN, PAGE_COMMON_EVENTS } from '@/config/eventName'
  import elementEditor from '@/mixins/elementEditor'
  import { dialogTypes } from '@/config/widgets'
  import { parse } from 'path-to-regexp'
  import URI from 'urijs'
  import { getConfigByKey, getVariableByVersion } from '@/renderer/tools/config'
  import TabsParams from '@/components/ParamsConfig/TabsParams'
  import CollapseParams from '@/components/ParamsConfig/CollapseParams.vue'
  import StepsParams from '@/components/ParamsConfig/StepsParams.vue'
  import { pageInject } from '@/renderer/tools/pageStore'
  import StyleConfigParams from '@/components/ParamsConfig/StyleConfigParams.vue'
  import AnimationConfigParams from '@/components/ParamsConfig/AnimationConfigParams.vue'
  import { notSupportedKeys } from '@/config/code-var'
  import { getAppVariables } from '@/utils'

  export default {
    inject: [...pageInject],
    name: 'HandlerDialog',
    components: {
      TreeSelect,
      VueJsonPretty,
      draggable,
      TabsParams,
      CollapseParams,
      StepsParams,
      StyleConfigParams,
      AnimationConfigParams,
    },
