        PLAY_ANIMATION,
        loading: false,
        dialogVisible: false,
        isEnableCondition: false,
        activeCollapses: ['success'],
        ruleOptions: [
          {
            label: '等于',
            value: '=',
            desc: '全等比较，对象不判引用地址'
          },
          {
            label: '为假',
            value: 'falsely',
            desc: '转为布尔值进行比较'
          },
          {
            label: '为真',
            value: 'truly',
            desc: '转为布尔值进行比较'
          },
          {
            label: '大于',
            value: '>',
            desc: '转为数值进行比较'
          },
          {
            label: '小于',
            value: '<',
            desc: '转为数值进行比较'
          },
          {
            label: '大于等于',
            value: '>=',
            desc: '转为数值进行比较'
          },
          {
            label: '小于等于',
            value: '<=',
            desc: '转为数值进行比较'
          },
          {
            label: '不等于',
            value: '!=',
            desc: '全等比较，对象不判引用地址'
          },
          {
            label: '包含',
            value: 'in',
            desc: '左边可为数组或字符串'
          },
          {
            label: '不包含',
            value: 'out',
            desc: '左边可为数组或字符串'
          },
          {
            label: '正则匹配',
            value: 'RegExp',
            desc: '左侧为字符串,右侧为正则(不含//),区分大小写'
          },
        ],
        actionOptions: [
          {
            label: '基础动作',
            options: [
              {
                value: 'control',
                label: '组件操作',
              },
            ],
          },
          {
            label: '链接动作',
            options: [
              {
                value: 'link',
                label: '跳转页面',
              },
              {
                value: 'open',
                label: '打开窗体',
              },
              {
                value: 'close',
                label: '关闭窗体',
              },
            ],
          },
          {
            label: '调用动作',
            options: [
              {
                value: 'api',
                label: '调用API',
              },
              {
                value: 'sql',
                label: '调用SQL',
              },
              {
                value: 'js',
                label: '调用JS脚本',
              },
              {
                value: 'devops',
                label: '调用自动化运维操作',
              },
              {
                value: 'commonAction',
                label: '调用公共事件处理行为',
              },
              {
                value: 'pageEmit',
                label: '触发页面事件',
              },
            ],
          },
          {
            label: '刷新动作',
            options: [
              {
                value: 'refresh',
                label: '刷新当前页',
              },
              {
                value: 'reset',
                label: '刷新组件',
              },
            ],
          },
        ],
        dataForm: {
          id: undefined,
          callbackId: undefined,
          functionId: undefined,
          eventName: '',
          name: '',
          condition: [],
          binds: {},
          action: '',
          linkOptions: {
            pageId: undefined,
            type: 'self',
            pageVariables: [],
            jumpType: 'page',
            linkUrl: '',
            linkParams: []
          },
          controlOptions: {
            elementId: undefined,
          },
          apiRequest: undefined,
          sqlRequest: undefined,
          callback: [],
          loadingConfig: {
            enable: true,
            text: '',
            showMask: false,
            background: ''
          },
          responseConfig: {
            isBlob: false,
            isUseHeaderFileName: true,
            fileName: undefined
          },
          pageEmit: {
            emitKey: '',
            params: []
          },
          commonActionId: ''
        },
        variableRowDetail: null,
        linkLoading: false,
        response: undefined,
        selectedPath: undefined,
        activeCollapse: ['config'],
        dataSet: undefined,
        defaultArguments: [
          {
            key: 'setValue',
            value: undefined,
            test: (path, newValue) => {},
            name: '内置方法.变量赋值',
            desc: 'setValue(path, newValue)<br/>{String} path 路径<br/>{any} newValue 值<br/>返回值：无',
            definedTest: true
          },
        ]
      }
    },
    computed: {
      pageData() {
        const pageTree = this.$store.state.CODE.lowCodeStore.pageData
        const pageList = treeUtil
          .toList(pageTree)
          .filter(
            (page) =>
              page.elementType === 'page' &&
              page.elementId !== this.layoutData.elementId
          )
        return _.uniqBy(pageList, 'pageDesignId')
      },
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      layoutData: {
        get() {
          return this.getStore().layoutData
        },
      },
      treeData() {
        const copy = _.cloneDeep(this.layoutData)
        treeUtil.forEach([copy], (node) => {
          const showChildren = []
          node.children.forEach(item => {
            if (['slot'].includes(item.elementType) && ['data'].includes(this.getElementPropsById(item.elementId).slotType)) {
              return
            }
            if (['dialog-container', 'timer-container'].includes(item.elementType)) {
              if (!item.children.length) {
                return
              }
              item.disabled = true
            }
            showChildren.push(item)
          })
          node.children = showChildren
          node.elementName =
            this.getElementPropsById(node.elementId).elementName || node.elementName
        })
        const children =
          copy.children.find((c) => c.elementType === 'dialog-container')
            ?.children || []
        switch (this.dataForm.action) {
          case 'open':
            return children.map((c) => ({ ...c, children: [] }))
          case 'close':
            return children.map((c) => ({ ...c, children: [] })).filter(({ elementType, elementVersion }) => {
              const { fns } = getConfigByKey.call(this, { elementType, elementVersion })
              return fns.find(item => item.fn === CLOSE)
            })
          default:
            return [copy]
        }
      },
      successCallback() {
        return this.dataForm.callback.filter((item) => item.type === 'success')
      },
      errorCallback() {
        return this.dataForm.callback.filter((item) => item.type === 'error')
      },
      linkUrl: {
        get() {
          return this.dataForm.linkOptions.linkUrl
        },
        set(value) {
          const uri = new URI(value)
          const pathString = uri.pathname()
          let dynamicPathArray = []
          try {
            dynamicPathArray = parse(pathString).filter(item => _.isObject(item)).map(item => item.name)
          } catch (e) {
            this.dataForm.linkOptions.linkUrl = value
            return
          }
          const dynamicPathParams = dynamicPathArray.map(name => {
            const current = this.dataForm.linkOptions.linkParams.find((d) => d.name === name)
            return {
              name,
              value: current?.value,
            }
          })
          const searchParams = uri.search(true)
          const arr = []
          Object.entries(searchParams).forEach(([name, value]) => {
            const current = this.dataForm.linkOptions.linkParams.find(d => d.name === name)
            arr.push({
              name,
              value
            })
          })
          arr.unshift(...dynamicPathParams)
          this.dataForm.linkOptions.linkParams = arr.length ? _.uniqBy(arr, 'name') : [{name: '', value: undefined}]
          this.dataForm.linkOptions.linkUrl = value
        },
      },
      commonActionsOptions() {
        const events = this.getStore().eventsData[this.layoutData.elementId]
        const actions = events ? (events[PAGE_COMMON_EVENTS] || []) : []
        return actions.map(item => ({ value: item.id, label: item.name }))
      },
      pageEmitOptions() {
        const { layoutData: { elementType, elementVersion, elementId } } = this.getStore()
        const data = getConfigByKey.call(this, { elementType, elementVersion, elementId })
        return data.events
      },
      pageEmitParams() {
        return this.pageEmitOptions.find(item => item.emitKey === this.dataForm.pageEmit.emitKey)?.params
      }
    },
    methods: {
      clear() {
        this.loading = false
        this.isEnableCondition = false
        this.variableRowDetail = null
        this.linkLoading = false
        this.response = undefined
        this.selectedPath = undefined
        this.dialogVisible = false
      },
      open(data) {
        this.dataForm = {
          id: undefined,
          callbackId: undefined,
          functionId: undefined,
          eventName: '',
          name: '',
          condition: [],
          binds: {},
          action: undefined,
          linkOptions: {
            pageId: undefined,
            type: 'self',
            pageVariables: [],
            jumpType: 'page',
            linkUrl: '',
            linkParams: []
          },
          controlOptions: {
            elementId: undefined,
          },
          apiRequest: undefined,
          sqlRequest: undefined,
          callback: [],
          loadingConfig: {
            enable: true,
            text: '',
            showMask: false,
            background: ''
          },
          responseConfig: {
            isBlob: false,
            isUseHeaderFileName: true,
            fileName: undefined
          },
          pageEmit: {
            emitKey: '',
            params: []
          },
          commonActionId: data.commonActionId
        }
        const copy = _.cloneDeep(data)
        Object.assign(this.dataForm, copy)
        this.isEnableCondition = !!this.dataForm.condition.length
        if (!this.dataForm.condition.length) {
          this.dataForm.condition = [
            {
              child: [
                {
                  variable: undefined,
                  rule: '=',
                  value: undefined,
                  unit: '&&',
                },
              ],
              unit: '||',
            },
            {
              child: [
                {
                  variable: undefined,
                  rule: '=',
                  value: undefined,
                  unit: '&&',
                }
              ],
              unit: '||',
            },
          ]
        }
        if (this.dataForm.action === 'link') {
          if(this.dataForm.linkOptions.jumpType === 'page') {
            this.handleLinkPageChange(this.dataForm.linkOptions.pageId)
          }
        }
        this.dialogVisible = true
      },
      cancel() {
        this.clear()
      },
      async handleGenerateData() {
        const res = _.cloneDeep(this.dataForm)
        res.condition = this.isEnableCondition ? res.condition : []
        let name
        switch (this.dataForm.action) {
        case 'link': {
          const { jumpType, pageId, linkUrl } = this.dataForm.linkOptions
          if (jumpType === 'page' && !pageId) {
            this.$message.error('请选择页面')
            return Promise.reject()
          }
          if (jumpType === 'link' && !linkUrl.trim()) {
            this.$message.error('请输入跳转链接')
            return Promise.reject()
          }
          res.binds = {
            [pageId || linkUrl]: {
              variable: {},
              fns: [
                {
                  name: COMMON_FN_LINK,
                  params: this.dataForm.linkOptions,
                },
              ],
            },
          }
        }
          break
        case 'open':
        case 'close':
        case 'reset':
        case 'refresh':
          if (!this.dataForm.controlOptions.elementId) {
            this.$message.error('请选择组件')
            return Promise.reject()
          }
          switch (this.dataForm.action) {
          case 'open':
            name = OPEN
            break
          case 'close':
            name = CLOSE
            break
          case 'reset':
          case 'refresh':
            name = RESET
            break
          }
          res.binds = {
            [this.dataForm.controlOptions.elementId]: {
              variable: {},
              fns: [
                {
                  name,
                  params: undefined,
                },
              ],
            },
          }
          break
        case 'api':
        case 'devops':
          res.binds = {
            [this.layoutData.elementId]: {
              variable: {},
              fns: [
                {
                  name: HTTP_REQUEST,
                  params: {
                    apiRequest: this.dataForm.apiRequest,
                    callback: this.dataForm.callback,
                    loadingConfig: this.dataForm.loadingConfig,
                    responseConfig: this.dataForm.action === 'api' ? this.dataForm.responseConfig : undefined
                  },
                },
              ],
            },
          }
          break
        case 'sql':
          res.binds = {
            [this.layoutData.elementId]: {
              variable: {},
              fns: [
                {
                  name: HTTP_REQUEST,
                  params: {
                    sqlRequest: this.dataForm.sqlRequest,
                    callback: this.dataForm.callback,
                    loadingConfig: this.dataForm.loadingConfig
                  },
                },
              ],
            },
          }
          break
        case 'js':
          await this.$refs.jsConfig.confirm()
          res.binds = {
            [this.layoutData.elementId]: {
              variable: {},
              fns: [
                {
                  name: COMMON_FN_JS,
                  params: {
                    functionId: this.dataForm.functionId,
                    variableId: `${FUNCTION_FLAG}${this.dataForm.functionId}`
                  },
                },
              ],
            },
          }
          res.functionId = this.dataForm.functionId
          break
          case 'pageEmit': {
            const { emitKey, params } = this.dataForm.pageEmit
            if (!emitKey) {
              this.$message.error('请选择事件')
              return Promise.reject()
            }
            res.binds = {
              [this.getStore().layoutData.elementId]: {
                variable: {},
                fns: [
                  {
                    name: COMMON_FN_PAGE_EMIT,
                    params: this.dataForm.pageEmit,
                  },
                ],
              },
            }
          }
          break
          case 'commonAction': {
            const id = this.dataForm.commonActionId
            if (!id) {
              this.$message.error('请选择事件处理程序')
              return Promise.reject()
            }
            res.binds = {
              [this.getStore().layoutData.elementId]: {
                variable: {},
                fns: [
                  {
                    name: COMMON_FN_COMMON_ACTION,
                    params: {
                      id
                    },
                  },
                ],
              },
            }
          }
          break
          case 'control': {
            for(const key in this.dataForm.binds) {
              const { fns } = this.dataForm.binds[key]
              if(fns && fns.length) {
                for(const fn of fns) {
                  // 只要有一个调用函数没选
                  if(fn.name === EXECUTE_CUSTOM_FN && fn.execute_fns?.some(item => !item.id)) {
                    this.$message.error('请选择函数')
                    return Promise.reject()
                  }
                }
              }
            }
          }
          break
        }
        this.$delete(res.binds, 'newBind')
        Object.values(res.binds).forEach((bind) => {
          this.$delete(bind.variable, 'newVariable')
        })
        return Promise.resolve(res)
      },
      confirm() {
        if (!this.dataForm.name) {
          this.$message.error('请输入处理标题')
          return
        }
        if (!this.dataForm.action) {
          this.$message.error('请选择动作')
          return
        }
        this.loading = true
        setTimeout(async () => {
          try {
            const res = await this.handleGenerateData()
            this.loading = false
            this.$emit('confirm', res)
            this.clear()
          } catch (e) {
            this.loading = false
          }
        }, 500)
      },
      handleClose(done) {
        this.clear()
        done()
      },
      handleActionChange(action) {
        this.dataForm.binds = {}
        this.dataForm.apiRequest = undefined
        this.dataForm.sqlRequest = undefined
        this.dataForm.callback = []
        switch (action) {
          case 'refresh':
            this.dataForm.controlOptions.elementId = this.layoutData.elementId
            break
          case 'control':
            if (!Object.keys(this.dataForm.binds).length) {
              this.handleAddBind()
            }
            break
        }
      },
      handleLinkPageChange(elementId) {
        const pageDesignId = this.pageData.find(item => item.elementId === elementId).pageDesignId
        this.getPageVariables(pageDesignId)
      },
      initPageEmitParams(value) {
        const pageEmitParams = this.pageEmitOptions.find(item => item.emitKey === value)?.params.map(item => ({ ...item, value: undefined }))
        this.dataForm.pageEmit.params = _.cloneDeep(pageEmitParams)
      },
      handleShowVariableSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.variableSelect.open({
          fullVariable: row[key],
          callbackId: this.dataForm.callbackId,
          emitEvent: this.dataForm.emitEvent
        })
      },
      handleVariableConfirm(selectedVariable) {
        const { row, key, cb } = this.variableRowDetail
        if (key) {
          this.$set(row, key, selectedVariable)
        }
        cb?.(selectedVariable)
      },
      handleAddConditionChild(index, child) {
        child.push({
          variable: undefined,
          rule: '=',
          value: undefined,
          unit: '||',
        })
      },
      handleDeleteConditionChild(index, child) {
        child.splice(index, 1)
        if (!child.length) {
          this.dataForm.condition = this.dataForm.condition.filter(
            (c) => c.child.length
          )
        }
      },
      handleAddCondition() {
        this.dataForm.condition.push({
          child: [
            {
              variable: undefined,
              rule: '=',
              value: undefined,
              unit: '&&',
            },
          ],
          unit: '||',
        })
      },
      handleDeleteCondition(index) {
        this.dataForm.condition.splice(index, 1)
      },
      getVariables(bind) {
        let variables = Object.entries(bind.variable).map(([key, value]) => {
          return {
            variable: key === 'newVariable' ? undefined : key,
            value,
            selected: key === 'newVariable' ? undefined : key,
          }
        })
        if (!variables.length) {
          variables = [
            {
              variable: undefined,
              value: undefined,
              selected: undefined,
            },
          ]
        }
        return variables
      },
      getVariablesByElementId(elementId) {
        if (elementId === 'app') {
          const appVariables = getAppVariables() || {}

          const app = Object.entries(appVariables).map(
            ([key, value]) => ({
              label: `$appVariables.${key}.value`,
              value: `$appVariables.${key}.value`,
              desc: value.desc,
            })
          )
          return app
        } else {
          const { elementType, elementVersion } = getNodeByElementId(this.layoutData, elementId) || {}
          const data = getVariableByVersion.call(this, { elementType, elementVersion, elementId })

          const cssMode = this.getElementPropsById(elementId).cssMode

          if (elementId === this.layoutData.elementId) {
            const innerData = Object.keys(data || {})
              .filter((key) => ![1, 2].includes(data[key].auth) && !['$dynamicVariables', '$appVariables'].includes(key))
              .map((key) => ({
                label: key,
                value: key,
                desc: data[key].desc,
                isNotSupported: notSupportedKeys.includes(key)
              })).filter(item => ((cssMode === 'config' && item.value !== '$stylesText') || (cssMode !== 'config' && item.value !== '$styles')))
            const dynamicVariables = this.getElementPropsById(this.layoutData.elementId).dynamicVariables || {}
            const dynamic = Object.entries(dynamicVariables).map(
              ([key, value]) => ({
                label: `$dynamicVariables.${key}.value`,
                value: `$dynamicVariables.${key}.value`,
                desc: value.desc,
              })
            )
            return [...dynamic, ...innerData]
          }

          return Object.keys(data || {})
            .filter((key) => ![1, 2].includes(data[key].auth))
            .map((key) => ({
              label: key,
              value: key,
              desc: data[key].desc,
              isNotSupported: notSupportedKeys.includes(key)
            }))
            .filter(item => ((cssMode === 'config' && item.value !== '$stylesText') || (cssMode !== 'config' && item.value !== '$styles')))
        }
      },
      getFnsByElementId(elementId) {
        if (['app', this.layoutData.elementId].includes(elementId)) {
          return [
            {
              label: '变量赋值',
              value: COMMON_FN_ASSIGN
            }
          ]
        }
        const currentNode = getNodeByElementId(this.layoutData, elementId)
        if (!currentNode) {
          return []
        }
        const data = getConfigByKey.call(this, { elementType: currentNode.elementType, elementVersion: currentNode.elementVersion })
        if (!data) {
          return []
        }
        const fns =
          data.fns?.map((fn) => ({
            label: fn.name,
            value: fn.fn,
          })) || []
        const commonFns = COMMON_FNS.map((fn) => ({
          label: fn.name,
          value: fn.fn,
        }))
        if (['timer'].includes(currentNode.elementType)) {
          return fns
        }
        if (dialogTypes.includes(currentNode.elementType)) {
          return [
            ...fns,
            {
              label: '变量赋值',
              value: COMMON_FN_ASSIGN
            }
          ]
        }
        return _.uniqBy(fns.concat(commonFns), 'value')
      },
      getCustomFnsByElementId(elementId) {
        return this.getStore().dynamicProps[elementId]?.fnsData?.map((fn) => ({
          label: fn.name,
          value: fn.fn,
        })) || []
      },
      customFnsChange(opt, elementId) {
        return opt.name = this.getCustomFnsByElementId(elementId).find(item => item.value === opt.id)?.label
      },
      isShowAddFnsBtn(elementId, fns) {
        const fnsOptions = this.getFnsByElementId(elementId)
        return fns.length !== fnsOptions.length
      },
      handleDeleteFns(fns, index) {
        fns.splice(index, 1)
      },
      handleAddFns(fns) {
        fns.push({
          name: '',
          params: undefined,
        })
      },
      handleAddBind() {
        this.$set(this.dataForm, 'binds', {
          ...this.dataForm.binds,
          newBind: {
            variable: {},
            fns: [
              {
                name: '',
                params: undefined,
                execute_fns: [{ id: '', name: '' }]
              },
            ],
          },
        })
      },
      handleDeleteBind(elementId) {
        this.$delete(this.dataForm.binds, elementId)
      },
      handleBindChange(selection, o) {
        const n = selection[0]?.elementId || 'newBind'
        if (n === o) {
          return
        }
        if (Object.keys(this.dataForm.binds).includes(n)) {
          this.$confirm('组件重复，请重新选择！')
          return
        }
        this.$set(this.dataForm, 'binds', {
          ...this.dataForm.binds,
          [n]: {
            variable: {},
            fns: [
              {
                name: '',
                params: undefined,
                execute_fns: [{ id: '', name: '' }]
              },
            ],
          },
        })
        this.$delete(this.dataForm.binds, o)
      },
      handleAddVariable(variable) {
        this.$set(variable, 'newVariable', undefined)
      },
      handleDeleteVariable(variable, key) {
        this.$delete(variable, key)
      },
      handleUpdateVariable(n, o, bind) {
        if (!o) {
          this.$set(bind.variable, n, undefined)
        }
        const newVariable = Object.fromEntries(
          Object.entries(bind.variable).map(([k, v]) => {
            if (k === o) {
              return [n, undefined]
            }
            return [k, v]
          })
        )
        this.$set(bind, 'variable', newVariable)
        this.$delete(bind.variable, 'newVariable')
      },
      handleUpdateVariableValue(variable, key) {
        return (selected) => {
          this.$set(variable, key, selected)
        }
      },
      getConfig(variable, elementId) {
        if (!variable || !elementId) {
          return {}
        }
        const key = variable.split('.')[1]
        if (this.layoutData.elementId === elementId && key) {
          const dynamicVariables = this.getElementPropsById(this.layoutData.elementId)
            .dynamicVariables
          return {
            desc: dynamicVariables[key]?.desc,
            type: 'String',
          }
        }
        const { elementType, elementVersion } = getNodeByElementId(this.layoutData, elementId) || {}
        if(elementId === 'app') {
          return {}
        }
        const data = getVariableByVersion.call(this, { elementType, elementVersion, elementId })
        return data[variable] || {}
      },
      getPageVariables(pageDesignId) {
        if (!pageDesignId) {
          this.dataForm.linkOptions.pageVariables = []
          return
        }
        this.linkLoading = true
        this.$http({
          url: API.appPageDesignPageConfig,
          data: {
            pageDesignId: Number(pageDesignId),
            type: 4,
          },
        })
          .then(({ data }) => {
            const config = data.find((d) => d.type === 4)?.config
            if (config) {
              this.dataForm.linkOptions.pageVariables = Object.entries(
                JSON.parse(config)
              )
                .map(([key, value]) => {
                  const current = this.dataForm.linkOptions.pageVariables.find(item => item.name === key)
                  return current || {
                    name: key,
                    ...value,
                  }
                })
                .filter((d) => d.isEntry)
            } else {
              this.dataForm.linkOptions.pageVariables = []
            }
          })
          .finally(() => {
            this.linkLoading = false
          })
      },
      handleControlChange(selection) {
        this.dataForm.controlOptions.elementId = selection[0]?.elementId
      },
      handleResponse({ response }) {
        this.response = response
      },
      handleEditHandler(handler) {
        if (this.response) {
          this.$http({
            url: API.appDesignJsonDataConfigSave,
            data: {
              appDesignId: Number(this.$route.query.vid),
              preferences: [
                {
                  key: `${this.layoutData.elementId}.$response.${handler.id}`,
                  value: JSON.stringify(this.response) || ''
                }
              ]
            }
          }).then(() => {
            this.$refs.handlerDialog.open(handler)
          })
        } else {
          this.$refs.handlerDialog.open(handler)
        }
      },
      handleDeleteHandler(id) {
        this.dataForm.callback = this.dataForm.callback.filter(
          (c) => c.id !== id
        )
      },
      handleAddHandler(type) {
        const id = getRandomHexadecimal(12)
        const newHandler = {
          type,
          id,
          callbackId: id,
          name: '',
          condition: [],
          disabled: false,
          emitEvent: this.dataForm.emitEvent
        }
        this.$http({
          url: API.appDesignJsonDataConfigSave,
          data: {
            appDesignId: Number(this.$route.query.vid),
            preferences: [
              {
                key: `${this.layoutData.elementId}.$response.${newHandler.id}`,
                value: JSON.stringify(this.response) || ''
              }
            ]
          }
        }).then(() => {
          this.$refs.handlerDialog.open(newHandler)
        })
      },
      handleUpdateEventData(res) {
        const index = this.dataForm.callback.findIndex((d) => d.id === res.id)
        if (index !== -1) {
          this.$set(this.dataForm.callback, index, res)
        } else {
          this.dataForm.callback.push(res)
        }
      },
      copyPath() {
        // copyText(this.selectedPath, '路径已复制')
      },
      showInputResponseDialog() {
        this.$refs.inputDialog.open({type: 'object', data: this, key: 'response', from: 'response'})
      },
      handleClearResponse() {
        this.response = null
      },
      handleInputResponse(value, from) {
        if (from !== 'response') {
          return
        }
        this.response = value
      },
      handleShowDataConfigSelect() {
        let type
        switch (this.dataForm.action) {
          case 'api':
          case 'devops':
          {
            type = this.dataForm.apiRequest.type
            break;
          }
          case 'sql': {
            type = this.dataForm.sqlRequest.sqlType === 1 ? 2 : 3
            break;
          }
          case 'static': {
            type = 1
            break;
          }
        }
        this.$refs.dataConfigSelect.open({ type })
      },
      handleShowDataConfigSave() {
        let config
        let type
        switch (true) {
        case this.dataForm.apiRequest !== undefined:
          config = {
            apiRequest: this.dataForm.apiRequest,
          }
          type = this.dataForm.apiRequest.type
          break
        case this.dataForm.sqlRequest !== undefined:
          config = {
            sqlRequest: this.dataForm.sqlRequest,
          }
          type = this.dataForm.sqlRequest.sqlType === 1 ? 2 : 3
          break
        case this.dataForm.staticData !== undefined:
          config = {
            staticData: this.dataForm.staticData,
          }
          type = 1
          break
        }
        this.$refs.dataConfigSave.open({ config: JSON.stringify(config), type })
      },
      handleDataConfigConfirm(data) {
        this.dataSet = _.cloneDeep(data)
        const { apiRequest, sqlRequest, staticData } = JSON.parse(data.config)
        this.dataForm.apiRequest = apiRequest
        this.dataForm.sqlRequest = sqlRequest
        this.dataForm.staticData = staticData
      },
      handleShowEventSelect() {
        this.$refs.eventSelect.open()
      },
      handleEventSelectConfirm(handler) {
        const { type, callbackId, eventName, id } = this.dataForm
        this.open({
          ...handler,
          id,
          eventName,
          type,
          callbackId,
          disabled: false
        })
      },
      handleJumpTypeChange(type) {
        if(type === 'link' && !this.dataForm.linkOptions.linkParams.length) {
          this.dataForm.linkOptions.linkParams = [{
            name: '',
            value: undefined
          }]
        }
        this.dataForm.linkOptions.type = 'self'
      },
      handleAddParams(index) {
        this.dataForm.linkOptions.linkParams.push({ name: '', value: undefined })
      },
      handleDeleteParams(index) {
        const paramsData = this.dataForm.linkOptions.linkParams
        if (paramsData.length === 1) {
          paramsData[0].name = ''
          paramsData[0].value = undefined
          this.handleParamsChange()
          return
        }
        paramsData.splice(index, 1)
        this.handleParamsChange()
      },
      paramsInputDisabled(name) {
        const uri = new URI(`${this.dataForm.linkOptions.linkUrl}`)
        const pathString = uri.pathname()
        let dynamicPathArray = []
        try {
          dynamicPathArray = parse(pathString).filter(item => _.isObject(item)).map(item => item.name)
        } catch (e) {
          return false
        }
        return dynamicPathArray.includes(name)
      },
      handleParamsChange() {
        const uri = new URI(`${this.dataForm.linkOptions.linkUrl}`)
        const pathString = uri.pathname()
        let dynamicPathArray = []
        try {
          dynamicPathArray = parse(pathString).filter(item => _.isObject(item)).map(item => item.name)
        } catch (e) {
          console.log(e)
          dynamicPathArray = []
        }
        const paramsArray = this.dataForm.linkOptions.linkParams.filter(d => d.name && !dynamicPathArray.includes(d.name))
        if (!paramsArray.length) {
          this.dataForm.linkOptions.linkUrl = uri.origin() + uri.pathname()
          return
        }
        this.dataForm.linkOptions.linkUrl = uri.origin() + uri.pathname() + '?' + paramsArray.map((d) => `${d.name}=${d.value}`).join('&')
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      }
    },
  }
</script>

