    }
  }

  .panel-title::v-deep {
    display: flex;
    align-items: center;
    height: 30px;
    padding-left: 10px;
    padding-right: 10px;
    background: #f0f6ff;
    font-weight: 700;
    .el-form-item__content {
      margin: 0!important;
    }
  }

  .condition-panel::v-deep {
    margin-bottom: 8px;

    .condition-panel-inner {
      width: 100%;
      min-height: 100px;
      max-height: 300px;
      background-color: #ffffff;
      padding: 10px;
      margin-bottom: 4px;
      overflow-y: auto;

      &.placeholder {
        min-height: 0;
        text-align: center;
      }

      .condition-item {
        .value-input {
          display: flex;
          align-items: center;
          justify-content: space-between;

          & > .value-input-element {
            flex: 1;

            & > .el-form-item {
              margin-bottom: 0;
            }
          }

          & > .select-btn {
            width: 60px;
            margin-left: 10px;
          }
        }
      }

      .setting {
        display: flex;
        align-items: center;
        padding: 10px;
        background: #ffffff;
      }
    }
  }

  .action-panel::v-deep {

    .action-panel-inner {
