<template>
  <div class="chart-field-selector">
    <el-row>
      <el-col :span="12">
        <template v-if="!isStaticData">
          <div class="chart-field-title">数据体路径</div>
          <div class="data-path drop-zone-path" data-key="sourcePath">
            <n-tooltip v-if="selectedDataPath && selectedDataPath.length" :value="hidePrefix(selectedDataPath)">
              <el-tag closable @close="selectedDataPath = '';init()" disable-transitions>{{ showLastPath(selectedDataPath) }}</el-tag>
            </n-tooltip>
            <span v-else>请从右侧拖拽节点</span>
          </div>
        </template>
        <!-- 动态配置开始 -->
        <el-row v-if="isDynamic">
          <div class="chart-field-title">变量映射</div>
          <n-table
            size='mini'
            :data="showDynamicVariables"
            border
            style="margin-top: 10px;width: calc(100% - 15px);"
          >
            <n-table-column label="变量名">
              <template #default='{ row }'>
                <div class="variable-label">
                  <span class="label" :class="{ require: row.require }">{{ row.label }}</span>
                  <el-tooltip
                    v-if="row.desc"
                    effect="dark"
                    :content="row.desc"
                    placement="top"
                  >
                    <i class="el-icon-question"></i>
                  </el-tooltip>
                </div>
              </template>
            </n-table-column>
            <n-table-column prop="key" label="编码"></n-table-column>
            <n-table-column label="数据路径" min-width="150px">
              <template #default='{ row }'>
                <div class="drop-zone-path" :data-key="row.key">
                  <el-tooltip
                    v-if="row.path.length"
                    effect="dark"
                    :content="row.path.join('/')"
                    placement="top"
                  >
                    <el-tag
                      closable
                      @close="row.path = []"
                      disable-transitions
                      >{{ row.path[row.path.length - 1] }}</el-tag
                    >
                  </el-tooltip>
                  <span v-else>请从右侧拖拽节点</span>
                </div>
              </template>
            </n-table-column>
          </n-table>
        </el-row>
        <!-- 动态配置结束 -->
        <template v-if="!isDynamic">
          <div class="chart-field-title">已提取字段</div>
          <div class="data-path field-content">
            <draggable
              v-model="allField"
              group="itxst"
              :move="moveTag"
              animation="300"
              draggable=".filed-item"
            >
              <transition-group>
                <el-tag class="filed-item" v-for="key in allField" :key="key">
                  {{ key }}
                </el-tag>
              </transition-group>
            </draggable>
            <span v-if="!allField.length" class="empty-tips"
              >{{ selectedDataPath ? '可用字段为空': '请先选取数据体路径' }}</span
            >
          </div>
          <el-row v-if="!['gauge', 'radar'].includes(chartType)">
            <div class="chart-field-title">{{ xTitle[chartType] }}(最多选一个)</div>

            <div class="field-area" to="xField">
              <draggable
                v-model="xField"
                group="itxst"
                animation="300"
                draggable=".filed-item"
              >
                <transition-group>
                  <el-tag
                    class="filed-item"
                    v-for="item in xField"
                    :key="item.key"
                    closable
                    @close="xField.splice(index, 1)"
                  >
                    {{ item.key }}
                  </el-tag> </transition-group
                ><span class="filed-item"></span>
              </draggable>
              <span class="empty-tips" v-if="!xField.length">拖拽字段至框内</span>
            </div>
          </el-row>
        </template>
        <el-row v-if="['radar'].includes(chartType)">
          <div class="chart-field-title">
            {{ xTitle[chartType] }}
            <el-button type="text" @click="addXFields"><i class="el-icon-circle-plus"></i>添加类目字段</el-button>
          </div>
          <div class="field-area">
            <div
              class="filed-item cursorNormal"
              v-for="(item,index) in xField"
              :key="index"
            >
              <el-input class="name-input" clearable v-model="item.name" size="mini" placeholder="输入类目名字"></el-input>
              <el-switch class="max-switch" v-model="item.useMax"></el-switch>
              <el-input-number v-if="item.useMax" v-model="item.max" size="mini"></el-input-number>
              <span class="max-desc" v-else>不设置最大值</span>
              <i class="el-icon-error" @click="xField.splice(index, 1)"></i>
            </div>
            <span class="empty-tips" v-if="!xField.length">请添加类目字段</span>
          </div>
        </el-row>
        <el-row v-if="!isDynamic">
          <div class="chart-field-title">{{ yTitle[chartType] }}</div>

          <div class="field-area">
            <el-form class="y-field-form" label-width="120px" to="yField">
              <draggable
                v-model="yField"
                group="itxst"
                animation="300"
                draggable=".filed-item"
              >
                <transition-group>
                  <template v-if="['line', 'bar', 'gauge', 'scatter', 'radar'].includes(chartType)">
                    <el-form-item
                      v-for="(item, index) in yField"
                      :label="item.key"
                      :key="item.key"
                      :title="item.key"
                    >
                      <el-input
                        v-model="item.value"
                        placeholder="请输入该字段对应的图例名称"
                      ></el-input>
                      <i
                        class="el-icon-error"
                        @click="yField.splice(index, 1)"
                      ></i>
                    </el-form-item>
                  </template>
                  <template v-else>
                    <el-tag
                      class="filed-item"
                      v-for="item in yField"
                      :key="item.key"
                      closable
                      @close="yField.splice(index, 1)"
                    >
                      {{ item.key }}
                    </el-tag>
                  </template>
                </transition-group>
                <span class="filed-item"></span>
              </draggable>
            </el-form>
            <span class="empty-tips" v-if="!yField.length">拖拽字段至框内</span>
          </div>
        </el-row>
      </el-col>
      <el-col :span="12">
        <el-radio-group v-if="showResponse" v-model="active">
          <el-radio-button label="response">响应体</el-radio-button>
          <el-radio-button label="preview" @click="init">预览</el-radio-button>
        </el-radio-group>
        <div class="field-area chart-demo-box">
          <template v-if="active === 'response' && showResponse">
            <template v-if="data">
              <vue-json-pretty
                class="json-pretty no-select"
                path="response"
                :data="data"
                :virtual="true"
                :itemHeight="24"
                selectableType="single"
                :showSelectController="true"
                :selectOnClickNode="true"
                v-model="selectedPath"
              >
              </vue-json-pretty>
            </template>
            <span v-else class="empty-tips">点击上方测试按钮</span>
          </template>

          <template v-if="active === 'preview' || !showResponse">
            <chart-demo
              v-if="chartRender"
              :type="chartType"
              :currentProps="{ ...currentProps, dataVariables, dynamicVariables }"
              :data="soureData"
              :xField="xField"
              :yField="yField"
            ></chart-demo>
            <span v-else class="empty-tips">{{
              selectedDataPath ? '当前数据路径无数据' : '需要先选择响应体中的映射字段'
            }}</span>
          </template>
        </div>
      </el-col>
    </el-row>
    <div class="dnd-cover" v-show="dndCoverStyle" :style="dndCoverStyle"></div>
  </div>
</template>

<script>
// func
import _ from 'lodash'
import { debounce } from '@/utils'
import { chartType } from '@/config/widgets'
import API from '@/api/application'

// components
import draggable from 'vuedraggable'
import chartDemo from '@/components/chartDemo'
import VueJsonPretty from '@/lib/vue-json-pretty'
import dnd from '@/mixins/dragAndDrop'
import { getPathList } from '@/utils/format'
export default {
  components: {
    draggable,
    chartDemo,
    VueJsonPretty,
  },
  mixins: [dnd],
  props: {
    data: {
      type: Object,
      require: true,
    },
    staticData: {
      type: Array,
      default: [],
    },
    activeDataType: {
      type: String,
      require: true,
    },
    dataVariables: {
      type: Array,
      default: [],
    },
    currentProps: {
      type: Object,
      require: true,
    },
    dynamicVariables: {
      type: Array,
      require: true,
    },
  },
  data() {
    return {
      dragVal: [],
      allField: [],
      xField: [],
      yField: [],
      soureData: [],
      active: 'response',
      selectedPath: '',
      xTitle: {
        line: 'X轴映射',
        bar: 'X轴映射',
        pie: '类目字段',
        gauge: 'X轴映射',
        scatter: 'X轴映射',
        radar: '类目字段',
        funnel: '类目字段',
      },
      yTitle: {
        line: 'Y轴映射',
        bar: 'Y轴映射',
        pie: '数值字段(最多选一个)',
        gauge: '仪表盘数据映射',
        scatter: 'Y轴映射',
        radar: '数值字段',
        funnel: '数值字段(最多选一个)',
      },
    }
  },
  mounted() {
    this.handleInitDnD()
  },
  methods: {
    handleDrop(event) {
      const { dataType } = this.currentProps
      const { key } = event.target.dataset
      if(dataType === 'dynamic') {
        const current = this.dynamicVariables.find((item) => item.key === key)
        const path = getPathList(this.selectedPath)
        current.path = path
      } else {
        if(key === 'sourcePath') {
          this.selectedDataPath = getPathList(this.selectedPath)
          this.init()
        }
      }
    },
    hidePrefix(path) {
      if(path instanceof Array) {
        return path.slice(1).join('/')
      }
      // 兼容旧数据
      else if(typeof path === 'string') {
        return path.split('.').slice(1).join('/')
      }
    },
    showLastPath(path) {
      return path[path.length - 1]
    },
    getFields() {
      const { dataVariables: data } = this
      this.xField = data.find((item) => item.key === 'xAxis')?.fields || []
      this.yField = data.find((item) => item.key === 'yAxis')?.fields || []
    },
    init: debounce(async function() {
      await this.$nextTick()
      const { data, staticData, selectedDataPath, isStaticData, currentProps: { dataType }, activeDataType } = this
      let result
      if(!isStaticData && data && selectedDataPath) {
        if(activeDataType === 'static' && dataType === 'dynamic') {
          result = staticData
        } else {
          result = _.get(
            {
              response: data,
            },
            selectedDataPath
          )
        }
      } else {
        if(isStaticData) {
          result = staticData
        }
      }
      // 不让数据为空,起码显示个轮廓
      result = result || []
      this.soureData = _.cloneDeep(result)
      if (result instanceof Array) {
        const [item1 = {}] = result
        this.allField = Object.keys(item1)
        this.initExportData(item1)
      } else {
        this.allField = []
      }
      this.allField = _.cloneDeep(this.allField)
    }, 100),
    moveTag: debounce(
      function (e) {
        try {
          const key = e.draggedContext.element
          const target = e.to.parentNode.getAttribute('to')
          if (target === 'xField') {
            !this.xField.find((item) => item.key === key) &&
              !this.xField.length &&
              this.xField.push({ key })
          } else if (target === 'yField') {
            // 饼图, 漏斗图只能选一个
            if (
              ['pie', 'funnel'].includes(this.chartType) &&
              this.yField.length
            ) {
              return
            }
            if (!this.yField.find((item) => item.key === key)) {
              const { yField_tpl: preset } = require(`@/template/components/${this.elementType}/${this.elementVersion}/watch-props.js`)
              this.yField.push({ ..._.cloneDeep(preset), key, value: key })
            }
          }
        } catch (e) {
          console.error(e)
        }
      },
      { time: 100, returnValue: false }
    ),
    addXFields() {
      const newKey = 'key' + (this.xField.length + 1)
      const { xField_tpl } = require(`@/template/components/${this.elementType}/${this.elementVersion}/watch-props.js`)
      this.xField.push({
        ..._.cloneDeep(xField_tpl),
        name: newKey
      })
    },
    async getValue() {
      if (!this.xField.length && !['gauge'].includes(this.chartType)) {
        this.$message.error('请选取' + this.xTitle)
        return Promise.reject()
      }
      if (!this.yField.length) {
        this.$message.error('请选取' + this.yTitle)
        return Promise.reject()
      }
      const data = this.dataVariables.map((item) => {
        switch (item.key) {
          case 'sourcePath':
            item.path = this.selectedDataPath || item.path
            break
          case 'xAxis':
            item.fields = this.xField
            break
          case 'yAxis':
            item.fields = this.yField
            break
        }
        return item
      })
      return Promise.resolve(data)
    },
    // 初始化组件变量中暴露的数据结构
    initExportData(json) {
      const { chartType, currentProps: { elementId } } = this
      const key = `${elementId}.$dotData`

      // 保存点选数据结构

      const value = (() => {
        switch(chartType) {
          case 'pie' :
          case 'funnel' :
          case 'gauge' : return { name: '', value: '' }
          case 'scatter' : return [0, 0]
          case 'radar' : return { name: '', value: [0, 1, 2, 3, 4, 5, 6] }
          default: return json
        }
      })()
      this.$http({
        url: API.appDesignJsonDataConfigSave,
        data: {
          appDesignId: Number(this.$route.query.vid),
          preferences: [{
            key,
            value: JSON.stringify(value)
          }]
        }
      })

      // 拥有数据缩放的
      if(['line', 'bar', 'scatter'].includes(chartType)) {
        this.$http({
          url: API.appDesignJsonDataConfigSave,
          data: {
            appDesignId: Number(this.$route.query.vid),
            preferences: [{
              key: `${elementId}.$dataZoomRange`,
              value: JSON.stringify([0, 0])
            }]
          }
        })
      }

      // 图表数据
      this.$http({
        url: API.appDesignJsonDataConfigSave,
        data: {
          appDesignId: Number(this.$route.query.vid),
          preferences: [{
            key: `${elementId}.$chartData`,
            value: JSON.stringify(this.soureData)
          }]
        }
      })
    }
  },

  computed: {
    isDynamic() {
      return this.currentProps.dataType === 'dynamic'
    },
    selectedDataPath: {
      get() {
        const { dataType, dataVariables } = this.currentProps
        if(dataType === 'dynamic') {
          return this.dynamicVariables.find(item => item.key === 'sourcePath') ?.path
        }
        return dataVariables.find(item => item.key === 'sourcePath') ?.path
      },
      set(val) {
        const { dataType, dataVariables } = this.currentProps
        const target = (dataType === 'dynamic' ? this.dynamicVariables : dataVariables).find(item => item.key === 'sourcePath')
        target.path = val
      }
    },
    showDynamicVariables() {
      if(this.activeDataType === 'dynamic') {
        return this.dynamicVariables
      }
      return this.dynamicVariables.filter(item => item.key !== 'sourcePath')
    },
    chartType() {
      return chartType[this.elementType]
    },
    showResponse() {
      return !this.isStaticData || this.isDynamic
    },
    isStaticData() {
      return this.activeDataType === 'static'
    },
    elementType() {
      return this.currentProps.elementType
    },
    elementVersion() {
      return this.currentProps.elementVersion
    },
    chartRender() {
      const { soureData = [], xField, yField } = this
      if(['gauge'].includes(this.chartType)) {
        return soureData.length && yField.length
      }
      return soureData.length && xField.length && yField.length
    }
  },

  watch: {
    dataVariables: {
      deep: true,
      immediate: true,
      handler() {
        this.getFields()
      },
    },
    showResponse: {
      immediate: true,
      handler() {
        this.init()
      },
    },
  },
}
</script>
