<template>
  <n-form-item class="config-item">
    <template slot='label'>
      <span>{{name}}</span>
      <el-tooltip v-if="tip" :content="tip" popper-class="tooltip-question" placement="top">
        <i style='margin-left: 5px;' class="el-icon-question"></i>
      </el-tooltip>
    </template>
    <el-button size='mini' type='primary' @click='handleShowFormat' v-if='!value'>配置</el-button>
    <div class='formatter-item' v-else>
      <el-button type='text' @click='handleShowFormat'>重新编辑</el-button>
      <i style='margin-left: 10px;' class='iconfont el-icon-delete' @click='value = undefined'></i>
    </div>
    <FormatCode ref="formatCode" @confirm="handleFormatterConfirm" />
  </n-form-item>
</template>

<script>
import _ from 'lodash';
import FormatCode from '@/components/DataFormat/code';
  export default {
    components: {
      FormatCode
    },
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      vue: {
        type: String,
      },
      tip: {
        type: String,
      },
      optionkey: {
        type: String,
      },
    },
    data() {
      return {

      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = {
            ...this.elementProps,
            [this.propKey]: value,
          }
          this.$emit('update', res)
        },
      },
    },
    methods: {
      handleShowFormat() {
        let defaultArguments
        switch (this.optionkey) {
          case 'progressContent':
            defaultArguments = [
              {
                key: 'percent',
                value: undefined,
                test: undefined,
                desc: '百分比'
              },
              {
                key: 'successPercent',
                value: undefined,
                test: undefined,
                desc: '成功百分比'
              },
            ]
            break
        }
        this.$refs.formatCode.open({
          formatter: this.value,
          defaultArguments
        })
      },
      handleFormatterConfirm(formatter) {
        this.value = formatter
      },
    }
  }
</script>

<style lang="less" scoped>
  div.config-item::v-deep {
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 8px;
    margin-right: 0;
    .el-form-item__content {
      flex: 1;
    }
    .el-form-item__label{
      min-width: 100px;
    }
    .formatter-item {
      display: flex;
      align-items: center;
      .iconfont {
        cursor: pointer;
        &:hover {
          color: red;
        }
      }
    }
  }
</style>
