<template>
  <div class="widget-config-item">
    <div
      :class="{
        name: true,
        required: requiredArr.includes(name),
      }"
    >
      {{ name }}
      <el-tooltip v-if="tip" :content="tip" popper-class="tooltip-question" placement="top">
        <i style='margin-left: 5px;' class="el-icon-question"></i>
      </el-tooltip>
    </div>
    <n-form-item class="config-item" label="" :prop="propKey">
      <el-switch active-color="#3F92FE" v-model="value"></el-switch>
    </n-form-item>
  </div>
</template>

<script>
  import { treeUtil, getNodeByElementId } from '@/utils/tree-utils'
  import { globalEmit } from '@/renderer/tools/eventBus'
  import { PROPS } from '@/config/eventKey'
  import { pageInject } from '@/renderer/tools/pageStore'

  export default {
    name: 'ConfigSwitch',
    inject: [...pageInject],
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      optionkey: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      tip: {
        type: String,
      }
    },
    data() {
      return {
        requiredArr: [],
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
          const currentNode = getNodeByElementId(this.layoutData, this.elementProps.elementId)
          switch (true) {
            case this.optionkey === 'cell': {
              treeUtil.forEach(currentNode.children, node => {
                if (['slot'].includes(node.elementType) && ['data'].includes(this.getElementPropsById(node.elementId).slotType)) {
                  globalEmit.call(this, PROPS, { elementId: node.elementId, data: {isVisible: value} })
                }
              })
              break
            }
            default: {
              treeUtil.forEach(currentNode.children, node => {
                if (this.getElementPropsById(node.elementId).slotName === this.optionkey) {
                  globalEmit.call(this, PROPS, { elementId: node.elementId, data: {isVisible: value} })
                }
              })
              break
            }
          }
        },
      },
    },
    methods: {
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      }
    }
  }
</script>

