  div.widget-config-item::v-deep {
    display: flex;
    height: 42px;
    .name {
      font-family: PingFangSC-Medium;
      font-size: 12px;
      color: #555555;
      letter-spacing: 0;
      font-weight: 500;
      margin-bottom: 5px;
      min-width: 100px;
      padding-right: 10px;
      display: flex;
      height: 36px;
      align-items: center;
    }
    .required::before {
      content: '*';
      color: red;
    }
    div.config-item::v-deep {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 8px;
      .el-form-item__content {
        flex: 1;
      }
    }
  }
</style>
