<template>
  <div class="tabs-params" v-if="config.params">
    <div class="tab-select">
      <div class="label">切换方式</div>
      <div class="value">
        <el-select size="mini" v-model="type" @change="handleChange">
          <el-option label="根据步骤" value="key"></el-option>
          <el-option label="根据变量" value="dynamicActiveKey"></el-option>
        </el-select>
      </div>
    </div>
    <div class="tab-select" v-if="type === 'key'">
      <div class="label" style="margin-left: 10px;">选择步骤</div>
      <div class="value">
        <el-select size="mini" v-model="config.params.activeKey">
          <el-option v-for="(tab, index) in stepsData" :key="index" :label="tab.title" :value="index">
            <div style="float: left">{{ tab.title }}</div>
              <div style="float: right; color: #8492a6; font-size: 12px">{{ index }}</div>
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="tab-label" v-if="type === 'dynamicActiveKey'">
      <div class="label" style="margin-left: 10px;">选择变量
        <el-tooltip popper-class="tooltip-question" placement="top">
          <div slot="content">需传入步骤索引，数字类型，如0</div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <div class="value">
        <el-tooltip
          v-if="config.params.dynamicActiveKey !== undefined"
          effect="dark"
          :content="formatTip(config.params.dynamicActiveKey)"
          placement="top"
        >
          <el-tag
            class="value-tag"
            closable
            disable-transitions
            @click="handleTagClick(config.params, 'dynamicActiveKey')"
            @close="config.params.dynamicActiveKey = undefined"
            :type='getTagType(config.params.dynamicActiveKey)'
          >
            {{ formatTag(getElementName(config.params.dynamicActiveKey), 12) }}
          </el-tag>
        </el-tooltip>
        <el-button-group v-else>
          <el-button size="mini" @click="handleShowVariableSelect(config.params, 'dynamicActiveKey')">变量</el-button>
        </el-button-group>
      </div>
    </div>
    <input-dialog @confirm='handleInputResponse' ref="inputDialog"></input-dialog>
    <variable-select
      ref="variableSelect"
      @confirm="handleVariableConfirm"
    ></variable-select>
  </div>
</template>

<script>
import elementEditor from '@/mixins/elementEditor'
export default {
  name: 'TabsParams',
  mixins: [elementEditor],
  props: {
    elementId: {
      type: String
    },
    config: {
      type: Object
    },
    callbackId: {
      type: String
    },
  },
  data() {
    return {
      type: 'key'
    }
  },
  computed: {
    stepsData() {
      const { stepsData } = this.getElementPropsById(this.elementId)
      return stepsData
    }
  },
  methods: {
    handleShowVariableSelect(row, key, cb) {
      this.variableRowDetail = { row, key, cb }
      this.$refs.variableSelect.open({
        fullVariable: row[key],
        callbackId: this.callbackId,
      })
    },
    handleVariableConfirm(selectedVariable) {
      const { row, key, cb } = this.variableRowDetail
      if (key) {
        this.$set(row, key, selectedVariable)
      }
      cb?.(selectedVariable)
    },
    handleReset() {
      const activeKey = 0
      this.$set(this.config, 'params', {
        activeKey,
        dynamicActiveKey: undefined
      })
    },
    handleChange(type) {
      this.$set(this.config, 'params', {
        activeKey: type === 'key' ? 0 : undefined,
        dynamicActiveKey: undefined
      })
    }
  },
  created() {
    if (!this.config.params) {
      this.handleReset()
    }
    this.type = this.config.params.dynamicActiveKey ? 'dynamicActiveKey' : 'key'
  }
}
</script>

<style lang="less" scoped>
.tabs-params {
  display: flex;
  align-items: center;
  .tab-select, .tab-label {
    display: flex;
    align-items: center;
    .label {
      width: 80px;
    }
  }
}
</style>
