<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-switch v-model="isChecked" @change="switchChange"></el-switch>
    <div class="fill-div"></div>
    <el-popover v-if="value.length" placement="right" width="400" trigger="click">
      <el-form label-position="left" label-width="50px" style="max-height: 500px;overflow-y: auto;">
        <el-form-item
          :label="`${index + 1}分`"
          v-for="(item, index) in value"
          :key="index"
        >
          <el-input
            :key="index"
            :value="item"
            @input="(v) => handleInputing(v, index)"
            clearable
            @clear="item = ''"
            placeholder="请输入辅助文字"
          ></el-input>
        </el-form-item>
      </el-form>
      <i slot="reference" class="el-icon-s-tools"></i>
    </el-popover>
  </n-form-item>
</template>

<script>
  export default {
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        isChecked: false
      }
    },
    computed: {
      value: {
        get() {
          const result = this.getOpt()
          return result
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
        },
      },
      maxValue() {
        return this.elementProps['maxValue']
      }
    },
    methods: {
      handleInputing(v, index) {
        let tempValue = this.value
        tempValue[index] = v
        this.value = tempValue
      },
      switchChange(value) {
        this.value = value ? this.getOpt(true) : []
      },
      /**
       * 未有值 || 已有值 的时候初始化数据
       * params {bollean} isInit 是否初始化数组(未有值)
       */
      getOpt(isInit) {
        const value = this.elementProps[this.propKey]
        if(!value.length && !isInit) return []
        return new Array(this.maxValue).fill(0).map((item,index) => {
            return value[index] || ''
          })
      }
    },
    mounted () {
      if(this.value.length) {
        this.isChecked = true
      }
    }
  }
</script>

