    min-width: 100px;
  }

  .el-form-item__content {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .fill-div {
      flex: 1;
    }
    .el-icon-s-tools {
      color: #3f92fe;
      cursor: pointer;
    }
  }
}
</style>
