<template>
  <div class="widget-config-item">
    <div class="name">{{ name }}</div>
    <div class="container">
      <el-checkbox v-model="value.all">不限</el-checkbox>
      <template v-if="!value.all">
        <el-button-group class="flex-button-group">
          <el-button
            size="mini"
            class="config-item-button"
            v-for="item in options"
            :key="item.value"
            :type="getType(item.value)"
            @click="radioChange(item.value)"
          >
            <span>
              {{ item.label }}
            </span>
            <el-tooltip
              v-if="item.tips"
              popper-class="tooltip-question"
              effect="dark"
              :content="item.tips"
              placement="top-start"
            >
              <i class="el-icon-question"></i>
            </el-tooltip>
          </el-button>
        </el-button-group>

        <el-checkbox-group v-model="value.ext">
          <el-checkbox
            v-for="item in extOption[value.type]"
            :key="item"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <div class="extra-tips">
          其他更多类型用英文逗号（,）分隔,如：jpg,png
        </div>
        <el-input
          v-model="value.extra"
          placeholder="输入上传格式"
          clearable
        ></el-input>
      </template>
    </div>
  </div>
</template>

<script>
import { extOption } from '@/config/uploadExt'
export default {
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
    },
    vue: {
      type: String,
    },
  },
  data() {
    return {
      options: [
        { label: '文件', value: 'file' },
        { label: '图片', value: 'image' },
        { label: '视频', value: 'video' },
      ],
      extOption,
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = {
          ...this.elementProps,
          [this.propKey]: value,
        }
        this.$emit('update', res)
      },
    },
  },
  methods: {
    getType(value) {
      return this.value.type === value ? 'primary' : 'default'
    },
    radioChange(value) {
      this.value.type = value
      this.value.ext = []
    },
  },
}
</script>

