<template>
  <div class="value-input">
    <el-tooltip
      v-if="value !== undefined"
      effect="dark"
      :content="formatTip(value)"
      placement="top"
    >
      <el-tag
        class="value-tag"
        closable
        disable-transitions
        @click="handleTagClick({ value }, 'value', undefined, type)"
        @close="$emit('remove')"
        :type="getTagType(value)"
      >
        {{ formatTag(getElementName(value), 12) }}
      </el-tag>
    </el-tooltip>
    <div v-else class="value-input-content">
      <el-button-group>
        <template>
          <el-button @click="showInputDialog('text', { value }, 'value')"
            >文本
          </el-button>
          <el-button @click="showInputDialog('number', { value }, 'value')"
            >数值
          </el-button>
          <el-button @click="showInputDialog('object', { value }, 'value')"
            >对象
          </el-button>
          <el-button @click="jsFunc">js函数 </el-button>
        </template>
      </el-button-group>
    </div>
  </div>
</template>

<script>
import elementEditor from '@/mixins/elementEditor'
export default {
  mixins: [elementEditor],
  components: {},
  props: ['value', 'type'],
  data() {
    return {}
  },
  created() {},
  mounted() {},
  methods: {
    showInputDialog(...args) {
      this.$emit('showInputDialog', args)
    },
    jsFunc() {
      this.showInputDialog('function', { value: '' }, 'value', 'function')
    },
  },

  computed: {},

  watch: {},
}
</script>
