      </div>
    </div>
    <n-form-item class="config-form-item" label="栅格比例" :prop="propKey">
      <el-input type="text" :value="inputValue" @input="handleInput" @change="handleChange" :placeholder="placeholder" />
    </n-form-item>
    <n-form-item class="config-form-item" label="表格模式" prop="tableMode">
      <el-switch v-model='tableMode'></el-switch>
