            @click="flexWrap = item.value"
          >
            {{ item.label }}
          </el-button>
        </el-button-group>
      </n-form-item>
      <n-form-item
        class="config-form-item"
        :label="flexDirection === 'column' ? '布局内容水平对齐' : '布局内容垂直对齐'"
        prop="alignItems"
      >
        <el-button-group class="display-group">
          <el-button
            class='display-button'
            v-for="item in alignItemsOptions"
            :key="item.value"
            :type="getItemsType(item.value)"
            @click="alignItems = item.value"
