          },
          {
            span: '3:3:3:3:3:3:3:3',
            icon: 'url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADIAAAAYAQMAAACCxjyiAAAAAXNSR0IB2cksfwAAAAlwSFlzAAALEwAACxMBAJqcGAAAAAZQTFRFvsPK////MlxxsQAAABpJREFUeJxjYJBgOMDDAARUpv+DwQE4TSN7AGqGIQ0uoJV/AAAAAElFTkSuQmCC)'
          },
        ],
        directionOptions: [
          {
            label: '横向',
            value: 'row',
          },
          {
            label: '纵向',
            value: 'column',
          },
        ],
        flexWrapOptions: [
          {
            label: '允许换行',
            value: 'wrap',
          },
          {
            label: '不允许换行',
            value: 'nowrap',
          },
        ],
        alignItemsOptions: [
          {
            label: '左',
            value: 'flex-start',
          },
          {
            label: '中',
            value: 'center',
          },
          {
            label: '右',
            value: 'flex-end',
          },
          {
            label: '拉伸',
            value: 'stretch',
          },
        ],
        justifyOptions: [
          {
            label: '左',
            value: 'flex-start',
          },
          {
            label: '中',
            value: 'center',
          },
          {
            label: '右',
            value: 'flex-end',
          },
          {
            label: '两侧',
            value: 'space-between',
          },
          {
            label: '平均',
            value: 'space-around',
          },
        ],
      }
    },
    computed: {
      layoutTypes() {
        switch(this.layoutType) {
          case 1:
            return this.layoutTypes1
          case 2:
            return this.layoutTypes2
        }
      },
      columnRatio: {
        get() {
          return this.elementProps[this.propKey]
        }
      },
      tableMode: {
        get() {
          return this.elementProps.tableMode
        },
        set(value) {
          const res = { ...this.elementProps, tableMode: value }
          this.$emit('update', res)
        }
      },
      flexWrap: {
        get() {
          return this.elementProps.flexWrap
        },
        set(value) {
          const res = { ...this.elementProps, flexWrap: value }
          this.$emit('update', res)
        }
      },
      flexDirection: {
        get() {
          return this.elementProps.flexDirection
        },
        set(value) {
          const res = { ...this.elementProps, flexDirection: value }
          this.$emit('update', res)
        }
      },
      alignItems: {
        get() {
          return this.elementProps.alignItems
        },
        set(value) {
          const res = { ...this.elementProps, alignItems: value }
          this.$emit('update', res)
        }
      },
      justifyContent: {
        get() {
          return this.elementProps.justifyContent
        },
        set(value) {
          const res = { ...this.elementProps, justifyContent: value }
          this.$emit('update', res)
        }
      },
      layoutData() {
        return this.getStore().layoutData
      },
      columnList: {
        get() {
          return getNodeByElementId(this.layoutData, this.elementId).children
        },
        set(value) {
          this._storeCommit(UPDATE_LAYOUT_DATA, {elementId: this.elementId, layout: { children: value }})
        },
      },
    },
    watch: {
      elementProps: {
        immediate: true,
        deep: true,
        handler(val) {
          const value = val.columnRatio
          if(this.inputValue !== value) {
            this.inputValue = value
          }
        }
      }
    },
    methods: {
      setValue(item) {
        if (item.span) {
          this.handleChange(item.span)
        }
      },
      handleInput(value) {
        // 过滤掉不是 "数字" 和 ":" 的值
        this.inputValue = value.replace(/[^0-9:]/g, '')
      },
      handleChange(value) {
        // 拷贝一份旧数据
        const oldCopyVal = getGlobalConfig.call(this)

        const src = value.split(':').map((item) => Number(item))
        let group = []
        const rows = []
        const reducer = (acc, cur) => {
          if (acc + cur >= 12) {
            rows.push(cur >= 12 ? [12] : [...group, cur])
            group = []
            return 0
          }
          group.push(cur)
          return acc + cur
        }
        src.reduce(reducer, src[0])
        const layouts = value.split(':').map((col, index) => {
          const current = this.columnList[index] || _.cloneDeep(layout)
          const type = 'layout'
          const id = getRandomHexadecimal(6)
          const span = col ? Math.ceil(Math.abs(Number(col))) : 1
          const elementId = current.elementId || `${type}-${id}`
          if (!this.columnList[index]) {
            const { children, ...others } = current
            this._storeCommit(INIT_DYNAMIC_PROPS, { elementId, props: {...others, elementId, span} })
          } else {
            globalEmit.call(this, PROPS, { elementId, data: {span}, pushHistory: false })
          }
          return {
            ...current,
            span,
            elementId,
          }
        })

        if (layouts.length < this.columnList.length) {
          const out = this.columnList.slice(layouts.length).map(item => item.children).flat(Infinity)
          _.last(layouts).children.push(...out)
        }

        const newColumnList = layouts.length
          ? layouts
          : [
            {
              ..._.cloneDeep(layout),
              elementId: `layout-${getRandomHexadecimal(6)}`,
            },
          ]

        this.columnList = newColumnList

        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res, {
          oldVal: oldCopyVal,
          val: getGlobalConfig.call(this),
          callback: ({ columnRatio }) => {
            // 更新每个布局内部的span宽度
            columnRatio.split(':').forEach((col, index) => {
              const current = this.columnList[index]
              const span = col ? Math.ceil(Math.abs(Number(col))) : 1
              current && globalEmit.call(this, PROPS, { elementId: current.elementId, data: {span}, pushHistory: false })
            })
          }
        })
      },
      getDirectionType(type) {
        return this.flexDirection === type ? 'primary' : 'default'
      },
      getFlexWrapType(type) {
        return this.flexWrap === type ? 'primary' : 'default'
      },
      getItemsType(type) {
        return this.alignItems === type ? 'primary' : 'default'
      },
      getJustifyType(type) {
        return this.justifyContent === type ? 'primary' : 'default'
      },
    },
  }
</script>

