<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="data-select-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    :title="title"
    :visible.sync="dialogVisible"
    width="1200px"
    top="30px"
    :before-close="handleClose"
    v-element-screen
  >
    <js-config class='js-config' ref='jsConfig' v-model='chartConfigId' :defaultArguments="defaultArguments" :tips="tips" :useType="useType" isJsFormat></js-config>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" :loading="loading" @click="confirm"
        >确 定</el-button
      >
    </div>
  </el-dialog>
</template>

<script>
import _ from 'lodash'
const tips = `支持JS原生语法，使用“参数名”进行取值, 使用return返回结果，示例JS：
  return {
    data: Array(10).fill(0).map((item, index) => ({ number: index * 1000 , scales: '名字' + index })),
    padding: 'auto',
    xField: 'number',
    yField: 'scales',
  }`
export default {
  name: 'DataSelect',
  components: {
  },
  inject: ['dndOptions'],
  props: {
    useType: {
      type: String,
      default: '',
    },
    elementProps: {
      type: Object
    },
    title: {
      type: String,
    },
    tips: {
      type: String,
      default: tips
    },
    defaultArguments: {
      type: Object,
      default: []
    }
  },
  data() {
    return {
      loading: false,
      activeCollapse: ['config'],
      dialogVisible: false,
      apiRequest: undefined,
      sqlRequest: undefined,
      staticData: undefined
    }
  },
  computed: {
  },
  methods: {
    open({ id }) {
      this.chartConfigId = id
      this.response = null
      this.dialogVisible = true
    },
    handleClose(done) {
      this.dialogVisible = false
      done()
    },
    cancel() {
      this.dialogVisible = false
    },
    async confirm() {
      await this.$refs.jsConfig.confirm()
      this.$emit('confirm', this.chartConfigId)
      this.dialogVisible = false
    },
  },
}
</script>

<style lang="less" scoped>
.js-config {
  padding-top: 15px;
}
</style>
