<template>
  <el-collapse class='config-collapse' v-model='activeCollapse'>
    <el-collapse-item name='config'>
      <template slot="title">
        <div class='config-collapse-title'>自动化运维操作</div>
      </template>
      <div class="api-config" v-loading="loading">
        <n-form
          class="data-select-form"
          ref="apiRequest"
          label-position="top"
          :model="apiRequest"
          :rules="rules"
          @submit.native.prevent
          size="mini"
        >
          <el-radio-group v-model="apiRequest.type" @change="changeType" class="api-button-group">
            <el-radio-button
              v-for="option in types"
              :key="option.value"
              :label="option.value"
              >{{option.label}}</el-radio-button>
          </el-radio-group>
          <div class="form-item-wrapper">
            <div class='url-title'>
              {{title}}
            </div>
            <n-form-item class="no-border" prop="url">
              <div class="inline-form-item" style='padding: 0'>
                <el-input :value="currentName" readonly placeholder="请选择" @click="handleShowDevopsDataSelect"></el-input>
                <!-- <el-button type="default" v-if="['script'].includes(apiRequest.type)" @click="handleShowDevopsDetail">跳转查看</el-button> -->
                <el-button type="primary" @click="handleShowDevopsDataSelect">选择</el-button>
                <el-button type="primary" @click="handleFetch">测试</el-button>
              </div>
            </n-form-item>
          </div>
          <div class="sql-desc">
            <template v-if="apiRequest.apiDetail">
              <div class="desc-item" v-if="!['script'].includes(apiRequest.type)">{{ apiRequest.apiDetail.description }}</div>
              <div class="desc-item" v-else>
                <el-tag v-for="item in apiRequest.apiDetail.scriptLabels" :key="item.labelId" style="margin-right: 10px;">{{item.labelTitle}}</el-tag>
              </div>
            </template>
          </div>
          <n-form-item label="">
            <div class="params-config">
              <el-button
                class="source-edit-button"
                type='primary'
                @click="
              showInputDialog(
                'object',
                paramsSource,
                activeParamsName,
                'paramsSource'
              )
            "
              >转换器</el-button>
              <n-table
                :data="currentParamsData"
                border
                style="width: 100%"
                header-cell-class-name="header-cell"
              >
                <n-table-column width="200" prop="key" label="参数名">
                  <template slot-scope="scope">
                    <el-input
                      v-model="scope.row.key"
                    ></el-input>
                  </template>
                </n-table-column>
                <n-table-column prop="value" label="参数值">
                  <template slot-scope="scope">
                    <div class="value-input">
                      <el-tooltip
                        v-if="scope.row.value !== undefined"
                        effect="dark"
                        :content="formatTip(scope.row.value)"
                        placement="top"
                      >
                        <el-tag
                          class="value-tag"
                          closable
                          disable-transitions
                          @click="handleTagClick(scope.row, 'value')"
                          @close="scope.row.value = undefined"
                          :type='getTagType(scope.row.value)'
                        >
                          {{ formatTag(getElementName(scope.row.value), 12) }}
                        </el-tag>
                      </el-tooltip>
                      <div v-else class="value-input-content">
                        <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                          <el-button
                            @click="showInputDialog('text', scope.row, 'value')"
                          >文本
                          </el-button>
                          <el-button
                            @click="showInputDialog('number', scope.row, 'value')"
                            :disabled="
                          ['paramsData', 'headersData'].includes(
                            activeParamsName
                          )
                        "
                          >数值
                          </el-button>
                          <el-button
                            @click="showInputDialog('object', scope.row, 'value')"
                            :disabled="
                          ['paramsData', 'headersData'].includes(
                            activeParamsName
                          )
                        "
                          >对象
                          </el-button>
                          <el-button
                            @click="handleShowVariableSelect(scope.row, 'value')"
                          >变量
                          </el-button>
                        </el-button-group>
                        <MoreTypeDropdown @change="(v) => scope.row.value = v" />
                      </div>
                    </div>
                  </template>
                </n-table-column>
                <n-table-column prop="test" label="测试值">
                  <template slot-scope="scope">
                    <div class="value-input">
                      <el-tooltip
                        v-if="scope.row.test !== undefined"
                        effect="dark"
                        :content="isFile(scope.row.test) ? scope.row.test.name : formatTip(scope.row.test)"
                        placement="top"
                      >
                        <el-tag
                          class="value-tag"
                          closable
                          disable-transitions
                          @click="handleTagClick(scope.row, 'test')"
                          @close="scope.row.test = undefined"
                        >
                          {{ isFile(scope.row.test) ? '文件' : formatTag(scope.row.test) }}
                        </el-tag>
                      </el-tooltip>
                      <div v-else class="value-input-content">
                        <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                          <el-button
                            @click="showInputDialog('text', scope.row, 'test')"
                          >文本
                          </el-button>
                          <el-button
                            @click="showInputDialog('number', scope.row, 'test')"
                          >数值
                          </el-button>
                          <el-button
                            @click="showInputDialog('object', scope.row, 'test')"
                          >对象
                          </el-button>
                          <el-button
                            @click="selectFile(scope.$index)"
                          >文件
                          </el-button>
                          <input type="file" style="display:none;" :ref="'fileRef' + scope.$index" @change="fileChange($event, scope.$index)">
                        </el-button-group>
                        <MoreTypeDropdown @change="(v) => scope.row.test = v" />
                      </div>
                    </div>
                  </template>
                </n-table-column>
                <n-table-column width="120" label="操作">
                  <template slot-scope="scope">
                    <div class="btns">
                      <div class="add-btn-primary" @click="handleAddParams">+</div>
                      <div
                        class="add-btn-primary"
                        @click="handleDeleteParams(scope.$index)"
                      >
                        -
                      </div>
                    </div>
                  </template>
                </n-table-column>
              </n-table>
            </div>
          </n-form-item>
        </n-form>
        <variable-select
          ref="variableSelect"
          @confirm="handleVariableConfirm"
        ></variable-select>
        <script-data ref="scriptData" @confirm="handleSelectConfirm"></script-data>
        <orchestration-data ref="orchestrationData" @confirm="handleSelectConfirm"></orchestration-data>
        <sncflower-data ref="sncflowerData" @confirm="handleSelectConfirm"></sncflower-data>
        <input-dialog @confirm="handleUpdateValue" ref="inputDialog"></input-dialog>
      </div>
    </el-collapse-item>
  </el-collapse>
</template>

<script>
  import GatewayDataSelect from '@/components/GatewayDataSelect/index'
  import ScriptData from '@/components/DevopsData/scriptData'
  import OrchestrationData from '@/components/DevopsData/orchestrationData'
  import SncflowerData from '@/components/DevopsData/sncflowerData'
  import CustomApiDataSelect from '@/components/CustomApiDataSelect/index'
  import {
    getValidators,
    required,
  } from '@/utils/validateUtils'
  import _ from 'lodash'
  import request from '@/request/request'
  import { getObjectTree, getApiRequestParams } from '@/utils/format'
  import elementEditor from '@/mixins/elementEditor'
  import { isValidateVariable } from '@/utils'
  import { pageInject } from '@/renderer/tools/pageStore'
  import API from '@/api/application'
  import { getCommonHeaders } from '@/utils'

  export default {
    name: 'DevopsConfig',
    inject: [...pageInject],
    components: {
      GatewayDataSelect,
      CustomApiDataSelect,
      ScriptData,
      OrchestrationData,
      SncflowerData,
    },
    mixins: [elementEditor],
    props: {
      apiRequest: {
        type: Object,
        required: true,
        default: () => ({
          url: '',
          method: 'POST',
          type: 'script',
          paramsData: [],
          bodyData: [
            {
              key: '',
              value: undefined,
              test: undefined,
              desc: undefined
            },
          ],
          headersData: [],
          apiDetail: undefined
        }),
      },
      callbackId: {
        type: String,
      },
      responseOptions: {
        type: Array,
      },
      emitEvent: {
        type: Object,
        required: false
      }
    },
    data() {
      return {
        loading: false,
        activeCollapse: ['config'],
        activeParamsName: 'bodyData',
        rules: {
          url: getValidators([
            {
              message: '请选择',
              include: required,
              trigger: 'change',
            }
          ]),
        },
        variableRowDetail: null,
        types: [
          {
            label: '操作',
            value: 'script',
          },
          {
            label: '编排',
            value: 'orchestration',
          },
          {
            label: '调度',
            value: 'sncflower',
          },
        ]
      }
    },
    computed: {
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      currentParamsData() {
        return this.apiRequest[this.activeParamsName]
      },
      paramsSource: {
        get() {
          return {
            bodyData: Object.fromEntries(
              this.apiRequest.bodyData.map((item) => [[item.key], item.value])
            ),
            paramsData: Object.fromEntries(
              this.apiRequest.paramsData.map((item) => [[item.key], item.value])
            ),
            headersData: Object.fromEntries(
              this.apiRequest.headersData.map((item) => [[item.key],item.value,])
            ),
          }
        },
        set(n) {
          this.apiRequest[this.activeParamsName] = Object.entries(n).map(
            ([key, value]) => ({
              key,
              value,
              test: isValidateVariable(value) ? undefined : JSON.parse(JSON.stringify(value === undefined ? null : value, function(k, v) { return isValidateVariable(v) ? null : v })),
            })
          )
        },
      },
      title() {
        switch(this.apiRequest.type) {
          case 'script': return '操作名称：'
          case 'orchestration': return '编排名称：'
          case 'sncflower': return '调度名称：'
        }
      },
      currentName() {
        switch(this.apiRequest.type) {
          case 'script': return this.apiRequest.apiDetail?.scriptName
          case 'orchestration': return this.apiRequest.apiDetail?.name
          case 'sncflower': return this.apiRequest.apiDetail?.name
        }
      }
    },
    watch: {
      apiRequest: {
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          const res = {
            ...n,
            bodyData: n.bodyData?.map(item => ({...item, value: item.value, test: item.test})),
            paramsData: n.paramsData?.map(item => ({...item, value: item.value, test: item.test})),
            headersData: n.headersData?.map(item => ({...item, value: item.value, test: item.test}))
          }
          this.$emit('update:apiRequest', res)
        },
        deep: true,
        immediate: true,
      },
    },
    methods: {
      isFile(file) {
        return file instanceof File
      },
      selectFile(index) {
        this.$refs['fileRef' + index].click()
      },
      fileChange(e, index) {
        const [file] = e.target.files
        this.currentParamsData[index].test = file
        e.target.value = ''
      },
      handleShowVariableSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.variableSelect.open({
          fullVariable: row[key],
          callbackId: this.callbackId,
          emitEvent: this.emitEvent
        })
      },
      handleVariableConfirm(selectedVariable) {
        const { row, key, cb } = this.variableRowDetail
        if (key) {
          this.$set(row, key, selectedVariable)
          if (row.key === 'snc-token') {
            const loginKey = window.localStorage.getItem('loginKey')
            if (loginKey) {
              const token = JSON.parse(loginKey)?.['snc-token']
              this.$set(row, 'test', token)
            }
          }
        }
        cb?.(selectedVariable)
      },
      handleAddParams() {
        this.currentParamsData.push({
          key: '',
          value: undefined,
          test: undefined,
        })
      },
      handleDeleteParams(index) {
        const paramsData = this.currentParamsData
        if (paramsData.length === 1) {
          paramsData[0].key = ''
          paramsData[0].value = undefined
          paramsData[0].test = undefined
          return
        }
        paramsData.splice(index, 1)
      },
      handleGetScriptId() {
        this.$emit('response', {
          response: null,
          responseOptions: [],
        })
        this.loading = true
        return new Promise((resolve, reject) => {
          request({
            url: API.atmScriptList,
            method: 'POST',
            data: {
              params: {
                condition: { isBuilt: this.apiRequest.apiDetail?.isBuilt, scriptName: this.apiRequest.apiDetail?.scriptName },
                pagination: { pagenum: 1, pagesize: 1 },
              },
            },
            headers: getCommonHeaders(),
          })
            .then(({data}) => {
              resolve(data.data.records[0].scriptId)
            })
            .catch((e) => {
              this.loading = false
              reject(e)
            })
        })
      },
      handleGetOrchestrationId() {
        this.$emit('response', {
          response: null,
          responseOptions: [],
        })
        this.loading = true
        return new Promise((resolve, reject) => {
          request({
            url: API.atmOrchestrationList,
            method: 'POST',
            data: {
              params: {
                condition: { executeType: 1, name: this.apiRequest.apiDetail?.name },
                pagination: { pagenum: 1, pagesize: 1 },
              },
            },
            headers: getCommonHeaders(),
          })
            .then(({data}) => {
              resolve(data.data.records[0].orchestrationId)
            })
            .catch((e) => {
              this.loading = false
              reject(e)
            })
        })
      },
      handleGetWorkflowId() {
        this.$emit('response', {
          response: null,
          responseOptions: [],
        })
        this.loading = true
        return new Promise((resolve, reject) => {
          request({
            url: API.atmSncflowerList,
            method: 'POST',
            data: {
              params: {
                condition: { name: this.apiRequest.apiDetail?.name },
                pagination: { pagenum: 1, pagesize: 1 },
              },
            },
            headers: getCommonHeaders(),
          })
            .then(({data}) => {
              resolve(data.data.records[0].id)
            })
            .catch((e) => {
              this.loading = false
              reject(e)
            })
        })
      },
      async handleFetch() {
        const params = getApiRequestParams.call(this, this.apiRequest)
        try {
          switch(this.apiRequest.type) {
            case 'script': {
              params.data.params.entity.layoutId = await this.handleGetScriptId()
              break
            }
            case 'orchestration': {
              params.data.params.orchestrationId = await this.handleGetOrchestrationId()
              break
            }
            case 'sncflower': {
              params.data.params.workflowRunBean.workflowId = await this.handleGetWorkflowId()
              break
            }
          }
        } catch (e) {
          this.loading = false
          this.$message.error('接口测试失败！')
          return
        }
        console.log(params)
        request({
          url: params.url,
          method: params.method,
          data: params.data,
          params: params.params,
          headers: params.headers,
        })
          .then((res) => {
            this.$emit('response', {
              response: res.data,
              responseOptions: getObjectTree(res.data),
            })
          })
          .catch((e) => {
            this.$message.error('接口测试失败！')
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleShowDevopsDataSelect() {
        this.$refs[`${this.apiRequest.type}Data`].open(this.apiRequest.apiDetail)
      },
      handleSelectConfirm({ apiRequest, apiDetail }) {
        this.apiRequest = _.cloneDeep({...apiRequest, apiDetail})
        this.$emit('update:apiRequest', this.apiRequest)
      },
      changeType(type) {
        this.apiRequest.type = type
        this.apiRequest.url = ''
        this.apiRequest.apiDetail = undefined
      },
    },
  }
</script>

