<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="选择数据模型"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <n-form
      class="data-select-form"
      label-position="top"
      :model='queryForm'
      size='mini'
      @submit.native.prevent
      v-loading.lock="loading"
    >
      <n-form-item>
        <div class="table-search">
          <el-select style='width: 200px; margin-right: 10px;' v-model="type" placeholder="请选择">
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <el-input
            class="search-input"
            size="mini"
            v-model="keyword"
            clearable
            placeholder="输入关键字查询"
          >
          </el-input>
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-search"
            @click="handleSearch(false)"
          >
          </el-button>
        </div>
      </n-form-item>
      <n-form-item>
        <n-table
          :data="dataList"
          border
          highlight-current-row
          @row-click="handleRowClick"
        >
          <n-table-column width="50px" label="选择">
            <template slot-scope="scope">
              <el-radio
                v-if="selectedRow && selectedRow.id === scope.row.id"
              ></el-radio>
              <el-radio v-else :value='false'></el-radio>
            </template>
          </n-table-column>
          <n-table-column prop="physicalName" label="物理名称">
          </n-table-column>
          <n-table-column prop="name" label="中文名称">
          </n-table-column>
          <n-table-column prop="dataSourceType" label="数据源类型">
          </n-table-column>
          <n-table-column prop="dataSourceName" label="数据源">
          </n-table-column>
          <n-table-column prop="updateUserName" label="更新人">
          </n-table-column>
          <n-table-column prop="updateTime" label="更新时间">
            <template slot-scope='scope'>
              <span>{{ formatTime(scope.row.updateTime) }}</span>
            </template>
          </n-table-column>
        </n-table>
        <div class="pagination">
          <pagination
            :page="queryForm.pagination.pagenum"
            :limit="queryForm.pagination.pagesize"
            :total="totalCount"
            @pagination="handlePageChange"
          />
        </div>
      </n-form-item>
    </n-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import API from '@/api/application'
  import Pagination from '@/components/Pagination'
  import { formatTime } from '@/utils/format'

  export default {
    name: 'SqlDataSelect',
    components: {
      Pagination
    },
    data() {
      return {
        dialogVisible: false,
        loading: false,
        totalCount: 0,
        type: 'physicalName',
        keyword: '',
        queryForm: {
          pagination: {
            pagenum: 1,
            pagesize: 10,
          },
          condition: {
            dataSourceName: '',
            status: 2,
          }
        },
        typeList: [
          {
            label: '物理名称',
            value: 'physicalName'
          },
          {
            label: '中文名称',
            value: 'name'
          },
        ],
        dataList: [],
        selectedRow: null
      }
    },
    methods: {
      formatTime,
      open({ dataSourceName, selectedRow }) {
        this.queryForm.condition.dataSourceName = dataSourceName
        this.selectedRow = selectedRow
        this.dialogVisible = true
        this.handleSearch(true)
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      confirm() {
        if (!this.selectedRow) {
          this.$message.error('请选择数据源')
          return
        }
        this.$emit('confirm', this.selectedRow)
        this.dialogVisible = false
      },
      handleSearch() {
        if (this.type === 'physicalName') {
          this.queryForm.condition.physicalName = this.keyword || undefined
        } else {
          this.queryForm.condition.name = this.keyword || undefined
        }
        this.loading = true
        this.$http({
          url: API.dataModelPage,
          data: this.queryForm,
        })
          .then(({ data }) => {
            this.totalCount = data.rowCount
            this.dataList = data.records
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleRowClick(row, column, event) {
        this.selectedRow = row
      },
      handlePageChange({ page, size }) {
        this.queryForm.pagination.pagenum = page
        this.queryForm.pagination.pagesize = size
        this.handleSearch(false)
      },
    }
  }
</script>

<style lang="less" scoped>
  .data-select-form::v-deep {
    .table-search {
      flex: 1;
      display: flex;
      align-items: center;
      float: right;
      .search-input {
        margin-right: 10px;
        .el-input-group__prepend {
          min-width: 100px;
        }
      }
    }
    .pagination {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 10px;
    }
  }
</style>
