<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="保存数据集"
    :visible.sync="dialogVisible"
    width="800px"
    top="5vh"
    :before-close="handleClose"
  >
    <n-form
      class='data-set-save-form'
      v-loading.lock='loading'
      ref="dataForm"
      label-position="top"
      :model="formData"
      :rules="rules"
      @submit.native.prevent
    >
      <n-form-item label='名称' prop='name'>
        <el-input v-model.trim='formData.name'></el-input>
      </n-form-item>
      <n-form-item label='描述' prop='description'>
        <el-input type='textarea' v-model.trim='formData.description'></el-input>
      </n-form-item>
    </n-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import { getValidators, required } from '@/utils/validateUtils'
  import API from '@/api/application'

  export default {
    name: 'DataConfigSave',
    data() {
      return {
        dialogVisible: false,
        loading: false,
        formData: {
          name: '',
          config: undefined,
          description: '',
          type: undefined
        },
        rules: {
          name: getValidators([
            {
              message: '请输入名称',
              include: required,
              trigger: 'change',
            },
          ]),
          description: getValidators([
            {
              message: '请输入描述信息',
              include: required,
              trigger: 'change',
            },
          ]),
        }
      }
    },
    methods: {
      async open({config, type}) {
        this.formData.type = this.getTypeNumber(type)
        this.formData.config = config
        this.dialogVisible = true
        await this.$nextTick()
        this.$refs.dataForm.clearValidate()
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      getTypeNumber(type) {
        switch (type) {
          case 'script': return 7
          case 'orchestration': return 8
          case 'sncflower': return 9
          default:
            return type
        }
      },
      async confirm() {
        await this.$refs.dataForm.validate()
        this.loading = true
        this.$http({
          url: API.dataSetSave,
          data: this.formData,
        })
          .then(({ data }) => {
            this.$message.success('保存成功')
            this.dialogVisible = false
          })
          .finally(() => {
            this.loading = false
          })
      }
    }
  }
</script>

<style lang="less" scoped></style>
