<template>
  <el-collapse class='config-collapse' v-model='activeCollapse'>
    <el-collapse-item name='config'>
      <template slot="title">
        <div class='config-collapse-title'>API配置</div>
      </template>
      <div class="api-config" v-loading="loading">
        <n-form
          class="data-select-form"
          ref="apiRequest"
          label-position="top"
          :model="apiRequest"
          :rules="rules"
          @submit.native.prevent
          size="mini"
        >
          <el-button-group class="api-button-group" style='margin-left: 10px;'>
            <!-- <el-button
              class="api-button"
              :type="apiRequest.type === 4 ? 'primary' : ''"
              @click="changeType(4)"
            >网关API
            </el-button> -->
            <el-button class='api-button' :type='apiRequest.type === 5 ? "primary" : ""' @click='changeType(5)'>自定义API</el-button>
            <el-button
              class="api-button"
              :type="apiRequest.type === 6 ? 'primary' : ''"
              @click="changeType(6)"
            >外部API
            </el-button>
          </el-button-group>
          <div class="form-item-wrapper">
            <div class='url-title'>
              URL
              <el-tooltip popper-class="tooltip-question" placement="top">
                <div slot="content">1.动态路径参数需要在url路径前加冒号表示，动态参数在params中配置，如/user/:id/:name<br/>2.测试阶段url的参数由所填的测试值决定<br/>3.运行阶段url的参数由所填写的参数值决定</div>
                <i class="el-icon-question"></i>
              </el-tooltip>
            </div>
            <el-select size='mini' class="method-select" v-if='![5].includes(apiRequest.type)' v-model="apiRequest.method">
              <el-option
                v-for="m in methods"
                :key="m"
                :label="m"
                :value="m"
              ></el-option>
            </el-select>
            <el-tag v-else style='margin-bottom: 10px; width: 165px; text-align: center;' disable-transitions>{{ apiRequest.method }}</el-tag>
            <n-form-item class="no-border" prop="url">
              <div class="inline-form-item" style='padding: 0'>
                <template v-if='[6].includes(apiRequest.type)'>
                  <el-checkbox v-model="apiRequest.useDynamicOrigin" @change="handleUseDynamicOriginChange" style="margin: 0 10px;">动态协议域名端口</el-checkbox>
                  <el-tooltip popper-class="tooltip-question" placement="top">
                    <div slot="content">启用动态协议域名端口，则允许选取一个应用变量作为协议域名端口，系统会自动过滤掉填写的协议域名端口</div>
                    <i style="margin-right: 5px;" class="el-icon-question"></i>
                  </el-tooltip>
                  <template v-if="apiRequest.useDynamicOrigin">
                    <el-tooltip
                      v-if="apiRequest.dynamicOrigin !== undefined"
                      effect="dark"
                      :content="formatTip(apiRequest.dynamicOrigin)"
                      placement="top"
                    >
                      <el-tag
                        class="value-tag"
                        closable
                        disable-transitions
                        @click="handleShowAppVariables(apiRequest, 'dynamicOrigin')"
                        @close="apiRequest.dynamicOrigin = undefined"
                        :type='getTagType(apiRequest.dynamicOrigin)'
                      >
                        {{ formatTag(getElementName(apiRequest.dynamicOrigin), 12) }}
                      </el-tag>
                    </el-tooltip>
                    <div v-else class="value-input-content">
                      <el-button
                          @click="
                            handleShowAppVariables(apiRequest, 'dynamicOrigin')
                          "
                          >选择协议域名端口
                        </el-button>
                    </div>
                    <el-input v-model="url" placeholder="请输入path"></el-input>
                  </template>
                </template>
                <el-input v-if="![6].includes(apiRequest.type) || ([6].includes(apiRequest.type) && !apiRequest.useDynamicOrigin)" v-model="url" :disabled='[4, 5].includes(apiRequest.type)' placeholder="请输入URL"></el-input>
                <el-button v-if='[4, 5].includes(apiRequest.type)' type="primary" @click="handleShowGatewayDataSelect">选择</el-button>
                <el-button
                  type="primary"
                  @click="handleFetch"
                >测试
                </el-button>
              </div>
            </n-form-item>
          </div>
          <div class="sql-desc" v-if="[4, 5].includes(apiRequest.type) && apiRequest.desc">
            <div class="desc-item">描述：{{ apiRequest.desc }}</div>
          </div>
          <n-form-item label="">
            <div class="params-config">
              <el-button
                class="source-edit-button"
                type='primary'
                @click="
              showInputDialog(
                'object',
                paramsSource,
                activeParamsName,
                'paramsSource'
              )
            "
              >转换器</el-button>
              <el-button-group class="api-button-group">
                <el-button
                  class="api-button"
                  :type="activeParamsName === 'bodyData' ? 'primary' : ''"
                  @click="activeParamsName = 'bodyData'"
                >Body（{{bodyLength}}）
                </el-button>
                <el-button
                  class="api-button"
                  :type="activeParamsName === 'paramsData' ? 'primary' : ''"
                  @click="activeParamsName = 'paramsData'"
                >Params（{{paramsLength}}）
                </el-button>
                <el-button
                  class="api-button"
                  :type="activeParamsName === 'headersData' ? 'primary' : ''"
                  @click="activeParamsName = 'headersData'"
                >Headers（{{headersLength}}）
                </el-button>
              </el-button-group>
              <n-table
                :data="currentParamsData"
                border
                style="width: 100%"
                header-cell-class-name="header-cell"
              >
                <n-table-column width="200" prop="key" label="参数名">
                  <template slot-scope="scope">
                    <el-select
                      style='width: 100%;'
                      v-if="activeParamsName === 'headersData'"
                      v-model="scope.row.key"
                      filterable
                      allow-create
                      default-first-option
                    >
                      <el-option
                        v-for="option in headersOptions"
                        :key="option"
                        :value="option"
                        :label="option"
                      ></el-option>
                    </el-select>
                    <el-input
                      v-else
                      v-model="scope.row.key"
                      :disabled='paramsInputDisabled(scope.row.key)'
                    ></el-input>
                  </template>
                </n-table-column>
                <n-table-column prop="value" label="参数值">
                  <template slot-scope="scope">
                    <div class="value-input">
                      <el-tooltip
                        v-if="scope.row.value !== undefined"
                        effect="dark"
                        :content="formatTip(scope.row.value)"
                        placement="top"
                      >
                        <el-tag
                          class="value-tag"
                          closable
                          disable-transitions
                          @click="handleTagClick(scope.row, 'value')"
                          @close="scope.row.value = undefined"
                          :type='getTagType(scope.row.value)'
                        >
                          {{ formatTag(getElementName(scope.row.value), 12) }}
                        </el-tag>
                      </el-tooltip>
                      <div v-else class="value-input-content">
                        <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;" v-if="activeParamsName !== 'headersData'">
                          <el-button
                            @click="showInputDialog('text', scope.row, 'value')"
                          >文本
                          </el-button>
                          <el-button
                            @click="showInputDialog('number', scope.row, 'value')"
                            :disabled="
                          ['paramsData', 'headersData'].includes(
                            activeParamsName
                          )
                        "
                          >数值
                          </el-button>
                          <el-button
                            @click="showInputDialog('object', scope.row, 'value')"
                            :disabled="
                          ['paramsData', 'headersData'].includes(
                            activeParamsName
                          )
                        "
                          >对象
                          </el-button>
                          <el-button
                            @click="handleShowVariableSelect(scope.row, 'value')"
                          >变量
                          </el-button>
                        </el-button-group>
                        <div class="headers-input" v-else>
                          <el-select
                            v-if="scope.row.key === 'Content-Type'"
                            v-model="scope.row.value"
                            filterable
                            allow-create
                            default-first-option
                          >
                            <el-option
                              v-for="option in contentTypeOptions"
                              :key="option"
                              :value="option"
                              :label="option"
                            ></el-option>
                          </el-select>
                          <el-input
                            v-else
                            :disabled="!scope.row.key"
                            v-model="scope.row.value"
                            clearable
                          ></el-input>
                          <el-button
                            @click="handleShowVariableSelect(scope.row, 'value')"
                          >变量
                          </el-button>
                        </div>
                        <MoreTypeDropdown @change="(v) => scope.row.value = v" />
                      </div>
                    </div>
                  </template>
                </n-table-column>
                <n-table-column prop="test" label="测试值">
                  <template slot-scope="scope">
                    <div class="value-input">
                      <el-tooltip
                        v-if="
                      scope.row.test !== undefined &&
                        !['paramsData', 'headersData'].includes(
                          activeParamsName
                        )
                    "
                        effect="dark"
                        :content="isFile(scope.row.test) ? scope.row.test.name : formatTip(scope.row.test)"
                        placement="top"
                      >
                        <el-tag
                          class="value-tag"
                          closable
                          disable-transitions
                          @click="handleTagClick(scope.row, 'test')"
                          @close="scope.row.test = undefined"
                        >
                          {{ isFile(scope.row.test) ? '文件' : formatTag(scope.row.test) }}
                        </el-tag>
                      </el-tooltip>
                      <div v-else class="value-input-content">
                        <el-button-group
                          style="flex: 1;display: flex; align-items:center; margin-right: 4px;"
                          v-if="
                        !['paramsData', 'headersData'].includes(
                          activeParamsName
                        )
                      "
                        >
                          <el-button
                            @click="showInputDialog('text', scope.row, 'test')"
                          >文本
                          </el-button>
                          <el-button
                            @click="showInputDialog('number', scope.row, 'test')"
                          >数值
                          </el-button>
                          <el-button
                            @click="showInputDialog('object', scope.row, 'test')"
                          >对象
                          </el-button>
                          <el-button
                            @click="selectFile(scope.$index)"
                          >文件
                          </el-button>
                          <input type="file" style="display:none;" :ref="'fileRef' + scope.$index" @change="fileChange($event, scope.$index)">
                        </el-button-group>
                        <template v-else>
                          <el-select
                            v-if="
                          scope.row.key === 'Content-Type' &&
                            activeParamsName === 'headersData'
                        "
                            v-model="scope.row.test"
                            filterable
                            allow-create
                            default-first-option
                          >
                            <el-option
                              v-for="option in contentTypeOptions"
                              :key="option"
                              :value="option"
                              :label="option"
                            ></el-option>
                          </el-select>
                          <el-input
                            v-else
                            :disabled="!scope.row.key"
                            v-model="scope.row.test"
                            clearable
                          ></el-input>
                        </template>
                        <MoreTypeDropdown @change="(v) => scope.row.test = v" />
                      </div>
                    </div>
                  </template>
                </n-table-column>
                <n-table-column width="80" label="操作">
                  <template slot-scope="scope">
                    <div class="btns">
                      <div class="add-btn-primary" @click="handleAddParams">+</div>
                      <div
                        v-if='!paramsInputDisabled(scope.row.key)'
                        class="add-btn-primary"
                        @click="handleDeleteParams(scope.$index)"
                      >
                        -
                      </div>
                    </div>
                  </template>
                </n-table-column>
              </n-table>
            </div>
          </n-form-item>
        </n-form>
        <variable-select
          ref="variableSelect"
          @confirm="handleVariableConfirm"
        ></variable-select>
        <gateway-data-select
          ref="gatewayDataSelect"
          @confirm="handleGatewayDataConfirm"
        ></gateway-data-select>
        <custom-api-data-select
          ref="customApiDataSelect"
          @confirm="handleGatewayDataConfirm"
        ></custom-api-data-select>
        <input-dialog @confirm="handleUpdateValue" ref="inputDialog"></input-dialog>
        <AppVariables ref="appVariables" @confirm="handleAppVariableConfirm" />
      </div>
    </el-collapse-item>
  </el-collapse>
</template>

<script>
  import GatewayDataSelect from '@/components/GatewayDataSelect/index'
  import CustomApiDataSelect from '@/components/CustomApiDataSelect/index'
  import {
    getValidators,
    required,
  } from '@/utils/validateUtils'
  import _ from 'lodash'
  import request from '@/request/request'
  import { getObjectTree, getApiRequestParams } from '@/utils/format'
  import elementEditor from '@/mixins/elementEditor'
  import { parse } from 'path-to-regexp'
  import URI from 'urijs'
  import { getAppVariables, getFileNameFromHeaders, isValidateVariable } from '@/utils'
  import { pageInject } from '@/renderer/tools/pageStore'

  export default {
    name: 'ApiConfig',
    inject: [...pageInject],
    components: {
      GatewayDataSelect,
      CustomApiDataSelect,
    },
    mixins: [elementEditor],
    props: {
      apiRequest: {
        type: Object,
        required: true,
        default: () => ({
          desc: '',
          url: '',
          method: 'POST',
          type: 6,
          useDynamicOrigin: false,
          dynamicOrigin: undefined,
          paramsData: [
            {
              key: '',
              value: undefined,
              test: undefined,
            },
          ],
          bodyData: [
            {
              key: '',
              value: undefined,
              test: undefined,
            },
          ],
          headersData: [
            {
              key: '',
              value: undefined,
              test: undefined,
            },
          ],
        }),
      },
      callbackId: {
        type: String,
      },
      responseOptions: {
        type: Array,
      },
      responseConfig: {
        type: Object,
        default: () => ({
          isBlob: false,
          isUseHeaderFileName: true,
          fileName: undefined
        })
      },
      emitEvent: {
        type: Object,
        required: false
      }
    },
    data() {
      return {
        loading: false,
        activeCollapse: ['config'],
        methods: ['POST', 'GET', 'PUT', 'DELETE', 'PATCH'],
        activeParamsName: 'bodyData',
        rules: {
          url: getValidators([
            {
              message: '请输入',
              include: required,
              trigger: 'change',
            }
          ]),
        },
        variableRowDetail: null,
        contentTypeOptions: [
          'application/json',
          'application/x-www-form-urlencoded',
          'application/octet-stream',
          'multipart/form-data',
        ],
        headersOptions: ['snc-token', 'Content-Type', 'Authorization']
      }
    },
    computed: {
      url: {
        get() {
          return this.apiRequest.url
        },
        set(value) {
          const preNoQueryValue = this.apiRequest.url.split('?')[0]
          const noQueryValue = value.split('?')[0]
          const preUri = new URI(preNoQueryValue)
          const uri = new URI(noQueryValue)
          const prePathString = preUri.pathname()
          const pathString = uri.pathname()
          let dynamicPathArray = []
          let preDynamicPathArray = []
          try {
            preDynamicPathArray = parse(prePathString).filter(item => _.isObject(item)).map(item => item.name)
            dynamicPathArray = parse(pathString).filter(item => _.isObject(item)).map(item => item.name)
          } catch (e) {
            this.apiRequest.url = noQueryValue
            return
          }
          const dynamicPathParams = dynamicPathArray.map(key => {
            const current = this.apiRequest.paramsData.find((d) => d.key === key)
            return {
              key,
              value: current?.value,
              test: current?.test
            }
          })
          const paramsData = this.apiRequest.paramsData.filter(item => ![...preDynamicPathArray, ...dynamicPathArray].includes(item.key))
          this.apiRequest.paramsData = dynamicPathParams.length ? _.uniqBy([...dynamicPathParams, ...paramsData], 'key') : paramsData.length ? paramsData : [{key: '', value: undefined, test: undefined}]
          this.apiRequest.url = noQueryValue
        },
      },
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      currentParamsData() {
        return this.apiRequest[this.activeParamsName]
      },
      paramsSource: {
        get() {
          return {
            bodyData: Object.fromEntries(
              this.apiRequest.bodyData.map((item) => [[item.key], item.value])
            ),
            paramsData: Object.fromEntries(
              this.apiRequest.paramsData.map((item) => [[item.key], item.value])
            ),
            headersData: Object.fromEntries(
              this.apiRequest.headersData.map((item) => [
                [item.key],
                item.value,
              ])
            ),
          }
        },
        set(n) {
          this.apiRequest[this.activeParamsName] = Object.entries(n).map(
            ([key, value]) => ({
              key,
              value,
              test: isValidateVariable(value) ? undefined : JSON.parse(JSON.stringify(value === undefined ? null : value, function(k, v) { return isValidateVariable(v) ? null : v })),
            })
          )
        },
      },
      bodyLength() {
        return this.apiRequest.bodyData?.filter((d) => d.key).length || 0
      },
      paramsLength() {
        return this.apiRequest.paramsData?.filter((d) => d.key).length || 0
      },
      headersLength() {
        return this.apiRequest.headersData?.filter((d) => d.key).length || 0
      },
    },
    watch: {
      apiRequest: {
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          const res = {
            ...n,
            bodyData: n.bodyData?.map(item => ({...item, value: item.value, test: item.test})),
            paramsData: n.paramsData?.map(item => ({...item, value: item.value, test: item.test})),
            headersData: n.headersData?.map(item => ({...item, value: item.value, test: item.test}))
          }
          if (res.type === 4 && res.url) {
            const uri = new URI(res.url)
            const pre = `${uri.origin()}/gateway`
            if (uri.origin()) {
              const protocol = window.location.protocol
              const host = window.location.host
              const current = `${protocol}//${host}/gateway`
              res.url = res.url.replace(pre, current)
            }
          }
          if (res.type === 5 && res.url) {
            const uri = new URI(res.url)
            const pre = `${uri.origin()}/app-data`
            const current = '/app-data'
            if (uri.origin()) {
              res.url = res.url.replace(pre, current)
            }
            const appPath = res.url.replace(`${current}/`, '').split('/')[0]
            const newAppPath = `app${this.$route.query.id}`
            res.url = res.url.replace(appPath, newAppPath)
          }
          this.$emit('update:apiRequest', res)
        },
        deep: true,
        immediate: true,
      },
    },
    methods: {
      isFile(file) {
        return file instanceof File
      },
      selectFile(index) {
        this.$refs['fileRef' + index].click()
      },
      fileChange(e, index) {
        const [file] = e.target.files
        this.currentParamsData[index].test = file
        e.target.value = ''
      },
      handleShowVariableSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.variableSelect.open({
          fullVariable: row[key],
          callbackId: this.callbackId,
          emitEvent: this.emitEvent
        })
      },
      handleVariableConfirm(selectedVariable) {
        const { row, key, cb } = this.variableRowDetail
        if (key) {
          this.$set(row, key, selectedVariable)
          if (row.key === 'snc-token') {
            const loginKey = window.localStorage.getItem('loginKey')
            if (loginKey) {
              const token = JSON.parse(loginKey)?.['snc-token']
              this.$set(row, 'test', token)
            }
          }
        }
        cb?.(selectedVariable)
      },
      handleAddParams() {
        this.currentParamsData.push({
          key: '',
          value: undefined,
          test: undefined,
        })
      },
      handleDeleteParams(index) {
        const paramsData = this.currentParamsData
        if (paramsData.length === 1) {
          paramsData[0].key = ''
          paramsData[0].value = undefined
          paramsData[0].test = undefined
          return
        }
        paramsData.splice(index, 1)
      },
      handleFetch() {
        this.$emit('response', {
          response: null,
          responseOptions: [],
        })
        const params = getApiRequestParams.call(this, this.apiRequest)
        this.loading = true
        request({
          url: params.url,
          method: params.method,
          data: params.data,
          params: params.params,
          headers: params.headers,
          responseType: this.responseConfig.isBlob ? 'blob' : 'json',
        })
          .then((res) => {
            let response = null
            let responseOptions = []
            if (!this.responseConfig.isBlob) {
              response = res.data
              responseOptions = getObjectTree(res.data)
            } else {
              const fileName = getFileNameFromHeaders(res.headers)
              const name = this.responseConfig.isUseHeaderFileName ? fileName : isValidateVariable(this.responseConfig.fileName) ? 'newFile' : this.responseConfig.fileName || 'newFile'
              const url = URL.createObjectURL(res.data)
              const aLink = document.createElement('a')
              aLink.download = name
              aLink.href = url
              aLink.dispatchEvent(new MouseEvent('click', {}))
            }

            this.$emit('response', {
              response,
              responseOptions
            })
          })
          .catch((e) => {
            this.$message.error('接口测试失败！')
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleShowGatewayDataSelect() {
        if (this.apiRequest.type === 4) {
          this.$refs.gatewayDataSelect.open()
        } else {
          this.$refs.customApiDataSelect.open()
        }
      },
      handleGatewayDataConfirm({ apiRequest, response, apiInfo }) {
        if (!this.bodyLength && !this.paramsLength && !this.headersLength) {
          this.apiRequest = _.cloneDeep({...apiRequest, desc: apiInfo.description})
          this.$emit('update:apiRequest', this.apiRequest)
          this.$emit('response', {
            response,
            responseOptions: getObjectTree(response),
          })
          return
        }
        this.$confirm('是否覆盖已配置的请求参数？').then(() => {
          this.apiRequest = _.cloneDeep({...apiRequest, desc: apiInfo.description})
          this.$emit('update:apiRequest', this.apiRequest)
          this.$emit('response', {
            response,
            responseOptions: getObjectTree(response),
          })
        }).catch(() => {
          this.apiRequest.url = apiRequest.url
          this.apiRequest.desc = apiInfo.description
          this.$emit('update:apiRequest', this.apiRequest)
          this.$emit('response', {
            response,
            responseOptions: getObjectTree(response),
          })
        })
      },
      changeType(type) {
        if (this.apiRequest.type === type) {
          return
        }
        this.apiRequest.url = ''
        this.apiRequest.type = type
      },
      paramsInputDisabled(key) {
        if (this.activeParamsName !== 'paramsData') {
          return false
        }
        const uri = new URI(`${this.url}`)
        const pathString = uri.pathname()
        let dynamicPathArray = []
        try {
          dynamicPathArray = parse(pathString).filter(item => _.isObject(item)).map(item => item.name)
        } catch (e) {
          return false
        }
        return dynamicPathArray.includes(key)
      },
      handleUseDynamicOriginChange(isUse) {
        const uri = new URI(`${this.url}`)
        this.url = isUse ? uri.pathname() || '/' : this.url
      },
      handleShowAppVariables(row, key) {
        this.variableRowDetail = { row, key }
        this.$refs.appVariables.open({isManage: false, fullVariable: row[key]})
      },
      handleAppVariableConfirm(selectedVariable) {
        const { row, key, cb } = this.variableRowDetail
        if (key) {
          this.$set(row, key, selectedVariable)
        }
        if (row === this.apiRequest && this.apiRequest.useDynamicOrigin && key === 'dynamicOrigin') {
          const appVariables = getAppVariables() || {}
          const appVariablesKey = row[key].replace(`${this.getStore().layoutData.elementId}.$appVariables.`, '')
          const dynamicUrl = `${_.get(appVariables, appVariablesKey)}`
          if (dynamicUrl.indexOf('http') !== 0) {
            this.$message.error('动态协议域名端口格式不正确，请确认当前选择的应用变量的默认值为正确的url格式，如https://....')
            this.$set(row, key, undefined)
          }
        }
      }
    },
  }
</script>

