<template>
  <div class="progress-config">
    <template v-if="elementProps.type === 'line'">
      <n-form-item class="config-item">
        <template slot='label'>
          <span>线条宽度</span>
          <el-tooltip content="进度条线的宽度，单位 px" popper-class="tooltip-question" placement="top">
            <i style='margin-left: 5px;' class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <el-input-number v-model="value[elementProps.type].strokeWidth" :min="1"></el-input-number>
      </n-form-item>
      <n-form-item class="config-item">
        <template slot='label'>
          <span>进度条颜色</span>
          <el-tooltip content="进度条的色彩，设置2种颜色时为渐变" popper-class="tooltip-question" placement="top">
            <i style='margin-left: 5px;' class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <el-radio-group v-model="isUseGradient" @change="handleIsUseGradientChange">
          <el-radio-button :label="false">纯色</el-radio-button>
          <el-radio-button :label="true">渐变色</el-radio-button>
        </el-radio-group>
      </n-form-item>
      <n-form-item class="config-item">
        <template slot='label'>
          <span>选择颜色</span>
        </template>
        <div v-if="!isUseGradient && typeof value[elementProps.type].strokeColor === 'string'" style="display: flex; align-items: center;">
          <el-color-picker
            v-model="value[elementProps.type].strokeColor"
            show-alpha
            :predefine="predefineColors">
          </el-color-picker>
        </div>
        <div v-if="isUseGradient && typeof value[elementProps.type].strokeColor === 'object'" style="display: flex; align-items: center;">
          <el-color-picker
            style="margin-right: 20px;"
            v-model="value[elementProps.type].strokeColor.from"
            show-alpha
            :predefine="predefineColors">
          </el-color-picker>
          <el-color-picker
            style="margin-right: 20px;"
            v-model="value[elementProps.type].strokeColor.to"
            show-alpha
            :predefine="predefineColors">
          </el-color-picker>
        </div>
      </n-form-item>
    </template>
    <template v-if="elementProps.type === 'circle'">
      <n-form-item class="config-item">
        <template slot='label'>
          <span>线条宽度</span>
          <el-tooltip content="进度条线的宽度，单位 px" popper-class="tooltip-question" placement="top">
            <i style='margin-left: 5px;' class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <el-input-number v-model="value[elementProps.type].strokeWidth" :min="1"></el-input-number>
      </n-form-item>
      <n-form-item class="config-item">
        <template slot='label'>
          <span>画布宽度</span>
          <el-tooltip content="圆形进度条画布宽度，单位 px" popper-class="tooltip-question" placement="top">
            <i style='margin-left: 5px;' class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <el-input-number v-model="value[elementProps.type].width" :min="1"></el-input-number>
      </n-form-item>
      <n-form-item class="config-item">
        <template slot='label'>
          <span>进度条颜色</span>
          <el-tooltip content="进度条的色彩，设置2种颜色时为渐变" popper-class="tooltip-question" placement="top">
            <i style='margin-left: 5px;' class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <el-radio-group v-model="isUseGradient" @change="handleIsUseGradientChange">
          <el-radio-button :label="false">纯色</el-radio-button>
          <el-radio-button :label="true">渐变色</el-radio-button>
        </el-radio-group>
      </n-form-item>
      <n-form-item class="config-item">
        <template slot='label'>
          <span>选择颜色</span>
        </template>
        <div v-if="!isUseGradient && typeof value[elementProps.type].strokeColor === 'string'" style="display: flex; align-items: center;">
          <el-color-picker
            v-model="value[elementProps.type].strokeColor"
            show-alpha
            :predefine="predefineColors">
          </el-color-picker>
        </div>
        <div v-if="isUseGradient && typeof value[elementProps.type].strokeColor === 'object'" style="display: flex; align-items: center;">
          <el-color-picker
            style="margin-right: 20px;"
            v-model="value[elementProps.type].strokeColor['0%']"
            show-alpha
            :predefine="predefineColors">
          </el-color-picker>
          <el-color-picker
            style="margin-right: 20px;"
            v-model="value[elementProps.type].strokeColor['100%']"
            show-alpha
            :predefine="predefineColors">
          </el-color-picker>
        </div>
      </n-form-item>
    </template>
    <template v-if="elementProps.type === 'dashboard'">
      <n-form-item class="config-item">
        <template slot='label'>
          <span>线条宽度</span>
          <el-tooltip content="进度条线的宽度，单位 px" popper-class="tooltip-question" placement="top">
            <i style='margin-left: 5px;' class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <el-input-number v-model="value[elementProps.type].strokeWidth" :min="1"></el-input-number>
      </n-form-item>
      <n-form-item class="config-item">
        <template slot='label'>
          <span>画布宽度</span>
          <el-tooltip content="仪表盘进度条画布宽度，单位 px" popper-class="tooltip-question" placement="top">
            <i style='margin-left: 5px;' class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <el-input-number v-model="value[elementProps.type].width" :min="1"></el-input-number>
      </n-form-item>
      <n-form-item class="config-item">
        <template slot='label'>
          <span>缺口角度</span>
          <el-tooltip content="仪表盘进度条缺口角度，可取值 0 ~ 360" popper-class="tooltip-question" placement="top">
            <i style='margin-left: 5px;' class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <el-input-number v-model="value[elementProps.type].gapDegree" step-strictly :min="0" :max="360"></el-input-number>
      </n-form-item>
      <n-form-item class="config-item">
        <template slot='label'>
          <span>缺口位置</span>
          <el-tooltip content="仪表盘进度条缺口位置" popper-class="tooltip-question" placement="top">
            <i style='margin-left: 5px;' class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <el-radio-group v-model="value[elementProps.type].gapPosition">
          <el-radio-button label="top">上</el-radio-button>
          <el-radio-button label="bottom">下</el-radio-button>
          <el-radio-button label="left">左</el-radio-button>
          <el-radio-button label="right">右</el-radio-button>
        </el-radio-group>
      </n-form-item>
      <n-form-item class="config-item">
        <template slot='label'>
          <span>进度条颜色</span>
          <el-tooltip content="进度条的色彩" popper-class="tooltip-question" placement="top">
            <i style='margin-left: 5px;' class="el-icon-question"></i>
          </el-tooltip>
        </template>
        <el-radio-group v-model="isUseGradient" @change="handleIsUseGradientChange">
          <el-radio-button :label="false">纯色</el-radio-button>
          <el-radio-button :label="true" disabled>渐变色</el-radio-button>
        </el-radio-group>
      </n-form-item>
      <n-form-item class="config-item">
        <template slot='label'>
          <span>选择颜色</span>
        </template>
        <div v-if="!isUseGradient" style="display: flex; align-items: center;">
          <el-color-picker
            v-model="value[elementProps.type].strokeColor"
            show-alpha
            :predefine="predefineColors">
          </el-color-picker>
        </div>
      </n-form-item>
    </template>
  </div>

</template>

<script>
import _ from 'lodash';
const defaultColor = 'rgba(30, 144, 255, 1)'
  export default {
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      vue: {
        type: String,
      },
    },
    data() {
      return {
        isUseGradient: false,
        predefineColors: [
          '#ff4500',
          '#ff8c00',
          '#ffd700',
          '#90ee90',
          '#00ced1',
          '#1e90ff',
          '#c71585',
          'rgb(255, 120, 0)',
        ]
      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = {
            ...this.elementProps,
            [this.propKey]: value,
          }
          this.$emit('update', res)
        },
      },
    },
    watch: {
      value: {
        deep: true,
        immediate: true,
        handler(n, o) {
          this.isUseGradient = typeof n[this.elementProps.type].strokeColor === 'object'
        }
      }
    },
    methods: {
      handleIsUseGradientChange(use) {
        let strokeColor = this.value[this.elementProps.type].strokeColor
        strokeColor = use ? this.elementProps.type === 'line' ? {from: defaultColor, to: defaultColor} : {'0%': defaultColor, '100%': defaultColor} : defaultColor
        const copy = _.cloneDeep(this.value)
        this.value = {
          ...copy,
          [this.elementProps.type]: {
            ...copy[this.elementProps.type],
            strokeColor: _.cloneDeep(strokeColor)
          }
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .progress-config::v-deep {
    div.config-item {
      display: flex;
      align-items: center;
      width: 100%;
      margin-bottom: 8px;
      margin-right: 0;
      .el-form-item__content {
        flex: 1;
        & > * {
          width: 100%;
        }
      }
      .el-form-item__label{
        min-width: 100px;
      }
    }
  }
</style>
