import interact from 'interactjs'
import _ from 'lodash'
import { getPathList } from '@/utils/format'

export default {
  data() {
    return {
      dndCoverStyle: undefined,
    }
  },
  methods: {
    handleInitDnD() {
      interact('.drop-zone-path').unset()
      interact('.is-highlight').unset()
      // 初始化拖拽
      const that = this
      interact('.is-highlight')
        .draggable({
          manualStart: true,
          inertia: false,
          // autoScroll: {
          //   container: document.querySelector('.page-canvas'),
          //   margin: 50,
          //   distance: 5,
          //   interval: 10,
          //   speed: 300,
          // },
          autoScroll: false,
          listeners: {
            move(event) {
              event.target.style.left = `${event.clientX - 50}px`
              event.target.style.top = `${event.clientY - 10}px`
            },
            end() {
              const draggingElements = document.querySelectorAll('.dragging-item')
              Array.prototype.slice.call(draggingElements).forEach(item => item.remove())
            }
          },
        })
        .on('down', _.throttle(function(event) {
          that._mousedownTarget = event.currentTarget
          that._holding = true
          clearTimeout(that._holdTimer)
          that._holdTimer = setTimeout(() => {
            that._holding = false
          }, 30)
        }, 20, {leading: true, trailing: false}))
        .on('up', function() {
          that._mousedownTarget = null
          clearTimeout(that._holdTimer)
        })
        .on('move', function(event) {
          if (!that._mousedownTarget) {
            return
          }
          if (that._holding) {
            return
          }
          const { interaction } = event
          if (interaction.pointerIsDown && !interaction.interacting()) {
            let element = document.createElement('div')
            element.classList.add('is-highlight')
            element.classList.add('dragging-item')
            element.style.left = `${event.clientX0 - 50}px`
            element.style.top = `${event.clientY0 - 10}px`
            const lastPath = _.last(getPathList(that.selectedPath))
            element.textContent = lastPath
            const container = document.querySelector('.app-design-page')
            container && container.appendChild(element)
            const interactable = event.interactable
            interaction.start({ name: 'drag' }, interactable, element)
          }
        })
      // 落点
      interact('.drop-zone-path')
        .dropzone({
          accept: '.is-highlight',
          overlap: 0.25,
          ondropmove: _.debounce(function(event) {
            const {
              left,
              top,
              width,
              height,
            } = event.currentTarget.getBoundingClientRect()
            that.dndCoverStyle = {
              width: `${width}px`,
              height: `${height}px`,
              left: `${left}px`,
              top: `${top}px`,
            }
          }, 20, {leading: false, trailing: true}),
          ondrop(event) {
            that.handleDrop(event)
          },
          ondropdeactivate: _.throttle(function(event) {
            that.dndCoverStyle = undefined
          }, 20, {leading: false, trailing: true})
        })
    }
  },
  beforeDestroy() {
    interact('.drop-zone-path').unset()
    interact('.is-highlight').unset()
  }
}
