<template>
  <div class="widget-config-item">
    <div
      :class="{
        name: true,
        required: requiredArr.includes(name),
      }"
    >
      {{ name }}
      <el-tooltip
        v-if="tip"
        :content="tip"
        popper-class="tooltip-question"
        placement="top"
      >
        <i style="margin-left: 5px;" class="el-icon-question"></i>
      </el-tooltip>
    </div>
    <n-form-item class="config-item" label="" :prop="propKey">
      <el-input
        :placeholder="placeholder"
        clearable
        v-model.trim="inputValue"
        @change="handleChange"
      ></el-input>
    </n-form-item>
  </div>
</template>

<script>
  import _ from 'lodash'
  import { getNodeByElementId } from '@/utils/tree-utils'
  import {
    UPDATE_DYNAMIC_PROPS,
  } from '@/config/storeKey'
  import { pageInject } from '@/renderer/tools/pageStore'
  import { addCssByStyle, getImageUrl } from '@/utils'
  import styleToCss from 'style-object-to-css-string'
  export default {
    inject: [...pageInject],
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      tip: {
        type: String,
      },
      optionkey: {
        type: String,
      },
    },
    data() {
      return {
        requiredArr: [],
        inputValue: undefined,
      }
    },
    computed: {
      value: {
        get() {
          switch (this.optionkey) {
            case 'dialogBodyHeight': {
              const currentNode = getNodeByElementId(
                this.getStore().layoutData,
                this.elementProps.elementId
              )
              const slot = currentNode.children.find(
                (item) => this.getElementPropsById(item.elementId).slotName === 'body'
              )
              return this.getElementPropsById(slot?.elementId).styleConfigList[0].styles.height
            }
            default:
              return this.elementProps.styleConfigList[0].styles[this.optionkey]
          }
        },
      },
    },
    watch: {
      value: {
        deep: true,
        immediate: true,
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.inputValue = n
        },
      },
    },
    methods: {
      handleChange(value) {
        switch (this.optionkey) {
          case 'dialogBodyHeight': {
            const currentNode = getNodeByElementId(
              this.getStore().layoutData,
              this.elementProps.elementId
            )
            const slot = currentNode.children.find(
              (item) => this.getElementPropsById(item.elementId).slotName === 'body'
            )
            const styleConfigList = _.cloneDeep(this.getElementPropsById(slot.elementId).styleConfigList)
            styleConfigList[0].styles.height = value
            this._storeCommit(UPDATE_DYNAMIC_PROPS, {
              dynamicProps: {
                [slot.elementId]: {
                  styleConfigList
                },
              },
            })
            this.handleUpdateStyle(styleConfigList, slot.elementId)
            break
          }
          default: {
            const styleConfigList = _.cloneDeep(this.elementProps.styleConfigList)
            styleConfigList[0].styles[this.optionkey] = value
            const res = {
              ...this.elementProps,
              styleConfigList
            }
            this.$emit('update', res)
            this.handleUpdateStyle(styleConfigList, this.elementProps.elementId)
            break
          }
        }
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      },
      handleUpdateStyle(styleConfigList, elementId) {
        const styleText = styleConfigList.map(item => {
          const { cssText, ...styleObject  } = JSON.parse(JSON.stringify(item.styles))
          if (!cssText && Object.keys(styleObject).length === 0) {
            return ''
          }
          if (styleObject.backgroundImage) {
            const isPic = new RegExp(/\w(\.gif|\.jpeg|\.png|\.jpg|\.bmp)/i).test(styleObject.backgroundImage || '')
            styleObject.backgroundImage = isPic ? `url(${getImageUrl(styleObject.backgroundImage)})` : styleObject.backgroundImage
          }
          const styleString = `${styleToCss(styleObject) || ''}${cssText || ''}`
          return `#${elementId}${item.query}{${styleString}}`
        }).join('')
        const id = `static-style_${elementId}`
        addCssByStyle(id, styleText)
      },
    },
  }
</script>

