    }
    div.config-item {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 8px;
      margin-right: 0;

      .el-form-item__content {
        flex: 1;
        display: flex;
        align-items: center;
        .iconfont {
          font-size: 14px;
          margin-left: 10px;
          &:hover {
            cursor: pointer;
            color: @drop-main-color;
          }
        }
      }
    }
  }
</style>
