<template>
  <el-dialog
    title="选择变量"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="1200px"
    top='5vh'
    :before-close="handleClose"
    custom-class="handler-dialog low-code-dialog"
  >
    <div @click.stop='popoverVisible = false' class="dialog-content" :class='{"is-func": ["func"].includes(currentMenu)}'>
      <div class="top">
        <div class="aside">
          <div
            class="menu-item"
            :class="{ selected: menu.value === currentMenu, disabled: rootNode && !['widget'].includes(menu.value) }"
            v-for="menu in menuList"
            :key="menu.value"
            @click="handleSelectMenu(menu, rootNode && !['widget'].includes(menu.value))"
          >
            {{ menu.label }}
          </div>
        </div>
        <div class="content">
          <template v-if='["callback"].includes(currentMenu)'>
            <n-table
              :data="[{}]"
              border
              highlight-current-row
              :max-height="currentMenu === 'widget' ? 330 : 360"
            >
              <n-table-column width="50px" label="选择">
                <template slot-scope="scope">
                  <el-radio></el-radio>
                </template>
              </n-table-column>
              <n-table-column prop="desc" label="变量名" width="337px">
                <template slot-scope="scope">
                  <el-tag>回调变量</el-tag>
                </template>
              </n-table-column>
              <n-table-column prop="label" label="编码" width="431px">
                <template slot-scope="scope">
                  <span>{{ selectedVariable }}</span>
                </template>
              </n-table-column>
              <n-table-column prop="widget" label="所属组件" width="158px">
                <template slot-scope="scope">
                  <span>-</span>
                </template>
              </n-table-column>
            </n-table>
          </template>
          <template v-if="!['callback', 'func'].includes(currentMenu)">
            <n-form
              class="data-form"
              ref="dataForm"
              :model="dataForm"
              @submit.native.prevent
              label-width="80px"
              label-position="left"
            >
              <n-form-item label="选择组件" v-if="currentMenu === 'widget'">
                <tree-select
                  v-if="dialogVisible"
                  :tree="treeData"
                  :width="896"
                  :multiple="false"
                  node-key="elementId"
                  :default-selected-keys="defaultSelectedKeys"
                  @change="handleTreeSelectChange(false, $event)"
                >
                </tree-select>
              </n-form-item>
            </n-form>
            <n-table
              :data="variables"
              border
              highlight-current-row
              :max-height="currentMenu === 'widget' ? 330 : 360"
              @row-click="handleRowClick"
              v-loading='loading'
            >
              <n-table-column width="50px" label="选择">
                <template slot-scope="scope">
                  <el-radio
                    v-if="isSelectedRow(scope.row.value)"
                  ></el-radio>
                  <el-radio v-else :value='false'></el-radio>
                </template>
              </n-table-column>
              <n-table-column prop="desc" label="变量名" width="337px">
                <template slot-scope="scope">
                  <el-tag v-if="scope.row.isNotSupported" type="info" style="text-decoration:line-through;">{{ scope.row.desc }}</el-tag>
                  <el-tag v-else>{{ scope.row.desc }}</el-tag>
                </template>
              </n-table-column>
              <n-table-column prop="label" label="编码" width="431px">
              </n-table-column>
              <n-table-column prop="widget" label="所属组件" width="158px">
              </n-table-column>
            </n-table>
          </template>
          <js-config v-if='["func"].includes(currentMenu)' ref='jsConfig' v-model='functionId' :pure-function='true' :callback-id='callbackId' :emitEvent="openData.emitEvent"></js-config>
        </div>
      </div>
      <div class="bottom" v-if='!["func"].includes(currentMenu)'>
        <div class="selected">
          <div style='display: flex; align-items: center;'>
            <div style='margin: 5px 0'>当前选择的变量：<el-tag v-if='selectedDesc'>{{ selectedDesc }}</el-tag></div>
            <div class="input" v-if="selectedVariable && isShowDetail(selectedVariable)">
              <!--            <span style='white-space: nowrap;'>{{ selectedVariable }}</span>-->
              <span v-popover:popover style="line-height: 28px;padding: 0 5px;">.</span>
              <el-input
                style='width: 400px;'
                class="suffix-input"
                size="mini"
                clearable
                v-model="suffix"
                @change='handleSuffixChange'
                placeholder="选取属性"
              ></el-input>
              <el-popover
                v-model="popoverVisible"
                placement="top"
                title="选择属性"
                trigger="manual"
                ref="popover"
              >
                <n-cascader-panel
                  v-model="suffixPath"
                  :options="responseOptions"
                  :props="{ checkStrictly: true }"
                  :show-all-levels="true"
                  clearable
                  @change='handlePathChange'
                ></n-cascader-panel>
              </el-popover>
              <el-button @click.stop='handleShowSelect' style='margin-left: 10px;' :disabled='!response || selectPathDisabled || !responseOptions' type='primary'>选择</el-button>
            </div>
            <div v-if='!selectedVariable' class="input input-empty">未选择</div>
          </div>
          <div class="desc" style='color: red;margin-top: 5px;'>
            备注：如果需要进一步选取变量自身的属性，可通过“."获取，数组则通过“[x]”获取下标为x的属性。
          </div>
        </div>
      </div>
    </div>
    <div @click.stop='popoverVisible = false' slot="footer" class="dialog-footer">
      <el-button @click="clear">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import TreeSelect from '@/components/WidgetTreeSelect/index'
  import { treeUtil, getNodeByElementId } from '@/utils/tree-utils'
  import _ from 'lodash'
  import { FUNCTION_FLAG } from '@/config'
  import API from '@/api/application'
  import { getPathList, getPathString, getObjectTree } from '@/utils/format'
  import { isValidJsonString } from '@/utils/validateUtils'
  import { getEmitParamsRegx, getVariableByVersion } from '@/renderer/tools/config'
  import { getCookies, isValidateVariable } from '@/utils'
  import { pageInject } from '@/renderer/tools/pageStore'
  import { notSupportedKeys } from '@/config/code-var'

  export default {
    name: 'VariableSelect',
    inject: [...pageInject],
    components: {
      TreeSelect,
    },
    data() {
      return {
        loading: false,
        dialogVisible: false,
        dataForm: {},
        currentMenu: 'widget',
        variables: [],
        appVariables: [],
        dynamicVariables: [],
        selectedVariable: undefined,
        selectedDesc: undefined,
        defaultSelectedKeys: [],
        suffix: undefined,
        response: undefined,
        responseOptions: undefined,
        suffixPath: [],
        popoverVisible: false,
        callbackId: undefined,
        functionId: undefined,
        hideFunc: false,
        rootNode: undefined,
        localStorage: {},
        sessionStorage: {},
        cookie: {},
        openData: {}, // 打开弹窗传进来的变量
        hasEmitParams: false, // 是否有emit参数
        emitParams: [],
        showMenus: []
      }
    },
    computed: {
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      layoutData: {
        get() {
          return this.getStore().layoutData
        },
      },
      treeData() {
        const copy = this.rootNode ? _.cloneDeep(this.rootNode) : _.cloneDeep(this.layoutData)
        treeUtil.forEach([copy], (node) => {
          const showChildren = []
          node.children.forEach(item => {
            if (['slot'].includes(item.elementType) && (this.getElementPropsById(item.elementId).isVisible === false || ['data'].includes(this.getElementPropsById(item.elementId).slotType))) {
              return
            }
            if (['dialog-container', 'timer-container'].includes(item.elementType)) {
              if (!item.children.length) {
                return
              }
              item.disabled = true
            }
            showChildren.push(item)
          })
          node.children = showChildren
          node.elementName =
            this.getElementPropsById(node.elementId).elementName || node.elementName
        })
        return copy.children
      },
      menuList() {
        let menu = []
        if (this.callbackId) {
          menu = [
            {
              label: '应用变量',
              value: 'app',
            },
            {
              label: '页面变量',
              value: 'page',
            },
            {
              label: '组件变量',
              value: 'widget',
            },
            {
              label: '回调变量',
              value: 'callback',
            },
          ]
        } else {
          menu = [
            {
              label: '应用变量',
              value: 'app',
            },
            {
              label: '页面变量',
              value: 'page',
            },
            {
              label: '组件变量',
              value: 'widget',
            },
          ]
        }
        if(this.hasEmitParams) {
          menu.push({
            label: '事件参数',
            value: 'emitParams',
          })
        }
        if (!this.hideFunc) {
          menu.push({
            label: '高级变量',
            value: 'func',
          })
        }
        if (this.showMenus.length) {
          return menu.filter(item => this.showMenus.includes(item.value))
        }
        return menu
      },
      currentFunction() {
        const functionsData = this.getStore().functionsData
        return functionsData.find(item => item.id === this.functionId)
      },
      selectPathDisabled() {
        return this.selectedVariable.includes('$dynamicVariables') || this.selectedVariable.includes('$appVariables')
      }
    },
    methods: {
      async open(data) {
        this.openData = data
        this.hasEmitParams = data.emitEvent && data.emitEvent.params?.length
        const { fullVariable, callbackId, hideFunc = false, rootNode, showMenus = [] } = data
        this.handleInitLocalData()
        await this.handleGetAppVariables()
        this.rootNode = rootNode
        this.hideFunc = hideFunc
        this.callbackId = callbackId
        this.showMenus = showMenus
        const functionId = fullVariable && fullVariable.includes(FUNCTION_FLAG) ? fullVariable.split(FUNCTION_FLAG)[1] : undefined
        const elementId = isValidateVariable(fullVariable) ? fullVariable.split('.$')[0] : undefined
        const firstVariable = fullVariable?.split('.')[1]
        let suffix
        let variable
        if (elementId) {
          if (firstVariable?.includes('[')) {
            variable = `${elementId}.${firstVariable.split('[')[0]}`
          } else {
            variable = `${elementId}.${firstVariable.split('.')[0]}`
          }
          suffix = fullVariable?.split(variable)[1]
        }
        const dynamicVariables = _.cloneDeep(this.getElementPropsById(this.layoutData.elementId).dynamicVariables || {})
        const widgetName = this.getElementPropsById(this.layoutData.elementId).elementName || this.layoutData.elementName
        this.dynamicVariables = Object.entries(dynamicVariables).map(
          ([key, value]) => ({
            label: `${this.layoutData.elementId}.$dynamicVariables.${key}.value`,
            value: `${this.layoutData.elementId}.$dynamicVariables.${key}.value`,
            desc: value.desc || key,
            widget: widgetName,
            elementId: this.layoutData.elementId,
          })
        )
        const hasLoginKey = !!window.localStorage.getItem('loginKey')
        const hasUserInfo = !!window.localStorage.getItem('userInfo')
        this.appVariables = [
          ...this.appVariables,
          hasLoginKey ?           {
            label: `${this.layoutData.elementId}.$localStorage.loginKey`,
            value: `${this.layoutData.elementId}.$localStorage.loginKey`,
            desc: '登录信息',
            widget: '应用',
            elementId: this.layoutData.elementId,
          } : undefined,
          hasUserInfo ?           {
            label: `${this.layoutData.elementId}.$localStorage.userInfo`,
            value: `${this.layoutData.elementId}.$localStorage.userInfo`,
            desc: '用户信息',
            widget: '应用',
            elementId: this.layoutData.elementId,
          } : undefined,
          {
            label: `${this.layoutData.elementId}.$localStorage`,
            value: `${this.layoutData.elementId}.$localStorage`,
            desc: '应用本地缓存LocalStorage',
            widget: '应用',
            elementId: this.layoutData.elementId,
          },
          {
            label: `${this.layoutData.elementId}.$sessionStorage`,
            value: `${this.layoutData.elementId}.$sessionStorage`,
            desc: '应用临时缓存sessionStorage',
            widget: '应用',
            elementId: this.layoutData.elementId,
          },
          {
            label: `${this.layoutData.elementId}.$cookie`,
            value: `${this.layoutData.elementId}.$cookie`,
            desc: '应用cookie',
            widget: '应用',
            elementId: this.layoutData.elementId,
          },
        ].filter(item => item)

        const currentNode = getNodeByElementId(this.layoutData, elementId || '')

        if (currentNode) {
          switch (currentNode.elementType) {
            case 'page':
              if (
                ['$localStorage', '$sessionStorage', '$cookie'].some((item) =>
                  `${fullVariable}`.includes(item)
                )
              ) {
                this.currentMenu = 'app'
                this.variables = _.cloneDeep(this.appVariables)
                this.selectedVariable = variable
                this.suffix = suffix ? suffix.slice(1, suffix.length) : ''
                this.suffixPath = getPathList(this.suffix)
                switch(true) {
                  case `${fullVariable}`.includes('$localStorage'): {
                    this.response = this.localStorage
                    this.responseOptions = getObjectTree(this.response)
                    break
                  }
                  case `${fullVariable}`.includes('$sessionStorage'): {
                    this.response = this.sessionStorage
                    this.responseOptions = getObjectTree(this.response)
                    break
                  }
                  case `${fullVariable}`.includes('$cookie'): {
                    this.response = this.cookie
                    this.responseOptions = getObjectTree(this.response)
                    break
                  }
                }
              } else if (
                ['$response'].some((item) => `${fullVariable}`.includes(item))
              ) {
                this.currentMenu = 'callback'
                this.selectedVariable = `${variable}.${callbackId}`
                this.suffix = suffix ? suffix.slice(callbackId.length + 2, suffix.length) : ''
                this.suffixPath = getPathList(this.suffix)
                this.selectedDesc = '回调变量'
                this.handleGetJsonConfig(this.selectedVariable)
              } else if(this.appVariables.some(item => fullVariable.indexOf(item.value) === 0)) {
                const currentAppVariable = this.appVariables.find(item => fullVariable.indexOf(item.value) === 0)
                this.selectedVariable = currentAppVariable?.value || fullVariable
                this.currentMenu = 'app'
                this.variables = _.cloneDeep(this.appVariables)
                const suffix = currentAppVariable ? fullVariable.replace(currentAppVariable.value, '') : ''
                this.suffix = suffix ? suffix.slice(1, suffix.length) : ''
                this.suffixPath = getPathList(this.suffix)
              } else {
                this.currentMenu = 'page'
                this.handleUpdateVariables(this.layoutData)
                const currentPageVariable = this.dynamicVariables.find(item => fullVariable.indexOf(item.value) === 0)
                this.selectedVariable = currentPageVariable?.value || fullVariable
                const suffix = currentPageVariable ? fullVariable.replace(currentPageVariable.value, '') : ''
                this.suffix = suffix ? suffix.slice(1, suffix.length) : ''
                this.suffixPath = getPathList(this.suffix)
              }
              break
            default:
              this.currentMenu = 'widget'
              this.handleUpdateVariables(currentNode)
              this.selectedVariable = variable
              this.suffix = suffix ? suffix.slice(1, suffix.length) : ''
              this.suffixPath = getPathList(this.suffix)
              this.handleGetJsonConfig(this.selectedVariable)
              break
          }
        }

        if (elementId) {
          if (!currentNode) {
            if (
              ['$localStorage', '$sessionStorage', '$cookie'].some((item) =>
                `${fullVariable}`.includes(item)
              )
            ) {
              this.currentMenu = 'app'
            } else if (
              ['$response'].some((item) => `${fullVariable}`.includes(item))
            ) {
              this.currentMenu = 'callback'
              this.suffix = suffix ? suffix.slice(callbackId.length + 2, suffix.length) : ''
              this.suffixPath = getPathList(this.suffix)
            } else if(this.appVariables.some(item => item.value === fullVariable)) {
              this.currentMenu = 'app'
            } else if(
              ['$dynamicVariables'].some((item) =>
                `${fullVariable}`.includes(item)
              )
            ) {
              this.currentMenu = 'page'
            } else {
              this.currentMenu = 'widget'
            }
          }
          this.defaultSelectedKeys = [elementId]
          this.selectedDesc = currentNode ? getVariableByVersion.call(this, { elementType: currentNode.elementType, elementVersion: currentNode.elementVersion, elementId })[firstVariable]?.desc : ''
        }
        if (functionId) {
          this.functionId = functionId
          this.$nextTick(() => {
            this.currentMenu = 'func'
            this.selectedVariable = fullVariable
            this.selectedDesc = this.currentFunction?.name
          })
        }
        if (this.currentMenu === 'callback' && this.callbackId) {
          this.selectedVariable = `${this.layoutData.elementId}.$response.${callbackId}`
          this.selectedDesc = '回调变量'
          this.handleGetJsonConfig(this.selectedVariable)
        }

        // 如果是有回调的参数
        if(this.hasEmitParams) {
          const { elementId, eventName, params } = data.emitEvent
          const { elementName } = this.getElementPropsById(elementId)
          this.emitParams = params.map((item, index) => ({
            label: `${elementId}.$emitEvent.args.[${index}]`,
            value: `${elementId}.$emitEvent.args.[${index}]`,
            desc: item.name || `第${index + 1}个参数`,
            widget: elementName,
            elementId,
          }))
          // 增加所有参数集合选项
          this.emitParams.unshift({
            label: `${elementId}.$emitEvent.args`,
            value: `${elementId}.$emitEvent.args`,
            desc: '所有参数 (数组, 同function中的 arguments)',
            widget: elementName,
            elementId,
          })
          const regx = getEmitParamsRegx(elementId)
          // 当前选中是否是emit回调参数
          if(regx.test(fullVariable)) {
            this.variables = _.cloneDeep(this.emitParams)
            const footer = fullVariable.replace(regx, '$3')
            const body = fullVariable.replace(footer, '')
            this.selectedVariable = body
            this.selectedDesc = this.emitParams.find(item => this.isSelectedRow(item.value))?.desc
            this.suffix = footer.slice(1)
            this.currentMenu = 'emitParams'
          }
        }
        if (this.showMenus.length && !this.showMenus.includes(this.currentMenu)) {
          this.handleSelectMenu({value: this.showMenus[0]})
        }
        this.dialogVisible = true
      },
      clear() {
        this.selectedVariable = undefined
        this.selectedDesc = undefined
        this.defaultSelectedKeys = []
        this.suffix = undefined
        this.response = undefined
        this.responseOptions = undefined
        this.callbackId = undefined
        this.suffixPath = []
        this.functionId = undefined
        this.loading = false
        this.dialogVisible = false
      },
      async confirm() {
        if (this.currentMenu === 'func') {
          await this.$refs.jsConfig.confirm()
          this.selectedVariable = `${FUNCTION_FLAG}${this.functionId}`
          this.selectedDesc = this.currentFunction?.name
        }
        if (!this.selectedVariable) {
          this.$message({ message: '请选择变量', type: 'error' })
          return
        }
        let path = this.suffix
        if (this.currentMenu === 'callback') {
          path =
            this.suffixPath && this.suffixPath.length
              ? getPathString(this.suffixPath)
              : this.suffix
        }
        this.$emit(
          'confirm',
          `${this.selectedVariable}${path ? `.${path}` : ''}`,
          this.selectedDesc || this.selectedVariable
        )
        this.clear()
      },
      handleClose(done) {
        this.clear()
        done()
      },
      handleSelectMenu(menu, disabled = false) {
        if (disabled) {
          return
        }
        this.currentMenu = menu.value
        this.suffix = undefined
        this.selectedVariable = undefined
        switch (menu.value) {
          case 'app':
            this.variables = _.cloneDeep(this.appVariables)
            break
          case 'page':
            this.handleUpdateVariables(this.layoutData)
            break
          case 'widget':
            this.variables = []
            break
          case 'callback':
            this.variables = []
            this.selectedDesc = '回调变量'
            this.suffix = ''
            this.suffixPath = []
            this.selectedVariable = `${this.layoutData.elementId}.$response.${this.callbackId}`
            this.handleGetJsonConfig(this.selectedVariable)
            break
          case 'func':
            this.variables = []
            this.selectedDesc = ''
            this.suffix = ''
            this.suffixPath = []
            this.selectedVariable = this.functionId ? `${FUNCTION_FLAG}${this.functionId}` : ''
            break
          case 'emitParams':
            this.variables = _.cloneDeep(this.emitParams)
            break
        }
      },
      handleTreeSelectChange(multiple, selection) {
        if (multiple) {
          return
        }
        const selected = selection[0]
        this.handleUpdateVariables(selected)
      },
      handleUpdateVariables(selected) {
        if (!selected) {
          this.variables = []
          return
        }
        const cssMode = this.getElementPropsById(selected.elementId).cssMode
        const data = getVariableByVersion.call(this, { elementType: selected.elementType, elementVersion: selected.elementVersion, elementId: selected.elementId })

        if (!data) {
          this.variables = []
          return
        }
        const widgetName = this.getElementPropsById(selected.elementId).elementName
        const allVariables = Object.entries(data || {})
        .filter(([key, value]) => ![2].includes(value.auth) && ((cssMode === 'config' && key !== '$stylesText') || (cssMode !== 'config' && key !== '$styles')))
        this.variables = allVariables
          .map(([key, value]) => ({
            label: `${selected.elementId}.${key}`,
            value: `${selected.elementId}.${key}`,
            desc: value.desc,
            widget: widgetName || selected.elementName,
            elementId: selected.elementId,
            isNotSupported: notSupportedKeys.includes(key)
          }))
        if (selected.elementType === 'page') {
          this.variables = [...this.variables, ...this.dynamicVariables].filter(item => item.value !== `${this.getStore().layoutData.elementId}.$elementId`)
        }
      },
      handleRowClick(row, column, event) {
        this.selectedVariable = row.value
        this.selectedDesc = row.desc
        this.suffix = ''
        this.suffixPath = []
        this.response = undefined
        this.responseOptions = undefined
        if(this.hasEmitParams && this.currentMenu === 'emitParams') {
          return
        }
        this.open({
          fullVariable: row.label,
          callbackId: this.callbackId,
          hideFunc: this.hideFunc,
          rootNode: this.rootNode,
          showMenus: this.showMenus
        })
      },
      handleShowSelect() {
        this.popoverVisible = !this.popoverVisible
      },
      handlePathChange(path) {
        this.suffix = getPathString(path)
      },
      handleGetJsonConfig: _.debounce( function (key) {
        this.$http({
          url: API.appDesignJsonDataConfigQuery,
          data: {
            appDesignId: Number(this.$route.query.vid),
            key,
          },
        })
          .then(({ data }) => {
            if (this.currentMenu === 'callback') {
              this.response = data && typeof data === 'string' && isValidJsonString(data) ? JSON.parse(data) : data
              this.responseOptions = _.isObject(this.response) ? getObjectTree(this.response) : undefined
              return
            }

            this.response = data ? JSON.parse(data) : undefined
            this.responseOptions = _.isObject(this.response) ? getObjectTree(this.response) : undefined
          })
          .finally(() => {
          })
      }, 300),
      handleSuffixChange(value) {
        this.suffixPath = getPathList(value)
      },
      isShowDetail(data) {
        if (this.currentMenu === 'callback' || this.currentMenu === 'emitParams') {
          return true
        }
        const elementId = data.split('.')[0]
        const firstVariable = data.split('.')[1]
        let variableName
        if (firstVariable.includes('[')) {
          variableName = firstVariable.split('[')[0]
        } else {
          variableName = firstVariable.split('.')[0]
        }
        if (elementId === this.layoutData.elementId && ['$dynamicVariables', '$appVariables'].includes(variableName)) {
          return true
        }
        const { elementType, elementVersion } = getNodeByElementId(this.layoutData, elementId)
        const config = getVariableByVersion.call(this, { elementType, elementVersion, elementId })[variableName]
        if (config) {
          let type
          if (typeof config.type === 'function') {
            type = config.type.name.toLowerCase()
          } else {
            type = config.type.toLowerCase()
          }
          return ['array', 'object'].includes(type)
        }
        return false
      },
      handleGetAppVariables() {
        return this.$http({
          url: API.appVariablesList,
          data: {
            appDesignId: Number(this.$route.query.vid),
          },
        })
          .then(({ data }) => {
            const dataList = data.map((item) => ({
              label: `${this.layoutData.elementId}.$appVariables.${item.name}.value`,
              value: `${this.layoutData.elementId}.$appVariables.${item.name}.value`,
              desc: item.description || item.name,
              widget: '应用',
              elementId: this.layoutData.elementId,
            }))
            this.appVariables = dataList
          })
      },
      isSelectedRow(value) {
        const { selectedVariable, suffix } = this
        const fullPath = `${selectedVariable}${suffix ? '.' + suffix : ''}`
        // 模糊匹配的所有结果
        const equalArr = this.variables.map(item => item.value).filter(value => {
          return fullPath.indexOf(value) !== -1
        })

        // 过滤掉较浅的匹配: 和同数组中的其他非自己项对比, 过滤掉 "被其他非自己的项全包含的结果"
        const result = equalArr.filter(item => !equalArr.find(o => {
          // 被其他非自己的项全包含的项
          return o !== item && o.indexOf(item) !== -1
        }))
        return selectedVariable && result.length && result[0] === value
      },
      handleInitLocalData() {
        const storageData = Object.entries(window.localStorage).filter(([key, value]) => !key.includes('STORE_PAGE_KEY')).map(([key, value]) => {
          if (value && isValidJsonString(value)) {
            return [key, JSON.parse(value)]
          }
          return [key, value]
        })
        const sessionData = Object.entries(window.sessionStorage).map(([key, value]) => {
          if (value && isValidJsonString(value)) {
            return [key, JSON.parse(value)]
          }
          return [key, value]
        })
        this.localStorage = Object.fromEntries(storageData)
        this.sessionStorage = Object.fromEntries(sessionData)
        this.cookie = getCookies()
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      }
    },
  }
</script>

