<template>
  <div class="json-code">
    <div class="highlight-code" ref="codeEditor" v-loading.lock='loading'></div>
    <ChangeHeight v-if="resizable" @heightChange="handleHeightChange" />
  </div>
</template>

<script>
  import { isValidJsonString } from '@/utils/validateUtils'
  import _ from 'lodash'
  import ChangeHeight from '@/components/ChangeHeight'
  export default {
    name: 'JsonCode',
    components: {
      ChangeHeight
    },
    props: {
      value: {
        type: [String, Object],
      },
      isObject: {
        type: Boolean,
        default: false
      },
      readOnly: {
        type: Boolean,
        default: false
      },
      resizable: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        loading: false,
        hints: [],
        monacoInstance: null,
        provider: null,
      }
    },
    computed: {
      code: {
        get() {
          return this.formatCode(this.value)
        },
        set(n) {
          const res = this.isObject ? JSON.parse(n) : n
          this.$emit('input', res)
        },
      },
    },
    watch: {
      value: {
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.code = this.formatCode(n)
          this.monacoInstance?.setValue(this.code)
        },
        immediate: true
      },
    },
    methods: {
      formatCode(obj) {
        if (typeof obj === 'string') {
          if (isValidJsonString(obj)) {
            try {
              JSON.parse(obj)
              return obj
            } catch (e) {
              console.log(e)
              this.$message.error('JSON解析失败！')
              return obj
            }
          } else {
            this.$message.error('JSON解析失败！')
            return obj
          }
        } else {
          let code
          try {
            code = JSON.stringify(obj, function(k, v) { return v === undefined ? null : v }, 4)
          } catch (e) {
            console.log(e)
            this.$message.error('JSON解析失败！')
          }
          return code || '{\t}'
        }
      },
      dispose() {
        if (this.monacoInstance) {
          if (this.monacoInstance.getModel()) {
            this.monacoInstance.getModel().dispose()
          }
          this.monacoInstance.dispose()
          this.monacoInstance = null
          if (this.provider) {
            this.provider.dispose()
            this.provider = null
          }
        }
      },
      async init() {
        const target = this.$refs.codeEditor
        if (!target) {
          return
        }
        this.loading = true
        const src = [import('monaco-editor/esm/vs/editor/editor.api.js')]
        const [monaco] = await Promise.all(src)
        this.dispose()
        this.monacoInstance = monaco.editor.create(target, {
          value: this.code,
          language: 'json',
          theme: 'vs',
          folding: false,
          foldingHighlight: true,
          foldingStrategy: 'indentation',
          showFoldingControls: 'always',
          disableLayerHinting: true,
          emptySelectionClipboard: false,
          selectionClipboard: false,
          automaticLayout: true,
          codeLens: false,
          scrollBeyondLastLine: false,
          colorDecorators: true,
          accessibilitySupport: 'off',
          lineNumbers: 'on',
          lineNumbersMinChars: 5,
          enableSplitViewResizing: false,
          roundedSelection: false,
          overviewRulerBorder: false,
          minimap: {
            enabled: false,
          },
          autoIndent: 'full',
          readOnly: this.readOnly,
          tabSize: 4,
        })
        // this.monacoInstance.onDidChangeModelContent(
        //   _.debounce(
        //     () => {
        //       if (!this.monacoInstance) {
        //         return
        //       }
        //       this.code = this.monacoInstance.getValue()
        //     },
        //     500,
        //     { leading: false, trailing: true }
        //   )
        // )
        this.monacoInstance.onDidFocusEditorText(() => {
          this.$emit('focus-change', true)
        })
        this.monacoInstance.onDidBlurEditorText(() => {
          this.doFormat()
          this.$emit('focus-change', false)
        })
        this.loading = false
      },
      doFormat() {
        if (!this.monacoInstance) {
          return
        }
        this.monacoInstance.getAction(['editor.action.formatDocument'])._run()
        setTimeout(() => {
          this.code = this.monacoInstance.getValue()
        }, 200)
      },
      handleHeightChange: _.throttle(function ({height}) {
        const calcHeight = this.$el.clientHeight + height
        const width = this.$el.clientWidth
        this.monacoInstance.layout({
          width,
          height: calcHeight < 300 ? 300 : calcHeight
        })
      }, 60)
    },
    mounted() {
      this.$nextTick(() => {
        this.init()
      })
    },
    beforeDestroy() {
      this.dispose()
    },
  }
</script>

<style lang="less" scoped>
  .json-code::v-deep {
    width: 100%;
    height: 100%;
    min-height: 300px;
    .highlight-code{
      width: 100%;
      height: 100%;
      min-height: 300px;
    }
  }
</style>
