<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="data-select-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="数据集配置"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="data-select" v-loading.lock='loading'>
      <data-set-config
        v-if='dialogVisible'
        ref='dataSetConfig'
        :request='{apiRequest, sqlRequest, staticData}'
        :type.sync='activeDataType'
        @response='handleResponse'
      ></data-set-config>
      <div class="data-select-item">
        <el-collapse class='config-collapse' v-model='activeCollapse'>
          <el-collapse-item name='config'>
            <template slot="title">
              <div class='config-collapse-title'>字段映射</div>
            </template>
            <div class="data-select-form data-content">
              <div class="data-config">
                <div class="data-table" :style='{width: "50%"}' v-loading='tableLoading'>
                  <el-collapse class='config-collapse' v-model='activeDataCollapse'>
                    <el-collapse-item name='data'>
                      <template slot="title">
                        <div class='config-collapse-title'>变量映射</div>
                      </template>
                      <vxe-table
                        ref='dataVariablesTable'
                        size='mini'
                        :data="dataVariables"
                        border
                        style="width: 100%;"
                        header-cell-class-name="header-cell"
                      >
                        <vxe-table-column field="label" title="变量名">
                          <template #default="{row}">
                            <div>
                              <span style="color: red;" v-if="['dataList'].includes(row.key)">*</span>
                              <span>{{row.label}}</span>
                              <el-tooltip :content="getTip(row.key)" popper-class="tooltip-question" placement="top">
                                <i style='margin-left: 5px;' class="el-icon-question"></i>
                              </el-tooltip>
                            </div>
                          </template>
                        </vxe-table-column>
                        <vxe-table-column field="key" title="编码"></vxe-table-column>
                        <vxe-table-column field="path" title="数据路径" min-width="150px">
                          <template #default='{ row }'>
                            <div class='drop-zone-path' :data-key='row.key'>
                              <el-tooltip
                                v-if='row.path.length'
                                effect="dark"
                                :content="row.path.join('/')"
                                placement="top"
                              >
                                <el-tag
                                  closable
                                  @close='row.path = []'
                                  disable-transitions
                                >{{row.path[row.path.length - 1]}}</el-tag>
                              </el-tooltip>
                              <span v-else>请从右侧拖拽节点</span>
                            </div>
                          </template>
                        </vxe-table-column>
                      </vxe-table>
                    </el-collapse-item>
                  </el-collapse>
                  <el-collapse class='config-collapse' v-model='activeDataCollapse'>
                    <el-collapse-item name='widget'>
                      <template slot="title">
                        <div class='config-collapse-title'>组件映射</div>
                      </template>
                      <vxe-table
                        ref='dataColumnTable'
                        size='mini'
                        :data="widgetVariables"
                        border
                        style="width: 100%"
                        header-cell-class-name="header-cell"
                      >
                        <vxe-table-column field="key" title="变量名" min-width="150px">
                          <template #default='{ row }'>
                            <div class="value-input">
                              <el-tooltip
                                v-if="row.key"
                                effect="dark"
                                :content="formatTip(row.key)"
                                placement="top"
                              >
                                <el-tag
                                  class="value-tag"
                                  closable
                                  disable-transitions
                                  @click="handleShowVariableSelect(row, 'key')"
                                  @close="row.key = undefined"
                                  :type='getTagType(row.key)'
                                >
                                  {{ formatTag(getElementName(row.key), 12) }}
                                </el-tag>
                              </el-tooltip>
                              <div v-else class="value-input-content">
                                <el-button @click="handleShowVariableSelect(row, 'key')">点击选择组件变量</el-button>
                              </div>
                            </div>
                          </template>
                        </vxe-table-column>
                        <vxe-table-column field="path" title="变量值">
                          <template #default='{ row }'>
                            <div class='drop-zone-path' :data-id='row.key'>
                              <el-tooltip
                                v-if='row.path.length'
                                effect="dark"
                                :content="row.path.join('/')"
                                placement="top"
                              >
                                <el-tag
                                  closable
                                  @close='row.path = []'
                                  disable-transitions
                                >{{row.path[row.path.length - 1]}}</el-tag>
                              </el-tooltip>
                              <span v-else>请从右侧拖拽节点</span>
                            </div>
                          </template>
                        </vxe-table-column>
                        <vxe-table-column title="操作" width="90px">
                          <template #default='{ row, rowIndex }'>
                            <div class="control-col">
                              <div class="btns">
                                <div class="add-btn-primary" @click='handleAddWidget'>+</div>
                                <div class="add-btn-primary" @click='handleDeleteWidget(row, rowIndex)'>-</div>
                              </div>
                            </div>
                          </template>
                        </vxe-table-column>
                      </vxe-table>
                    </el-collapse-item>
                  </el-collapse>
                </div>
                <div class="data-result">
                  <div class="title">
                    <div class="name">
                      响应体
                      <template v-if='activeDataType !== "static"'>
                        <el-button size='mini' @click="showInputResponseDialog">手动录入</el-button>
                        <el-button size='mini' v-if='response' @click='handleClearResponse'>清除</el-button>
                      </template>
                    </div>
                  </div>
                  <div class="body">
                    <vue-json-pretty
                      v-show='response'
                      class="json-pretty no-select"
                      path="response"
                      :data="response"
                      :virtual='true'
                      :itemHeight='24'
                      v-model="selectedPath"
                      @click='copyPath'
                      selectableType="single"
                      :showSelectController="true"
                      :selectOnClickNode="true"
                    >
                    </vue-json-pretty>
                    <div v-if='!response' class='empty-text'>暂无数据</div>
                  </div>
                </div>
              </div>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
      <variable-select
        ref="variableSelect"
        @confirm="handleVariableConfirm"
      ></variable-select>
      <input-dialog @confirm="handleInputResponse" ref="inputDialog"></input-dialog>
      <div class="dnd-cover" v-show="dndCoverStyle" :style="dndCoverStyle"></div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" :loading='loading' @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import DataSetConfig from '@/components/DataSetConfig'
  import dnd from '@/mixins/dragAndDrop'
  import VueJsonPretty from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  import _ from 'lodash'
  import { isValidJsonString } from '@/utils/validateUtils'
  import {
    getNodeByElementId,
  } from '@/utils/tree-utils'
  import API from '@/api/application'
  import { getObjectTree, getPathList } from '@/utils/format'
  import elementEditor from '@/mixins/elementEditor'
  import { getVariableByVersion } from '@/renderer/tools/config'
  import { isValidateVariable } from '@/utils'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    name: 'PanelData',
    components: {
      VueJsonPretty,
      DataSetConfig,
    },
    inject: ['dndOptions', ...pageInject],
    mixins: [dnd, elementEditor],
    data() {
      return {
        loading: false,
        activeCollapse: ['config'],
        activeDataCollapse: ['data', 'widget'],
        dialogVisible: false,
        dataVariables: undefined,
        apiRequest: undefined,
        sqlRequest: undefined,
        staticData: undefined,
        selectedPath: undefined,
        responseOptions: [],
        response: undefined,
        jsonLoading: false,
        tableLoading: false,
        activeDataType: '',
        widgetVariables: [],
        variableRowDetail: null
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      currentElement() {
        return getNodeByElementId(this.layoutData, this.dndOptions.selectedId)
      },
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      currentProps() {
        return this.dynamicProps[this.dndOptions.selectedId] || {}
      },
    },
    methods: {
      open({ dataVariables, apiRequest, sqlRequest, staticData, widgetVariables }) {
        this.widgetVariables = widgetVariables && widgetVariables.length ? _.cloneDeep(widgetVariables) : [{key: undefined, path: []}]
        this.dataVariables = dataVariables ? _.cloneDeep(dataVariables) : []
        this.staticData = _.cloneDeep(staticData) || undefined
        this.apiRequest = _.cloneDeep(apiRequest) || undefined
        this.sqlRequest = _.cloneDeep(sqlRequest) || undefined
        this.response = this.staticData || null
        this.selectedPath = undefined
        this.dialogVisible = true
        this.$nextTick(() => {
          this.handleInitDnD()
        })
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      handleGeneratePanelData() {
        const widgetVariables = this.widgetVariables.filter(item => item.key)
        const res = {
          widgetVariables,
          dataVariables: this.dataVariables,
          apiRequest: this.apiRequest,
          sqlRequest: this.sqlRequest,
          staticData: this.staticData,
        }
        return res
      },
      async confirm() {
        const { apiRequest, sqlRequest, staticData } = this.$refs.dataSetConfig.getResult()
        this.apiRequest = apiRequest
        this.sqlRequest = sqlRequest
        this.staticData = staticData
        this.loading = true
        setTimeout(() => {
          const path = this.dataVariables.find(item => item.key === 'dataList')?.path
          if (!path.length) {
            this.$message.error('请配置变量映射 > 面板数据的数据路径！')
            this.loading = false
            return
          }
          this.handleSaveConfigTemp(path)
          const res = this.handleGeneratePanelData()
          this.loading = false
          this.$emit('confirm', res)
          this.dialogVisible = false
        }, 100)
      },
      handleSaveConfigTemp(path) {
        const target = path.length ? _.get(this.response, path) : this.response
        let res
        switch (true) {
        case typeof target === 'string':
          if (isValidJsonString(target)) {
            try {
              res = JSON.parse(target.trim())[0]
            } catch (e) {
              console.log(e)
            }
          }
          break
        case Array.isArray(target) && _.isObject(target[0]):
          res = target[0]
          break
        case _.isObject(target):
          res = target
          break
        }
        if (!res) {
          return
        }
        this.$http({
          url: API.appDesignJsonDataConfigSave,
          data: {
            appDesignId: Number(this.$route.query.vid),
            preferences: [
              {
                key: `${this.currentElement.elementId}.$currentRow`,
                value: JSON.stringify(res) || ''
              }
            ]
          }
        })
      },
      handleResponse({ response, responseOptions, isStatic }) {
        this.response = response
        this.responseOptions = responseOptions
        if (isStatic) {
          this.staticData = _.cloneDeep(response)
        }
      },
      copyPath() {
        // copyText(this.selectedPath, '路径已复制')
      },
      showInputResponseDialog() {
        this.$refs.inputDialog.open({type: 'object', data: this, key: 'response'})
      },
      handleInputResponse(value) {
        this.response = value
        this.responseOptions = getObjectTree(value)
      },
      handleClearResponse() {
        this.response = null
        this.responseOptions = []
      },
      getVariablesByElementId(id) {
        const elementId = id === 'app' ? this.layoutData.elementId : id
        const { elementType, elementVersion } = getNodeByElementId(this.layoutData, elementId)
        const data = getVariableByVersion.call(this, {elementType, elementVersion, elementId})
        if (!data) {
          return []
        }
        return Object.keys(data.config || {})
          .filter((key) => ![1, 2].includes(data?.config[key].auth))
          .map((key) => ({
            label: key,
            value: key,
            desc: data.config[key].desc,
          }))
      },
      handleDrop(event) {
        const { id, key } = event.target.dataset
        if (id) {
          const current = this.widgetVariables.find(item => item.key === id)
          current.path = getPathList(this.selectedPath)
        } else if (key) {
          const current = this.dataVariables.find(item => item.key === key)
          current.path = getPathList(this.selectedPath)
        }
      },
      handleAddWidget() {
        this.widgetVariables.push({key: undefined, path: []})
      },
      handleDeleteWidget(row, index) {
        if (this.widgetVariables.length === 1) {
          return
        }
        this.widgetVariables.splice(index, 1)
      },
      handleShowVariableSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.variableSelect.open({
          fullVariable: row[key],
          rootNode: this.currentElement
        })
      },
      handleVariableConfirm(selectedVariable) {
        const { row, key, cb } = this.variableRowDetail
        if (this.widgetVariables.some(item => item.key && item.key === selectedVariable)) {
          this.$message.error('变量重复')
          return
        }
        if (key) {
          this.$set(row, key, selectedVariable)
        }
        cb?.(selectedVariable)
      },
      getElementId(variable) {
        return isValidateVariable(variable) ? variable.split('.$')[0] : undefined
      },
      getTip(key) {
        switch (key) {
          case 'dataList':
            return '面板数据字段，需传入对象或数组类型的数据，传入为数组类型时，内部组件会循环生成多个'
        }
      }
    },
  }
</script>

