<template>
  <div class="tabs-params" v-if="config.params">
    <template v-if="!isChangeBadge">
      <div class="tab-select">
        <div class="label">{{ isChange ? '切换方式' : '新增方式' }}</div>
        <div class="value">
          <el-select size="mini" v-model="type" @change="handleChange">
            <el-option label="根据标签" value="key"></el-option>
            <el-option label="根据标题" value="label"></el-option>
          </el-select>
        </div>
      </div>
      <div class="tab-select" v-if="type === 'key'">
        <div class="label" style="margin-left: 10px;">选择标签</div>
        <div class="value">
          <el-select size="mini" v-model="config.params.tabName">
            <el-option v-for="tab in tabsData" :key="tab.key" :label="tab.label" :value="tab.key">
              <div style="float: left">{{ tab.label }}</div>
              <!-- <div style="float: right; color: #8492a6; font-size: 12px">{{ tab.key }}</div> -->
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="tab-label" v-if="type === 'label'">
        <div class="label" style="margin-left: 10px;">标签标题</div>
        <div class="value">
          <el-tooltip
            v-if="config.params.label !== undefined"
            effect="dark"
            :content="formatTip(config.params.label)"
            placement="top"
          >
            <el-tag
              class="value-tag"
              closable
              disable-transitions
              @click="handleTagClick(config.params, 'label')"
              @close="config.params.label = undefined"
              :type='getTagType(config.params.label)'
            >
              {{ formatTag(getElementName(config.params.label), 12) }}
            </el-tag>
          </el-tooltip>
          <el-button-group v-else>
            <el-button size="mini" @click="showInputDialog('text', config.params, 'label')">文本</el-button>
            <el-button size="mini" @click="handleShowVariableSelect(config.params, 'label')">变量</el-button>
          </el-button-group>
        </div>
      </div>
    </template>
    <template v-else>
      <div>
        <n-table
          :data="badgeList"
          border
          style="width: 600px"
          header-cell-class-name="header-cell"
        >
          <n-table-column prop="tab" label="标签">
            <template slot-scope="scope">
              <div class="value-input">
                <el-tooltip
                  v-if="scope.row.tab !== undefined"
                  effect="dark"
                  :content="formatTabTip(scope.row.tab)"
                  placement="top"
                >
                  <el-tag
                    class="value-tag"
                    closable
                    disable-transitions
                    @click="handleTabTagClick(scope.row, 'tab')"
                    @close="scope.row.tab = undefined"
                    :type='getTabTagType(scope.row.tab)'
                  >
                    {{ getTabTagFormat(scope.row.tab) }}
                  </el-tag>
                </el-tooltip>
                <div v-else class="value-input-content">
                  <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                    <el-button
                      @click="showTabsSelect(scope.row, 'tab')"
                    >选择
                    </el-button>
                    <el-button
                      @click="handleShowVariableSelect(scope.row, 'tab')"
                    >标签标题
                    </el-button>
                  </el-button-group>
                </div>
              </div>
            </template>
          </n-table-column>
          <n-table-column width="200" prop="badgeMessage" label="标记内容">
            <template slot-scope="scope">
              <div class="value-input">
                <el-tooltip
                  v-if="scope.row.badgeMessage !== undefined"
                  effect="dark"
                  :content="formatTip(scope.row.badgeMessage)"
                  placement="top"
                >
                  <el-tag
                    class="value-tag"
                    closable
                    disable-transitions
                    @click="handleTagClick(scope.row, 'badgeMessage')"
                    @close="scope.row.badgeMessage = undefined"
                    :type='getTagType(scope.row.badgeMessage)'
                  >
                    {{ formatTag(getElementName(scope.row.badgeMessage), 12) }}
                  </el-tag>
                </el-tooltip>
                <div v-else class="value-input-content">
                  <el-button-group>
                    <el-button
                      @click="
                        showInputDialog('text', scope.row, 'badgeMessage')
                      "
                      >文本
                    </el-button>
                    <el-button
                      @click="
                        handleShowVariableSelect(scope.row, 'badgeMessage')
                      "
                      >变量
                    </el-button>
                  </el-button-group>
                </div>
              </div>
            </template>
          </n-table-column>
          <n-table-column width="80" label="操作">
            <template slot-scope="scope">
              <div class="btns">
                <div class="add-btn-primary" v-if="isShowAdd" @click="handleAddTab">+</div>
                <div class="add-btn-primary" v-if="isShowDelete" @click="handleDeleteTab(scope.$index)">-</div>
              </div>
            </template>
          </n-table-column>
        </n-table>
      </div>
    </template>
    <input-dialog @confirm='handleInputResponse' ref="inputDialog"></input-dialog>
    <variable-select
      ref="variableSelect"
      @confirm="handleVariableConfirm"
    ></variable-select>
    <el-dialog
      custom-class="low-code-dialog"
      class="input-dialog"
      title="选择标签"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      :modal-append-to-body="true"
      :append-to-body="true"
      width="1000px"
      :before-close="handleClose"
    >
      选择标签
      <el-select style="margin-left: 10px;" v-model="selectedTabKey">
        <el-option v-for="item in showBadgeList" :key="item.key" :label="item.label" :value="item.key" :disabled="badgeList.some(b => b.tab === item.key)"></el-option>
      </el-select>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import elementEditor from '@/mixins/elementEditor'
import _ from 'lodash';
export default {
  name: 'TabsParams',
  mixins: [elementEditor],
  props: {
    elementId: {
      type: String
    },
    config: {
      type: Object
    },
    callbackId: {
      type: String
    },
    isChange: {
      type: Boolean
    },
    isChangeBadge: {
      type: Boolean
    }
  },
  data() {
    return {
      type: 'key',
      badgeList: [],
      showBadgeList: [],
      selectedTabKey: undefined,
      dialogVisible: false,
    }
  },
  computed: {
    tabsData() {
      const { tabsData } = this.getElementPropsById(this.elementId)
      return tabsData
    },
    isShowAdd() {
      return this.badgeList.length < this.showBadgeList.length
    },
    isShowDelete() {
      return this.badgeList.length > 1
    }
  },
  watch: {
    badgeList: {
      deep: true,
      handler(n, o) {
        if (!this.isChangeBadge) {
          return
        }
        this.config.params.badgeList = _.cloneDeep(n)
      }
    }
  },
  methods: {
    handleShowVariableSelect(row, key, cb) {
      this.variableRowDetail = { row, key, cb }
      this.$refs.variableSelect.open({
        fullVariable: row[key],
        callbackId: this.callbackId,
      })
    },
    handleVariableConfirm(selectedVariable) {
      const { row, key, cb } = this.variableRowDetail
      if (key) {
        this.$set(row, key, selectedVariable)
      }
      cb?.(selectedVariable)
    },
    handleReset() {
      const { key, label } = this.isChange ? this.tabsData.filter(item => !item.hidden)[0] || {} : this.tabsData[0]
      this.$set(this.config, 'params', {
        tabName: key,
        label: undefined,
        badgeList: this.isChangeBadge ? this.config.params?.badgeList : undefined
      })
    },
    showTabsSelect(row, key) {
      this.variableRowDetail = { row, key }
      this.selectedTabKey = row[key]
      this.dialogVisible = true
    },
    formatTabTip(tab) {
      const currentTab = this.showBadgeList.find(item => item.key === tab)
      return currentTab ? currentTab.label : this.formatTip(tab)
    },
    handleTabTagClick(row, key) {
      const currentTab = this.showBadgeList.find(item => item.key === row[key])
      if (currentTab) {
        this.showTabsSelect(row, key)
      } else {
        this.handleShowVariableSelect(row, key)
      }
    },
    getTabTagType(tab) {
      const currentTab = this.showBadgeList.find(item => item.key === tab)
      return currentTab ? '' : this.getTagType(tab)
    },
    getTabTagFormat(tab) {
      const currentTab = this.showBadgeList.find(item => item.key === tab)
      return currentTab ? currentTab.label : this.formatTag(this.getElementName(tab), 12)
    },
    handleAddTab() {
      this.badgeList.push({
        tab: undefined,
        badgeMessage: undefined
      })
    },
    handleDeleteTab(index) {
      this.badgeList.splice(index, 1)
    },
    handleClose(done) {
      this.dialogVisible = false
      done()
    },
    cancel() {
      this.selectedTabKey = undefined
      this.dialogVisible = false
    },
    confirm() {
      const { row, key } = this.variableRowDetail
      if (key) {
        this.$set(row, key, this.selectedTabKey)
      }
      this.selectedTabKey = undefined
      this.dialogVisible = false
    },
    handleChange(type) {
      const { key, label } = this.isChange ? this.tabsData.filter(item => !item.hidden)[0] || {} : this.tabsData[0]
      this.$set(this.config, 'params', {
        tabName: type === 'key' ? key : undefined,
        label: undefined
      })
    }
  },
  created() {
    if (!this.config.params) {
      this.handleReset()
    }
    this.type = this.config.params.label ? 'label' : 'key'
    this.showBadgeList = this.tabsData.filter(item => item.showBadge)
    this.badgeList = _.cloneDeep(this.config.params.badgeList || (this.showBadgeList.length ? [{tab: this.showBadgeList[0].key, badgeMessage: undefined}] : []))
  }
}
</script>

<style lang="less" scoped>
.tabs-params {
  display: flex;
  align-items: center;
  .tab-select, .tab-label {
    display: flex;
    align-items: center;
    .label {
      width: 80px;
    }
  }
}
</style>
