<template>
  <div class="option-config">
    <div class="mode-select">
      <data-selector
        btnText="配置"
        optionkey="chart"
        v-bind="$props"
        @update="dataSelectUpdate"
      ></data-selector>
    </div>

    <!-- Y轴配置开始 -->
    <div class="dot-line"></div>
    <div class="field-group">
      <div class="group-item group-header">
        <div class="group-title">数值字段</div>
      </div>
      <div class="group-item" v-for="(item, index) in yFields" :key="index">
        <div class="group-title">{{ item.value }}</div>
        <div class="group-opt">
          <!-- Y轴字段配置开始 -->
          <el-popover popper-class="y-popover" placement="left" width="350">
            <el-form size="mini" class="no-mb" label-width="120px" label-position="left">
              <el-form-item label="字段名称" style="margin-bottom: 10px;">
                <el-input v-model="item.key"></el-input>
              </el-form-item>
              <el-form-item label="数据名称">
                <el-input v-model="item.value"></el-input>
              </el-form-item>
              <div class="dot-line"></div>
              <el-form-item label="数据标签">
                <el-switch v-model="item.titleShow"></el-switch>
              </el-form-item>
              <template v-if="item.titleShow">
                <el-form-item label="数据标签高度">
                  <range-select
                    v-model="item.titleHeight"
                    :options="lengthOptions"
                  ></range-select>
                </el-form-item>
                <el-form-item label="数据标签宽度">
                  <range-select
                    v-model="item.titleWidth"
                    :options="lengthOptions"
                  ></range-select>
                </el-form-item>
                <el-form-item label="数据标签颜色">
                   <el-color-picker v-model="item.titleColor"></el-color-picker>
                </el-form-item>
                <el-form-item label="数据标签背景色">
                   <el-color-picker v-model="item.titleBackgroundColor"></el-color-picker>
                </el-form-item>
                <el-form-item label="数据标签水平偏移">
                  <range-select
                    v-model="item.titleOffsetX"
                    :options="offsetOptions"
                  ></range-select>
                </el-form-item>
                <el-form-item label="数据标签垂直偏移">
                  <range-select
                    v-model="item.titleOffsetY"
                    :options="offsetOptions"
                  ></range-select>
                </el-form-item>
              </template>
              <el-form-item label="数据详情">
                <el-switch v-model="item.detailShow"></el-switch>
              </el-form-item>
              <template v-if="item.detailShow">
                <el-form-item label="数据详情水平偏移">
                  <range-select
                    v-model="item.detailOffsetX"
                    :options="offsetOptions"
                  ></range-select>
                </el-form-item>
                <el-form-item label="数据详情垂直偏移">
                  <range-select
                    v-model="item.detailOffsetY"
                    :options="offsetOptions"
                  ></range-select>
                </el-form-item>
                <el-form-item label="数据详情格式化">
                  <el-switch v-model="item.detailUseformatter"></el-switch>
                </el-form-item>
                <el-form-item v-if="item.detailUseformatter" label="格式化内容">
                  <el-tooltip
                    popper-class="tooltip-question"
                    effect="dark"
                    :content="formatTips"
                    placement="top-start"
                  >
                    <i class="el-icon-question"></i>
                  </el-tooltip>
                  <el-input
                    v-model="item.detailFormatter"
                    placeholder="输入格式化内容"
                  ></el-input>
                </el-form-item>
              </template>
            </el-form>
            <el-button slot="reference" type="text"
              >编辑</el-button
            >
          </el-popover>
          <!-- Y轴字段配置结束 -->
        </div>
        <div class="group-opt">
          <el-button class="delete-btn" slot="reference" type="text"
          @click="deleteItem(index)"
              >删除</el-button
            >
        </div>
      </div>
      <div class="group-item" style="padding-left: 5px;">
        <el-button type="text" @click="addYFields">
          <i class="el-icon-circle-plus"></i>
          添加数据字段
        </el-button>
      </div>
    </div>
    <!-- Y轴配置结束 -->
  </div>
</template>

<script>
import _ from 'lodash'
import dataSelector from '../config-data'
import Select from '@/components/Select'
import rangeSelect from '@/components/rangeSelector'
import { formatTips } from '@/config'
export default {
  components: {
    dataSelector,
    Select,
    rangeSelect,
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    multiple: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      lengthOptions: {
        min: 0,
        unit: [{ value: '', label: 'px', max: 1000 }],
      },
      offsetOptions: {
        unit: [
          { value: '', label: 'px', min: -300, max: 300 },
          { value: '%', label: '%', min: -200, max: 200 },
        ],
      },
      formatTips,
      yFieldsIndex: -1,
      yFields: []
    }
  },
  computed: {
    dataVariables: {
      get() {
        return this.elementProps['dataVariables']
      },
      set(value) {
        this.$emit('update', {
          ...this.elementProps,
          dataVariables: value,
        })
      },
    },
  },
  watch: {
    dataVariables: {
      deep: true,
      immediate: true,
      handler(val) {
        this.yFieldsIndex = val.findIndex((item) => item.key === 'yAxis')
        this.yFields = val[this.yFieldsIndex]?.fields || []
      },
    },
  },
  methods: {
    addYFields() {
      const newKey = 'key' + (this.yFields.length + 1)
      const { yField_tpl } = require(`@/template/components/gauge-chart/${this.elementProps.elementVersion}/watch-props.js`)
      this.yFields.push({
        ..._.cloneDeep(yField_tpl),
        key: newKey,
        value: newKey
      })
      this.dataVariables[this.yFieldsIndex].fields = _.cloneDeep(this.yFields)
    },
    dataSelectUpdate(data) {
      this.$emit('update', data)
    },
    deleteItem(index) {
      this.yFields.splice(index, 1)
      this.dataVariables[this.yFieldsIndex].fields = _.cloneDeep(this.yFields)
    }
  },
}
</script>

