<template>
  <el-dialog
    title="数据格式化"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="1000px"
    top="5vh"
    :before-close="handleClose"
    custom-class="handler-dialog low-code-dialog"
  >
    <div class="dialog-content">
      <n-tabs v-model="activeName" type="card" @tab-click="handleTabClick">
        <n-tab-pane label="普通" name="normal">
          <div class="input-item">
            <div class="label">选择分类</div>
            <div class="value">
              <el-select size="mini" v-model="formatterType" @change="functionName = ''">
                <el-option
                  v-for="option in normalFormatterTypeList"
                  :key="option.value"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>
            </div>
          </div>
          <div class="input-item">
            <div class="label">选择函数</div>
            <div class="value">
              <el-select size="mini" v-model="functionName">
                <el-option
                  v-for="option in functionList"
                  :key="option.value"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>
            </div>
          </div>
          <el-divider></el-divider>
          <template v-if="functionName === 'fixNumber'">
            <div class="input-item">
              <div class="label">选择保留小数位数</div>
              <div class="value">
                <el-select size="mini" v-model="fixed">
                  <el-option
                    v-for="option in [0, 1, 2, 3, 4]"
                    :key="option"
                    :label="option"
                    :value="option"
                  ></el-option>
                </el-select>
              </div>
            </div>
          </template>
          <template v-if="functionName === 'enumeration'">
            <div class='input-item' v-for='(enu, index) in enumeration' :key="index">
              <div class='label'>枚举值</div>
              <div class='value'>
                <el-input size='mini' v-model.trim='enu.value'></el-input>
              </div>
              <i style='margin-left: 10px;' class='iconfont el-icon-right'></i>
              <div class='value'>
                <el-input size='mini' v-model.trim='enu.label'></el-input>
              </div>
              <div class="btns">
                <div class="add-btn-primary" @click="handleAddEnu">+</div>
                <div
                  class="add-btn-primary"
                  @click="handleDeleteEnu(enu)"
                >
                  -
                </div>
              </div>
            </div>
          </template>
          <template v-if="functionName === 'dateFormat'">
            <div class="input-item">
              <div class="label">选择日期格式</div>
              <div class="value">
                <el-select size="mini" v-model="valueFormat">
                  <el-option
                    v-for="option in dateFormatOptions"
                    :key="option"
                    :label="option"
                    :value="option"
                  ></el-option>
                </el-select>
              </div>
            </div>
          </template>
          <template v-if="functionName === 'exec'">
            <div class="input-item">
              <div class="label">正则表达式
                <el-tooltip popper-class="tooltip-question" placement="top">
                  <div slot="content">1.执行js的正则exec方法对字符串进行提取，返回提取到的第一个结果，没有则返回空字符串<br>2.注意：表达式前后请不要再输入斜杠"/"及属性gim</div>
                  <i class="el-icon-question"></i>
                </el-tooltip>
              </div>
              <div class="value">
                <el-input v-model.trim="regexp" placeholder="请输入正则表达式" style="width: 500px;margin-bottom: 10px; margin-top: 30px;"></el-input>
                  <div>
                    <el-checkbox v-model="isI">忽略大小写</el-checkbox>
                    <el-checkbox v-model="isM">多行匹配</el-checkbox>
                  </div>
              </div>
            </div>
          </template>
        </n-tab-pane>
        <n-tab-pane label="高级" name="js" lazy>
          <div style='height: 34px; line-height: 34px;'>
            <span>渲染方式：</span>
            <el-radio-group v-model="renderType">
              <el-tooltip content="渲染为文本类型，返回值为undefined时不显示" placement="top">
                <el-radio label="default">文本</el-radio>
              </el-tooltip>
              <el-tooltip content='渲染为html标签' placement="top">
                <el-radio label="html">html</el-radio>
              </el-tooltip>
            </el-radio-group>
            <template v-if='renderType === "html"'>
              <el-button style='margin-left: 20px;' size='mini' type='primary' @click='handleShowHtmlTemplate'>选择html模板</el-button>
              <htmlTemplate ref='htmlTemplate' @confirm='handleSelectHtmlTemplate'></htmlTemplate>
            </template>
          </div>
          <js-config ref='jsConfig' v-model='functionId' :pure-function='true' :default-arguments='defaultArguments'></js-config>
        </n-tab-pane>
      </n-tabs>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="clear">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import HtmlTemplate from './htmlTemplate'
  import JsConfig from '@/components/JsConfig'
  import { FUNCTION_FLAG } from '@/config'
  import _ from 'lodash'

  export default {
    name: 'DataFormat',
    components: {
      JsConfig,
      HtmlTemplate
    },
    data() {
      return {
        dialogVisible: false,
        activeName: 'normal',
        fixed: 2,
        valueFormat: 'YYYY-MM-DD HH:mm',
        functionId: undefined,
        defaultArguments: [],
        enumeration: [
          {
            value: '',
            label: ''
          }
        ],
        dateFormatOptions: [
          'YYYY-MM-DD',
          'YYYY-MM-DD HH:mm',
          'YYYY-MM-DD HH:mm:ss',
          'HH:mm:ss',
          'HH:mm',
        ],
        renderType: 'default',
        formatterType: 'number',
        normalFormatterTypeList: [
          {
            value: 'number',
            label: '数值'
          },
          {
            value: 'string',
            label: '文本'
          },
          {
            value: 'date',
            label: '日期'
          },
        ],
        functionName: 'fixNumber',
        regexp: '',
        isI: false,
        isM: false,
      }
    },
    computed: {
      functionList() {
        switch(this.formatterType) {
          case 'number': {
            return [{label: '小数位数', value: 'fixNumber'}]
          }
          case 'string': {
            return [{label: '枚举值', value: 'enumeration'}, {label: '正则提取', value: 'exec'}]
          }
          case 'date': {
            return [{label: '日期格式转换', value: 'dateFormat'}]
          }
        }
      }
    },
    methods: {
      open({ formatter, defaultArguments }) {
        if (formatter) {
          const formatterCopy = _.cloneDeep(formatter)
          this.activeName = formatterCopy.type === 'js' ? 'js' : 'normal'
          this.formatterType = formatterCopy.type
          this.functionName = formatterCopy.functionName
          this.fixed = formatterCopy.fixed || 2
          this.regexp = formatterCopy.regexp || ''
          this.isI = formatterCopy.isI || false
          this.isM = formatterCopy.isM || false
          this.valueFormat = formatterCopy.valueFormat || 'YYYY-MM-DD HH:mm'
          this.enumeration = formatterCopy.enumeration || [{ value: '', label: '' }]
          if (this.activeName === 'js') {
            this.renderType = formatterCopy.renderType
            this.functionId = formatterCopy.value.split(FUNCTION_FLAG)[1]
          }
        }
        this.defaultArguments = defaultArguments || []
        this.dialogVisible = true
      },
      handleClose(done) {
        this.clear()
        done()
      },
      clear() {
        this.renderType = 'default'
        this.regexp = ''
        this.isI = false
        this.isM = false
        this.functionId = undefined
        this.activeName = 'normal'
        this.enumeration = [
          {
            value: '',
            label: ''
          }
        ]
        this.formatterType = 'number'
        this.functionName = 'fixNumber'
        this.dialogVisible = false
      },
      getNumberFormatter() {
        switch(this.functionName) {
          case 'fixNumber': { return { fixed: this.fixed, } }
        }
      },
      getStringFormatter() {
        switch(this.functionName) {
          case 'enumeration': { return { enumeration: this.enumeration } }
          case 'exec': { return { regexp: this.regexp, isI: this.isI, isM: this.isM } }
        }
      },
      getDateFormatter() {
        switch(this.functionName) {
          case 'dateFormat': { return { valueFormat: this.valueFormat } }
        }
      },
      async confirm() {
        if (this.activeName !== 'js' && !this.functionName) {
          this.$message.error('请选择格式化函数！')
          return
        }
        let formatter
        switch (this.formatterType) {
          case 'number':
            formatter = this.getNumberFormatter()
            break
          case 'string':
            formatter = this.getStringFormatter()
            break
          case 'date':
            formatter = this.getDateFormatter()
            break
          case 'js':
            await this.$refs.jsConfig.confirm()
            formatter = {
              type: 'js',
              functionName: 'cellValueFormat',
              renderType: this.renderType,
              value: `${FUNCTION_FLAG}${this.functionId}`
            }
            break
        }
        formatter.functionName = formatter.functionName || this.functionName
        formatter.type = formatter.type || this.formatterType
        if (formatter.regexp === '') {
          this.$message.error('请输入正则表达式！')
          return
        }
        if (formatter.regexp) {
          try {
            new RegExp(formatter.regexp)
          } catch (error) {
            this.$message.error('正则表达式不合法！')
            return
          }
        }
        this.$emit('confirm', formatter)
        this.clear()
      },
      handleAddEnu() {
        this.enumeration.push({
          value: '',
          label: ''
        })
      },
      handleDeleteEnu(enu) {
        if (this.enumeration.length === 1) {
          return
        }
        this.enumeration = this.enumeration.filter(item => item.value !== enu.value)
      },
      handleShowHtmlTemplate() {
        this.$refs.htmlTemplate.open()
      },
      handleSelectHtmlTemplate(template) {
        this.$refs.jsConfig.updateCode(template.code)
      },
      handleTabClick(vm) {
        this.formatterType = vm.name === 'js' ? 'js' : ''
        if (!this.formatterType) {
          this.functionName = ''
        }
      }
    },
  }
</script>

<style lang="less" scoped>
  .dialog-content::v-deep {
    .el-tab-pane {
      max-height: 480px;
      overflow-x: hidden;
      overflow-y: auto;
    }
    .input-item {
      display: flex;
      align-items: center;
      font-size: 12px;
      margin-bottom: 10px;

      .label {
        width: 100px;
      }
      .value {
        margin-left: 10px;
      }
      .btns {
        display: flex;
        width: 60px;
        margin-left: 10px;

        .add-btn-primary {
          margin-left: 10px;
        }
      }
    }
  }
</style>
