<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-select v-model="value" placeholder="请选择">
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      >
      </el-option>
    </el-select>
  </n-form-item>
</template>

<script>
  export default {
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      optionkey: {
        type: String,
      },
      vue: {
        type: String,
      },
    },
    data() {
      return {
        type: 0,
        options: [
          {
            label: '时',
            value: 'HH',
          },
          {
            label: '时:分',
            value: 'HH:mm',
          },
          {
            label: '时:分:秒',
            value: 'HH:mm:ss',
          },
          {
            label: '年',
            value: 'yyyy',
          },
          {
            label: '年-月',
            value: 'yyyy-MM',
          },
          {
            label: '年-月-日',
            value: 'yyyy-MM-dd',
          },
          {
            label: '年-月-日 时:分',
            value: 'yyyy-MM-dd HH:mm',
          },
          {
            label: '年-月-日 时:分:秒',
            value: 'yyyy-MM-dd HH:mm:ss',
          },
          {
            label: '时间戳',
            value: 'timestamp',
          },
        ],
      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = {
            ...this.elementProps,
            [this.propKey]: value,
          }
          this.$emit('update', res)
        },
      }
    },
  }
</script>

<style lang="less" scoped>
  div.config-item::v-deep {
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 8px;
    margin-right: 0;
    .el-form-item__content {
      flex: 1;
      & > * {
        width: 100%;
      }
    }
    .el-form-item__label{
      min-width: 100px;
    }
  }
</style>
