<template>
  <el-form size="mini" class="range-selector">
    <el-slider
      :value="value.value"
      :min="min"
      :max="max"
      show-input
      :show-input-controls="false"
      input-size="mini"
      @input="inputChange"
    ></el-slider>
    <template v-if="options && options.unit && options.unit.length">
      <Select
        :value="value.unit"
        :options="options.unit"
        :clearable="false"
        @change="selectChange"
        size="mini"
      ></Select>
    </template>
  </el-form>
</template>

<script>
import Select from '@/components/Select'
import { debounce } from '@/utils'
export default {
  components: {
    Select,
  },
  props: {
    value: {
      type: Array,
      default: () => ({ value: '', unit: '' }),
    },
    options: {
      type: Array,
      default: () => ({ unit: [] }),
    }
  },
  data() {
    return {}
  },
  created() {},
  mounted() {},
  methods: {
    inputChange: debounce(function (val) {
      this.$emit('input', { ...this.value, value: val })
    }),
    selectChange(val) {
      this.$emit('input', { ...this.value, unit: val })
    }
  },

  computed: {
    max() {
      const unitMax = this.options.unit.find(
        (item) => item.value === this.value.unit
      )?.max
      return unitMax || this.options.max
    },
    min() {
      const unitMin = this.options.unit.find(
        (item) => item.value === this.value.unit
      )?.min
      return unitMin || this.options.min
    },
  },

  watch: {},
}
</script>
