<template>
  <div class="option-config">
    <div class="mode-select">
      <data-selector
        btnText="配置"
        optionkey="chart"
        v-bind="$props"
        @update="dataSelectUpdate"
      ></data-selector>
    </div>

    <!-- X轴配置开始 -->
    <div class="dot-line"></div>
    <div class="field-group">
      <div class="group-item group-header">
        <div class="group-title">类目字段</div>
      </div>
      <div class="group-item" v-for="(item, index) in xFields" :key="index">
        <div class="group-title">{{ item.key }}</div>
        <div class="group-opt">
          <!-- X轴字段配置开始 -->
          <el-popover placement="bottom-start" width="300">
            <el-form-item
              class="flex-item"
              label="类目字段"
              label-width="100px"
            >
              <el-input v-model="rowInfo.key"></el-input>
            </el-form-item>
            <div style="text-align: right; margin: 0">
              <el-button
                size="mini"
                type="text"
                @click="() => $refs['x-field-item'][index].$el.click()"
                >取消</el-button
              >
              <el-button
                type="primary"
                size="mini"
                @click="
                  () => {
                    setData(item, rowInfo)
                    $refs['x-field-item'][index].$el.click()
                  }
                "
                >确定</el-button
              >
            </div>
            <el-button
              ref="x-field-item"
              slot="reference"
              type="text"
              @click="rowEdit(item)"
              >编辑</el-button
            >
          </el-popover>
          <!-- X轴字段配置结束 -->
        </div>
        <div class="group-opt">
          <el-button class="delete-btn" slot="reference" type="text"
          @click="deleteXItem(index)"
              >删除</el-button
            >
        </div>
      </div>
      <div v-if="!xFields.length" class="group-item" style="padding-left: 5px;">
        <el-button type="text"  @click="addXFields">
          <i class="el-icon-circle-plus"></i>
          添加数据字段
        </el-button>
      </div>
    </div>

    <!-- X轴配置结束 -->
    <!-- Y轴配置开始 -->
    <div class="dot-line"></div>
    <div class="field-group">
      <div class="group-item group-header">
        <div class="group-title">数值字段</div>
        <div class="group-opt">
          <!-- Y轴配置开始 -->
          <el-popover popper-class="pie-popover" placement="left" width="350">
            <el-form class="no-mb" label-width="120px" label-position="left">
              <el-form-item label="序列名称">
                <el-input v-model="ySetting.title"></el-input>
              </el-form-item>
              <div class="dot-line"></div>
              <el-form-item label="数据标签">
                <el-switch v-model="ySetting.dataLabelShow"></el-switch>
              </el-form-item>
              <template v-if="ySetting.dataLabelShow">
                <el-form-item label="数据标签位置">
                  <Select
                    v-model="ySetting.dataLabelPosition"
                    :options="positionOptions"
                  ></Select>
                </el-form-item>
                <el-form-item label="标签格式化">
                  <el-switch
                    v-model="ySetting.dataLabelUseformatter"
                  ></el-switch>
                </el-form-item>
                <el-form-item
                  v-if="ySetting.dataLabelUseformatter"
                  label="格式化内容"
                >
                  <el-tooltip
                    popper-class="tooltip-question"
                    effect="dark"
                    :content="formatTips"
                    placement="top-start"
                  >
                    <i class="el-icon-question"></i>
                  </el-tooltip>
                  <el-input
                    v-model="ySetting.dataLabelFormatter"
                    placeholder="输入格式化内容"
                  ></el-input>
                </el-form-item>
              </template>
              <div class="dot-line"></div>
              <el-form-item label="标线">
                <el-switch v-model="ySetting.labelLine"></el-switch>
              </el-form-item>
              <el-form-item label="数据排序">
                <el-radio-group v-model="ySetting.sortType">
                  <el-radio-button v-for="item in sortTypes" :key="item.value" :label="item.value">{{ item.label }}</el-radio-button>
                </el-radio-group>
              </el-form-item>
            </el-form>
            <el-button slot="reference" type="text">编辑</el-button>
          </el-popover>
          <!-- Y轴配置结束 -->
        </div>
      </div>
      <div class="group-item" v-for="(item, index) in yFields" :key="index">
        <div class="group-title">{{ item.key }}</div>
        <div class="group-opt">
          <!-- Y轴字段配置开始 -->
          <el-popover placement="bottom-start" width="300">
            <el-form-item
              class="flex-item"
              label="数值字段"
              label-width="100px"
            >
              <el-input v-model="rowInfo.key"></el-input>
            </el-form-item>
            <div style="text-align: right; margin: 0">
              <el-button
                size="mini"
                type="text"
                @click="() => $refs['y-field-item'][index].$el.click()"
                >取消</el-button
              >
              <el-button
