          </el-popover>
          <!-- Y轴字段配置结束 -->
        </div>
        <div class="group-opt">
          <el-button class="delete-btn" slot="reference" type="text"
          @click="deleteYItem(index)"
              >删除</el-button
            >
        </div>
      </div>
      <div v-if="!yFields.length" class="group-item" style="padding-left: 5px;">
        <el-button type="text"  @click="addYFields">
          <i class="el-icon-circle-plus"></i>
          添加数据字段
        </el-button>
      </div>
    </div>
    <!-- Y轴配置结束 -->
  </div>
</template>

<script>
import _ from 'lodash'
import dataSelector from '../config-data'
import Select from '@/components/Select'
import { formatTips } from '@/config'
export default {
  components: {
    dataSelector,
    Select,
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    multiple: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      // 点击字段行编辑的数据
      rowInfo: {},
      xFields: [],
      yFields: [],
      sortTypes: [
        { label: '升序', value: 'ascending' },
        { label: '倒序', value: 'descending' },
        { label: '无', value: 'none' }
      ],
      formatTips,
    }
  },
  computed: {
    dataVariables: {
      get() {
        return this.elementProps['dataVariables']
      },
      set(value) {
        this.$emit('update', {
          ...this.elementProps,
          dataVariables: value,
        })
      },
    },
    ySetting: {
      get() {
        return this.elementProps['ySetting']
      },
      set(value) {
        this.$emit('update', {
          ...this.elementProps,
          ySetting: value,
        })
      },
    },
    preset() {
      const { elementVersion } = this.elementProps
      const { yField_tpl } = require(`@/template/components/pie-chart/${elementVersion}/watch-props.js`)
      return yField_tpl
    },
    positionOptions() {
      const { elementType, orient } = this.elementProps
      if(elementType === 'pie-chart') {
        return [
          {
            label: '扇区外侧',
            value: 'outside',
          },
          {
            label: '扇区内部',
            value: 'inside',
          },
          {
            label: '中心',
            value: 'center',
          },
        ]
      } else if(elementType === 'funnel-chart') {
        if(orient === 'vertical') {
          return [
            {
              label: '左',
              value: 'left',
            },
            {
              label: '右',
              value: 'right',
            },
            {
              label: '内部',
              value: 'inside',
            },
          ]
        }
        return [
          {
            label: '上',
            value: 'top',
          },
          {
            label: '下',
            value: 'bottom',
          },
          {
            label: '内部',
            value: 'inside',
          },
        ]
      } else {
        return [
          {
            label: '左',
            value: 'left',
          },
          {
            label: '右',
            value: 'right',
          },
          {
            label: '上',
            value: 'top',
          },
          {
            label: '下',
            value: 'bottom',
          },
          {
            label: '内部',
            value: 'inside',
          },
        ]
      }
    }
  },
  watch: {
    dataVariables: {
      deep: true,
      immediate: true,
      handler(val) {
        this.xFieldsIndex = val.findIndex((item) => item.key === 'xAxis')
        this.xFields = val[this.xFieldsIndex]?.fields || []
        this.yFieldsIndex = val.findIndex((item) => item.key === 'yAxis')
        this.yFields = val[this.yFieldsIndex]?.fields || []
      },
    },
  },
  methods: {
    addXFields() {
      const newKey = 'key' + (this.xFields.length + 1)
      this.xFields.push({
        ..._.cloneDeep(this.preset),
        key: newKey,
        value: newKey
      })
      this.dataVariables[this.xFieldsIndex].fields = _.cloneDeep(this.xFields)
    },
    deleteXItem(index) {
      this.xFields.splice(index, 1)
      this.dataVariables[this.xFieldsIndex].fields = _.cloneDeep(this.xFields)
    },
    addYFields() {
      const newKey = 'key' + (this.yFields.length + 1)
      this.yFields.push({
        ..._.cloneDeep(this.preset),
        key: newKey,
        value: newKey
      })
      this.dataVariables[this.yFieldsIndex].fields = _.cloneDeep(this.yFields)
    },
    deleteYItem(index) {
      this.yFields.splice(index, 1)
      this.dataVariables[this.yFieldsIndex].fields = _.cloneDeep(this.yFields)
    },
    dataSelectUpdate(data) {
      this.$emit('update', data)
    },
    rowEdit(item) {
      this.rowInfo = _.cloneDeep(item)
    },
    setData(item, data) {
      for (let key in data) {
        item[key] = data[key]
      }
    },
  }
}
</script>

