<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-switch v-model="isChecked" @change="switchChange"></el-switch>
    <div class="fill-div"></div>
    <el-popover v-if="isChecked" placement="right" width="250" trigger="click">
      <el-form class="setting-form" label-position="left" label-width="100px">
        <el-form-item label="导出图片">
          <el-switch v-model="value.saveAsImage"></el-switch>
        </el-form-item>
        <el-form-item label="数据视图">
          <el-switch v-model="value.dataView"></el-switch>
        </el-form-item>
        <el-form-item v-if="linBarType" label="动态类型切换">
          <el-switch v-model="value.magicType"></el-switch>
        </el-form-item>
        <el-form-item v-if="linBarType" label="数据区域缩放">
          <el-switch v-model="value.dataZoom"></el-switch>
        </el-form-item>
        <el-form-item label="重置">
          <el-switch v-model="value.restore"></el-switch>
        </el-form-item>
        <el-form-item label="排布方式">
          <el-radio-group v-model="value.orient">
            <el-radio-button label="horizontal">横向</el-radio-button>
            <el-radio-button label="vertical">纵向</el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <i slot="reference" class="el-icon-s-tools"></i>
    </el-popover>
  </n-form-item>
</template>

<script>
import { chartType } from '@/config/widgets'
export default {
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
    chartType() {
      return chartType[this.elementProps.elementType]
    },
    // 折线或者柱状
    linBarType() {
      return ['line', 'bar', 'scatter'].includes(this.chartType)
    },
    isChecked() {
      return !!Object.keys(this.value).length
    }
  },
  methods: {
    switchChange(value) {
      this.value = value
        ? {
          saveAsImage: true,
          dataView: true,
          magicType: this.linBarType ? true : false,
          dataZoom: this.linBarType ? true : false,
          restore: true,
          orient: 'horizontal'
        }
        : {}
    }
  },
  mounted() {},
}
</script>

