<template>
  <div class="widget-config-item">
    <n-form-item class="config-item" :label="name" :prop="propKey">
      <el-tooltip v-if="options.tips" popper-class="tooltip-question" effect="dark" :content="options.tips" placement="top-start">
        <i class="el-icon-question"></i>
      </el-tooltip>
      <range-selector v-model="value" :options="options"></range-selector>
    </n-form-item>
  </div>
</template>

<script>
import _ from 'lodash'
import RangeSelector from '@/components/rangeSelector'
export default {
  components: {
    RangeSelector
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
    },
    options: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {}
  },
  computed: {
    value: {
      get() {
        const value = this.elementProps[this.propKey]
        return value
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    }
  },
  watch: {},
  methods: {},
}
</script>

