    :append-to-body="true"
    title="组件生成配置"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="data-select" v-loading.lock='loading'>
      <div class="data-select-item">
        <el-collapse class='config-collapse' v-model='activeDataCollapse'>
          <el-collapse-item name='data'>
            <template slot="title">
              <div class='config-collapse-title'>选择数据来源</div>
            </template>
            <div class="data-select-form data-content">
              <n-tabs class='tabs' tab-position="top" v-model='dataType'>
                <n-tab-pane label="数据模型" name='model'>
                  <div class='data-item'>
                    <div class='label'>指定数据源:</div>
                    <div class='value'>
                      <el-input :value='sqlSource.name'></el-input>
                      <el-button class='btn' size='mini' type='primary' @click='handleShowSqlDataSelect'>选择</el-button>
                    </div>
                  </div>
                  <div class='data-item'>
                    <div class='label'>选择数据模型:</div>
                    <div class='value'>
                      <el-input v-if='selectedModel' :value='selectedModel.name'></el-input>
                      <el-input v-else></el-input>
                      <el-button class='btn' size='mini' type='primary' :disabled='!sqlSource.id' @click='handleShowModelSelect'>选择</el-button>
                    </div>
                  </div>
                </n-tab-pane>
                <n-tab-pane label="服务网关" name='api'>
                  <div class='data-item'>
                    <div class='label'>URL:</div>
                    <div class='value'>
                      <el-input v-if="apiInfo" readonly :value="apiInfo.serviceCode"></el-input>
                      <el-input v-else readonly></el-input>
                      <el-button class='btn' size='mini' type='primary' @click='handleShowGatewayDataSelect'>选择</el-button>
                    </div>
                  </div>
                  <div class='data-item' v-if="apiInfo">
                    <div class='label'></div>
                    <div class='value' style="color: #888;font-size: 12px;">
                      {{ apiInfo.description }}
                    </div>
                  </div>
                </n-tab-pane>
              </n-tabs>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
      <div class="data-select-item">
        <el-collapse class='config-collapse' v-model='activeCollapse'>
          <el-collapse-item name='config'>
            <template slot="title">
              <div class='config-collapse-title'>字段组件映射</div>
            </template>
            <div class="data-select-form data-content">
              <div class="data-config">
                <div class="data-table" :style='{width: dataType === "api" ? "50%" : "100%"}' v-loading='tableLoading'>
                  <vxe-table
                    ref='dataVariablesTable'
                    size='mini'
                    :data="dataVariables"
                    border
                    :auto-resize='true'
                    style="width: 100%;"
                    header-cell-class-name="header-cell"
                  >
                    <vxe-table-column type="checkbox" width="42px" align="center" />
                    <vxe-table-column field="key" title="字段名称"></vxe-table-column>
                    <vxe-table-column field="label" title="中文名称" min-width="100px">
                      <template #default='{ row }'>
                        <el-input v-model='row.label' clearable></el-input>
                      </template>
                    </vxe-table-column>
                    <vxe-table-column field="vue" title="外观映射">
                      <template #default='{ row }'>
                        <el-select v-model='row.vue'>
                          <el-option
                            v-for='item in formItems'
                            :key='item.value'
                            :label='item.name'
                            :value='item.value'
                          ></el-option>
                        </el-select>
                      </template>
                    </vxe-table-column>
                  </vxe-table>
                </div>
                <div class="data-result" v-if='dataType === "api"'>
                  <el-button
                    class='path-select-btn'
                    size='mini'
                    type="primary"
                    @click='handleGenerateKey'>一键生成字段
                      <el-tooltip content="请在下方结构树上选中“表单数据”节点，点击【一键生成数据列】按钮，系统会解析该节点下的所有子节点为表单字段" popper-class="tooltip-question" placement="top">
                        <i style='margin-left: 5px; color: black; font-size: 14px;' class="el-icon-question"></i>
                      </el-tooltip>
                    </el-button>
                  <n-tabs class='tabs' tab-position="top" v-model='apiType'>
                    <n-tab-pane label="请求体" name='request'>
                      <div class="body">
                        <vue-json-pretty
                          v-show='request'
                          class="json-pretty no-select"
                          path="response"
                          :data="request"
                          :virtual='true'
                          :itemHeight='24'
                          v-model="selectedPath"
                          @click='copyPath'
                          selectableType="single"
                          :showSelectController="true"
                          :selectOnClickNode="true"
                        >
                        </vue-json-pretty>
                        <div v-if='!request' style='text-align: center'>暂无数据</div>
                      </div>
                    </n-tab-pane>
                    <n-tab-pane label="响应体" name='response'>
                      <div class="body">
                        <vue-json-pretty
                          v-show='response'
                          class="json-pretty no-select"
                          path="response"
                          :data="response"
                          :virtual='true'
                          :itemHeight='24'
                          v-model="selectedPath"
                          @click='copyPath'
                          selectableType="single"
                          :showSelectController="true"
                          :selectOnClickNode="true"
                        >
                        </vue-json-pretty>
                        <div v-if='!response' style='text-align: center'>暂无数据</div>
                      </div>
                    </n-tab-pane>
                  </n-tabs>
                </div>
              </div>
            </div>
          </el-collapse-item>

        </el-collapse>
      </div>
    </div>
    <gateway-data-select
      ref="gatewayDataSelect"
      @confirm="handleGatewayDataConfirm"
    ></gateway-data-select>
    <sql-data-select
      ref="sqlDataSelect"
      @confirm="handleSqlDataConfirm"
    ></sql-data-select>
    <model-data-select
      ref="modelDataSelect"
      @confirm="handleModelDataConfirm"
    ></model-data-select>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" :loading='loading' :disabled='!dataVariables.length' @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import VueJsonPretty from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  import GatewayDataSelect from '@/components/GatewayDataSelect/index'
  import ModelDataSelect from '@/components/ModelDataSelect/index'

  import {
    getNodeByElementId, getResetIdWidget, treeUtil,
  } from '@/utils/tree-utils'
  import {
    DELETE_LAYOUT_DATA,
    INIT_DYNAMIC_PROPS,
    UPDATE_DYNAMIC_PROPS,
    UPDATE_LAYOUT_DATA,
  } from '@/config/storeKey'
  import API from '@/api/application'
  import _ from 'lodash'
  import { isValidJsonString } from '@/utils/validateUtils'
  import SqlDataSelect from '@/components/SqlDataSelect'
  import { getLastVersion } from '@/config/module'
  import { sleep } from '@/utils'
  import { getPropByKey, getPropsValues } from '@/renderer/tools/config'
  import { defaultKeys } from '@/config/widgets'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    name: 'PanelData',
    components: {
      VueJsonPretty,
      GatewayDataSelect,
      SqlDataSelect,
      ModelDataSelect
    },
    inject: ['dndOptions', ...pageInject],
    data() {
      return {
        loading: false,
        activeCollapse: ['config'],
        activeDataCollapse: ['data'],
        dialogVisible: false,
        selectedPath: undefined,
        responseOptions: [],
        response: undefined,
        request: undefined,
        jsonLoading: false,
        tableLoading: false,
        dataVariables: [],
        dataType: 'model',
        apiType: 'request',
        formItems: [
          {
            name: '输入框',
            value: 'input'
          },
          {
            name: '文本框',
            value: 'textarea'
          },
          {
            name: '下拉选择框',
            value: 'select'
          },
          {
            name: '穿梭框',
            value: 'transfer'
          },
          {
            name: '数字框',
            value: 'input-number'
          },
          {
            name: '单选框',
            value: 'radio-group'
          },
          {
            name: '多选框',
            value: 'checkbox-group'
          },
          {
            name: '日期',
            value: 'date-time-picker'
          },
          {
            name: '日期范围',
            value: 'date-time-range-picker'
          },
          {
            name: '开关',
            value: 'switch'
          },
          {
            name: '级联选择器',
            value: 'cascader'
          },
          {
            name: '树状选择器',
            value: 'tree'
          },
          {
            name: '颜色选取器',
            value: 'color-picker'
          },
          {
            name: '评分',
            value: 'rate'
          },
        ],
        sqlSource: {
          name: '',
          id: undefined
        },
        selectedModel: null,
        apiInfo: null
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      currentElement() {
        return getNodeByElementId(this.layoutData, this.dndOptions.selectedId)
      },
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      currentProps() {
        return this.dynamicProps[this.dndOptions.selectedId] || {}
      },
    },
    methods: {
      open() {
        this.response = null
        this.request = null
        this.selectedPath = undefined
        this.dataVariables = []
        this.sqlSource = {
          name: '',
          id: undefined
        }
        this.selectedModel = null
        this.apiInfo = null
        this.dialogVisible = true
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      async confirm() {
        this.loading = true
        await sleep(10)
        const records = this.$refs.dataVariablesTable.getCheckboxRecords()
        const selectedList = records.map(item => item.key)
        const result = this.dataVariables.filter(item => selectedList.includes(item.key))
        const children = _.cloneDeep(this.currentElement.children)
        await this._storeDispatch(DELETE_LAYOUT_DATA, { elementIdList: children.map(item => item.elementId) })
        const layout = {
          elementId: '',
          elementType: 'layout',
          elementName: '布局单元',
          elementVersion: getLastVersion('layout'),
          children: [],
        }
        const newLayout = getResetIdWidget.call(this, layout)
        this.currentElement.children.push(newLayout)
        const needUpdateProps = []
        const needInitProps = []
        result.forEach(item => {
          const draggingWidget = this.dndOptions.widgets.find(
            (widget) => widget.elementType === item.vue
          )
          const newWidget = getResetIdWidget.call(this, draggingWidget)
          treeUtil.forEach([newWidget], (source) => {
            const {children, ...node} = source
            const props = {...node, ...(node.defaultProps || {})}
            defaultKeys.forEach(key => {
              this.$delete(props, key)
            })
            needInitProps.push({
              elementId: node.elementId,
              props: _.cloneDeep(props),
            })
            defaultKeys.forEach(key => {
              this.$delete(source, key)
            })
          })
          needUpdateProps.push({
            [newWidget.elementId]: {
              useSlotProps: true,
              prop: item.key,
              label: item.label,
              value: getPropsValues.call(this, newWidget).value
            }
          })
          newLayout.children.push(newWidget)
        })
        this._storeCommit(INIT_DYNAMIC_PROPS, {
          propsList: needInitProps
        })
        this._storeCommit(UPDATE_DYNAMIC_PROPS, {
          dynamicPropsList: needUpdateProps
        })
        this._storeCommit(UPDATE_LAYOUT_DATA, {elementId: this.currentElement.elementId, layout: { children: this.currentElement.children }})
        this.loading = false
        this.dialogVisible = false
      },
      copyPath() {
        // copyText(this.selectedPath, '路径已复制')
      },
      showInputResponseDialog() {
        this.$refs.inputDialog.open({type: 'object', data: this, key: 'response'})
      },
      handleShowGatewayDataSelect() {
        this.$refs.gatewayDataSelect.open()
      },
      handleGatewayDataConfirm({ apiRequest, response, apiInfo, detail }) {
        this.apiInfo = apiInfo || {}
        this.response = JSON.parse(detail.responseText)
        this.request = JSON.parse(detail.requestText)
      },
      handleShowSqlDataSelect() {
        this.$refs.sqlDataSelect.open()
      },
      handleSqlDataConfirm(data) {
        this.sqlSource.name = data.name
        this.sqlSource.id = data.id
      },
      handleShowModelSelect() {
        this.$refs.modelDataSelect.open({ dataSourceName: this.sqlSource.name, selectedRow: this.selectedModel })
      },
      handleModelDataConfirm(data) {
        this.selectedModel = data
        this.handleGetDataModelDetailById(data.id)
      },
      handleGetDataModelDetailById(datasetId) {
        this.tableLoading = true
        this.$http({
          url: API.dataModelDetail,
          data: {
            datasetId
          },
        })
          .then(({ data }) => {
            this.dataVariables = data.map(item => ({ key: item.physicalName, label: item.name, vue: 'input' }))
            this.$nextTick(() => {
              this.$refs.dataVariablesTable.setAllCheckboxRow(true)
            })
          })
          .finally(() => {
            this.tableLoading = false
          })
      },
      handleGenerateKey() {
        if (!this.response && !this.request) {
          this.$message.error('请选择一个提供表单数据的服务网关或手动输入请响应体数据！')
          return
        }
        if (!this.selectedPath) {
          this.$message.error('请选择一个有数据的表单数据节点！')
          return
        }
        const path = this.selectedPath.replace('response.', '')
        const source = this.apiType === 'request' ? this.request : this.response
        const target = _.get(source, path)
        let res = target
        switch (true) {
        case typeof target === 'string':
          if (isValidJsonString(target)) {
            try {
              res = JSON.parse(target.trim())
            } catch (e) {
              console.log(e)
            }
          }
          break
        }
        if (!_.isObject(res)) {
          this.$message.error('请选择一个有数据的表单数据节点！')
          return
        }
        this.dataVariables = Object.keys(res).map(key => ({key, label: '', vue: 'input'}))
        this.$nextTick(() => {
          this.$refs.dataVariablesTable.setAllCheckboxRow(true)
        })
      }
    }
  }
</script>

