<template>
  <div class="variable-input" v-if="elementProps.showControl">
    <div
      class="content"
      :class="{ 'is-show-detail': isShowDetail }"
      @click="isShowDetail = !isShowDetail"
    >
      <div class="label">
        <i class="arrow-icon iconfont icon-right"></i> {{ name }}
      </div>
    </div>
    <div class="inner-content" v-if="isShowDetail">
      <div class="title"><span>类型</span><span>标题</span></div>
      <draggable
        v-model="value"
        forceFallback="true"
        group="column"
        animation="300"
      >
        <transition-group>
          <div class="content" v-for="(btn, index) in value" :key="btn.type + '' + index">
            <el-select v-model="btn.type">
              <el-option label="文字" value="text"></el-option>
              <el-option label="图标" value="icon"></el-option>
            </el-select>
            <el-input v-if="btn.type === 'text'" v-model="btn.label"></el-input>
            <el-input v-if="btn.type === 'icon'" v-model="btn.icon"></el-input>
            <div class="config">
              <i
                class="iconfont icon-shanchu8"
                @click="handleDeleteColumn(index)"
              ></i>
            </div>
          </div>
        </transition-group>
      </draggable>

      <div class="content add" v-popover:popover @click="handleAddColumn">
        <i class="iconfont el-icon-circle-plus-outline"></i>
        添加按钮
      </div>
    </div>
  </div>
</template>

<script>
  import draggable from 'vuedraggable'
  import { UPDATE_EVENTS_DATA } from '@/config/storeKey'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    inject: [...pageInject],
    components: {
      draggable,
    },
    props: {
      elementId: {
        type: String,
      },
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        isShowDetail: false,
      }
    },
    computed: {
      eventsData: {
        get() {
          return this.getStore().eventsData
        },
        set(n) {
          this._storeCommit(UPDATE_EVENTS_DATA, { eventsData: n })
        },
      },
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
        },
      },
    },
    methods: {
      handleDeleteColumn(index) {
        this.value.splice(index, 1)
        this.value = [...this.value]
      },
      handleAddColumn() {
        this.value = [
          ...this.value,
          {
            label: '',
            key: '',
            type: 'text',
            icon: '',
            events: [],
          },
        ]
      },
    },
  }
</script>

