<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="data-select-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="数据集配置"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="data-select" v-loading.lock="loading">
      <data-set-config
        v-if="dialogVisible"
        ref="dataSetConfig"
        :request="{ apiRequest }"
        :type.sync="activeDataType"
        isHideSQL
        isHideStatic
        @response="handleResponse"
      ></data-set-config>
      <div class="data-select-item">
        <el-collapse class="config-collapse" v-model="activeCollapse">
          <el-collapse-item name="config">
            <template slot="title">
              <div class="config-collapse-title">字段映射</div>
            </template>
            <div class="data-select-form data-content">
              <div class="data-config">
                <div
                  class="data-table"
                  :style="{
                    width: activeDataType === 'static' ? '100%' : '50%',
                  }"
                  v-loading="tableLoading"
                >
                  <el-collapse
                    v-if="activeDataType !== 'static'"
                    class="config-collapse"
                    v-model="activeDataCollapse"
                  >
                    <el-collapse-item name="data">
                      <template slot="title">
                        <div class="config-collapse-title">变量映射</div>
                      </template>
                      <vxe-table
                        ref="dataVariablesTable"
                        size="mini"
                        :data="dataVariables"
                        border
                        style="width: 100%"
                        header-cell-class-name="header-cell"
                      >
                        <vxe-table-column
                          field="label"
                          title="变量名"
                        ></vxe-table-column>
                        <vxe-table-column
                          field="key"
                          title="编码"
                        ></vxe-table-column>
                        <vxe-table-column
                          field="path"
                          title="数据路径"
                          min-width="150px"
                        >
                          <template #default="{ row }">
                            <div class="drop-zone-path" :data-key="row.key">
                              <el-tooltip
                                v-if="row.path.length"
                                effect="dark"
                                :content="row.path.join('/')"
                                placement="top"
                              >
                                <el-tag
                                  closable
                                  @close="row.path = []"
                                  disable-transitions
                                  >{{ row.path[row.path.length - 1] }}</el-tag
                                >
                              </el-tooltip>
                              <span v-else>请从右侧拖拽节点</span>
                            </div>
                          </template>
                        </vxe-table-column>
                      </vxe-table>
                    </el-collapse-item>
                  </el-collapse>
                </div>
                <div class="data-result" v-if="activeDataType !== 'static'">
                  <div class="title">
                    <div class="name">
                      响应体
                      <el-button
                        size="mini"
                        type="primary"
                        @click="showInputResponseDialog"
                        >手动录入</el-button
                      >
                      <el-button
                        size="mini"
                        v-if="response"
                        @click="handleClearResponse"
                        >清除</el-button
                      >
                    </div>
                  </div>
                  <div class="body">
                    <vue-json-pretty
                      v-show="response"
                      class="json-pretty no-select"
                      path="response"
                      :data="response"
                      :virtual="true"
                      :itemHeight="32"
                      v-model="selectedPath"
                      selectableType="single"
                      :showSelectController="true"
                      :selectOnClickNode="true"
                    >
                    </vue-json-pretty>
                    <div v-if="!response" class="empty-text">暂无数据</div>
                  </div>
                </div>
              </div>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
      <input-dialog
        @confirm="handleInputResponse"
        ref="inputDialog"
      ></input-dialog>
      <div
        class="dnd-cover"
        v-show="dndCoverStyle"
        :style="dndCoverStyle"
      ></div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" :loading="loading" @click="confirm"
        >确 定</el-button
      >
    </div>
  </el-dialog>
</template>

<script>
import DataSetConfig from '@/components/DataSetConfig'
import VueJsonPretty from '@/lib/vue-json-pretty'
import 'vue-json-pretty/lib/styles.css'
import _ from 'lodash'
import { getObjectTree, getPathList } from '@/utils/format'
import dnd from '@/mixins/dragAndDrop'
import { pageInject } from '@/renderer/tools/pageStore'
export default {
  name: 'TableData',
  components: {
    VueJsonPretty,
    DataSetConfig,
  },
  inject: ['dndOptions', pageInject],
  mixins: [dnd],
  data() {
    return {
      loading: false,
      activeCollapse: ['config'],
      activeDataCollapse: ['data', 'column'],
      dialogVisible: false,
      dataVariables: undefined,
      apiRequest: undefined,
      selectedPath: undefined,
      responseOptions: [],
      response: undefined,
      tableLoading: false,
      activeDataType: '',
    }
  },
  computed: {
    layoutData() {
      return this.getStore().layoutData
    }
  },
  methods: {
    open({ dataVariables, apiRequest }) {
      this.dataVariables = dataVariables ? _.cloneDeep(dataVariables) : []
      this.apiRequest = _.cloneDeep(apiRequest) || undefined
      this.response = null
      this.selectedPath = undefined
      this.dialogVisible = true
      this.$nextTick(() => {
        this.handleInitDnD()
      })
    },
    handleClose(done) {
      this.dialogVisible = false
      done()
    },
    cancel() {
      this.dialogVisible = false
    },
    async confirm() {
      const { apiRequest } = this.$refs.dataSetConfig.getResult()
      this.apiRequest = apiRequest
      this.loading = true
      setTimeout(() => {
        const res = {
          dataVariables: this.dataVariables,
          apiRequest: this.apiRequest,
        }
        this.loading = false
        this.$emit('confirm', res)
        this.dialogVisible = false
      }, 100)
    },
    handleResponse({ response, responseOptions, isStatic }) {
      this.response = response
      this.responseOptions = responseOptions
    },
    showInputResponseDialog() {
      this.$refs.inputDialog.open({
        type: 'object',
        data: this,
        key: 'response',
      })
    },
    handleInputResponse(value) {
      this.response = value
      this.responseOptions = getObjectTree(value)
    },
    handleClearResponse() {
      this.response = null
      this.responseOptions = []
    },
    handleDrop(event) {
      const { key } = event.target.dataset
      const current = this.dataVariables.find((item) => item.key === key)
      const path = getPathList(this.selectedPath)
      current.path = path.slice(1)
    },
  },
}
</script>

