<template>
  <div>
    <div class="widget-config-item">
      <div class="name">{{ name }}</div>
      <div class="container">
        <el-button-group class="flex-button-group">
          <el-button
            size="mini"
            class="config-item-button"
            v-for="item in options"
            :key="item.value"
            @click="value = item.value"
            :type="getType(item.value)"
          >
            <span>
              {{ item.label }}
            </span>
          </el-button>
        </el-button-group>
      </div>
    </div>
    <div v-if="value === 'vertical'" class="widget-config-item">
      <div class="name">漏斗对齐方式</div>
      <div class="container">
        <el-button-group class="flex-button-group">
          <el-button
            size="mini"
            class="config-item-button"
            v-for="item in roseOptions"
            :key="item.value"
            @click="funnelAlign = item.value"
            :type="getFunnelAlignType(item.value)"
          >
            <span>
              {{ item.label }}
            </span>
          </el-button>
        </el-button-group>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = {
          ...this.elementProps,
          [this.propKey]: value,
        }
        const { dataLabelPosition } = res.ySetting
        // 应产品要求, 当漏斗类型切换时, 漏斗对齐方式要相应调整,  数据标签位置数据要改变
        if (value === 'horizontal') {
          res.funnelAlign = 'center'
          res.ySetting.dataLabelPosition = { left: 'bottom', right: 'top' }[dataLabelPosition] || dataLabelPosition
        } else {
          res.ySetting.dataLabelPosition = { bottom: 'left', top: 'right' }[dataLabelPosition] || dataLabelPosition
        }
        this.$emit('update', res)
      },
    },
    funnelAlign: {
      get() {
        return this.elementProps['funnelAlign']
      },
      set(value) {
        console.log(value)
        const res = {
          ...this.elementProps,
          funnelAlign: value,
        }
        this.$emit('update', res)
      },
    },
  },
  data() {
    return {
      options: [
        {
          label: '垂直',
          value: 'vertical',
        },
        {
          label: '水平',
          value: 'horizontal',
        },
      ],
      roseOptions: [
        {
          label: '左',
          value: 'left',
        },
        {
          label: '中',
          value: 'center',
        },
        {
          label: '右',
          value: 'right',
        },
      ],
    }
  },
  methods: {
    getType(value) {
      return this.value.includes(value) ? 'primary' : 'default'
    },
    getFunnelAlignType(value) {
      return this.funnelAlign === value ? 'primary' : 'default'
    },
  },
}
</script>

