<template>
  <div class="variable-input">
    <div class="content" v-for="(v, k) of value" :key="k">
      <el-tooltip effect="dark" :content="k" placement="top">
        <div class="name">{{ v.desc }}</div>
      </el-tooltip>
      <div class="config">
        <el-button
          type="text"
          size="mini"
          v-popover:popover
          @click="handleEditVariable(k, v)"
        >编辑</el-button>
        <el-button
          type="text"
          size="mini"
          v-popover:popover
          @click="handleDeleteVariable(k, v)"
        >删除</el-button>
      </div>
    </div>
    <div class="add-btn-primary" style='margin-top: 10px;' v-popover:popover @click="handleAddVariable">
      +
    </div>
    <el-popover
      :title="oldKey ? '编辑变量' : '新增变量'"
      width="360"
      trigger="manual"
      ref="popover"
      popper-class="has-modal-popper"
      placement="bottom"
      :visible-arrow="false"
      v-model="popoverVisible"
      @after-enter="handleShowModal"
      @hide="handleHideModal"
    >
      <div class="variable-body" v-loading="loading">
        <n-form
          ref="variableForm"
          :model="variableForm"
          :rules="rules"
          label-width="90px"
          @submit.native.prevent
        >
          <n-form-item>
            <template slot='label'>
              <span>作为入参</span>
              <el-tooltip content="开启则表示跳转到当前页面时需要将此参数携带到浏览器url上,开启后变量的值只支持字符串类型" popper-class="tooltip-question" placement="top">
                <i style='margin-left: 5px;' class="el-icon-question"></i>
              </el-tooltip>
            </template>
            <el-switch v-model="variableForm.isEntry" @change="handleIsEntryChange"></el-switch>
          </n-form-item>
          <n-form-item prop="name">
            <template slot='label'>
              <span>变量编码</span>
              <el-tooltip :content="`禁止使用以下系统保留字段作为变量编码：${innerKeys}`" popper-class="tooltip-question" placement="top">
                <i style='margin-left: 5px;' class="el-icon-question"></i>
              </el-tooltip>
            </template>
            <el-input v-model="variableForm.name"></el-input>
          </n-form-item>
          <n-form-item label="变量名" prop="desc">
            <el-input v-model="variableForm.desc"></el-input>
          </n-form-item>
          <n-form-item>
            <template slot='label'>
              <span>默认值</span>
            </template>
            <el-input v-if="variableForm.isEntry" v-model="variableForm.value"></el-input>
            <template v-else>
              <el-tooltip
                v-if="variableForm.value !== undefined"
                effect="dark"
                :content="formatTip(variableForm.value)"
                placement="top"
              >
                <el-tag
                  class="value-tag"
                  closable
                  disable-transitions
                  @click="handleTagClick(variableForm, 'value')"
                  @close="variableForm.value = undefined"
                  :type='getTagType(variableForm.value)'
                >
                  {{ formatTag(getElementName(variableForm.value), 12) }}
                </el-tag>
              </el-tooltip>
              <template v-else>
                <el-button-group class="default-value">
                  <el-button size="mini" @click="showInputDialog('text', variableForm, 'value')">文本</el-button>
                  <el-button size="mini" @click="showInputDialog('number', variableForm, 'value')">数值</el-button>
                  <el-button size="mini" @click="showInputDialog('object', variableForm, 'value')">对象</el-button>
                </el-button-group>
                <MoreTypeDropdown @change="(v) => variableForm.value = v" />
              </template>
            </template>
          </n-form-item>
        </n-form>
        <div style="text-align: right; margin: 0">
          <el-button type="primary" size="mini" @click="handleConfirm"
            >确认
          </el-button>
          <el-button size="mini" type="text" @click="popoverVisible = false"
            >取消
          </el-button>
        </div>
      </div>
    </el-popover>
    <input-dialog @confirm="handleUpdateValue" ref="inputDialog"></input-dialog>
  </div>
</template>

<script>
  import {
    getValidators,
    required,
    hasSpecialCharacter,
  } from '@/utils/validateUtils'
  import API from '@/api/application'
  import _ from 'lodash'
  import elementEditor from '@/mixins/elementEditor'
import { pageInject } from '@/renderer/tools/pageStore'

  const innerKeys = ['id', 'code', 'pageId', 'appId', 'vid']

  export default {
    inject: [...pageInject],
    mixins: [elementEditor],
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        innerKeys,
        loading: false,
        popoverVisible: false,
        variableForm: {
          name: '',
          desc: '',
          isEntry: false,
          value: undefined,
        },
        oldKey: undefined,
        rules: {
          name: getValidators([
            {
              message: '请输入变量编码',
              include: required,
              required: true,
              trigger: 'change',
            },
            {
              message: '变量编码不能包含特殊字符',
              include: hasSpecialCharacter,
              trigger: 'change',
            },
          ]),
          desc: getValidators([
            {
              message: '请输入变量名',
              include: required,
              required: true,
              trigger: 'change',
            },
          ]),
        },
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { [this.propKey]: value }
          this.$emit('update', res)
        },
      },
    },
    methods: {
      async handleAddVariable() {
        this.popoverVisible = true
        this.$nextTick()
        this.$refs.variableForm.resetFields()
        this.variableForm = {
          name: '',
          desc: '',
          isEntry: false,
          value: undefined,
        }
        this.oldKey = undefined
      },
      handleEditVariable(key, value) {
        this.popoverVisible = true
        this.$nextTick()
        this.$refs.variableForm.resetFields()
        this.variableForm = {
          name: key,
          desc: value.desc,
          isEntry: value.isEntry,
          value: value.value,
        }
        this.oldKey = key
      },
      handleDeleteVariable(key, value) {
        this.$delete(this.value, key)
        this.value = { ...this.value }
        const res = this.value
        this.handleSaveData(res)
        this.popoverVisible = false
      },
      handleUpdateEntry(isEntry, key, value) {
        this.oldKey = key
        const data = {...value, name: key}
        if (this.oldKey && this.oldKey !== data.name) {
          this.$delete(this.value, this.oldKey)
        }
        const res = {
          ...this.value,
          [data.name]: {
            isEntry: data.isEntry,
            desc: data.desc,
            value: data.value,
          },
        }
        this.handleSaveData(res)
      },
      handleSaveData(res) {
        this.loading = true
        this.$http({
          url: API.appPageDesignSavePageConfig,
          data: [
            {
              pageDesignId: Number(this.layoutData.pageDesignId),
              type: 4,
              config: JSON.stringify(res),
            },
          ],
        })
          .then(() => {
            this.value = res
            this.popoverVisible = false
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleConfirm() {
        this.$refs.variableForm.validate((valid) => {
          if (!valid) {
            return
          }
          const data = this.variableForm
          if (innerKeys.includes(data.name)) {
            this.$message.error(`禁止使用以下系统保留字段作为变量编码：${innerKeys}`)
            return
          }
          // 避免改动之前的this.value从而导致二次赋值,让历史记录增加了错误的记录
          const value = _.cloneDeep(this.value)
          if (this.oldKey && this.oldKey !== data.name) {
            this.$delete(value, this.oldKey)
          }

          const res = {
            ...value,
            [data.name]: {
              isEntry: data.isEntry,
              desc: data.desc,
              value: data.value,
            },
          }
          this.handleSaveData(res)
        })
      },
      handleShowModal() {
        const popperElement = this.$refs.popover.popperElm
        popperElement.style.top = '220px'
        popperElement.style.right = '40px'
        popperElement.style.left = 'unset'
        popperElement.style.display = 'block'
        const modalElement = document.createElement('div')
        modalElement.classList.add('fullscreen-modal')
        modalElement.appendChild(popperElement)
        document.body.appendChild(modalElement)
      },
      handleHideModal() {
        const modalElement = document.querySelector('.fullscreen-modal')
        modalElement?.remove()
      },
      handleIsEntryChange(v) {
       this.variableForm.value = v ? `${this.variableForm.value !== undefined ? this.variableForm.value : ''}` : this.variableForm.value
      }
    },
  }
</script>

<style lang="less" scoped>
  .variable-input::v-deep {
    margin-bottom: 8px;

    .content {
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      height: 34px;
      padding: 0 15px;
      border: 1px solid #e6e6e6;
      border-top: none;

      &:first-child {
        border-top: 1px solid #e6e6e6;
      }

      .name {
        flex: 1;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      .desc {
        margin-right: 10px;
      }

      .isEntry {
        margin-right: 10px;
      }

      .config {
      }
    }

    .default-value {
      display: flex;
      align-items: center;
    }
  }
</style>
