<template>
  <div class="config-auto-upload">
    <div class="widget-config-item">
      <div
        :class="{
          name: true,
          required: requiredArr.includes(name),
        }"
      >
        {{ name }}
        <el-tooltip
          :content="autoTip"
          popper-class="tooltip-question"
          placement="top"
        >
          <i style="margin-left: 5px" class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <n-form-item class="config-item" label="">
        <el-radio-group v-model="value">
          <el-radio-button label="manual">手动</el-radio-button>
          <el-radio-button label="auto">自动</el-radio-button>
        </el-radio-group>
      </n-form-item>
    </div>
    <template v-if="value === 'auto'">
      <div class="widget-config-item">
        <div class="name">上传配置</div>
        <n-form-item class="config-item" label="">
          <el-button type="primary" @click="openUploadData">{{ elementProps.apiRequest ? 'API请求' : '配置' }}</el-button>
          <upload-data ref="uploadData" @confirm="handleConfirm"></upload-data>
        </n-form-item>
      </div>
      <div class="widget-config-item">
        <div class="name">最大上传文件数</div>
        <n-form-item class="config-item" label="">
          <el-input-number v-model="limit" :min="1"></el-input-number>
        </n-form-item>
      </div>
    </template>
  </div>
</template>

<script>
import uploadData from '@/components/DataSelect/uploadData'
export default {
  name: 'ConfigUploadMode',
  components: {
    uploadData,
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    tip: {
      type: String,
    },
  },
  data() {
    return {
      requiredArr: [],
      autoTip: '开启自动上传后,可支持多选,组件值为上传接口成功后返回的访问链接',
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
    limit: {
      get() {
        return this.elementProps['limit']
      },
      set(value) {
        const res = { ...this.elementProps, limit: value }
        this.$emit('update', res)
      },
    },
  },
  methods: {
    openUploadData() {
      const { dataVariables, elementId } = this.elementProps
      let { apiRequest } = this.elementProps
      if (!apiRequest) {
        apiRequest = {
          desc: '',
          url: '',
          method: 'POST',
          type: 4,
          paramsData: [
            {
              key: '',
              value: undefined,
              test: undefined,
            },
          ],
          bodyData: [
            { key: 'file', value: `${elementId}.$file` },
            { key: 'fileName', value: `${elementId}.$fileName` },
          ],
          headersData: [
            {
              key: 'Content-Type',
              test: 'multipart/form-data',
              value: 'multipart/form-data',
            },
          ],
        }
      }
      this.$refs.uploadData.open({
        dataVariables,
        apiRequest,
      })
    },
    handleConfirm(res) {
      this.$emit('update', {
        ...this.elementProps,
        ...res,
      })
    },
  },
}
</script>

