<template>
  <div class="config-wrapper">
    <component
      :is="currentElement"
      v-bind="$props"
      @update="updateElementProps"
    ></component>
  </div>
</template>

<script>
  import Vue from 'vue'
  import configComponents from '@/components/ConfigWrapper/components'
  export default {
    name: 'ConfigWrapper',
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
        default: '',
      },
      tip: {
        type: String,
      },
      vue: {
        type: String,
      },
      elementId: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      optionkey: {
        type: String,
      },
      options: {
        type: Object
      },
      multiple: {
        type: Boolean,
        default: true
      },
      types: {
        type: Array
      }
    },
    computed: {
      currentElement() {
        return Vue.component(this.vue, configComponents[this.vue])
      },
    },
    methods: {
      updateElementProps(elementProps) {
        this.$emit('confirm', elementProps[this.propKey])
      },
    },
  }
</script>

<style lang="less" scoped></style>
