<template>
  <div v-if='isConfigVisible' class="widget-config-item">
    <div
      :class="{
        name: true,
        required: requiredArr.includes(name),
      }"
    >
      {{ name }}
      <el-tooltip v-if="tip" :content="tip" popper-class="tooltip-question" placement="top">
        <i style='margin-left: 5px;' class="el-icon-question"></i>
      </el-tooltip>
    </div>
    <n-form-item class="config-item" label="" :prop="propKey">
      <el-switch active-color="#3F92FE" v-model="value" :active-value="config.on" :inactive-value="config.off"></el-switch>
    </n-form-item>
  </div>
</template>

<script>
import config from './config'
  export default {
    name: 'ConfigSwitch',
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      tip: {
        type: String,
      },
      optionkey: {
        type: String,
      },
    },
    data() {
      return {
        requiredArr: [],
      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
        },
      },
      isConfigVisible() {
        if (['autoFetch'].includes(this.optionkey)) {
          const { apiRequest, sqlRequest } = this.elementProps
          return apiRequest || sqlRequest
        }
        return true
      },
      config() {
        return config[this.optionkey] || { on: true, off: false }
      }
    },
  }
</script>

<style lang="less" scoped>
  div.widget-config-item::v-deep {
    display: flex;
    height: 42px;
    .name {
      font-family: PingFangSC-Medium;
      font-size: 12px;
      color: #555555;
      letter-spacing: 0;
      font-weight: 500;
      margin-bottom: 5px;
      min-width: 100px;
      padding-right: 10px;
      display: flex;
      height: 36px;
      align-items: center;
    }
    .required::before {
      content: '*';
      color: red;
    }
    div.config-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 8px;
      .el-form-item__content {
        flex: 1;
        display: flex;
        align-items: center;
        .el-icon-question {
          margin-left: 10px;
        }
      }
    }
  }
</style>
