<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-color-picker v-model="value" :show-alpha="true"></el-color-picker>
  </n-form-item>
</template>

<script>
  export default {
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      vue: {
        type: String,
      },
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = {
            ...this.elementProps,
            [this.propKey]: value,
          }
          this.$emit('update', res)
        },
      },
    },
  }
</script>

<style lang="less" scoped>
  div.config-item::v-deep {
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 8px;
    .el-form-item__label {
      width: 100px;
    }
  }
</style>
