<template>
  <el-dropdown :size="size" @command="handleChange">
    <el-button :size="size">
      更多<i class="el-icon-arrow-down el-icon--right"></i>
    </el-button>
    <el-dropdown-menu class="top-dropdown" slot="dropdown">
      <el-dropdown-item
        v-for="item in types"
        :key="item.label"
        :command="item.value"
        >{{ item.label }}</el-dropdown-item
      >
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
export default {
  name: 'MoreTypeDropdown',
  props: {
    types: {
      type: Array,
      default: () => [
        {
          label: 'null',
          value: null,
        },
        {
          label: 'true',
          value: true,
        },
        {
          label: 'false',
          value: false,
        },
        {
          label: '空文本',
          value: '',
        },
        {
          label: '空数组',
          value: [],
        },
        {
          label: '空对象',
          value: {},
        },
      ],
    },
    size: {
      type: String,
      default: 'mini',
    },
  },
  methods: {
    handleChange(value) {
      this.$emit('change', value)
    },
  },
}
</script>