import { formatTag, formatTip, getAppVariables, isValidateVariable } from '@/utils'
import { FUNCTION_FLAG, JS_FLAG, ELEMENT_NOT_FOUND, UTILS_FUNCTION } from '@/config'
import { getNodeByElementId } from '@/utils/tree-utils'
import { getVariableByVersion } from '@/renderer/tools/config'
import { utils } from '@/components/FunctionSelect/utils'
import { pageInject } from '@/renderer/tools/pageStore'
export default {
  inject: [...pageInject],
  methods: {
    formatTag,
    formatTip,
    isValidateVariable,
    getElementName(variable) {
      if (typeof variable !== 'string') {
        return formatTip(variable)
      }
      const functionId = variable && variable.includes(FUNCTION_FLAG) ? variable.split(FUNCTION_FLAG)[1] : undefined
      const utilsFunctionId = variable && variable.indexOf(UTILS_FUNCTION) === 0 ? variable : undefined
      const jsId = variable && variable.includes(JS_FLAG) ? variable.split(JS_FLAG)[1] : undefined
      const elementId = isValidateVariable(variable) ? variable.split('.$')[0] : undefined
      if (!functionId && !elementId && !jsId && !utilsFunctionId) {
        return formatTip(variable)
      }
      if (utilsFunctionId) {
        const currentUtil = utils.find(item => item.id === variable)
        if (currentUtil) {
          return `工具函数.${currentUtil.name}`
        }
      }
      const firstVariable = variable.split('.')[1]
      let variableName
      if (elementId) {
        if (firstVariable?.includes('[')) {
          variableName = firstVariable.split('[')[0]
        } else {
          variableName = firstVariable.split('.')[0]
        }
        const current = this.getElementPropsById(elementId)
        if (!current) {
          return ELEMENT_NOT_FOUND
        }
        const name = current.elementName
        const { elementType, elementVersion } = current
        const data = getVariableByVersion.call(this, {elementType, elementVersion, elementId})
        const config = data[variableName]
        if (variableName.includes('appVariables')) {
          const appVariables = getAppVariables()
          const variableKey = variable.replace(`${elementId}.$appVariables.`, '').split('.value')[0]
          const currentVariable = appVariables[variableKey]
          return currentVariable ? `应用变量.${currentVariable.desc}` : ELEMENT_NOT_FOUND
        }
        return config ? `${name}.${config.desc}` : `${name}` || ELEMENT_NOT_FOUND
      }
      if (functionId) {
        const functionsData = this.getStore().functionsData
        const currentFunction =  functionsData.find(item => item.id === functionId)
        return currentFunction ? `高级变量.${currentFunction.name}` : ELEMENT_NOT_FOUND
      }
      if (jsId) {
        const jsData = this.getStore().jsData
        const currentJsData =  jsData.find(item => item.id === jsId)
        return currentJsData ? `动作.${currentJsData.name}` : ELEMENT_NOT_FOUND
      }
    },
    getTagType(variable) {
      return this.getElementName(variable) === ELEMENT_NOT_FOUND ? 'danger' : ''
    },
    handleTagClick(data, key, attrOptions, type) {
      const value = data[key]
      if(type === 'function') {
        return this.showInputDialog('function', data, key, 'function')
      }
      if (typeof value === 'string') {
        if (!['test', 'mockValue'].includes(key) && isValidateVariable(value) || value.indexOf(FUNCTION_FLAG) === 0) {
          this.handleShowVariableSelect(data, key)
          return
        }
        if (value.indexOf(JS_FLAG) === 0) {
          this.handleShowFunctionSelect(data, key)
          return
        }

        if (value.indexOf(UTILS_FUNCTION) === 0) {
          this.handleShowUtilsSelect(data, key)
          return
        }

        if (attrOptions && !['page'].includes(attrOptions.elementType)) {
          this.showInputDialog('attrVue', data, key, undefined, attrOptions)
          return
        }

        this.showInputDialog('text', data, key)
        return
      }
      if (attrOptions && !['page'].includes(attrOptions.elementType)) {
        this.showInputDialog('attrVue', data, key, undefined, attrOptions)
        return
      }
      if (typeof value === 'object' && value) {
        if(!(value instanceof File)) {
          this.showInputDialog('object', data, key)
          return
        }
      }
      if (typeof value === 'number') {
        this.showInputDialog('number', data, key)
        return
      }
    },
    showInputDialog(type, data, key, from, attrOptions) {
      this.$refs.inputDialog.open({ type, data, key, from, attrOptions, callbackId: this.callbackId, emitEvent: this.emitEvent })
    },
    handleUpdateValue(data, from) {
      if (from) {
        this[from] = data
      }
    },
    getAttrOptions(elementId, config) {
      const { elementType, elementVersion } = getNodeByElementId(this.getStore().layoutData, elementId)
      return { elementType, elementVersion, config }
    },
    getElementPropsById(elementId) {
      const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
      return this.getStore().dynamicProps[realId] || {}
    }
  },
}
