<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-switch v-model="value"></el-switch>
    <div class="fill-div"></div>
    <el-popover placement="right" width="300" trigger="click">
      <el-form
        size="mini"
        class="setting-form"
        label-position="left"
        label-width="90px"
      >
        <el-form-item label="网格斑马纹">
          <el-switch v-model="splitArea"></el-switch>
        </el-form-item>
        <el-form-item label="网格隔线">
          <el-switch v-model="splitLine"></el-switch>
        </el-form-item>
        <el-form-item label="网格轴线">
          <el-switch v-model="axisLine"></el-switch>
        </el-form-item>
      </el-form>
      <i slot="reference" class="el-icon-s-tools"></i>
    </el-popover>
  </n-form-item>
</template>

<script>
export default {
  components: {
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
    splitArea: {
      get() {
        return this.elementProps['splitArea']
      },
      set(value) {
        const res = { ...this.elementProps, 'splitArea': value }
        this.$emit('update', res)
      },
    },
    splitLine: {
      get() {
        return this.elementProps['splitLine']
      },
      set(value) {
        const res = { ...this.elementProps, 'splitLine': value }
        this.$emit('update', res)
      },
    },
    axisLine: {
      get() {
        return this.elementProps['axisLine']
      },
      set(value) {
        const res = { ...this.elementProps, 'axisLine': value }
        this.$emit('update', res)
      },
    },
  },
  methods: {},
  mounted() {},
  created() {},
}
</script>

