<template>
  <el-dialog
    title="选择高级变量"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="1000px"
    top='5vh'
    :before-close="handleClose"
    custom-class="handler-dialog low-code-dialog"
  >
    <n-form
      class="data-form"
      ref="dataForm"
      :model="dataForm"
      @submit.native.prevent
      label-width="80px"
      label-position="left"
    >
      <n-form-item label="条件查询">
        <div class="search-item">
          <el-input style="margin: 0 10px;" v-model="keyword" placeholder="请输入名称查询" clearable @clear="handleSearch"></el-input>
          <el-button type="primary" icon="el-icon-search" @click="handleSearch">查询</el-button>
        </div>
      </n-form-item>
      <n-form-item label='变量列表'>
        <n-table
          :data="functionList.filter(item => item.isShow)"
          border
          highlight-current-row
          @row-click="handleRowClick"
          max-height="550px"
          size="mini"
        >
          <n-table-column v-if="!isManage" width="50px" label="选择">
            <template slot-scope="scope">
              <el-radio v-if="getIsSelected(scope.row)"></el-radio>
              <el-radio v-else :value='false'></el-radio>
            </template>
          </n-table-column>
          <n-table-column prop="name" label="高级变量名称">
          </n-table-column>
          <n-table-column prop="code" label="代码" show-overflow-tooltip>
          </n-table-column>
        </n-table>
      </n-form-item>
    </n-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="clear">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import _ from 'lodash'
  import { pageInject } from '@/renderer/tools/pageStore'

  export default {
    name: 'JsCodeSelect',
    inject: [...pageInject],
    data() {
      return {
        dialogVisible: false,
        dataForm: {},
        functionList: [],
        selectedFunction: null,
        keyword: ''
      }
    },
    computed: {
      functionsData() {
        return this.getStore().functionsData
      },
    },
    methods: {
      open(data = {}) {
        this.handleUpdateFunctionList()
        this.dialogVisible = true
      },
      clear() {
        this.selectedFunction = null
        this.functionList = []
        this.keyword = ''
        this.dialogVisible = false
      },
      handleClose(done) {
        this.clear()
        done()
      },
      confirm() {
        if (!this.selectedFunction) {
          this.$message.error('请选择高级变量！')
          return
        }
        const res = _.cloneDeep(this.selectedFunction)
        delete res.isShow
        this.$emit('confirm', res)
        this.clear()
      },
      handleUpdateFunctionList() {
        this.functionList = this.functionsData.map(item => ({...item, isShow: true}))
      },
      handleRowClick(row, column, event) {
        this.selectedFunction = row
      },
      getIsSelected(row) {
        return this.selectedFunction === row
      },
      handleSearch() {
        this.functionList = this.functionList.map(item => ({...item, isShow: !this.keyword || item.name.includes(this.keyword)}))
      },
    },
  }
</script>

<style lang="less" scoped>
  .data-form::v-deep {
    .search-item {
      display: flex;
      align-items: center;
    }
  }
</style>
