<template>
  <div class="highlight-code" ref="codeEditor" v-loading.lock='loading'></div>
</template>

<script>
  import _ from 'lodash'
  const formatCssCode = (cssText) => {
    const value = cssText || ''
    return `:root {\r\n${value ? `\t${value}` : '\t'}\r\n}`
  }

  export default {
    name: 'CssCode',
    props: {
      value: {
        type: String,
      },
    },
    data() {
      return {
        loading: false,
        hints: [],
        monacoInstance: null,
        provider: null,
      }
    },
    computed: {
      code: {
        get() {
          return formatCssCode(this.value)
        },
        set(n) {
          const trimedValue = n.trim()
          if (trimedValue.lastIndexOf('{') !== 6 || trimedValue.indexOf('}') !== trimedValue.length - 1) {
            this.$message.error('请将css代码写到:root {}大括号中!')
            return
          }
          const value = n
            .replace(':root {', '')
            .replace('}', '')
            .trim()
          this.$emit('input', value)
        },
      },
    },
    watch: {
      value: {
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.code = formatCssCode(n)
          this.monacoInstance?.setValue(this.code)
        },
        immediate: true
      },
    },
    methods: {
      dispose() {
        if (this.monacoInstance) {
          if (this.monacoInstance.getModel()) {
            this.monacoInstance.getModel().dispose()
          }
          this.monacoInstance.dispose()
          this.monacoInstance = null
          if (this.provider) {
            this.provider.dispose()
            this.provider = null
          }
        }
      },
      async init() {
        const target = this.$refs.codeEditor
        if (!target) {
          return
        }
        this.loading = true
        const src = [
          import('monaco-editor/esm/vs/editor/editor.api.js'),
          import('monaco-editor/esm/vs/basic-languages/css/css.contribution')
        ]
        const [monaco] = await Promise.all(src)
        this.dispose()
        this.monacoInstance = monaco.editor.create(target, {
          value: this.code,
          language: 'css',
          theme: 'vs',
          folding: false,
          foldingHighlight: true,
          foldingStrategy: 'indentation',
          showFoldingControls: 'always',
          disableLayerHinting: true,
          emptySelectionClipboard: false,
          selectionClipboard: false,
          automaticLayout: true,
          codeLens: false,
          scrollBeyondLastLine: false,
          colorDecorators: true,
          accessibilitySupport: 'off',
          lineNumbers: 'on',
          lineNumbersMinChars: 2,
          enableSplitViewResizing: false,
          roundedSelection: false,
          overviewRulerBorder: false,
          minimap: {
            enabled: false,
          },
          autoIndent: 'full',
          wordWrap: 'on',
          readOnly: false,
          tabSize: 2,
        })

        // this.monacoInstance.onDidChangeModelContent(
        //   _.debounce(
        //     () => {
        //       if (!this.monacoInstance) {
        //         return
        //       }
        //       this.code = this.monacoInstance.getValue()
        //     },
        //     500,
        //     { leading: false, trailing: true }
        //   )
        // )

        this.monacoInstance.onDidFocusEditorText(() => {
          this.$emit('focus-change', true)
        })
        this.monacoInstance.onDidBlurEditorText(() => {
          this.doFormat()
          this.$emit('focus-change', false)
        })
        this.loading = false
      },
      doFormat() {
        if (!this.monacoInstance) {
          return
        }
        this.code = this.monacoInstance.getValue()
        this.monacoInstance.getAction(['editor.action.formatDocument'])._run()
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.init()
      })
    },
    beforeDestroy() {
      this.dispose()
    },
  }
</script>

<style lang="less" scoped>
  .trigger-btn {
    margin: 10px auto;
  }

  .highlight-code::v-deep {
    width: 100%;
    min-height: 300px;
    .editor-widget .suggest-widget {
      z-index: 200;
    }
  }
</style>
