<template>
  <el-dialog
    title="选择工具函数"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="1200px"
    top='5vh'
    :before-close="handleClose"
    custom-class="handler-dialog low-code-dialog"
  >
    <div class="dialog-content">
      <n-form
        class="data-form"
        ref="dataForm"
        :model="dataForm"
        @submit.native.prevent
        label-width="50px"
        label-position="left"
      >
        <n-form-item label="筛选">
          <div class="search-item">
            <el-input style="margin: 0 10px;width: 300px;" v-model="dataForm.keyword" placeholder="请输入名称筛选" clearable></el-input>
          </div>
        </n-form-item>
      </n-form>
      <vxe-table
        class="x-table"
        ref="xTable"
        size="mini"
        :height="480"
        :data="visibleUtils"
        border
        header-cell-class-name="header-cell"
        row-id="key"
        :radio-config="{trigger: 'row', highlight: true}"
      >
        <vxe-table-column type="radio" title="选择" align="center" width="60"></vxe-table-column>
        <vxe-table-column field="name" title="方法名" width="140"></vxe-table-column>
        <vxe-table-column field="type" title="定义"></vxe-table-column>
        <vxe-table-column field="desc" type="html" title="描述"></vxe-table-column>
        <vxe-table-column type="expand" title="示例" align="center" width="60">
          <template #content="{ row }">
            <div style="padding: 10px; background: #f5f5f5;" v-html="row.demo"></div>
          </template>
        </vxe-table-column>
      </vxe-table>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="clear">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { UTILS_FUNCTION } from '@/config'
import { utils } from './utils'
export default {
  name: 'UtilsSelect',
  data() {
    return {
      utils,
      dialogVisible: false,
      dataForm: {
        keyword: ''
      },
    }
  },
  computed: {
    visibleUtils() {
      return this.dataForm.keyword ? utils.filter(item => item.name.toLowerCase().includes(this.dataForm.keyword.toLowerCase())) : utils
    }
  },
  methods: {
    open({ fullVariable } = {}) {
      const utilsFunctionId = fullVariable && fullVariable.indexOf(UTILS_FUNCTION) === 0 ? fullVariable : undefined
      if (utilsFunctionId) {
        this.$nextTick(() => {
          const row = utils.find(item => item.id === fullVariable)
          if(row) {
            this.$refs.xTable.setRadioRow(row)
          }
        })
      }
      this.dialogVisible = true
    },
    handleClose(done) {
      this.dialogVisible = false
      done()
    },
    clear() {
      this.dialogVisible = false
    },
    confirm() {
      const xTable = this.$refs.xTable
      const selectedRow = xTable?.getRadioRecord()
      if (!selectedRow) {
        this.$message.error('请选择工具函数')
        return
      }
      this.$emit('confirm', selectedRow.id)
      this.clear()
    }
  }
}
</script>

<style scoped lang="less">
.dialog-content::v-deep {
  .x-table {
    .vxe-cell--html {
      white-space: nowrap!important;
    }
  }
}
</style>