<template>
  <div class="variable-input">
    <div class="inner-content">
      <div class="title" v-popover:popover>
        <el-checkbox v-if="deleteMode" :indeterminate="isIndeterminate" v-model="isSelectAll" @change="handleCheckAllChange"></el-checkbox>
        <span class="col-title">提醒分类名称</span>
        <span class="key">操作</span>
      </div>
      <draggable
        v-model="value"
        forceFallback="true"
        group="column"
        animation="300"
        handle=".el-icon-rank"
      >
        <transition-group>
          <div class="content" :style='{opacity: col.isHidden ? "0.5" : "1"}' v-for="(col, index) in value" :key="col.groupName">
            <el-checkbox v-if="deleteMode" v-model="selectedList" :label="col.groupName" @change="radioChange"></el-checkbox>
            <i class="icon el-icon-rank" />
            <span class="label">{{ col.groupName }}</span>
<!--            <span class="key">{{ col.key }}</span>-->
            <div class="caozuo">
              <el-button type="text" @click="handleEditColumn(index, col)"
                >编辑
              </el-button>
              <el-button type="text" @click="handleDeleteColumn(index)"
                >删除
              </el-button>
            </div>
          </div>
        </transition-group>
      </draggable>
    </div>
    <template v-if="!deleteMode">
      <div
        class="add-reg-exp-btn"
        style="cursor:pointer"
        @click="handleAddColumn"
      >
        +
      </div>
      <el-button size="mini" type="danger" @click="deleteMode = true">批量删除</el-button>
    </template>
    <template v-else>
      <el-button size="mini" @click="deleteMode = false">取消</el-button>
      <el-button size="mini" type="danger" @click="doDelete">删除</el-button>
    </template>
    <el-popover
      width="300"
      trigger="manual"
      ref="popover"
      :title="variableForm.isAdd ? '新增' : '编辑'"
      v-model="popoverVisible"
    >
      <div class="variable-body">
        <n-form
          ref="variableForm"
          :model="variableForm"
          :rules="rules"
          label-width="85px"
          @submit.native.prevent
          size='mini'
        >
          <n-form-item label="分类名称" prop="groupName">
            <el-input v-model.trim="variableForm.groupName"></el-input>
          </n-form-item>
          <n-form-item label="主题色" prop="backgroundColor">
            <el-color-picker
              v-model="variableForm.backgroundColor"
              show-alpha
              size="mini"
              :predefine="predefineColors"
            />
          </n-form-item>
          <n-form-item label="文字颜色" prop="textColor">
            <el-color-picker
              v-model="variableForm.textColor"
              show-alpha
              size="mini"
              :predefine="predefineColors"
            />
          </n-form-item>
          <n-form-item label="模板内容" prop="templates">
            <el-input
              v-for='(item, index) in variableForm.templates'
              :key='index'
              clearable
              @clear='variableForm.templates.splice(index, 1)'
              v-model='variableForm.templates[index]'
            ></el-input>
            <div
              class="add-btn-primary"
              style="cursor:pointer"
              @click="handleAddTemplate"
            >
              +
            </div>
          </n-form-item>
          <n-form-item label="允许自定义" prop="editable">
            <el-switch v-model="variableForm.editable"></el-switch>
          </n-form-item>
        </n-form>
        <div style="text-align: right; margin: 0">
          <el-button type="primary" size="mini" @click="handleConfirm"
            >确认
          </el-button>
          <el-button size="mini" type="text" @click="popoverVisible = false"
            >取消
          </el-button>
        </div>
      </div>
    </el-popover>
  </div>
</template>

<script>
  import { getValidators, required } from '@/utils/validateUtils'
  import _ from 'lodash'
  import draggable from 'vuedraggable'

  export default {
    components: {
      draggable,
    },
    inject: ['dndOptions'],
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        variableForm: {
          groupName: '',
          templates: [],
          editable: true,
          backgroundColor: '#3f92fe',
          textColor: '#ffffff',
        },
        rules: {
          groupName: getValidators([
            {
              message: '请输入分类名称',
              include: required,
              trigger: 'change',
            },
          ])
        },
        popoverVisible: false,
        edtingIndex: undefined,
        deleteMode: false,
        isSelectAll: false,
        isIndeterminate: false,
        selectedList: [],
        predefineColors: [
          '#ff4500',
          '#ff8c00',
          '#ffd700',
          '#90ee90',
          '#00ced1',
          '#1e90ff',
          '#c71585',
          'rgb(255, 120, 0)',
          '#ff0000',
        ],
      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
        },
      },
    },
    methods: {
      errorToast() {
        this.$message.error('至少要保留一个')
      },
      doDelete() {
        if(this.selectedList.length === this.value.length) {
          return this.errorToast()
        }
        this.value = this.value.filter(item => !this.selectedList.includes(item.key))
        this.handleCheckAllChange(false)
        this.deleteMode = false
      },
      handleCheckAllChange(val) {
        this.selectedList = val ? this.value.map(item => item.groupName) : []
        this.isIndeterminate = false;
      },
      radioChange() {
        let checkedCount = this.selectedList.length;
        this.isSelectAll = checkedCount === this.value.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.value.length;
      },
      handleEditColumn(index, value) {
        this.popoverVisible = true
        this.$nextTick()
        this.$refs.variableForm.resetFields()
        this.variableForm = _.cloneDeep({ ...value, isAdd: false })
        this.edtingIndex = index
      },
      handleDeleteColumn(index) {
        if (this.value.length === 1) {
          return this.errorToast()
        }
        this.value.splice(index, 1)
        this.value = [...this.value]
      },
      handleAddTemplate() {
        this.variableForm.templates.push('')
      },
      handleAddColumn() {
        this.popoverVisible = true
        this.$nextTick()
        this.$refs.variableForm.resetFields()
        this.variableForm = {
          isAdd: true,
          groupName: '',
          templates: [],
          editable: true,
          backgroundColor: '#3f92fe',
          textColor: '#ffffff',
        }
        this.edtingIndex = undefined
      },
      handleConfirm() {
        this.$refs.variableForm.validate((valid) => {
          if (!valid) {
            return
          }
          let tempValue = _.cloneDeep(this.value)
          const form = _.cloneDeep(this.variableForm)
          delete form.isAdd
          if (this.edtingIndex === undefined) {
            const res = {
              ...this.elementProps,
              [this.propKey]: [...tempValue, {...form, templates: _.uniq(form.templates.filter(item => item))}],
            }
            this.$emit('update', res)
          } else {
            tempValue[this.edtingIndex] = {...form, templates: _.uniq(form.templates.filter(item => item))}
            const res = {
              ...this.elementProps,
              [this.propKey]: tempValue,
            }
            this.$emit('update', res)
          }
          this.popoverVisible = false
        })
      },
    },
  }
</script>

