<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="选择数据源"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-loading.lock="loading">
      <div class="data-table">
        <n-table
          :data="dataList"
          border
          highlight-current-row
          @row-click="handleRowClick"
        >
          <n-table-column width="50px" label="选择">
            <template slot-scope="scope">
              <el-radio v-if="selectedRow === scope.row"></el-radio>
              <el-radio v-else :value="false"></el-radio>
            </template>
          </n-table-column>
          <n-table-column prop="name" label="API名称"></n-table-column>
          <n-table-column prop="path" label="API路径"></n-table-column>
          <n-table-column prop="description" label="API描述"></n-table-column>
        </n-table>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import customApi from '@/api/customApi'

  export default {
    name: 'CustomApiDataSelect',
    data() {
      return {
        dialogVisible: false,
        loading: false,
        dataList: [],
        selectedRow: null,
      }
    },
    methods: {
      open(data) {
        this.selectedRow = data
        this.dialogVisible = true
        this.handleSearch(true)
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      confirm() {
        if (!this.selectedRow) {
          this.$message.error('请选择数据源')
          return
        }
        this.handleGetServeDetail()
      },
      handleGetServeDetail() {
        const data = this.selectedRow
        const body = Object.entries(data.requestBody || {}).map(
          ([key, value]) => ({
            key,
            value,
            test: value,
          })
        )
        // const protocol = window.location.protocol
        // const host = window.location.host
        let apiRequest = {
          url: `/app-data${data.path}`,
          method: 'POST',
          type: 5,
          paramsData: [],
          bodyData: body.length
            ? body
            : [{ key: '', value: undefined, test: undefined }],
          headersData: [],
        }
        const result = {
          apiRequest,
          response: null,
          apiInfo: this.selectedRow,
        }
        this.$emit('confirm', result)
        this.dialogVisible = false
      },
      handleSearch() {
        this.loading = true
        this.$http({
          url: customApi.apiDesignListPublished,
          data: {
            appId: +this.$route.query.id,
          },
        })
          .then((res) => {
            this.dataList = res.data.map((item) => ({
              ...item,
              path: `/app${this.$route.query.id}/${item.path}`,
            }))
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleRowClick(row, column, event) {
        this.selectedRow = row
      },
    },
  }
</script>

<style lang="less" scoped>
  .dialog-content::v-deep {
    .pagination {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 10px;
    }
  }
</style>
