<template>
  <div class="icon_selector">
    <div class="col">
      <span class="label">{{ name }}</span>
      <i v-if="iconClass" class="show-icon" :class="iconClass"></i>
      <n-popover
        width="400"
        placement="bottom"
        trigger="click"
        popper-class="top-popper"
      >
        <n-tabs v-model="iconType" type="card">
          <n-tab-pane label="默认图标" name="default">
            <div class="iconfont-list">
              <i
                v-for="(item, index) in icons"
                :key="index"
                :class="item"
                @click="valChange(item);handleShownPopover()"
              ></i>
            </div>
          </n-tab-pane>
          <n-tab-pane label="图标库选择" name="iconfont">
            <div class="iconfont-list" v-loading="loading">
              <i
                v-for="(item, index) in iconfonts"
                :key="index"
                :class="item"
                @click="valChange(item);handleShownPopover()"
              ></i>
            </div>
          </n-tab-pane>
        </n-tabs>

        <el-button
          slot="reference"
          circle
          type="primary"
          size="mini"
          icon="el-icon-edit"
          style="margin-right: 10px;"
          ref="referenceBtn"
        ></el-button>
      </n-popover>

      <el-button
        v-if="iconClass"
        circle
        type="danger"
        size="mini"
        icon="el-icon-delete"
        @click.stop="valChange('')"
      ></el-button>
    </div>
  </div>
</template>

<script>
import { icons } from './setting'
import { Popover } from 'element-ui'
import request from '@/request/request'
export default {
  components: {
    nPopover: Popover
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      icons,
      loading: false,
      iconClass: '',
      iconfonts: [],
      iconType: 'default'
    }
  },
  created() {},
  mounted() {
    this.handleGetIconfont()
  },
  destroyed() {
  },
  methods: {
    valChange(val) {
      this.iconClass = val
      const res = { ...this.elementProps, [this.propKey]: val }
      this.$emit('update', res)
    },
    handleShownPopover() {
      this.$nextTick(() => {
        this.$refs.referenceBtn.$el.click()
      })
    },
    handleGetIconfont() {
      this.loading = true
      request({
        url: `${this.$store.state.CODE.lowCodeStore.appSourcePrefix}apps/${this.$route.query.id}/iconfont/iconfont.json`,
        method: 'GET',
        header: {
          'Content-Type': 'application/json',
        },
      })
        .then((res) => {
          const iconfontData = res.data
          this.iconfonts = iconfontData.glyphs.map(icon => `${iconfontData.font_family} ${iconfontData.css_prefix_text}${icon.font_class}`)
        })
        .finally(() => {
          this.loading = false
        })
    },
  },

  computed: {},
  watch: {
    elementProps: {
      deep: true,
      immediate: true,
      handler() {
        this.iconClass = this.elementProps[this.propKey] || ''
      },
    },
  },
}
</script>
<style lang="less" scoped>
.icon_selector {
  height: 42px;
  .col {
    display: flex;
    align-items: center;
    .label {
      color: #48576a;
      font-size: 12px;
      min-width: 100px;
      padding-right: 10px;
    }
  }
  .show-icon {
    font-size: 25px;
    margin-right: 10px;
  }
}
.el-popover {
  .iconfont-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    height: 200px;
    overflow-y: auto;
    i {
      font-size: 25px;
      cursor: pointer;
      margin-right: 5px;
      margin-bottom: 5px;
    }
  }
}
</style>
