<template>
  <div class="reg-exp-config">
    <!-- <p class="title">验证配置</p> -->
    <div class="list-container">
      <div class="config-item" v-for="(item, index) in value" :key="index">
        <!-- <div class="config-item-col using-status">
                    <el-checkbox
                        v-model="item.using"
                        @change="(v) => handelUseStateChange(v, index)"
                    />
                </div> -->
        <div class="config-item-col reg-exp-name">{{ item.name }}</div>
        <div class="config-item-col regx-tools">
          <el-button
            type="text"
            style="color:#54A0FF"
            @click="handleEditRegExp(index, item)"
            >编辑</el-button
          >
          <el-button
            type="text"
            style="color:#54A0FF"
            @click="handleDeleteRegExp(index)"
            >删除</el-button
          >
          <!-- <i
            class="iconfont icon-edit"
            @click="handleEditRegExp(index, item)"
          />
          <i class="iconfont icon-delete" @click="handleDeleteRegExp(index)" /> -->
        </div>
      </div>
    </div>
    <div
      class="add-reg-exp-btn"
      style="cursor:pointer"
      v-popover:popover
      @click="handleAddRegExp"
    >
      +
    </div>
    <el-popover
      width="400"
      trigger="manual"
      ref="popover"
      v-model="popoverVisible"
    >
      <div class="variable-body">
        <n-form
          ref="variableForm"
          :model="variableForm"
          :rules="rules"
          inline
          label-width="85px"
          @submit.native.prevent
        >
          <n-form-item label="规则名称" required prop="name">
            <el-input v-model="variableForm.name" style="width:250px"></el-input>
          </n-form-item>
          <n-form-item label="自定义规则" required prop="reg">
            <el-input v-model="variableForm.reg">
              <template slot="append">
                <span
                  style="cursor:pointer"
                  class="select-reg-btn"
                  @click="handleSelectRegOpen"
                  >选择</span
                >
              </template>
            </el-input>
          </n-form-item>
          <n-form-item label="错误提示" prop="errorTips" required>
            <el-input v-model="variableForm.errorTips" style="width:250px"></el-input>
          </n-form-item>
        </n-form>
        <div style="text-align: right; margin: 0">
          <el-button type="primary" size="mini" @click="handleConfirm"
            >确认</el-button
          >
          <el-button size="mini" type="text" @click="popoverVisible = false"
            >取消</el-button
          >
        </div>
      </div>
    </el-popover>
    <select-reg-modal ref="selectRegModal" @confirm="handleSelectRegConfirm" />
  </div>
</template>

<script>
  import { getValidators, required } from '@/utils/validateUtils'
  import _ from 'lodash'

  import selectRegModal from '@/components/ConfigWrapper/components/reg-exp-config/selectRegModal'

  export default {
    components: {
      selectRegModal,
    },
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        variableForm: {
          name: '',
          reg: '',
          errorTips: '',
        },
        rules: {
          name: getValidators([
            {
              message: '请输入规则名',
              include: required,
              trigger: 'change',
            },
          ]),
          reg: getValidators([
            {
              message: '请输入正则表达式',
              include: required,
              trigger: 'change',
            },
            {
              message: '请输入正确格式(运算符前需加转义符“\\”)',
              include: function(val) {
                try {
                  new RegExp(val)
                } catch(err) {
                  console.error(err)
                  return true
                }
                return false
              },
              trigger: 'change',
            },
          ]),
          errorTips: getValidators([
            {
              message: '请输入错误提示',
              include: required,
              trigger: 'change',
            },
          ]),
        },
        popoverVisible: false,
        edtingIndex: undefined,
      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
        },
      },
    },
    methods: {
      // handelUseStateChange(v, index) {
      //     let tempValue = _.cloneDeep(this.value)
      //     tempValue[index].using = v
      //     const res = {
      //         ...this.elementProps,
      //         [this.propKey]: tempValue,
      //     }
      //     this.$emit("update", res)
      // },
      handleAddRegExp() {
        this.popoverVisible = true
        this.$nextTick()
        this.$refs.variableForm.resetFields()
        this.variableForm = {
          name: '',
          reg: '',
          errorTips: '',
        }
        this.edtingIndex = undefined
      },
      handleEditRegExp(index, value) {
        this.popoverVisible = true
        this.$nextTick()
        this.$refs.variableForm.resetFields()
        this.variableForm = _.cloneDeep(value)
        this.edtingIndex = index
      },
      handleDeleteRegExp(index) {
        let tempValue = _.cloneDeep(this.value)
        tempValue.splice(index, 1)
        const res = {
          ...this.elementProps,
          [this.propKey]: tempValue,
        }
        this.$emit('update', res)
      },
      handleConfirm() {
        this.$refs.variableForm.validate((valid) => {
          if (!valid) {
            return
          }
          let tempValue = _.cloneDeep(this.value)
          const variableForm = _.cloneDeep(this.variableForm)
          if (this.edtingIndex === undefined) {
            tempValue.push(variableForm)
            const res = {
              ...this.elementProps,
              [this.propKey]: tempValue,
            }
            this.$emit('update', res)
          } else {
            tempValue[this.edtingIndex] = variableForm
            const res = {
              ...this.elementProps,
              [this.propKey]: tempValue,
            }
            this.$emit('update', res)
          }
          this.popoverVisible = false
        })
      },
      // 打开选择正则表达式弹窗
      handleSelectRegOpen() {
        this.$refs.selectRegModal.show()
      },
      handleSelectRegConfirm(data) {
        this.variableForm = { ...data }
        this.$refs.selectRegModal.close()
      },
    },
  }
</script>

