<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="data-select-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="数据集配置"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="data-select" v-loading.lock='loading'>
      <data-set-config
        v-if='dialogVisible'
        ref='dataSetConfig'
        :request='{apiRequest, sqlRequest, staticData}'
        :type.sync='activeDataType'
        @response='handleResponse'
      ></data-set-config>
      <div class="data-select-item">
        <el-collapse class='config-collapse' v-model='activeCollapse'>
          <el-collapse-item name='config'>
            <template slot="title">
              <div class='config-collapse-title'>字段映射</div>
            </template>
            <div class="data-select-form data-content">
              <div class="data-config">
                <div class="data-table" :style='{width: activeDataType === "static" ? "100%" : "50%"}' v-loading='tableLoading'>
                  <el-collapse v-if='activeDataType !== "static"' class='config-collapse' v-model='activeDataCollapse'>
                    <el-collapse-item name='data'>
                      <template slot="title">
                        <div class='config-collapse-title'>变量映射</div>
                      </template>
                      <vxe-table
                        ref='dataVariablesTable'
                        size='mini'
                        :data="dataVariables"
                        border
                        style="width: 100%;"
                        header-cell-class-name="header-cell"
                      >
                        <vxe-table-column field="label" title="变量名">
                          <template #default="{row}">
                            <div>
                              <span style="color: red;" v-if="['dataList'].includes(row.key)">*</span>
                              <span>{{row.label}}</span>
                              <el-tooltip :content="getTip(row.key)" popper-class="tooltip-question" placement="top">
                                <i style='margin-left: 5px;' class="el-icon-question"></i>
                              </el-tooltip>
                            </div>
                          </template>
                        </vxe-table-column>
                        <vxe-table-column field="key" title="编码"></vxe-table-column>
                        <vxe-table-column field="path" title="数据路径" min-width="150px">
                          <template #header>
                            <span>数据路径</span>
                            <el-tooltip popper-class="tooltip-question" placement="top">
                              <div style="display: flex; flex-direction: column; align-items: center; justify-content: center;" slot="content">
                                <span style="margin: 10px;">请在响应体结构树上选中“表格数据”节点，点击拖拽至数据路径中</span>
                                <video
                                  style="width: 400px;"
                                  autoplay="autoplay"
                                  loop="loop"
                                  muted
                                  :src="$getImgPrefix('/video/field-map.mp4')"
                                ></video>
                              </div>
                              <i style='margin-left: 5px;' class="el-icon-question"></i>
                            </el-tooltip>
                          </template>
                          <template #default='{ row }'>
                            <div class='drop-zone-path' :data-key='row.key'>
                              <el-tooltip
                                v-if='row.path.length'
                                effect="dark"
                                :content="row.path.join('/')"
                                placement="top"
                              >
                                <el-tag
                                  closable
                                  @close='row.path = []'
                                  disable-transitions
                                >{{row.path[row.path.length - 1]}}</el-tag>
                              </el-tooltip>
                              <span v-else>请从右侧拖拽节点</span>
                            </div>
                          </template>
                        </vxe-table-column>
                      </vxe-table>
                    </el-collapse-item>
                  </el-collapse>
                  <el-collapse class='config-collapse' v-model='activeDataCollapse'>
                    <el-collapse-item name='column'>
                      <template slot="title">
                        <div class='config-collapse-title'>数据列映射</div>
                      </template>
                      <vxe-table
                        ref='dataColumnTable'
                        size='mini'
                        :data="dataColumn"
                        border
                        style="width: 100%"
                        header-cell-class-name="header-cell"
                      >
                        <vxe-table-column field="label" title="列标题">
                          <template #default='{ row }'>
                            <el-input v-model="row.label"></el-input>
                          </template>
                        </vxe-table-column>
                        <vxe-table-column field="key" title="列编码">
                          <template #default='{ row }'>
                            <el-select
                              v-model="row.key"
                              allow-create
                              clearable
                              filterable
                              default-first-option
                            >
                              <el-option
                                v-for="key in dataListKeys"
                                :key="key"
                                :title="key"
                                :value="key"
                              ></el-option>
                            </el-select>
                          </template>
                        </vxe-table-column>
                        <vxe-table-column field="path" title="数据路径" min-width="150px">
                          <template #default='{ row }'>
                            <div class='drop-zone-path' :data-id='row.key'>
                              <el-tooltip
                                v-if='row.path.length'
                                effect="dark"
                                :content="row.path.join('/')"
                                placement="top"
                              >
                                <el-tag
                                  closable
                                  @close='row.path = []'
                                  disable-transitions
                                >{{row.path[row.path.length - 1]}}</el-tag>
                              </el-tooltip>
                              <span v-else>请从右侧拖拽节点</span>
                            </div>
                          </template>
                        </vxe-table-column>
                      </vxe-table>
                    </el-collapse-item>
                  </el-collapse>
                </div>
                <div class="data-result" v-if='activeDataType !== "static"'>
                  <div class="title">
                    <div class="name">
                      响应体
                      <el-button size='mini' @click="showInputResponseDialog">手动录入</el-button>
                      <el-button size='mini' v-if='response' @click='handleClearResponse'>清除</el-button>
                    </div>
                    <el-button type="primary" :loading='jsonLoading' @click="handleAutoGenerate"
                    >一键生成数据列
                      <el-tooltip content="请在响应体结构树上选中“表格数据”节点，点击【一键生成数据列】按钮，系统会解析该节点下的所有子节点为表格的数据列" popper-class="tooltip-question" placement="top">
                        <i style='margin-left: 5px; color: black; font-size: 14px;' class="el-icon-question"></i>
                      </el-tooltip>
                    </el-button>
                  </div>
                  <div class="body">
                    <vue-json-pretty
                      v-show='response'
                      class="json-pretty no-select"
                      path="response"
                      :data="response"
                      :virtual='true'
                      :itemHeight='32'
                      v-model="selectedPath"
                      @click='copyPath'
                      selectableType="single"
                      :showSelectController="true"
                      :selectOnClickNode="true"
                    >
                    </vue-json-pretty>
                    <div v-if='!response' class='empty-text'>点击“测试”请求响应体</div>
                  </div>
                </div>
              </div>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
      <input-dialog @confirm="handleInputResponse" ref="inputDialog"></input-dialog>
      <div class="dnd-cover" v-show="dndCoverStyle" :style="dndCoverStyle"></div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" :loading='loading' @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import DataSetConfig from '@/components/DataSetConfig'
  import VueJsonPretty from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  import _ from 'lodash'
  import { isValidJsonString } from '@/utils/validateUtils'
  import {
    getNodeByElementId,
    getResetIdWidget,
    treeUtil,
  } from '@/utils/tree-utils'
  import {
    DELETE_LAYOUT_DATA,
    INIT_DYNAMIC_PROPS,
    UPDATE_LAYOUT_DATA,
  } from '@/config/storeKey'
  import { getLastVersion } from '@/config/module'
  import API from '@/api/application'
  import { getObjectTree, getPathList, getPathString } from '@/utils/format'
  import dnd from '@/mixins/dragAndDrop'
  import { sleep } from '@/utils'
  import { defaultKeys } from '@/config/widgets'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    name: 'TableData',
    components: {
      VueJsonPretty,
      DataSetConfig,
    },
    inject: ['dndOptions', ...pageInject],
    mixins: [dnd],
    data() {
      return {
        loading: false,
        activeCollapse: ['config'],
        activeDataCollapse: ['data', 'column'],
        dialogVisible: false,
        dataColumn: undefined,
        dataVariables: undefined,
        apiRequest: undefined,
        sqlRequest: undefined,
        staticData: undefined,
        selectedPath: undefined,
        responseOptions: [],
        response: undefined,
        dataListKeys: [],
        jsonLoading: false,
        tableLoading: false,
        activeDataType: ''
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      currentElement() {
        return getNodeByElementId(this.layoutData, this.dndOptions.selectedId)
      },
      currentProps() {
        return this.getElementPropsById(this.dndOptions.selectedId)
      },
    },
    methods: {
      open({ dataColumn, dataVariables, apiRequest, sqlRequest, staticData }) {
        this.dataColumn = dataColumn ? _.cloneDeep(dataColumn) : []
        this.dataVariables = dataVariables ? _.cloneDeep(dataVariables) : []
        this.staticData = _.cloneDeep(staticData) || undefined
        this.apiRequest = _.cloneDeep(apiRequest) || undefined
        this.sqlRequest = _.cloneDeep(sqlRequest) || undefined
        this.response = null
        this.selectedPath = undefined
        this.dialogVisible = true
        this.$nextTick(() => {
          this.handleInitDnD()
        })
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      async handleGenerateTableData() {
        const res = {
          dataColumn: this.dataColumn,
          dataVariables: this.dataVariables,
          apiRequest: this.apiRequest,
          sqlRequest: this.sqlRequest,
          staticData: this.staticData,
        }
        const preSlots = this.currentElement.children.filter(
          (item) =>
            ['slot'].includes(item.elementType) &&
            ['data'].includes(this.getElementPropsById(item.elementId).slotType)
        )
        const preSlotsKeys = preSlots.map(item => this.getElementPropsById(item.elementId).slotName)
        const dataColKeys = this.dataColumn.map(item => item.key)
        const deleteSlotKeys = preSlotsKeys.filter(key => !dataColKeys.includes(key))
        const deleteSlotIds = preSlots.filter(item => deleteSlotKeys.includes(this.getElementPropsById(item.elementId).slotName)).map(item => item.elementId)
        await this._storeDispatch(DELETE_LAYOUT_DATA, { elementIdList: deleteSlotIds })
        const needInitProps = []
        const children = []
        this.dataColumn.forEach((item) => {
          const slotName = item.key
          if (preSlotsKeys.includes(slotName) || !item.isUseSlot) {
            if (preSlotsKeys.includes(slotName)) {
              item.isUseSlot = true
            }
            return
          }
          const widget = {
            elementId: '',
            elementType: 'slot',
            elementVersion: getLastVersion('slot'),
            elementName: item.label,
            defaultProps: {
              slotName,
              slotType: 'data',
              canDropInsideTypes: ['text', 'tag', 'href', 'button'],
            },
            children: [
              {
                elementId: '',
                elementType: 'text',
                elementVersion: getLastVersion('text'),
                elementName: '文本',
                children: [],
                defaultProps: {
                  useSlotProps: true,
                  prop: slotName,
                },
              },
            ],
          }
          const newWidget = getResetIdWidget.call(this, widget)
          treeUtil.forEach([newWidget], (source) => {
            const {children, ...node} = source
            const props = {...node, ...(node.defaultProps || {}) }
            defaultKeys.forEach(key => {
              this.$delete(props, key)
            })
            needInitProps.push({
              elementId: node.elementId,
              props: _.cloneDeep(props),
            })
            defaultKeys.forEach(key => {
              this.$delete(source, key)
            })
          })

          children.push(newWidget)
        })
        this._storeCommit(INIT_DYNAMIC_PROPS, {
          dynamicPropsList: needInitProps
        })
        this._storeCommit(UPDATE_LAYOUT_DATA, {elementId: this.currentElement.elementId, layout: { children: [...this.currentElement.children, ...children] }})
        return Promise.resolve(res)
      },
      async confirm() {
        const { apiRequest, sqlRequest, staticData } = this.$refs.dataSetConfig.getResult()
        this.apiRequest = apiRequest
        this.sqlRequest = sqlRequest
        this.staticData = staticData
        this.loading = true
        await sleep(10)
        const path = this.dataVariables.find(item => item.key === 'dataList')?.path
        if (!path.length && this.activeDataType !== 'static') {
          this.$message.error('请配置变量映射 > 表格数据的数据路径！')
          this.loading = false
          return
        }
        this.handleSaveConfigTemp(path)
        const res = await this.handleGenerateTableData()
        this.loading = false
        this.$emit('confirm', res)
        this.dialogVisible = false
      },
      handleDataListPathChange(selectPath, key) {
        if (key === 'dataList') {
          const path = getPathString(selectPath)
          this.handleGenerateConfig(path)
        }
      },
      handleAutoGenerate() {
        if (!this.response) {
          this.$message.error('请点击测试按钮请求响应体数据或手动输入响应体数据')
          return
        }
        if (!this.selectedPath) {
          this.$message.error('请用鼠标点击下方数据区域选择一个数据字段')
          return
        }
        this.jsonLoading = true
        const path = this.selectedPath.replace('response.', '')
        const dataList = this.dataVariables.find((d) => d.key === 'dataList')
        const pathList = getPathList(path)
        const parentPathList = pathList.slice(0, pathList.length - 1)
        const data = _.get(this.response, parentPathList) || {}
        this.dataVariables = this.dataVariables.map(item => {
          if (typeof data[item.key] !== 'undefined') {
            return {...item, path: parentPathList.concat(item.key)}
          }
          switch(true) {
          case item.key === 'totalCount':
            if (typeof data.rowCount !== 'undefined') {
              return {...item, path: parentPathList.concat('rowCount')}
            }
            if (typeof data.total !== 'undefined') {
              return {...item, path: parentPathList.concat('total')}
            }
          }
          return item
        })
        if (dataList) {
          dataList.path = pathList
        }
        this.handleGenerateConfig(path)
      },
      async handleGenerateConfig(path) {
        const target = _.get(this.response, path)
        let res
        switch (true) {
        case typeof target === 'string':
          if (isValidJsonString(target)) {
            try {
              res = JSON.parse(target.trim())[0]
            } catch (e) {
              console.log(e)
            }
          }
          break
        case Array.isArray(target) && _.isObject(target[0]):
          res = target[0]
          break
        }
        this.jsonLoading = false
        if (!res) {
          this.$confirm('数据解析失败，请手动映射', '提示', {
            showCancelButton: false,
          })
          return
        }
        const pathList = getPathList(path)
        this.dataListKeys = Object.keys(res)
        if (this.dataColumn.length) {
          await this.$confirm(
            '此操作将覆盖数据列映射的配置，是否继续？',
            '提示'
          )
        }
        this.dataColumn = this.dataListKeys.map((key) => ({
          label: `${key}`,
          key,
          align: 'left',
          width: undefined,
          minWidth: 160,
          sortable: false,
          isHidden: false,
          isUseSlot: false,
          path: [...pathList, '0', key],
          formatter: undefined,
          fixed: undefined,
        }))
      },
      handleSaveConfigTemp(path) {
        const target = path.length ? _.get(this.response, path) : this.response
        let res
        switch (true) {
        case typeof target === 'string':
          if (isValidJsonString(target)) {
            try {
              res = JSON.parse(target.trim())[0]
            } catch (e) {
              console.log(e)
            }
          }
          break
        case Array.isArray(target) && _.isObject(target[0]):
          res = target[0]
          break
        }
        if (!res) {
          return
        }
        this.$http({
          url: API.appDesignJsonDataConfigSave,
          data: {
            appDesignId: Number(this.$route.query.vid),
            preferences: [
              {
                key: `${this.currentElement.elementId}.$currentRow`,
                value: JSON.stringify(res) || ''
              }
            ]
          }
        })
      },
      handleResponse({ response, responseOptions, isStatic }) {
        if (isStatic) {
          this.handleStaticResponse({response, responseOptions})
          return
        }
        this.response = response
        this.responseOptions = responseOptions
      },
      async handleStaticResponse({ response, responseOptions }) {
        if (!response) {
          return
        }
        if (!Array.isArray(response)) {
          this.$message.error('数据格式不正确, 需传入数组，且数组内每一项都为对象，如[{"name": "张三", "age": 28}]')
          return
        }
        const res = response[0]
        if (!_.isObject(res)) {
          this.$message.error('数据格式不正确, 需传入数组，且数组内每一项都为对象，如[{"name": "张三", "age": 28}]')
          return
        }
        if (this.dataColumn.length) {
          await this.$confirm(
            '此操作将覆盖数据列映射的配置，是否继续？',
            '提示'
          )
        }
        const dataList = this.dataVariables.find((d) => d.key === 'dataList')
        if (dataList) {
          dataList.path = []
          const pathList = []
          this.dataListKeys = Object.keys(res)
          this.dataColumn = this.dataListKeys.map((key) => ({
            label: `${key}`,
            key,
            align: 'left',
            width: undefined,
            minWidth: 160,
            sortable: false,
            isHidden: false,
            isUseSlot: false,
            path: [...pathList, '0', key],
            formatter: undefined
          }))
        }
        this.staticData = _.cloneDeep(response)
      },
      copyPath() {
        // copyText(this.selectedPath, '路径已复制')
      },
      showInputResponseDialog() {
        this.$refs.inputDialog.open({type: 'object', data: this, key: 'response'})
      },
      handleInputResponse(value) {
        this.response = value
        this.responseOptions = getObjectTree(value)
      },
      handleClearResponse() {
        this.response = null
        this.responseOptions = []
      },
      handleDrop(event) {
        const { id, key } = event.target.dataset
        if (id) {
          const current = this.dataColumn.find(item => item.key === id)
          const path = getPathList(this.selectedPath)
          current.path = path.slice(1)
        } else if (key) {
          const current = this.dataVariables.find(item => item.key === key)
          const path = getPathList(this.selectedPath)
          current.path = path.slice(1)
        }
      },
      getTip(key) {
        switch (key) {
          case 'dataList':
            return '表格数据字段，需传入的类型为对象数组，也可传入字符串，会尝试解析为对象数组\n建议使用右侧的一键映射数据列进行设置'
          case 'totalCount':
            return '表格总行数字段，需传入数字类型'
        }
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      }
    },
  }
</script>

