<template>
  <el-dialog
    title="格式化函数"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="1000px"
    top="5vh"
    :before-close="handleClose"
    custom-class="handler-dialog low-code-dialog"
  >
    <div class="dialog-content">
      <js-config ref='jsConfig' v-model='functionId' :pure-function='true' :default-arguments='defaultArguments'></js-config>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="clear">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import JsConfig from '@/components/JsConfig'
  import { FUNCTION_FLAG } from '@/config'
  import _ from 'lodash'

  export default {
    name: 'DataFormat',
    components: {
      JsConfig,
    },
    data() {
      return {
        dialogVisible: false,
        functionId: undefined,
        defaultArguments: [],
      }
    },
    methods: {
      open({ formatter, defaultArguments }) {
        if (formatter) {
          const formatterCopy = _.cloneDeep(formatter)
          this.functionId = formatterCopy.id.split(FUNCTION_FLAG)[1]
        }
        this.defaultArguments = defaultArguments || []
        this.dialogVisible = true
      },
      handleClose(done) {
        this.clear()
        done()
      },
      clear() {
        this.functionId = undefined
        this.dialogVisible = false
      },
      async confirm() {
        let formatter
        await this.$refs.jsConfig.confirm()
        formatter = {
          id: `${FUNCTION_FLAG}${this.functionId}`
        }
        this.$emit('confirm', formatter)
        this.clear()
      },
    },
  }
</script>

