<template>
  <div>
    <div class="widget-config-item">
      <div class="name">{{ name }}</div>
      <div class="container">
        <el-button-group class="flex-button-group">
          <el-button
            size="mini"
            class="config-item-button"
            v-for="item in options"
            :key="item.value"
            @click="value = item.value"
            :type="item.value === 'radius' ? isRoseType() : getType(item.value)"
          >
            <span>
              {{ item.label }}
            </span>
          </el-button>
        </el-button-group>
      </div>
    </div>
    <div v-if="isRoseType()" class="widget-config-item">
      <div class="name">南丁格尔类型</div>
      <div class="container">
        <el-button-group class="flex-button-group">
          <el-button
            size="mini"
            class="config-item-button"
            v-for="item in roseOptions"
            :key="item.value"
            @click="value = item.value"
            :type="getType(item.value)"
          >
            <span>
              {{ item.label }}
            </span>
          </el-button>
        </el-button-group>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = {
          ...this.elementProps,
          [this.propKey]: value,
          innerRadius: value === 'ring' ? { value: 45, unit: '%' } : { value: 0, unit: '%' }
        }
        this.$emit('update', res)
      },
    },
  },
  data() {
    return {
      roseType: '',
      options: [
        {
          label: '标准',
          value: 'default',
        },
        {
          label: '南丁格尔',
          value: 'radius',
        },
        {
          label: '圆环图',
          value: 'ring',
        },
      ],
      roseOptions: [
        {
          label: '半径模式',
          value: 'radius',
        },
        {
          label: '面积模式',
          value: 'area',
        },
      ],
    }
  },
  methods: {
    getType(value) {
      return this.value.includes(value) ? 'primary' : 'default'
    },
    isRoseType() {
      return ['radius', 'area'].includes(this.value) ? 'primary' : undefined
    },
  },
}
</script>

