<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-switch v-model="isChecked" @change="switchChange"></el-switch>
    <div class="fill-div"></div>
    <el-popover v-if="value" placement="right" width="320" trigger="click">
      <el-form class="setting-form" label-position="left" label-width="120px">
        <el-form-item label="同序列柱间距离">
          <el-input-number
            v-model="value.barGap.value"
          ></el-input-number>
          {{ value.barGap.unit }}
        </el-form-item>
        <el-form-item label="不同序列柱间距离">
          <el-input-number
            v-model="value.barCategoryGap.value"
          ></el-input-number>
          <Select
            v-model="value.barCategoryGap.unit"
            :options="options"
            style="width: 60px;"
            :clearable="false"
          ></Select>
        </el-form-item>
      </el-form>
      <i slot="reference" class="el-icon-s-tools"></i>
    </el-popover>
  </n-form-item>
</template>

<script>
import Select from '@/components/Select'
export default {
  components: {
    Select,
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      options: [
        {
          label: 'px',
          value: '',
        },
        {
          label: '%',
          value: '%',
        },
      ],
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
    isChecked() {
      return !!this.value
    }
  },
  methods: {
    switchChange(value) {
      this.value = value
        ? {
            barGap: { value: 30, unit: '%' },
            barCategoryGap: { value: 20, unit: '%' },
          }
        : false
    },
  },
  mounted() {},
}
</script>

