<template>
  <div class="option-config">
    <div
      v-if="popoverVisible"
      class="option-config-model"
      @click="popoverVisible = false"
    ></div>
    <div class="mode-select">
      <el-radio-group
        v-model="dataOrigin"
        fill="#3f92fe"
        @change="handleDataOriginChange"
      >
        <el-radio-button label="static">选项配置</el-radio-button>
        <el-radio-button label="dynamic">动态数据</el-radio-button>
      </el-radio-group>
    </div>
    <div class="static-set-cont" v-show="dataOrigin == 'static'">
      <div v-show="!sourceMode">
        <div class="list-container">
          <div class="inner-content">
            <div class="title">
              <span class="col-title">选项名</span>
              <span class="key">{{['dropdown'].includes(optionkey) ? '操作' : '默认选中'}}</span>
            </div>
            <draggable
              v-model="staticData"
              forceFallback="true"
              group="option"
              animation="300"
              handle=".el-icon-rank"
            >
              <transition-group>
                <div
                  class="content"
                  :style="{ opacity: item.disabled ? '0.5' : '1' }"
                  v-for="(item, index) in staticData"
                  :key="item.value + '-' + index"
                >
                  <i class="icon el-icon-rank" />
                  <span class="label">{{ item.label }}</span>
                  <span class="key" v-if="!['dropdown'].includes(optionkey)">
                    <el-checkbox
                      v-model="item.isDefault"
                      @change="(v) => handelUseStateChange(v, index)"
                    />
                  </span>
                  <div class="caozuo">
                    <el-button
                      type="text"
                      @click="handleEditOption(index, item)"
                      >编辑
                    </el-button>
                    <el-button type="text" @click="handleDeleteOption(index)"
                      >删除
                    </el-button>
                  </div>
                </div>
              </transition-group>
            </draggable>
          </div>
        </div>

        <el-button class="option-btn" type="text" v-popover:popover @click="handleAddOption">
          <i class="iconfont el-icon-circle-plus-outline"></i>
          添加选项
        </el-button>
        <el-button
          type="text"
          class="option-btn"
          @click="sourceMode = true"
        >
          <i class="iconfont icon-jiaoben"></i>
          源码模式
        </el-button>
      </div>
      <div v-if="sourceMode" class="source-editor">
        <static-config
          show-cancel
          :static-data.sync="staticData"
          @response="handleStaticResponse"
          @cancel="cancel"
        />
      </div>
      <el-popover
        width="300"
        trigger="manual"
        ref="popover"
        v-model="popoverVisible"
      >
        <div class="variable-body">
          <n-form
            class="popover-config-form"
            ref="variableForm"
            :model="variableForm"
            :rules="rules"
            inline
            label-width="85px"
            @submit.native.prevent
          >
            <n-form-item v-if="!['dropdown'].includes(optionkey)" label="默认选中">
              <el-switch v-model="variableForm.isDefault"></el-switch>
            </n-form-item>
            <n-form-item label="显示值" prop="label">
              <el-input v-model="variableForm.label"></el-input>
            </n-form-item>
            <n-form-item label="选项值" prop="value">
              <el-input clearable v-model="variableForm.value"></el-input>
            </n-form-item>
            <n-form-item v-if="['hasIcon'].includes(optionkey)" prop="icon">
              <template slot="label">
                <n-tooltip value="该选项需设置类型为“按钮组”后才会生效"></n-tooltip>  图标
              </template>
              <div class="icon_selector">
                <div class="col">
                  <span></span>
                  <i v-if="variableForm.icon" class="show-icon" :class="variableForm.icon"></i>
                  <n-popover
                    width="400"
                    placement="bottom"
                    trigger="click"
                    popper-class="top-popper"
                  >
                    <n-tabs v-model="iconType" type="card">
                      <n-tab-pane label="默认图标" name="default">
                        <div class="iconfont-list">
                          <i
                            v-for="(item, index) in icons"
                            :key="index"
                            :class="item"
                            @click="iconChange(item);handleShownPopover()"
                          ></i>
                        </div>
                      </n-tab-pane>
                      <n-tab-pane label="图标库选择" name="iconfont">
                        <div class="iconfont-list" v-loading="loading">
                          <i
                            v-for="(item, index) in iconfonts"
                            :key="index"
                            :class="item"
                            @click="iconChange(item);handleShownPopover()"
                          ></i>
                        </div>
                      </n-tab-pane>
                    </n-tabs>
                    <el-button
                      slot="reference"
                      circle
                      type="primary"
                      size="mini"
                      icon="el-icon-edit"
                      style="margin-right: 10px;"
                      ref="referenceBtn"
                    ></el-button>
                  </n-popover>

                  <el-button
                    v-if="variableForm.icon"
                    circle
                    type="danger"
                    size="mini"
                    icon="el-icon-delete"
                    @click.stop="iconChange('')"
                  ></el-button>
                </div>
              </div>
            </n-form-item>
            <n-form-item label="是否禁用" prop="disabled">
              <el-switch v-model="variableForm.disabled"></el-switch>
            </n-form-item>
          </n-form>
          <div style="text-align: right; margin: 0">
            <el-button type="primary" size="mini" @click="handleConfirm"
              >确认</el-button
            >
            <el-button size="mini" type="text" @click="popoverVisible = false"
              >取消</el-button
            >
          </div>
        </div>
      </el-popover>
      <select-reg-modal
        ref="selectRegModal"
        @confirm="handleSelectRegConfirm"
      />
    </div>
    <div v-show="dataOrigin == 'dynamic'" class="dynamic-btn">
      <data-selector
        name=""
        btnText="数据集配置"
        use-type="options"
        v-bind="$props"
        @update="dataSelectUpdate"
      ></data-selector>
    </div>
  </div>
</template>

<script>
import { getValidators, required } from '@/utils/validateUtils'
import { icons } from '../icon-select/setting'
import _ from 'lodash'
import draggable from 'vuedraggable'
import dataSelector from '../config-data'
import StaticConfig from '@/components/StaticConfig/index'
import { Popover } from 'element-ui'
import request from '@/request/request'
export default {
  components: {
    draggable,
    dataSelector,
    StaticConfig,
    nPopover: Popover
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
    },
  },
  data() {
    return {
      icons,
      dataOrigin: 'static',
      sourceMode: false,
      variableForm: {
        isDefault: false,
        label: '',
        value: undefined,
        icon: ''
      },
      rules: {
        value: [
          {
            required: true,
            trigger: 'change',
            validator: (rule, value, callback) => {
              const { staticData, edtingIndex } = this
              let judge
              if(edtingIndex === undefined) {
                judge = staticData.some(item => item.value === value)
              } else {
                const index = staticData.findIndex(item => item.value === value)
                judge = index !== -1 && index !== edtingIndex
              }
              if(judge) {
                callback(new Error('该选项值已存在'))
              } else {
                callback()
              }
            }
          },
        ]
      },
      popoverVisible: false,
      edtingIndex: undefined,
      loading: false,
      iconfonts: [],
      iconType: 'default'
    }
  },
  computed: {
    multiple() {
      const { elementType, multiple } = this.elementProps
      if(elementType === 'radio-group') {
        return false
      }
      if(Object.prototype.hasOwnProperty.call(this.elementProps, 'multiple')) {
        return multiple
      }
      return true
    },
    value() {
      return this.elementProps[this.propKey]
    },
    staticData: {
      get() {
        return this.elementProps['staticData']
      },
      set(value) {
        const res = { ...this.elementProps, staticData: _.cloneDeep(value) }
        this.$emit('update', res)
      },
    }
  },
  methods: {
    dataSelectUpdate(data) {
      this.$emit('update', {
        ...data,
        staticData: this.staticData
      })
    },
    updateOptions(staticData) {
      const res = {
        ...this.elementProps,
        staticData,
      }
      this.$emit('update', res)
    },
    handleDataOriginChange(v) {
      this.dataOrigin = v
    },
    handelUseStateChange(v, index) {
      let tempStaticData = _.cloneDeep(this.staticData || [])
      // 不支持多选时吧其他选项设为false
      if (!this.multiple) {
        tempStaticData.some((item) => {
          item.isDefault = false
        })
      }
      tempStaticData[index].isDefault = v
      this.updateOptions(tempStaticData)
    },
    handleAddOption() {
      this.popoverVisible = true
      this.$nextTick()
      this.$refs.variableForm.resetFields()
      this.variableForm = {
        isDefault: false,
        label: '',
        value: undefined,
        icon: ''
      }
      this.edtingIndex = undefined
    },
    handleEditOption(index, value) {
      this.popoverVisible = true
      this.$nextTick()
      this.$refs.variableForm.resetFields()
      this.variableForm = _.cloneDeep(value)
      this.edtingIndex = index
    },
    handleDeleteOption(index) {
      let tempStaticData = _.cloneDeep(this.staticData)
      tempStaticData.splice(index, 1)
      this.updateOptions(tempStaticData)
    },
    handleConfirm() {
      this.$refs.variableForm.validate((valid) => {
        if (!valid) {
          return
        }
        let tempStaticData = _.cloneDeep(this.staticData || [])
        // 如果添加或编辑的这一项是默认选中， 先将其他项的默认选中关闭
        if (this.variableForm.isDefault) {
          tempStaticData.some((item) => {
            item.isDefault = false
          })
        }
        if (this.edtingIndex === undefined) {
          tempStaticData.push(this.variableForm)
        } else {
          tempStaticData[this.edtingIndex] = this.variableForm
        }
        this.updateOptions(_.cloneDeep(tempStaticData))
        this.popoverVisible = false
      })
    },
    // 打开选择正则表达式弹窗
    handleSelectRegOpen() {
      this.$refs.selectRegModal.show()
    },
    handleSelectRegConfirm(data) {
      this.variableForm = { ...data }
      this.$refs.selectRegModal.close()
    },
    async handleStaticResponse({ response, responseOptions }) {
      if (!response) {
        return
      }
      if (!Array.isArray(response)) {
        this.$message.error('数据格式不正确')
        return
      }
      const res = response[0]
      if (!_.isObject(res)) {
        this.$message.error('数据格式不正确')
        return
      }
      this.staticData = _.cloneDeep(response)
      this.sourceMode = false
    },
    cancel() {
      this.sourceMode = false
    },
    iconChange(val) {
      this.variableForm.icon = val
    },
    handleShownPopover() {
      this.$nextTick(() => {
        this.$refs.referenceBtn.$el.click()
      })
    },
    handleGetIconfont() {
      this.loading = true
      request({
        url: `${this.$store.state.CODE.lowCodeStore.appSourcePrefix}apps/${this.$route.query.id}/iconfont/iconfont.json`,
        method: 'GET',
        header: {
          'Content-Type': 'application/json',
        },
      })
        .then((res) => {
          const iconfontData = res.data
          this.iconfonts = iconfontData.glyphs.map(icon => `${iconfontData.font_family} ${iconfontData.css_prefix_text}${icon.font_class}`)
        })
        .finally(() => {
          this.loading = false
        })
    },
  },
  mounted() {
    this.handleGetIconfont()
    const { apiRequest, sqlRequest } = this.elementProps
    this.dataOrigin = apiRequest || sqlRequest ? 'dynamic' : 'static'
  },
}
</script>

