<template>
  <div>
    <n-form-item class="config-item">
      <template slot="label">
        <span>{{ name }}</span>
        <el-tooltip
          :content="tip"
          popper-class="tooltip-question"
          placement="top"
        >
          <i style="margin-left: 5px" class="el-icon-question"></i>
        </el-tooltip>
      </template>
      <div style="display: flex;align-items: center;">
        <el-button v-if="!value" type="primary" @click="chartConfigOpen"
          >配置</el-button
        >
        <template v-else>
          <el-button type="text" @click="chartConfigOpen">代码编辑</el-button>
          <i class="iconfont icon-delete" style="margin-left: 15px" @click="value = ''"></i>
        </template>
      </div>
    </n-form-item>
    <jsFormat
      title="自定义配置"
      ref="jsFormat"
      useType="chartConfig"
      @confirm="chartConfigConfirm"
      :defaultArguments="defaultArguments"
      :tips="codeTips"
    ></jsFormat>
  </div>
</template>

<script>
import jsFormat from '@/components/jsFormat'
import { FUNCTION_FLAG } from '@/config'
import trigger from '@/renderer/tools/trigger'
import _ from 'lodash'
export default {
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementId: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
    },
    vue: {
      type: String,
    },
  },
  data() {
    return {
      tip: '对于一些无法配置的属性, 可以直接写代码覆盖默认配置\n详情查阅echarts文档: https://echarts.apache.org/zh/option.html#title',
      defaultArguments: [
        {
          key: 'options',
          value: undefined,
          test: {},
          name: '图表配置',
          desc: '图表预设的配置',
          definedTest: true,
        },
      ],
      codeTips: `支持JS原生语法，使用“参数名”进行取值, 使用return返回结果，示例JS：<Br>
      // 隐藏图表标题<Br>
      options.title.show = false<Br>
      // 图例名称格式化<Br>
      options.legend.formatter = function (name) {<Br>
        return 'Legend ' + name;<Br>
      }<Br>
      return options`,
    }
  },
  components: {
    jsFormat,
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = {
          ...this.elementProps,
          [this.propKey]: value,
        }
        this.$emit('update', res)
      },
    },
  },
  methods: {
    chartConfigOpen() {
      this.defaultArguments[0].test = _.cloneDeep(
        trigger.getRef(this.elementId).chartOptions || {}
      )
      this.$refs.jsFormat.open({
        id: this.value.replace(FUNCTION_FLAG, ''),
      })
    },
    chartConfigConfirm(id) {
      this.value = `${FUNCTION_FLAG}${id}`
    },
    chartDataConfirm(res) {
      this.$emit('update', {
        ...this.elementProps,
        ...res,
      })
    },
  },
}
</script>

