  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 8px;
  margin-right: 0;
  .el-form-item__content {
    flex: 1;
    & > * {
      width: 100%;
    }
  }
  .el-form-item__label {
    min-width: 100px;
  }
}
</style>
