<template>
  <div class="widget-config-item">
    <n-form-item
      v-if='isShowPropsSwitch'
      class="config-item"
      prop="useSlotProps"
    >
      <template slot='label'>
        <span>动态数据</span>
        <el-tooltip content="设置为开表示组件数据受外部影响，如表单、插槽" popper-class="tooltip-question" placement="top">
          <i style='margin-left: 5px;' class="el-icon-question"></i>
        </el-tooltip>
      </template>
      <el-switch v-model="useSlotProps"></el-switch>
    </n-form-item>
    <n-form-item v-if='(useSlotProps && isInSlot) || (isFormItem && isInForm && useSlotProps)' class="config-item" :prop="propKey" :error="propError" :style="propError ? 'margin-bottom: 18px;' : ''">
      <template slot='label'>
        <span>字段</span>
        <el-tooltip content="当前组件所在的表单域或插槽中所关联的字段名" popper-class="tooltip-question" placement="top">
          <i style='margin-left: 5px;' class="el-icon-question"></i>
        </el-tooltip>
      </template>
      <el-input :placeholder="placeholder" clearable v-model.trim="inputValue" @change='handleChange'></el-input>
    </n-form-item>
  </div>
</template>

<script>
  import _ from 'lodash'
  import { getParentNodeByElementId, getParentNodeListByElementId } from '@/utils/tree-utils'
  import { formTypes } from '@/config/widgets'
  import { pageInject } from '@/renderer/tools/pageStore'

  export default {
    inject: [...pageInject],
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        requiredArr: [],
        inputValue: undefined,
      }
    },
    computed: {
      propError() {
        if(this.inputValue && this.inputValue.indexOf('.') !== -1 ) {
          return '字段名不能包含“ . ”号'
        }
        return false
      },
      layoutData: {
        get() {
          return this.getStore().layoutData
        },
      },
      isFormItem() {
        return formTypes.includes(this.elementProps.elementType)
      },
      isInSlot() {
        const parent = getParentNodeByElementId(
          this.layoutData,
          this.elementProps.elementId
        )
        return parent && ['slot'].includes(parent.elementType) && ['data'].includes(this.getElementPropsById(parent.elementId).slotType)
      },
      isInForm() {
        const parentList = getParentNodeListByElementId(
          this.layoutData,
          this.elementProps.elementId
        )
        return parentList && parentList.some(item => ['form-container'].includes(item.elementType))
      },
      isShowPropsSwitch() {
        return (this.isFormItem && this.isInForm) || (!this.isFormItem && this.isInSlot)
      },
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
      },
      useSlotProps: {
        get() {
          return this.elementProps.useSlotProps
        },
        set(value) {
          const res = { ...this.elementProps, useSlotProps: value }
          this.$emit('update', res)
        },
      },
    },
    watch: {
      value: {
        deep: true,
        immediate: true,
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.inputValue = n
        }
      }
    },
    methods: {
      handleChange(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      }
    }
  }
</script>

<style lang="less" scoped>
  div.widget-config-item::v-deep {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .name {
      font-family: PingFangSC-Medium;
      font-size: 12px;
      color: #555555;
      letter-spacing: 0;
      font-weight: 500;
      margin-bottom: 5px;
      min-width: 100px;
    }

    .required::before {
      content: '*';
      color: red;
    }

    div.config-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 8px;

      .el-form-item__label {
        min-width: 100px;
      }

      .el-form-item__content {
        flex: 1;
      }
    }
  }
</style>
