<template>
  <div>
    <el-form :model="{ value }">
      <n-table border :data="value" style="width: 100%">
        <n-table-column
          label="暴露函数名"
          width="auto"
        >
          <template slot-scope="scope">
            <el-form-item :prop="'value.' + scope.$index + '.customName'"  :rules="[{ required: true, message: '函数名不能为空', trigger: 'change', validator: validatePass}]">
              <el-input v-model="scope.row.customName" placeholder="输入函数名称" clearable></el-input>
            </el-form-item>
          </template>
        </n-table-column>
        <n-table-column
          prop="name"
          label="源事件名"
          width="auto"
        >
        </n-table-column>
        <n-table-column
          label="操作"
          width="60px"
        >
          <template slot-scope="scope">
            <ul>
              <li>
                <el-button size="mini" type="text" @click="editFunctions(scope.row.id)">编辑</el-button>
              </li>
              <li>
                <el-button size="mini" type="text" style="color: red;" @click="deleteFunctions(scope.$index)">删除</el-button>
              </li>
            </ul>
          </template>
        </n-table-column>
      </n-table>
    </el-form>
    <div
      class="add-btn-primary"
      style="margin-top: 10px"
      v-popover:popover
      @click="addFunctions"
    >
      +
    </div>
    <event-select
      ref="eventSelect"
      @confirm="handleEventSelectConfirm"
    ></event-select>
  </div>
</template>

<script>
import elementEditor from '@/mixins/elementEditor'
import _ from 'lodash'
export default {
  mixins: [elementEditor],
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
    },
    vue: {
      type: String,
    },
    tip: {
      type: String,
    },
  },
  data() {
    return {
      validatePass(rule, value, callback) {
        if(value && value.trim()) {
          callback()
        } else {
          callback(new Error())
        }
      }
    }
  },
  components: {},
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = {
          ...this.elementProps,
          [this.propKey]: value,
        }
        this.$emit('update', res)
      },
    },
  },
  watch: {},
  methods: {
    addFunctions() {
      this.$refs.eventSelect.open()
    },
    editFunctions(id) {
      this.$refs.eventSelect.open({ ...this.elementProps, selectedId: id })
    },
    deleteFunctions(index) {
      this.value.splice(index, 1)
    },
    handleEventSelectConfirm({ id, name }, editId) {
      // 编辑
      if(editId) {
        const target = this.value.find(item => item.id === editId)
        target.id = id
        target.name = name
      }
      // 新增
      else {
        const target = this.value.find(item => item.id === id)
        if(target) {
          target.customName = name
        } else {
          this.value.push({ id, name, customName: name })
        }
      }
    },
  },
}
</script>

