<template>
  <div class='js-code'>
    <!-- <div class="full-screen-control">
      <i v-if="isMaximum" class="iconfont icon-liuchengtu-quxiaoquanping" title="点击缩小" @click="minEditor"></i>
      <i v-else class="iconfont icon-quanping2" title="点击放大" @click="maxEditor"></i>
    </div> -->
    <div class="highlight-code" ref="codeEditor" :class="{ 'custom-class': customClass }" v-loading.lock="loading"></div>
    <ChangeHeight v-if="resizable" @heightChange="handleHeightChange" />
  </div>
</template>

<script>
  import _ from 'lodash'
  import { JS_FLAG, UTILS_FUNCTION } from '@/config'
  import { runCodeInWorker } from '@/utils'
  import { formatTime } from '@/utils/format'
  import { utils } from '../FunctionSelect/utils'
  import ChangeHeight from '@/components/ChangeHeight'

  export default {
    name: 'JsCode',
    components: {
      ChangeHeight
    },
    props: {
      value: {
        type: String,
      },
      paramsData: {
        type: Array,
        default: () => [],
      },
      resizable: {
        type: Boolean,
        default: false
      },
      customClass: {
        type: Boolean,
        default: false
      },
      useType: {
        type: String
      }
    },
    data() {
      return {
        loading: false,
        hints: [],
        monacoInstance: null,
        provider: null,
        logList: [],
        code: '',
        isMaximum: false,
        originSize: {
          width: '',
          height: ''
        }
      }
    },
    computed: {
      dynamicHints() {
        return this.paramsData.map((item) => item.key).filter(item => item)
      },
    },
    watch: {
      paramsData: {
        handler(n, o) {
          if (n.length) {
            this.initHints()
          }
        },
        deep: true,
        immediate: true,
      },
      value: {
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.code = n
          this.monacoInstance?.setValue(n)
        },
        immediate: true
      }
    },
    methods: {
      initHints() {
        this.hints = Array.from(new Set(this.dynamicHints)).sort()
      },
      dispose() {
        if (this.monacoInstance) {
          if (this.monacoInstance.getModel()) {
            this.monacoInstance.getModel().dispose()
          }
          this.monacoInstance.dispose()
          this.monacoInstance = null
          if (this.provider) {
            this.provider.dispose()
            this.provider = null
          }
        }
      },
      async init() {
        const target = this.$refs.codeEditor
        if (!target) {
          return
        }
        this.loading = true
        this.code = this.value
        const src = [
          import('monaco-editor/esm/vs/editor/editor.api.js'),
          import('monaco-editor/esm/vs/basic-languages/javascript/javascript.contribution'),
        ]
        const [monaco] = await Promise.all(src)
        const that = this
        this.dispose()
        const createCompleters = (textUntilPosition) => {
          //过滤特殊字符
          const _textUntilPosition = textUntilPosition
            .replace(/[\*\[\]@\$\(\)]/g, '')
            .replace(/(\s+|\.)/g, ' ')
          //切割成数组
          const arr = _textUntilPosition.split(' ')
          //取当前输入值
          const activeStr = arr[arr.length - 1]
          //获得输入值的长度
          const len = activeStr.length

          //获得编辑区域内已经存在的内容
          const rexp = new RegExp('([^\\w]|^)' + activeStr + '\\w*', 'gim')
          const match = that.value.match(rexp)
          const _hints = !match
            ? []
            : match.map((ele) => {
                const rexp = new RegExp(activeStr, 'gim')
                const search = ele.search(rexp)
                return ele.substr(search)
              })

          //查找匹配当前输入值的元素
          const hints = Array.from(new Set([...that.hints, ..._hints]))
            .sort()
            .filter((ele) => {
              let rexp = new RegExp(ele.substr(0, len), 'gim')
              return (match && match.length === 1 && ele === activeStr) ||
                ele.length === 1
                ? false
                : activeStr.match(rexp)
            })
          //添加内容提示
          return hints.map((ele) => {
            return {
              label: ele,
              kind:
                that.hints.indexOf(ele) > -1
                  ? monaco.languages.CompletionItemKind.Keyword
                  : monaco.languages.CompletionItemKind.Text,
              documentation: ele,
              insertText: ele,
            }
          })
        }
        this.provider = monaco.languages.registerCompletionItemProvider('javascript', {
          // triggerCharacters: ['$'],
          provideCompletionItems(model, position) {
            const textUntilPosition = model.getValueInRange({
              startLineNumber: position.lineNumber,
              startColumn: 1,
              endLineNumber: position.lineNumber,
              endColumn: position.column,
            })
            const suggestions = createCompleters(textUntilPosition)
            return {
              suggestions,
            }
          },
        })

        // 初始化编辑器实例
        this.monacoInstance = monaco.editor.create(target, {
          value: this.code,
          language: 'javascript',
          theme: 'vs',
          folding: false,
          foldingHighlight: true,
          foldingStrategy: 'indentation',
          showFoldingControls: 'always',
          disableLayerHinting: true,
          emptySelectionClipboard: false,
          selectionClipboard: false,
          automaticLayout: true,
          codeLens: false,
          scrollBeyondLastLine: false,
          colorDecorators: true,
          accessibilitySupport: 'off',
          lineNumbers: 'on',
          lineNumbersMinChars: 5,
          enableSplitViewResizing: false,
          roundedSelection: false,
          overviewRulerBorder: false,
          minimap: {
            enabled: true,
          },
          autoIndent: 'full',
          readOnly: false,
          tabSize: 2,
        })

        // 监听编辑器content变化事件
        // this.monacoInstance.onDidChangeModelContent(
        //   _.debounce(
        //     () => {
        //       if (!this.monacoInstance) {
        //         return
        //       }
        //       this.code = this.monacoInstance.getValue()
        //     },
        //     500,
        //     { leading: false, trailing: true }
        //   )
        // )
        this.monacoInstance.onDidFocusEditorText(() => {
          this.$emit('focus-change', true)
        })
        this.monacoInstance.onDidBlurEditorText(() => {
          if (!this.monacoInstance) {
            return
          }
          this.code = this.monacoInstance.getValue()
          this.$emit('input', this.code)
          this.$emit('focus-change', false)
        })
        this.loading = false
      },
      getParams() {
        const params = this.paramsData.filter((d) => d.key)
        return Object.fromEntries(
          params.map((d) => {
            if(d.value?.indexOf(JS_FLAG) === 0) {
              return [d.key, () => {}]
            }
            if(d.value?.indexOf(UTILS_FUNCTION) === 0) {
              const currentUtil = utils.find(item => item.id === d.value)
              return [d.key, currentUtil?.code || d.value]
            }
            return [d.key, d.test]
          })
        )
      },
      async handleTest(isSubmit = false, callback = () => {}) {
        this.code = this.monacoInstance?.getValue()
        if (!this.code) {
          this.$message.error('请输入代码')
          return false
        }
        const params = this.getParams()
        const values = Object.values(params)
        this.loading = true
        try {
          const res = await runCodeInWorker(Object.keys(params), this.code, values)
          if(this.useType === 'chartConfig') {
            if(typeof res !== 'object') {
              throw new Error('返回值必须是个对象! 当前值: ' + res)
            }
          }
          if (!isSubmit) {
            const returnValue = res && typeof res === 'object' ? JSON.stringify(res, null, 2) : res
            this.logList.push(`[${formatTime(undefined, 'HH:mm:ss')}]测试通过，返回值：${returnValue}`)
            this.$message.success('代码测试通过。')
          }
          if (isSubmit) {
            this.logList = []
            this.$emit('input', this.code)
          }
          callback({ valid: true, res })
          this.loading = false
          return true
        } catch (e) {
          this.$message.error('代码测试未通过，请重新修改。')
          this.logList.push(`[${formatTime(undefined, 'HH:mm:ss')}]测试未通过，错误：${e}`)
          callback({ valid: false })
          this.loading = false
          return false
        }
      },
      // handleTest1(isSubmit = false) {
      //   this.code = this.monacoInstance?.getValue()
      //   if (!this.code) {
      //     this.$message.error('请输入代码')
      //     return false
      //   }
      //   const params = this.getParams()
      //   const args = [...Object.keys(params), 'setValue', this.code]
      //   const values = Object.values(params)
      //   this.loading = true
      //   const setValue = this.pureFunction ? undefined : () => {}
      //   try {
      //     // todo: js 沙箱机制
      //     const func = new Function(...args)
      //     const res = func(...values, setValue)
      //     if (!isSubmit) {
      //       this.logList.push(`测试通过，返回值：${res}`)
      //       this.$message.success('代码测试通过。')
      //     }
      //     if (isSubmit) {
      //       this.logList = []
      //       this.$emit('input', this.code)
      //     }
      //     this.loading = false
      //     return true
      //   } catch (e) {
      //     this.$message.error('代码测试未通过，请重新修改。')
      //     this.logList.push(`测试未通过，错误：${e}`)
      //     this.loading = false
      //     return false
      //   }
      // },
      // 放大
      maxEditor () {
        const dom = this.$el
        this.originSize = {
          width: dom.clientWidth,
          height: dom.clientHeight
        }
        dom.classList.add('editor-fullscreen')
        this.monacoInstance.layout({
          height: document.body.clientHeight,
          width: document.body.clientWidth
        })
        this.isMaximum = true
        this.$emit('screenChange', this.isMaximum)
      },
      // 缩小
      minEditor () {
        const dom = this.$el
        dom.classList.remove('editor-fullscreen')
        this.monacoInstance.layout({
          height: this.originSize.height,
          width: this.originSize.width
        })
        this.isMaximum = false
        this.$emit('screenChange', this.isMaximum)
      },
      handleHeightChange: _.throttle(function ({height}) {
        const calcHeight = this.$el.clientHeight + height
        const width = this.$el.clientWidth
        const nHeight = calcHeight < 244 ? 224 : calcHeight
        this.monacoInstance.layout({
          width,
          height: nHeight
        })
        this.$emit('heightChange', nHeight)
      }, 60)
    },
    created() {
      this.initHints()
    },
    mounted() {
      this.$nextTick(() => {
        this.init()
      })
    },
    beforeDestroy() {
      this.dispose()
    },
  }
</script>

<style lang="less" scoped>
  .js-code::v-deep {
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 244px;
    &.editor-fullscreen {
      position: fixed !important;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100% !important;
      z-index: 9999999;
    }
    .highlight-code {
      width: 100%;
      height: 100%;
      min-height: 244px;
      &.custom-class {
        min-height: 550px;
      }
    }
    .full-screen-control {
      position: absolute;
      right: 0;
      top: 0;
      z-index: 100;
    }
  }
</style>
