<script>
import _ from 'lodash'
import dataSelector from '../config-data'
import Select from '@/components/Select'
import { chartType } from '@/config/widgets'
import { formatTips } from '@/config'
import { getConfig, getPropsValues } from '@/renderer/tools/config'
import { pageInject } from '@/renderer/tools/pageStore'
export default {
  inject: [...pageInject],
  components: {
    dataSelector,
    Select
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    multiple: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      popoverVisible: false,
      useFormat: false,
      // 点击字段行编辑的数据
      rowInfo: {},
      xFields: [],
      yFields: [],
      formatOption: {},
      // X轴标题位置
      xPositionOptions: [
        {
          label: '左',
          value: 'start',
        },
        {
          label: '中',
          value: 'center',
        },
        {
          label: '右',
          value: 'end',
        },
      ],
      YPositionOptions: [
        {
          label: '上',
          value: 'end',
        },
        {
          label: '中',
          value: 'center',
        },
        {
          label: '下',
          value: 'start',
        },
      ],
      positionOptions: [
        {
          label: '上',
          value: 'top',
        },
        {
          label: '下',
          value: 'bottom',
        },
        {
          label: '左',
          value: 'left',
        },
        {
          label: '右',
          value: 'right',
        },
        {
          label: '数据中间',
          value: 'inside',
        },
      ],
      sizeOptions: [
        {
          label: '小',
          value: 'small',
        },
        {
          label: '中',
          value: 'medium',
        },
        {
          label: '大',
          value: 'large',
        },
      ],
      symbolOptions: [
        { label: '圆', value: 'circle' },
        { label: '矩形', value: 'rect' },
        { label: '圆角矩形', value: 'roundRect' },
        { label: '三角形', value: 'triangle' },
        { label: '钻石', value: 'diamond' },
        { label: '圆钉', value: 'pin' },
        { label: '箭头', value: 'arrow' },
        { label: '无', value: 'none' },
      ],
      formatTips,
    }
  },
  computed: {
    dataType: {
      get() {
        return this.elementProps['dataType']
      },
      set(value) {
        const res = {
          ...this.elementProps,
          dataType: value
        }
        const { elementType, elementVersion } = this.elementProps
        if(value === 'dynamic') {
          const { dynamicRequestData, yFieldsMap } = getConfig(elementType, elementVersion)
          res.staticData = dynamicRequestData
          res.dynamicVariables = getPropsValues.call(this, { elementType, elementVersion }).dynamicVariables
          res.dataVariables = this.elementProps.dataVariables.map(item => {
            if(item.key === 'yAxis') {
              item.fields = yFieldsMap
            }
            return item
          })
        } else {
          const { staticData, dataVariables } = getPropsValues.call(this, { elementType, elementVersion })
          res.staticData = staticData
          res.dataVariables = dataVariables
        }
        this.$emit('update', res)
      },
    },
    showDataType() {
      return Object.prototype.hasOwnProperty.call(this.elementProps, 'dataType') && this.elementProps['dynamicVariables']
    },
    dataVariables: {
      get() {
        return this.elementProps['dataVariables']
      },
      set(value) {
        this.$emit('update', {
          ...this.elementProps,
          dataVariables: value,
        })
      },
    },
    xSetting: {
      get() {
        return this.elementProps['xSetting']
      },
      set(value) {
        this.$emit('update', {
          ...this.elementProps,
          xSetting: value,
        })
      },
    },
    ySetting: {
      get() {
        return this.elementProps['ySetting']
      },
      set(value) {
        this.$emit('update', {
          ...this.elementProps,
          ySetting: value,
        })
      },
    },
    chartType() {
      return chartType[this.elementProps.elementType]
    },
    preset() {
      const { elementType, elementVersion } = this.elementProps
      const { yField_tpl } = require(`@/template/components/${elementType}/${elementVersion}/watch-props.js`)
      return yField_tpl
    }
  },
  watch: {
    dataVariables: {
      deep: true,
      immediate: true,
      handler(val) {
        this.xFieldsIndex = val.findIndex((item) => item.key === 'xAxis')
        this.xFields = val[this.xFieldsIndex]?.fields || []
        this.yFieldsIndex = val.findIndex((item) => item.key === 'yAxis')
        this.yFields = val[this.yFieldsIndex]?.fields || []
      },
    },
  },
  methods: {
    addXFields() {
      const newKey = 'key' + (this.xFields.length + 1)
      this.xFields.push({
        ..._.cloneDeep(this.preset),
        name: newKey
      })
      this.dataVariables[this.xFieldsIndex].fields = _.cloneDeep(this.xFields)
    },
    deleteXItem(index) {
      this.xFields.splice(index, 1)
      this.dataVariables[this.xFieldsIndex].fields = _.cloneDeep(this.xFields)
    },
    addYFields() {
      const newKey = 'key' + (this.yFields.length + 1)
      this.yFields.push({
        ..._.cloneDeep(this.preset),
        key: newKey,
        value: newKey
      })
      this.dataVariables[this.yFieldsIndex].fields = _.cloneDeep(this.yFields)
    },
    deleteYItem(index) {
      this.yFields.splice(index, 1)
      this.dataVariables[this.yFieldsIndex].fields = _.cloneDeep(this.yFields)
    },
    dataSelectUpdate(data) {
      this.$emit('update', data)
    },
    rowEdit(item) {
      this.rowInfo = _.cloneDeep(item)
    },
    setData(item, data) {
      for (let key in data) {
        item[key] = data[key]
      }
    },
  },
  mounted() {
    this.useFormat = !!this.ySetting.formatter
  },
}
</script>

