<template>
  <div class='form-config'>
    <div class='config-item'>
      <div class='label'>数据配置</div>
      <div class='value'>
        <el-button-group>
          <el-button :disabled="dataInfo" size='mini' :type='type === "static" ? "primary" : ""' @click='type = "static"'>
            数据集
            <el-tooltip content="可配置静态表格列及表格数据" popper-class="tooltip-question" placement="top">
              <i style='margin-left: 5px; color: black; font-size: 14px;' class="el-icon-question"></i>
            </el-tooltip>
            </el-button>
          <el-button :disabled="dataInfo" size='mini' :type='type === "dynamic" ? "primary" : ""' @click='type = "dynamic"'>
            动态表格
            <el-tooltip content="可配置动态表格列及表格数据" popper-class="tooltip-question" placement="top">
              <i style='margin-left: 5px; color: black; font-size: 14px;' class="el-icon-question"></i>
            </el-tooltip>
            </el-button>
        </el-button-group>
      </div>
    </div>
    <div class='config-item' v-if='!dataInfo'>
      <div class='label'>{{ type === 'static' ? '数据集' : '动态表格' }}</div>
      <div class='value'>
        <el-button size='mini' type='primary' :loading='loading' @click='handleShowConfig'>配置</el-button>
      </div>
    </div>
    <div class='config-item' v-else>
      <div class='label'>当前配置</div>
      <div class='value'>
        <el-button type='text' :loading='loading' @click='handleShowConfig'>{{ dataInfo }}</el-button>
        <div class='data-tools' @click='handleDelete'>
          <i class='iconfont icon-delete'></i>
        </div>
      </div>
    </div>
    <table-data ref='tableData' @confirm="handleConfirm"></table-data>
    <column-data ref='columnData' @confirm="handleConfirm"></column-data>
  </div>
</template>

<script>
  import tableData from '@/components/DataSelect/tableData'
  import columnData from '@/components/DataSelect/columnData'
  export default {
    components: {
      tableData,
      columnData
    },
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      optionkey: {
        type: String,
      },
    },
    data() {
      return {
        loading: false,
        type: 'static'
      }
    },
    computed: {
      dataInfo() {
        const { apiRequest, sqlRequest, staticData } = this.elementProps
        switch (true) {
          case !!apiRequest:
            return 'API数据集'
          case !!sqlRequest:
            return 'SQL数据集'
          case !!staticData:
            return '静态数据集'
          default:
            return undefined
        }
      }
    },
    watch: {
      'elementProps.dynamicColumn': {
        handler(n, o) {
          this.type = n ? 'dynamic' : 'static'
        },
        immediate: true
      }
    },
    methods: {
      handleShowConfig() {
        const refKey = this.type === 'static' ? 'tableData' : 'columnData'
        this.loading = true
        setTimeout(() => {
          this.$refs[refKey].open({
            dataColumn: this.elementProps.dataColumn,
            dynamicColumn: this.elementProps.dynamicColumn,
            dataVariables: this.elementProps.dataVariables,
            apiRequest: this.elementProps.apiRequest,
            sqlRequest: this.elementProps.sqlRequest,
            staticData: this.elementProps.staticData,
          })
          setTimeout(() => {
            this.loading = false
          }, 100)
        }, 100)

      },
      handleConfirm(res) {
        this.$emit('update', {
          ...this.elementProps,
          ...res,
        })
        this.type = res.dynamicColumn ? 'dynamic' : 'static'
      },
      handleDelete() {
        const res = {
          apiRequest: undefined,
          sqlRequest: undefined,
          staticData: undefined,
          dynamicColumn: undefined,
          dataVariables: this.elementProps.dataVariables?.map(item => ({...item, path: []})),
          dataColumn: this.elementProps.dataColumn?.map(item => ({...item, path: []})),
        }
        this.$emit('update', {
          ...this.elementProps,
          ...res,
        })
      }
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .form-config::v-deep {
    div.config-item {
      display: flex;
      align-items: center;
      margin-bottom: 8px;
      .label {
        width: 100px;
        font-size: 12px;
        color: #48576a;
      }
      .value {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        .data-tools {
          margin-left: 10px;
          &:hover {
            cursor: pointer;
            color: red;
          }
        }
      }
    }
  }
</style>
