<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="选择调度"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-loading.lock="loading">
      <n-form
        class="data-select-form"
        :model="queryForm"
        size="mini"
        inline
        label-with="80px"
        @submit.native.prevent
      >
        <n-form-item label="">
          <el-select
            style="width: 100px"
            v-model="searchType"
            @change="handleResetCondition"
          >
            <el-option label="名称" value="name"></el-option>
            <el-option label="描述" value="description"></el-option>
            <el-option label="更新用户" value="updateUserName"></el-option>
            <el-option label="更新时间" value="updateTime"></el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="">
          <el-input
            class="search-input"
            size="mini"
            v-model="queryForm.condition[searchType]"
            placeholder="请输入"
          >
          </el-input>
        </n-form-item>
        <el-button
          type="primary"
          size="mini"
          icon="el-icon-search"
          @click="handleSearch(false)"
        >
        </el-button>
        <el-button type="primary" size="mini" @click="handleSearch(true)">
          重置
        </el-button>
      </n-form>
      <div class="data-table">
        <n-table
          :data="dataList"
          border
          highlight-current-row
          @row-click="handleRowClick"
          size="mini"
          height="410px"
        >
          <n-table-column width="50px" label="选择">
            <template slot-scope="scope">
              <el-radio
                v-if="
                  selectedRow && selectedRow.name === scope.row.name
                "
              ></el-radio>
              <el-radio v-else :value="false"></el-radio>
            </template>
          </n-table-column>
          <n-table-column
            prop="name"
            label="名称"
            min-width="150px"
          ></n-table-column>
          <n-table-column
            prop="description"
            label="描述"
            min-width="100px"
          >
          </n-table-column>
          <n-table-column
            prop="updateUserName"
            label="更新用户"
            min-width="100px"
          >
          </n-table-column>
          <n-table-column
            prop="updateTime"
            label="更新时间"
            min-width="100px"
          >
          <template slot-scope="scope">
              <span>{{ formatTime(scope.row.updateTime) }}</span>
            </template>
          </n-table-column>
        </n-table>
        <div class="pagination">
          <pagination
            :page="queryForm.pagination.pagenum"
            :limit="queryForm.pagination.pagesize"
            :total="totalCount"
            @pagination="handlePageChange"
          />
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import API from '@/api/application'
import Pagination from '@/components/Pagination'
import request from '@/request/request'
import { getCommonHeaders } from '@/utils'
import { formatTime } from '@/utils/format'

export default {
  name: 'sncflowerData',
  components: {
    Pagination,
  },
  data() {
    return {
      dialogVisible: false,
      loading: false,
      totalCount: 0,
      queryForm: {
        condition: { name: null, description: null, updateUserName: null, updateTime: null },
        pagination: { pagenum: 1, pagesize: 100, sort: null },
      },
      dataList: [],
      selectedRow: null,
      searchType: 'name',
    }
  },
  methods: {
    formatTime,
    open(data) {
      this.selectedRow = data
      this.dialogVisible = true
      this.handleSearch(true)
    },
    handleClose(done) {
      this.dialogVisible = false
      done()
    },
    cancel() {
      this.dialogVisible = false
    },
    confirm() {
      if (!this.selectedRow) {
        this.$message.error('请选择')
        return
      }
      this.handleGetServeDetail()
    },
    handleGetServeDetail() {
      this.loading = true
      request({
        url: API.atmSncflowerDetail,
        method: 'POST',
        data: {
          params: {
            name: this.selectedRow.name,
          },
        },
        headers: getCommonHeaders(),
      })
        .then(({ data }) => {
          const workflowInputModels = data.data.workflowInputModels
          const root = {
            params: {
              workflowRunBean: {
                workflowId: data.data.id,
                workflowInputModels,
              },
            },
          }

          const body = Object.entries(root).map(([key, value]) => ({
            key,
            value,
            test: value,
          }))
          const apiRequest = {
            url: '/sncflower/workflowInfo/runWorkflow',
            method: 'POST',
            type: 'sncflower',
            paramsData: [{ key: '', value: undefined, test: undefined }],
            bodyData: body.length
              ? body
              : [{ key: '', value: undefined, test: undefined }],
            headersData: [{ key: '', value: undefined, test: undefined }],
          }
          const result = {
            apiRequest,
            apiDetail: data.data,
          }
          this.$emit('confirm', result)
          this.dialogVisible = false
        })
        .catch((e) => {
          this.$message.error('数据解析错误')
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleSearch(reset) {
      this.loading = true
      if (reset) {
        this.queryForm = {
          condition: { name: null, description: null, updateUserName: null, updateTime: null },
          pagination: { pagenum: 1, pagesize: 100, sort: null },
        }
      }
      request({
        url: API.atmSncflowerList,
        method: 'POST',
        data: {
          params: this.queryForm,
        },
        headers: getCommonHeaders(),
      })
        .then(({ data }) => {
          this.totalCount = data.data.rowCount
          this.dataList = data.data.records
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleRowClick(row, column, event) {
      this.selectedRow = row
    },
    handlePageChange({ page, size }) {
      this.queryForm.pagination.pagenum = page
      this.queryForm.pagination.pagesize = size
      this.handleSearch(false)
    },
    handleResetCondition(type) {
      this.queryForm.condition.name = null
      this.queryForm.condition.description = null
      this.queryForm.condition.updateUserName = null
      this.queryForm.condition.updateTime = null
    },
  },
}
</script>

<style lang="less" scoped>
.dialog-content::v-deep {
  .data-select-form {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
  }
  .pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
  }
}
</style>
