<template>
  <div class="widget-config-item">
    <div
      :class="{
        name: true,
        required: requiredArr.includes(name),
      }"
    >
      {{ name }}
      <el-tooltip
        v-if="tip"
        :content="tip"
        popper-class="tooltip-question"
        placement="top"
      >
        <i style="margin-left: 5px;" class="el-icon-question"></i>
      </el-tooltip>
    </div>
    <n-form-item class="config-item" label="" :prop="propKey">
      <template v-if='types && types.length'>
        <el-tooltip v-for='type in types' :key='type' :content="getContent(target.value)" popper-class="tooltip-question" placement="top">
          <el-button
            @click="showInputDialog(getType(type), target, 'value')"
          >{{ target.value ? '重新输入' : getTypeName(type) }}
          </el-button>
        </el-tooltip>
      </template>
      <el-tooltip v-else :content="getContent(target.value)" popper-class="tooltip-question" placement="top">
        <el-button
          @click="showInputDialog('object', target, 'value')"
        >{{target.value ? '重新输入' : '输入对象'}}
        </el-button>
      </el-tooltip>
    </n-form-item>
    <input-dialog @confirm='handleInputResponse' ref="inputDialog"></input-dialog>
  </div>
</template>

<script>
  import _ from 'lodash'
  import { formatTip } from '@/utils'

  export default {
    name: 'ConfigObject',
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      tip: {
        type: String,
      },
      optionkey: {
        type: String,
      },
      types: {
        type: Array,
      },
    },
    data() {
      return {
        requiredArr: [],
        target: {
          value: undefined
        }
      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
        },
      },
    },
    watch: {
      value: {
        deep: true,
        immediate: true,
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.target.value = _.cloneDeep(n)
        }
      }
    },
    methods: {
      showInputDialog(type, data, key, from, attrOptions) {
        this.$refs.inputDialog.open({ type, data, key, from, attrOptions, callbackId: this.callbackId })
      },
      handleInputResponse(value) {
        this.value = _.cloneDeep(value)
      },
      getContent(value) {
        return formatTip(value)
      },
      getType(type) {
        switch (type) {
          case 'string':
            return 'text'
          case 'css':
            return 'css'
          case 'array':
            return 'object'
          default:
            return type
        }
      },
      getTypeName(type) {
        switch (type) {
          case 'string':
            return '文本'
          case 'number':
            return '数值'
          case 'object':
            return '对象'
          default:
            return type
        }
      },
    },
  }
</script>

