      margin-bottom: 5px;
      min-width: 100px;
      padding-right: 10px;
      display: flex;
      height: 36px;
      align-items: center;
    }

    .required::before {
      content: '*';
      color: red;
    }

    div.config-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 8px;

      .el-form-item__content {
        flex: 1;
        display: flex;
        align-items: center;

        .el-icon-question {
          margin-left: 10px;
        }
      }
    }
  }
</style>
