<template>
  <div class="variable-input">
    <div class="inner-content">
      <div class="title" v-popover:popover>
        <el-checkbox v-if="deleteMode" :indeterminate="isIndeterminate" v-model="isSelectAll" @change="handleCheckAllChange"></el-checkbox>
        <span class="col-title">标签标题</span>
        <span class="key">默认选中</span>
      </div>
      <draggable
        v-model="value"
        forceFallback="true"
        group="column"
        animation="300"
        handle=".el-icon-rank"
      >
        <transition-group>
          <div class="content" :style='{opacity: col.hidden ? "0.5" : "1"}' v-for="(col, index) in value" :key="col.key">
            <el-checkbox v-if="deleteMode" v-model="selectedList" :label="col.key" @change="radioChange"></el-checkbox>
            <i class="icon el-icon-rank" />
            <span class="label">{{ col.label }}</span>
            <span class="key">
              <el-switch v-model='col.active' @change='handleActiveChange(col)'></el-switch>
            </span>
            <div class="caozuo">
              <el-button type="text" @click="handleEditColumn(index, col)"
                >编辑
              </el-button>
              <el-button type="text" @click="handleDeleteColumn(index)"
                >删除
              </el-button>
            </div>
          </div>
        </transition-group>
      </draggable>
    </div>
    <template v-if="!deleteMode">
      <div
        class="add-reg-exp-btn"
        style="cursor:pointer"
        @click="handleAddColumn"
      >
        +
      </div>
      <el-button size="mini" type="danger" @click="deleteMode = true">批量删除</el-button>
    </template>
    <template v-else>
      <el-button size="mini" @click="deleteMode = false">取消</el-button>
      <el-button size="mini" type="danger" @click="doDelete">删除</el-button>
    </template>
    <el-popover
      width="400"
      trigger="manual"
      ref="popover"
      :title="variableForm.isAdd ? '新增' : '编辑'"
      v-model="popoverVisible"
    >
      <div class="variable-body">
        <n-form
          ref="variableForm"
          :model="variableForm"
          :rules="rules"
          label-width="85px"
          @submit.native.prevent
        >
          <n-form-item label="标签标题" required prop="label">
            <el-input v-model.trim="variableForm.label"></el-input>
          </n-form-item>
          <n-form-item label="标签编码" required prop="key">
            <el-input v-model.trim="variableForm.key"></el-input>
          </n-form-item>
          <n-form-item label="可关闭" prop="closable">
            <el-switch v-model="variableForm.closable"></el-switch>
          </n-form-item>
          <n-form-item label="是否隐藏" prop="hidden">
            <el-switch v-model="variableForm.hidden"></el-switch>
          </n-form-item>
          <n-form-item label="默认选中" prop="active">
            <el-switch v-model="variableForm.active" @change='handleFormActiveChange'></el-switch>
          </n-form-item>
          <n-form-item label="内容延迟加载" prop="lazy">
            <el-switch v-model="variableForm.lazy"></el-switch>
          </n-form-item>
          <n-form-item label="显示标记" prop="showBadge">
            <el-switch v-model="variableForm.showBadge"></el-switch>
          </n-form-item>
          <n-form-item v-if="variableForm.showBadge" label="标记内容" prop="badgeMessage">
            <el-input v-model.trim="variableForm.badgeMessage" placeholder="请输入数字/文本"></el-input>
          </n-form-item>
          <n-form-item v-if="variableForm.showBadge" label="标记主题" prop="badgeType">
            <el-radio-group size="mini" v-model="variableForm.badgeType">
              <el-radio-button label="primary">默认</el-radio-button>
              <el-radio-button label="success">成功</el-radio-button>
              <el-radio-button label="warning">警告</el-radio-button>
              <el-radio-button label="danger">危险</el-radio-button>
              <el-radio-button label="info">信息</el-radio-button>
            </el-radio-group>
          </n-form-item>
        </n-form>
        <div style="text-align: right; margin: 0">
          <el-button type="primary" size="mini" @click="handleConfirm"
            >确认
          </el-button>
          <el-button size="mini" type="text" @click="popoverVisible = false"
            >取消
          </el-button>
        </div>
      </div>
    </el-popover>
  </div>
</template>

<script>
  import { getValidators, required } from '@/utils/validateUtils'
  import _ from 'lodash'
  import draggable from 'vuedraggable'
  import {
    getNodeByElementId,
    getRandomHexadecimal,
    buildNewWidget,
  } from '@/utils/tree-utils'
  import { DELETE_LAYOUT_DATA, UPDATE_DYNAMIC_PROPS } from '@/config/storeKey'
  import { getLastVersion } from '@/config/module'
  import { pageInject } from '@/renderer/tools/pageStore'

  const defaultFormData = {
    label: '',
    key: '',
    lazy: true,
    disabled: false,
    closable: false,
    hidden: false,
    active: false,
    showBadge: false,
    badgeMessage: '0',
    badgeType: 'primary',
    max: 99,
    isDot: false,
  }

  export default {
    components: {
      draggable,
    },
    inject: ['dndOptions', ...pageInject],
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        variableForm: _.cloneDeep(defaultFormData),
        rules: {
          label: getValidators([
            {
              message: '请输入标签标题',
              include: required,
              trigger: 'change',
            },
          ]),
          key: getValidators([
            {
              message: '请输入标签编码',
              include: required,
              trigger: 'change',
            },
          ]),
        },
        popoverVisible: false,
        edtingIndex: undefined,
        deleteMode: false,
        isSelectAll: false,
        isIndeterminate: false,
        selectedList: []
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      currentElement() {
        return getNodeByElementId(this.layoutData, this.elementProps.elementId)
      },
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
        },
      },
    },
    methods: {
      errorToast() {
        this.$message.error('至少要保留一个')
      },
      doDelete() {
        if(this.selectedList.length === this.value.length) {
          return this.errorToast()
        }
        this.value = this.value.filter(item => !this.selectedList.includes(item.key))
        const deleteSlotIds = this.selectedList.map(key => {
          const deleteElement = this.currentElement.children.find(c => this.getElementPropsById(c.elementId).slotName === key)
          return deleteElement.elementId
        })
        this._storeDispatch(DELETE_LAYOUT_DATA, { elementIdList: deleteSlotIds })
        this.dndOptions.selectedId = this.currentElement.elementId
        this.handleCheckAllChange(false)
        this.deleteMode = false
      },
      handleCheckAllChange(val) {
        this.selectedList = val ? this.value.map(item => item.key) : []
        this.isIndeterminate = false;
      },
      radioChange() {
        let checkedCount = this.selectedList.length;
        this.isSelectAll = checkedCount === this.value.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.value.length;
      },
      handleFormActiveChange(v) {
        if (v) {
          this.value.forEach(item => {
            item.active = false
          })
        }
      },
      handleActiveChange(data) {
        this.value.forEach(item => {
          item.active = item.key === data.key ? data.active : false
        })
        this.value = [...this.value]
      },
      handleEditColumn(index, value) {
        this.popoverVisible = true
        this.$nextTick()
        this.$refs.variableForm.resetFields()
        this.variableForm = _.cloneDeep({ ...value, isAdd: false })
        this.edtingIndex = index
      },
      handleDeleteColumn(index) {
        if (this.value.length === 1) {
          return this.errorToast()
        }
        const deleteKey = this.value[index].key
        const deleteElement = this.currentElement.children.find(c => this.getElementPropsById(c.elementId).slotName === deleteKey)
        this._storeDispatch(DELETE_LAYOUT_DATA, { elementId: deleteElement.elementId })
        this.dndOptions.selectedId = this.currentElement.elementId
        this.value.splice(index, 1)
        this.value = [...this.value]
      },
      handleAddColumn() {
        this.popoverVisible = true
        this.$nextTick()
        this.$refs.variableForm.resetFields()
        this.variableForm = _.cloneDeep(defaultFormData)
        this.variableForm.isAdd = true
        this.variableForm.key = `tab${getRandomHexadecimal(6)}`
        this.edtingIndex = undefined
      },
      handleConfirm() {
        this.$refs.variableForm.validate((valid) => {
          if (!valid) {
            return
          }
          let tempValue = _.cloneDeep(this.value)
          if (this.edtingIndex === undefined) {
            const form = _.cloneDeep(this.variableForm)
            if (tempValue.some(item => item.key === form.key)) {
              this.$message.error('编码不能重复！')
              return
            }
            delete form.isAdd
            const widget = {
              elementId: '',
              elementType: 'slot',
              elementVersion: getLastVersion('slot'),
              elementName: form.label,
              defaultProps: {
                slotName: form.key,
              },
              children: [],
            }
            buildNewWidget.call(this, widget, this.currentElement.elementId, 'center')
            if (form.active) {
              tempValue.forEach(item => item.active = false)
            }
            tempValue.push(form)
            const res = {
              ...this.elementProps,
              [this.propKey]: tempValue,
            }
            this.$emit('update', res)
          } else {
            const form = _.cloneDeep(this.variableForm)
            delete form.isAdd
            const oldKey = tempValue[this.edtingIndex].key
            const newKey = form.key
            if (oldKey !== newKey && tempValue.some(item => item.key === newKey)) {
              this.$message.error('编码不能重复！')
              return
            }
            const newLabel = form.label
            const current = this.currentElement.children.find(c => this.getElementPropsById(c.elementId).slotName === oldKey)
            const needUpdateProps = []
            needUpdateProps.push({ [current.elementId]: {
                slotName: newKey,
                elementName: newLabel,
              }
            })
            this._storeCommit(UPDATE_DYNAMIC_PROPS, {
              dynamicPropsList: needUpdateProps
            })
            if (form.active) {
              tempValue.forEach(item => item.active = false)
            }
            tempValue[this.edtingIndex] = form
            const res = {
              ...this.elementProps,
              [this.propKey]: tempValue,
            }

            this.$emit('update', res)
          }
          this.popoverVisible = false
        })
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      }
    },
  }
</script>

