<template>
  <div class="merge-cells-config">
    <vxe-table
      class="edit-table"
      border="default"
      show-overflow
      ref="xTable"
      :data="tableData"
      size="mini"
      align="center"
      :auto-resize='true'
      :edit-config="{
        trigger: 'click',
        mode: 'row',
        showIcon: false,
      }"
      @edit-closed="handleSave"
    >
      <vxe-column type="checkbox" width="40" v-if="deleteMode"></vxe-column>
      <vxe-column field="row" title="行坐标" width="60" :edit-render="{ defaultValue: 0 }">
        <template #edit="{ row }">
          <vxe-input v-model="row.row" type="number" :min="0"></vxe-input>
        </template>
      </vxe-column>
      <vxe-column field="col" title="列坐标" width="60" :edit-render="{ defaultValue: 0 }">
        <template #edit="{ row }">
          <vxe-input v-model="row.col" type="number" :min="0"></vxe-input>
        </template>
      </vxe-column>
      <vxe-column
        field="rowspan"
        title="合并行数"
        width="70"
        :edit-render="{ defaultValue: 0 }"
      >
        <template #edit="{ row }">
          <vxe-input v-model="row.rowspan" type="number" :min="0"></vxe-input>
        </template>
      </vxe-column>
      <vxe-column
        field="colspan"
        title="合并列数"
        width="70"
        :edit-render="{ defaultValue: 0 }"
      >
        <template #edit="{ row }">
          <vxe-input v-model="row.colspan" type="number" :min="0"></vxe-input>
        </template>
      </vxe-column>
      <vxe-column title="操作" width="50">
        <template #default="{ row }">
          <el-button type="text" size="mini" @click="deleteRow(row)">删除</el-button>
        </template>
      </vxe-column>
    </vxe-table>
    <template v-if="!deleteMode">
      <div style="display: flex; align-items: center">
        <div class="add-btn-primary" @click="insertRow(-1)">+</div>
        <el-button
          style="margin-left: 20px"
          size="mini"
          type="danger"
          @click="deleteMode = true"
          >批量删除</el-button
        >
      </div>
    </template>
    <template v-else>
      <el-button size="mini" @click="deleteMode = false">取消</el-button>
      <el-button size="mini" type="danger" @click="doDelete">删除</el-button>
    </template>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  props: {
    elementId: {
      type: String
    },
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      deleteMode: false,
      tableData: []
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
  },
  watch: {
    elementId: {
      immediate: true,
      handler(n, o) {
        this.updateTableData()
      }
    }
  },
  methods: {
    updateValue() {
      this.value = _.cloneDeep(this.tableData)
    },
    updateTableData() {
      this.tableData = _.cloneDeep(this.value)
    },
    async insertRow(row) {
      const $table = this.$refs.xTable
      const record = {
        row: 0,
        col: 0,
        rowspan: 0,
        colspan: 0,
      }
      const { row: newRow } = await $table.insertAt(record, row)
      await $table.setActiveCell(newRow, 'row')
    },
    deleteRow(row) {
      const $table = this.$refs.xTable
      $table.remove(row)
      this.handleSave()
    },
    doDelete() {
      const $table = this.$refs.xTable
      $table.removeCheckboxRow()
      this.handleSave()
    },
    handleSave() {
      const $table = this.$refs.xTable
      const newTableData = $table.getTableData().fullData.map(d => {
        delete d._XID
        Object.keys(d).forEach(k => d[k] = Number(d[k]))
        return d
      })
      this.tableData = newTableData
      this.updateValue()
    },
  },
}
</script>

<style lang="less" scoped>
.merge-cells-config {
  .edit-table {
    margin-bottom: 10px;
  }
}
</style>
