<template>
  <el-collapse class='config-collapse' v-model='activeCollapse'>
    <el-collapse-item name='config'>
      <template slot="title">
        <div class='config-collapse-title'>SQL配置</div>
      </template>
      <div class="sql-config" v-loading="loading">
        <n-form
          class="data-select-form"
          ref="sqlRequest"
          label-position="top"
          :model="sqlRequest"
          :rules="rules"
          size="mini"
          @submit.native.prevent
        >
          <n-form-item class="sql-form-item" label="">
            <div class="inline-form-item">
              <div class="form-item-label">选择数据源</div>
              <div class="sql-data-select-wrapper">
                <div class="left-buttons">
                  <el-input
                    class="sql-name"
                    v-if="sqlRequest.sqlData"
                    :value="sqlRequest.sqlData.name"
                  ></el-input>
                  <el-input class="sql-name" v-else readonly></el-input>
                  <el-button type="primary" @click="handleShowSqlDataSelect"
                  >选择</el-button
                  >
                </div>
                <el-button
                  type="primary"
                  @click="handleFetch"
                >测试</el-button
                >
              </div>
            </div>
            <div class="sql-desc" v-if="sqlRequest.sqlData">
              <div class="desc-item">
                类型：
                <el-tag>{{ sqlRequest.sqlData.type }}</el-tag>
              </div>
              <div class="desc-item">数据库：{{ sqlRequest.sqlData.databaseName }}</div>
              <div class="desc-item">描述：{{ sqlRequest.sqlData.description }}</div>
            </div>
          </n-form-item>
          <n-form-item class="sql-form-item" label="SQL" prop="sql">
            <el-button-group class='sql-type-buttons'>
              <el-button :type='sqlRequest.sqlType === 1 ? "primary" : ""' @click='sqlRequest.sqlType = 1'>动态SQL</el-button>
              <el-button :type='sqlRequest.sqlType === 2 ? "primary" : ""' @click='sqlRequest.sqlType = 2'>模板SQL</el-button>
            </el-button-group>
            <el-tooltip popper-class="tooltip-question" placement="left">
              <div slot="content" v-html='sqlRequest.sqlType === 1 ? sqlTemplate1 : sqlTemplate2'></div>
              <i class="el-icon-question sql-type-buttons tooltip"></i>
            </el-tooltip>
            <div class='empty-code' v-html='sqlTemplate'></div>
            <sql-code class="sql-code" @focus-change='focusChange' :resizable="true" :dynamic-hints='dynamicHints' v-model="sqlRequest.sql"></sql-code>
          </n-form-item>
          <n-form-item label="">
            <div class="params-config">
              <el-button
                class="source-edit-button"
                type='primary'
                @click="
              showInputDialog(
                'object',
                paramsSource,
                'sqlParamsData',
                'paramsSource'
              )
            "
              >转换器</el-button>
              <n-table :data="sqlParamsData" border style="width: 100%;margin-top: 10px;">
                <n-table-column width="200" prop="key" label="参数名">
                  <template slot-scope="scope">
                    <el-input v-model="scope.row.key"></el-input>
                  </template>
                </n-table-column>
                <n-table-column prop="value" label="参数值">
                  <template slot-scope="scope">
                    <div class="value-input">
                      <el-tooltip
                        v-if="scope.row.value !== undefined"
                        effect="dark"
                        :content="formatTip(scope.row.value)"
                        placement="top"
                      >
                        <el-tag
                          class="value-tag"
                          closable
                          disable-transitions
                          @click="handleTagClick(scope.row, 'value')"
                          @close="scope.row.value = undefined"
                          :type='getTagType(scope.row.value)'
                        >
                          {{ formatTag(getElementName(scope.row.value), 12) }}
                        </el-tag>
                      </el-tooltip>
                      <div v-else class="value-input-content">
                        <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                          <el-button
                            @click="showInputDialog('text', scope.row, 'value')"
                          >文本
                          </el-button>
                          <el-button
                            @click="showInputDialog('number', scope.row, 'value')"
                          >数值
                          </el-button>
                          <el-button
                            @click="handleShowVariableSelect(scope.row, 'value')"
                          >变量
                          </el-button>
                        </el-button-group>
                        <MoreTypeDropdown @change="(v) => scope.row.value = v" />
                      </div>
                    </div>
                  </template>
                </n-table-column>
                <n-table-column prop="test" label="测试值">
                  <template slot-scope="scope">
                    <div class="value-input">
                      <el-tooltip
                        v-if="scope.row.test !== undefined"
                        effect="dark"
                        :content="formatTip(scope.row.test)"
                        placement="top"
                      >
                        <el-tag
                          class="value-tag"
                          closable
                          disable-transitions
                          @click="handleTagClick(scope.row, 'test')"
                          @close="scope.row.test = undefined"
                        >
                          {{ formatTag(scope.row.test) }}
                        </el-tag>
                      </el-tooltip>
                      <div v-else class="value-input-content">
                        <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                          <el-button
                            @click="showInputDialog('text', scope.row, 'test')"
                          >文本
                          </el-button>
                          <el-button
                            @click="showInputDialog('number', scope.row, 'test')"
                          >数值
                          </el-button>
                        </el-button-group>
                        <MoreTypeDropdown @change="(v) => scope.row.test = v" />
                      </div>
                    </div>
                  </template>
                </n-table-column>
                <n-table-column width="180" label="操作">
                  <template slot-scope="scope">
                    <div class="btns">
                      <div class="add-btn-primary" @click="handleAddParams">+</div>
                      <div
                        class="add-btn-primary"
                        @click="handleDeleteParams(scope.$index)"
                      >
                        -
                      </div>
                    </div>
                  </template>
                </n-table-column>
              </n-table>
            </div>
          </n-form-item>
        </n-form>
        <variable-select
          ref="variableSelect"
          @confirm="handleVariableConfirm"
        ></variable-select>
        <sql-data-select
          ref="sqlDataSelect"
          @confirm="handleSqlDataConfirm"
        ></sql-data-select>
        <input-dialog @confirm="handleUpdateValue" ref="inputDialog"></input-dialog>
      </div>
    </el-collapse-item>
  </el-collapse>
</template>

<script>
  import SqlDataSelect from '@/components/SqlDataSelect/index'
  import SqlCode from '@/components/SqlCode'
  import {
    getValidators,
    required,
  } from '@/utils/validateUtils'
  import _ from 'lodash'
  import Api from '@/api/application'
  import { getObjectTree } from '@/utils/format'
  import { encryptByDES } from '@/utils/encrypt'
  import elementEditor from '@/mixins/elementEditor'
  import { isValidateVariable } from '@/utils'
  import { pageInject } from '@/renderer/tools/pageStore'

  const sqlTemplate1 = `使用“#{参数名}”格式进行参数取值（系统自动进行非空判断），示例SQL：<br>
select id,name,age,sex,create_time from user <br>
where id=#{id} and name=#{name} and age=#age`
  const sqlTemplate2 = `使用freemarker模板语法进行参数非空判断和取值，示例SQL：<br>
select id,name,age,sex,create_time from user <br>
where 1=1 <br>
<#if id??> and id=\${id}</#if> <br>
<#if id??> and name='\${name}'</#if> <br>
<#if id??> and age=\${age}</#if>`

  export default {
    name: 'SqlConfig',
    inject: [...pageInject],
    components: {
      SqlCode,
      SqlDataSelect,
    },
    mixins: [elementEditor],
    props: {
      sqlRequest: {
        type: Object,
        required: true,
        default: () => ({
          sql: '',
          type: 2,
          sqlType: 1,
          sqlData: undefined,
          dataSourceId: undefined,
          select: false,
          sqlParamsData: [
            {
              key: '',
              value: undefined,
              test: undefined,
            },
          ],
        }),
      },
      callbackId: {
        type: String,
      },
      onlySelect: {
        type: Boolean,
        default: false
      },
      emitEvent: {
        type: Object,
        required: false
      }
    },
    data() {
      return {
        sqlTemplate1,
        sqlTemplate2,
        loading: false,
        activeCollapse: ['config'],
        rules: {
          sql: getValidators([
            {
              message: '请输入SQL',
              include: required,
              trigger: 'change',
            },
          ]),
        },
        variableRowDetail: null,
        editorFocus: false
      }
    },
    computed: {
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      sqlTemplate() {
        if (this.sqlRequest.sql || this.editorFocus) {
          return ''
        }
        return this.sqlRequest.sqlType === 1 ? sqlTemplate1 : sqlTemplate2
      },
      sqlParamsData() {
        return this.sqlRequest.sqlParamsData
      },
      dynamicHints() {
        return this.sqlParamsData.map(item => `#{${item.key}}`)
      },
      paramsSource: {
        get() {
          return {
            sqlParamsData: Object.fromEntries(
              this.sqlRequest.sqlParamsData.map((item) => [
                [item.key],
                item.value,
              ])
            ),
          }
        },
        set(n) {
          this.sqlRequest.sqlParamsData = Object.entries(
            n
          ).map(([key, value]) => ({ key, value, test: isValidateVariable(value) ? undefined : JSON.parse(JSON.stringify(value === undefined ? null : value, function(k, v) { return isValidateVariable(v) ? null : v })) }))
        },
      }
    },
    watch: {
      sqlRequest: {
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.$emit('update:sqlRequest', { ...n, select: this.onlySelect, sqlParamsData: n.sqlParamsData.map(item => ({...item, value: item.value, test: item.test})) })
        },
        deep: true,
        immediate: true,
      },
    },
    methods: {
      focusChange(n) {
        this.editorFocus = n
      },
      handleShowVariableSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.variableSelect.open({
          fullVariable: row[key],
          callbackId: this.callbackId,
          emitEvent: this.emitEvent
        })
      },
      handleVariableConfirm(selectedVariable) {
        const { row, key, cb } = this.variableRowDetail
        if (key) {
          this.$set(row, key, selectedVariable)
        }
        cb?.(selectedVariable)
      },
      handleAddParams() {
        this.sqlRequest.sqlParamsData.push({
          key: '',
          value: undefined,
          test: undefined,
        })
      },
      handleDeleteParams(index) {
        const paramsData = this.sqlRequest.sqlParamsData
        if (paramsData.length === 1) {
          paramsData[0].key = ''
          paramsData[0].value = undefined
          paramsData[0].test = undefined
          return
        }
        paramsData.splice(index, 1)
      },
      getParams() {
        const { sql, dataSourceId, sqlParamsData, sqlType, select } = this.sqlRequest
        const bodyData = [
          {
            key: 'params',
            value: Object.fromEntries(
              sqlParamsData
                .filter((d) => d.key)
                .map((d) => {
                  return [d.key, d.value]
                })
            ),
            test: Object.fromEntries(
              sqlParamsData
                .filter((d) => d.key)
                .map((d) => {
                  return [d.key, d.test]
                })
            ),
          },
        ]
        const url = sqlType === 1 ? Api.pageDataExecuteDynamicSQL : Api.pageDataExecuteTemplateSQL
        const data = Object.fromEntries(
          bodyData.map((d) => {
            return [d.key, d.test]
          })
        )
        return {
          url,
          data: {
            ...data,
            sql: encryptByDES(sql),
            dataSourceId,
            select
          },
        }
      },
      async handleFetch() {
        await this.$nextTick()
        if(!this.sqlRequest.dataSourceId) {
          return this.$message.error('请选择数据集')
        }
        if (!this.sqlRequest.sql) {
          return this.$message.error('请输入sql')
        }
        this.$emit('response', {
          response: null,
          responseOptions: [],
        })
        const params = this.getParams()
        this.loading = true
        this.$http({
          url: params.url,
          data: params.data,
        })
          .then((res) => {
            this.$emit('response', {
              response: res,
              responseOptions: getObjectTree(res),
            })
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleShowSqlDataSelect() {
        this.$refs.sqlDataSelect.open()
      },
      handleSqlDataConfirm(data) {
        this.sqlRequest.sqlData = data
        this.sqlRequest.dataSourceId = data.id
      },
    },
  }
</script>

