<template>
  <el-dialog
    title="html模板"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="600px"
    top="5vh"
    :before-close="handleClose"
    custom-class="handler-dialog low-code-dialog"
  >
    <div class='dialog-content'>
      <vxe-table
        ref="xTable"
        :data="htmlTemplateList"
        border
        highlight-current-row
        style="width: 100%;"
        @cell-click='handleCellClick'
        @radio-change='handleRadioChange'
      >
        <vxe-table-column
          type="radio"
          width="42px"
          align="center"
        />
        <vxe-table-column field="label" title="类型"></vxe-table-column>
        <vxe-table-column field="html" type='html' title="示例"></vxe-table-column>
      </vxe-table>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="clear">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import _ from 'lodash'
  export default {
    name: 'htmlTemplate',
    data() {
      return {
        dialogVisible: false,
        htmlTemplateList: [
          {
            label: '标签',
            html: '<span class="el-tag el-tag--primary el-tag--light">标签</span>',
            code: 'return `<span class="el-tag el-tag--primary el-tag--light">${cellValue}</span>`'
          },
          {
            label: 'a链接',
            html: '<a href="#">http://www.xxx.com</a>',
            code: 'return `<a href="${cellValue}" target="_black">${cellValue}</a>`'
          },
        ],
        selectedRow: null
      }
    },
    methods: {
      open() {
        this.dialogVisible = true
      },
      handleClose(done) {
        this.clear()
        done()
      },
      clear() {
        this.selectedRow = null
        const xTable = this.$refs.xTable
        xTable.clearRadioRow()
        xTable.clearCurrentRow()
        this.dialogVisible = false
      },
      confirm() {
        if (!this.selectedRow) {
          this.$message.error('请选择一个模板')
          return
        }
        this.$emit('confirm', this.selectedRow)
        this.clear()
      },
      handleRadioChange({ row }) {
        const xTable = this.$refs.xTable
        xTable.setCurrentRow(row)
        this.selectedRow = _.cloneDeep(row)
      },
      handleCellClick({ row }) {
        const xTable = this.$refs.xTable
        xTable.setRadioRow(row)
        this.selectedRow = _.cloneDeep(row)
      }
    }
  }
</script>

<style lang='less' scoped>
  .dialog-content::v-deep {

  }
</style>
