<template>
  <div class="steps-config">
    <n-form-item class="config-item" label="步骤条方向" v-if="elementProps.type !== 'navigation'">
      <el-radio-group v-model="direction">
        <el-radio-button label="horizontal">水平</el-radio-button>
        <el-radio-button label="vertical" >垂直</el-radio-button>
      </el-radio-group>
    </n-form-item>
    <n-form-item class="config-item" v-if="direction === 'vertical'">
      <template slot='label'>
        <span>步骤条宽度</span>
        <el-tooltip content="支持配置垂直方向的进度条宽度，单位为px" popper-class="tooltip-question" placement="top">
          <i style='margin-left: 5px;' class="el-icon-question"></i>
        </el-tooltip>
      </template>
      <el-input-number v-model="stepWidth" :min="180"></el-input-number>
    </n-form-item>
    <n-form-item class="config-item" label="标签放置位置" v-if="elementProps.type !== 'navigation' && direction === 'horizontal'">
      <el-radio-group v-model="labelPlacement">
        <el-radio-button label="horizontal">水平</el-radio-button>
        <el-radio-button label="vertical">垂直</el-radio-button>
      </el-radio-group>
    </n-form-item>
    <n-form-item class="config-item" label="步骤条间隔" v-if="direction === 'vertical'">
      <el-radio-group v-model="stretch">
        <el-radio-button :label="false">紧凑</el-radio-button>
        <el-radio-button :label="true">平均分布</el-radio-button>
      </el-radio-group>
    </n-form-item>
    <n-form-item class="config-item">
      <template slot='label'>
        <span>内容区</span>
        <el-tooltip content="开启显示步骤内容区" popper-class="tooltip-question" placement="top">
          <i style='margin-left: 5px;' class="el-icon-question"></i>
        </el-tooltip>
      </template>
      <el-switch v-model="contentVisible"></el-switch>
    </n-form-item>
    <n-form-item class="config-item" v-if="contentVisible">
      <template slot='label'>
        <span>内容懒加载</span>
        <el-tooltip content="开启后只渲染当前步骤的内容" popper-class="tooltip-question" placement="top">
          <i style='margin-left: 5px;' class="el-icon-question"></i>
        </el-tooltip>
      </template>
      <el-switch v-model="lazy"></el-switch>
    </n-form-item>
    <n-form-item class="config-item" v-if="elementProps.type !== 'navigation'">
      <template slot='label'>
        <span>点状步骤条</span>
        <el-tooltip content="开启后显示为点状图标,只支持线条风格的步骤条" popper-class="tooltip-question" placement="top">
          <i style='margin-left: 5px;' class="el-icon-question"></i>
        </el-tooltip>
      </template>
      <el-switch v-model="progressDot"></el-switch>
    </n-form-item>
  </div>

</template>

<script>
import _ from 'lodash';
  export default {
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      vue: {
        type: String,
      },
    },
    data() {
      return {
      }
    },
    computed: {
      contentVisible: {
        get() {
          return this.elementProps.contentVisible
        },
        set(value) {
          const res = {
            ...this.elementProps,
            contentVisible: value,
          }
          this.$emit('update', res)
        }
      },
      lazy: {
        get() {
          return this.elementProps.lazy
        },
        set(value) {
          const res = {
            ...this.elementProps,
            lazy: value,
          }
          this.$emit('update', res)
        }
      },
      progressDot: {
        get() {
          return this.elementProps.progressDot
        },
        set(value) {
          const res = {
            ...this.elementProps,
            progressDot: value,
          }
          this.$emit('update', res)
        }
      },
      labelPlacement: {
        get() {
          return this.elementProps.labelPlacement
        },
        set(value) {
          const res = {
            ...this.elementProps,
            labelPlacement: value,
          }
          this.$emit('update', res)
        }
      },
      direction: {
        get() {
          return this.elementProps.direction
        },
        set(value) {
          const res = {
            ...this.elementProps,
            direction: value,
          }
          this.$emit('update', res)
        }
      },
      stepWidth: {
        get() {
          return this.elementProps.stepWidth
        },
        set(value) {
          const res = {
            ...this.elementProps,
            stepWidth: value,
          }
          this.$emit('update', res)
        }
      },
      stretch: {
        get() {
          return this.elementProps.stretch
        },
        set(value) {
          const res = {
            ...this.elementProps,
            stretch: value,
          }
          this.$emit('update', res)
        }
      },
    },
    methods: {
    }
  }
</script>

<style lang="less" scoped>
  .steps-config::v-deep {
    div.config-item {
      display: flex;
      align-items: center;
      width: 100%;
      margin-bottom: 8px;
      margin-right: 0;
      .el-form-item__content {
        flex: 1;
        & > * {
          width: 100%;
        }
        .el-radio-group {
          display: flex;
          .el-radio-button {
            flex: 1;
            .el-radio-button__inner {
              width: 100%;
            }
          }
        }
      }
      .el-form-item__label{
        min-width: 100px;
      }
    }
  }
</style>
