<template>
  <el-dialog
    custom-class="low-code-dialog"
    width="400px"
    title="配置"
    :visible.sync="visible"
    :close-on-click-modal="false"
  >
    <div class="form-content">
      <el-form-item label="字段类型">
        <Select
          v-model="value.type"
          :options="typeOption"
          :clearable="false"
        ></Select>
      </el-form-item>
      <template v-if="value.type === 'number'">
        <el-form-item label="百分数">
          <el-switch v-model="value.float"></el-switch>
        </el-form-item>
        <el-form-item label="小数位" v-if="value.float">
          <Select
            v-model="value.floatNum"
            :options="floatType"
            :clearable="false"
          ></Select>
        </el-form-item>
      </template>
      <el-form-item v-if="value.type === 'text'" label="格式">
        <el-input v-model="value.text"></el-input>
      </el-form-item>
      <el-form-item v-if="value.type === 'date'" label="日期格式">
        <Select
          v-model="value.date"
          :options="dataType"
          :clearable="false"
        ></Select>
      </el-form-item>
      <template v-if="value.type === 'js'">
        <div class="code-content-box">
          <js-code
            class="sql-code"
            ref="jsCode"
            v-model="value.js"
            :params-data="paramsData"
            @focus-change="focusChange"
          ></js-code>
          <div v-if="!value.js && !isFocus" class="empty-tips">{{ emptyTips }}</div>
        </div>

        <div class="log-wrapper" v-if="isShowLog">
          <el-button
            class="clear-btn"
            size="mini"
            :disabled="!logList.length"
            @click="handleClearLog"
            >清空日志</el-button
          >
          <el-button class="clear-btn" size="mini" @click="isShowLog = false"
            >关闭</el-button
          >
          <div class="log-list">
            <div class="log-item" v-for="(log, index) in logList" :key="index">
              {{ log }}
            </div>
          </div>
        </div>
      </template>
    </div>

    <span slot="footer" class="dialog-footer">
      <el-button @click.stop="$emit('update:visible', false)">取 消</el-button>
      <el-button type="primary" @click.stop="confirm">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
import JsCode from '@/components/JsCode'
import Select from '@/components/Select'
export default {
  components: {
    JsCode,
    Select,
  },
  props: {
    visible: {
      type: Boolean,
    },
    value: {
      type: Object,
    },
  },
  data() {
    return {
      isShowLog: false,
      isFocus: false,
      logList: [],
      paramsData: [{ key: 'value', 'test': 1625074020000 }, { key: 'index', 'test': 0  }, { key: '$format', 'test': this.$format  }],
      typeOption: [
        { label: '数值', value: 'number' },
        { label: '文本', value: 'text' },
        { label: '日期', value: 'date' },
        { label: '高级', value: 'js' },
      ],
      floatType: [
        { label: 1, value: 1 },
        { label: 2, value: 2 },
        { label: 3, value: 3 },
        { label: 4, value: 4 },
      ],
      dataType: [
        { label: 'yyyy-MM', value: 'yyyy-MM' },
        { label: 'yyyy-MM-dd', value: 'yyyy-MM-dd' },
        { label: 'yyyy-MM-dd hh:mm', value: 'yyyy-MM-dd hh:mm' },
        { label: 'yyyy-MM-dd hh:mm:ss', value: 'yyyy-MM-dd hh:mm:ss' },
        { label: 'yyyy:MM', value: 'yyyy:MM' },
        { label: 'yyyy:MM:dd', value: 'yyyy:MM:dd' },
        { label: 'yyyy:MM:dd hh:mm', value: 'yyyy:MM:dd hh:mm' },
        { label: 'yyyy:MM:dd hh:mm:ss', value: 'yyyy:MM:dd hh:mm:ss' },
      ],
      actions: new Map([
        ['number', this.formatNumber],
        ['text', this.formatText],
        ['date', this.formatDate],
        ['js', this.formatJs],
        ['default', () => console.log('default')],
      ]),
      emptyTips: '1、传入(value, index, $format)三个参数, 分别对应 “当前值”,“当前索引”, “日期格式化方法” \n2、使用return返回结果，示例JS：\n var date = new Date(value * 1000); \n var text = $format("yyyy-MM-dd", date); \n return "第" + (index + 1) + "项" + text;'
    }
  },
  created() {},
  mounted() {},
  methods: {
    formatNumber() {
      const { float, floatNum } = this.value
      if (float) {
        return `return Number(value).toFixed(${floatNum})`
      } else {
        return 'return Number(value)'
      }
    },
    formatText() {
      return this.value.text
    },
    formatDate() {
      return `return $format("${this.value.date}", value)`
    },
    formatJs() {
      return this.value.js
    },
    focusChange(n) {
      if (n) {
        this.isShowLog = false
      }
      this.isFocus = n
    },
    async handleTest() {
      await this.$nextTick()
      if (!this.value.js) {
        return
      }
      const result = await this.$refs.jsCode.handleTest(false)
      this.logList = this.$refs.jsCode.logList
      if (result) {
        return Promise.resolve()
      }
      this.isShowLog = true
      return Promise.reject()
    },
    handleClearLog() {
      this.$refs.jsCode.logList = []
      this.logList = []
    },
    async confirm() {
      const { type } = this.value
      if (type === 'js') {
        await this.handleTest()
      }
      const action = this.actions.get(type) || this.actions.get('default')
      const formatter = action()
      this.$emit('update:visible', false)
      this.$emit('confirm', {
        formatter,
        formatOption: this.value,
      })
    },
  },

  computed: {},

  watch: {},
}
</script>
<style lang='less' scoped>
.form-content::v-deep {
  .el-form-item__label {
    width: 85px;
  }
  .code-content-box {
    position: relative;
    .empty-tips {
      pointer-events: none;
      position: absolute;
      top: 25px;
      left: 60px;
      z-index: 100;
      width: 80%;
      height: 100%;
      font-size: 14px;
      color: #aaaaaa;
      white-space: pre-wrap;
      word-break: break-all;
    }
  }
}

.log-wrapper {
  border-top: 1px dashed gainsboro;
  padding-top: 10px;
}
</style>