<template>
  <div class="widget-config-item">
    <div
      :class="{
        name: true,
        required: requiredArr.includes(name),
      }"
    >
      {{ name }}
    </div>
    <n-form-item class="config-item" label="" :prop="propKey">
      <el-input :placeholder="placeholder" clearable v-model.trim="inputValue" @change='handleChange'></el-input>
      <el-tooltip placement="top">
        <div slot="content" style="white-space: nowrap;">
          唯一标识：{{ elementProps.elementId }}
          <i
            class="el-icon-document-copy"
            @click="copyText(elementProps.elementId)"
          ></i>
        </div>
        <i class="el-icon-notebook-2"></i>
      </el-tooltip>
    </n-form-item>
  </div>
</template>

<script>
import { copyText } from '@/utils'
import _ from 'lodash'
export default {
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      requiredArr: [],
      inputValue: undefined
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
    },
  },
  watch: {
    value: {
      deep: true,
      immediate: true,
      handler(n, o) {
        if (_.isEqual(n, o)) {
          return
        }
        this.inputValue = n
      }
    }
  },
  methods: {
    copyText,
    handleChange(value) {
      const res = { ...this.elementProps, [this.propKey]: value }
      this.$emit('update', res)
    }
  },
}
</script>

