  @import '~@/styles/index.less';
div.widget-config-item::v-deep {
  display: flex;
  align-items: center;
  .name {
    font-family: PingFangSC-Medium;
    font-size: 12px;
    color: #555555;
    letter-spacing: 0;
    font-weight: 500;
    margin-bottom: 5px;
    width: 100px;
  }
  .required::before {
    content: '*';
    color: red;
  }
  div.config-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 8px;

    .el-form-item__content {
      flex: 1;
      display: flex;
      align-items: center;
      .el-icon-notebook-2 {
        font-size: 14px;
        margin-left: 10px;
        &:hover {
          cursor: pointer;
          color: @drop-main-color;
        }
      }
    }
  }
}
.el-tooltip__popper {
  .el-icon-document-copy {
    cursor: pointer;
    margin-left: 5px;
  }
}
</style>
