<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="data-select-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="动态表格配置"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="data-select" v-loading.lock='loading'>
      <data-set-config
        v-if='dialogVisible'
        ref='dataSetConfig'
        :request='{apiRequest, sqlRequest, staticData}'
        :type.sync='activeDataType'
        @response='handleResponse'
      ></data-set-config>
      <div class="data-select-item">
        <el-collapse class='config-collapse' v-model='activeCollapse'>
          <el-collapse-item name='config'>
            <template slot="title">
              <div class='config-collapse-title'>字段映射</div>
            </template>
            <div class="data-select-form data-content">
              <div class="data-config">
                <div class="data-table" :style='{width: "50%"}' v-loading='tableLoading'>
                  <el-collapse class='config-collapse' v-model='activeDataCollapse'>
                    <el-collapse-item name='data'>
                      <template slot="title">
                        <div class='config-collapse-title'>变量映射</div>
                      </template>
                      <vxe-table
                        ref='dataVariablesTable'
                        size='mini'
                        :data="dynamicColumn"
                        border
                        style="width: 100%;"
                        header-cell-class-name="header-cell"
                      >
                        <vxe-table-column field="label" title="变量名">
                          <template #default="{row}">
                            <div>
                              <span style="color: red;" v-if="!['totalCount'].includes(row.key)">*</span>
                              <span>{{row.label}}</span>
                              <el-tooltip :content="getTip(row.key)" popper-class="tooltip-question" placement="top">
                                <i style='margin-left: 5px;' class="el-icon-question"></i>
                              </el-tooltip>
                            </div>
                          </template>
                        </vxe-table-column>
                        <vxe-table-column field="key" title="编码"></vxe-table-column>
                        <vxe-table-column field="path" title="数据路径" min-width="150px">
                          <template #header>
                            <span>数据路径</span>
                            <el-tooltip popper-class="tooltip-question" placement="top">
                              <div style="display: flex; flex-direction: column; align-items: center; justify-content: center;" slot="content">
                                <span style="margin: 10px;">请在响应体结构树上选中“表格数据”节点，点击拖拽至数据路径中</span>
                                <video
                                  style="width: 400px;"
                                  autoplay="autoplay"
                                  loop="loop"
                                  muted
                                  :src="$getImgPrefix('/video/field-map.mp4')"
                                ></video>
                              </div>
                              <i style='margin-left: 5px;' class="el-icon-question"></i>
                            </el-tooltip>
                          </template>
                          <template #default='{ row }'>
                            <div class='drop-zone-path' :data-key='row.key'>
                              <el-tooltip
                                v-if='row.path.length'
                                effect="dark"
                                :content="row.path.join('/')"
                                placement="top"
                              >
                                <el-tag
                                  closable
                                  @close='row.path = []'
                                  disable-transitions
                                >{{row.path[row.path.length - 1]}}</el-tag>
                              </el-tooltip>
                              <span v-else>请从右侧拖拽节点</span>
                            </div>
                          </template>
                        </vxe-table-column>
                      </vxe-table>
                    </el-collapse-item>
                  </el-collapse>
                </div>
                <div class="data-result">
                  <div class="title">
                    <div class="name">
                      响应体
                      <el-button size='mini' @click="showInputResponseDialog">手动录入</el-button>
                      <el-button size='mini' v-if='response' @click='handleClearResponse'>清除</el-button>
                    </div>
                  </div>
                  <div class="body">
                    <vue-json-pretty
                      v-show='response'
                      class="json-pretty no-select"
                      path="response"
                      :data="response"
                      :virtual='true'
                      :itemHeight='32'
                      v-model="selectedPath"
                      @click='copyPath'
                      selectableType="single"
                      :showSelectController="true"
                      :selectOnClickNode="true"
                    >
                    </vue-json-pretty>
                    <div v-if='!response' class='empty-text'>点击“测试”请求响应体</div>
                  </div>
                </div>
              </div>
            </div>
          </el-collapse-item>
        </el-collapse>
      </div>
      <input-dialog @confirm="handleInputResponse" ref="inputDialog"></input-dialog>
      <div class="dnd-cover" v-show="dndCoverStyle" :style="dndCoverStyle"></div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" :loading='loading' @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import DataSetConfig from '@/components/DataSetConfig'
  import VueJsonPretty from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  import _ from 'lodash'
  import {
    getNodeByElementId,
    getResetIdWidget,
    treeUtil,
  } from '@/utils/tree-utils'
  import {
    DELETE_LAYOUT_DATA,
    INIT_DYNAMIC_PROPS,
    UPDATE_LAYOUT_DATA,
  } from '@/config/storeKey'
  import { getLastVersion } from '@/config/module'
  import { getObjectTree, getPathList } from '@/utils/format'
  import dnd from '@/mixins/dragAndDrop'
  import { sleep } from '@/utils'
  import { defaultKeys } from '@/config/widgets'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    name: 'TableData',
    components: {
      VueJsonPretty,
      DataSetConfig,
    },
    inject: ['dndOptions', ...pageInject],
    mixins: [dnd],
    data() {
      return {
        loading: false,
        activeCollapse: ['config'],
        activeDataCollapse: ['data', 'column'],
        dialogVisible: false,
        dataColumn: undefined,
        dynamicColumn: undefined,
        apiRequest: undefined,
        sqlRequest: undefined,
        staticData: undefined,
        selectedPath: undefined,
        responseOptions: [],
        response: undefined,
        dataListKeys: [],
        jsonLoading: false,
        tableLoading: false,
        activeDataType: ''
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      currentElement() {
        return getNodeByElementId(this.layoutData, this.dndOptions.selectedId)
      },
    },
    methods: {
      open({ dynamicColumn, dataColumn, apiRequest, sqlRequest, staticData }) {
        this.dynamicColumn = dynamicColumn ? _.cloneDeep(dynamicColumn) : [
          {
            label: '表格数据',
            key: 'dataList',
            path: [],
          },
          {
            label: '总行数',
            key: 'totalCount',
            path: [],
          },
          {
            label: '列编码',
            key: 'key',
            path: [],
          },
          {
            label: '列标题',
            key: 'name',
            path: [],
          },
          {
            label: '列数据',
            key: 'value',
            path: [],
          },
        ]
        this.dataColumn = dataColumn ? _.cloneDeep(dataColumn) : []
        this.staticData = _.cloneDeep(staticData) || undefined
        this.apiRequest = _.cloneDeep(apiRequest) || undefined
        this.sqlRequest = _.cloneDeep(sqlRequest) || undefined
        this.response = null
        this.selectedPath = undefined
        this.dialogVisible = true
        this.$nextTick(() => {
          this.handleInitDnD()
        })
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      async handleGenerateTableData() {
        const res = {
          dynamicColumn: this.dynamicColumn,
          dataColumn: this.dataColumn,
          apiRequest: this.apiRequest,
          sqlRequest: this.sqlRequest,
          staticData: this.staticData,
        }
        const preSlots = this.currentElement.children.filter(
          (item) =>
            ['slot'].includes(item.elementType) &&
            ['data'].includes(this.getElementPropsById(item.elementId).slotType)
        )
        const preSlotsKeys = preSlots.map(item => this.getElementPropsById(item.elementId).slotName)
        const dataColKeys = this.dataColumn.map(item => item.key)
        const deleteSlotKeys = preSlotsKeys.filter(key => !dataColKeys.includes(key))
        const deleteSlotIds = preSlots.filter(item => deleteSlotKeys.includes(this.getElementPropsById(item.elementId).slotName)).map(item => item.elementId)
        await this._storeDispatch(DELETE_LAYOUT_DATA, { elementIdList: deleteSlotIds })
        const needInitProps = []
        const children = []
        this.dataColumn.forEach((item) => {
          const slotName = item.key
          if (preSlotsKeys.includes(slotName) || !item.isUseSlot) {
            if (preSlotsKeys.includes(slotName)) {
              item.isUseSlot = true
            }
            return
          }
          const widget = {
            elementId: '',
            elementType: 'slot',
            elementVersion: getLastVersion('slot'),
            elementName: item.label,
            defaultProps: {
              slotName,
              slotType: 'data',
              canDropInsideTypes: ['text', 'tag', 'href', 'button'],
            },
            children: [
              {
                elementId: '',
                elementType: 'text',
                elementVersion: getLastVersion('text'),
                elementName: '文本',
                children: [],
                defaultProps: {
                  useSlotProps: true,
                  prop: slotName,
                },
              },
            ],
          }
          const newWidget = getResetIdWidget.call(this, widget)
          treeUtil.forEach([newWidget], (source) => {
            const {children, ...node} = source
            const props = {...node, ...(node.defaultProps || {}) }
            defaultKeys.forEach(key => {
              this.$delete(props, key)
            })
            needInitProps.push({
              elementId: node.elementId,
              props: _.cloneDeep(props),
            })
            defaultKeys.forEach(key => {
              this.$delete(source, key)
            })
          })

          children.push(newWidget)
        })
        this._storeCommit(INIT_DYNAMIC_PROPS, {
          propsList: needInitProps
        })
        this._storeCommit(UPDATE_LAYOUT_DATA, {elementId: this.currentElement.elementId, layout: { children: [...this.currentElement.children, ...children] }})
        return Promise.resolve(res)
      },
      async confirm() {
        const { apiRequest, sqlRequest, staticData } = this.$refs.dataSetConfig.getResult()
        this.apiRequest = apiRequest
        this.sqlRequest = sqlRequest
        this.staticData = staticData
        this.loading = true
        await sleep(10)

        const pathList = this.dynamicColumn.filter(item => ['dataList', 'key', 'name', 'value'].includes(item.key)).map(item => item.path)
        const noPass = pathList.some(path => {
          return !path.length
        })
        if (noPass) {
          this.$message.error('请配置变量映射!')
          this.loading = false
          return
        }
        const res = await this.handleGenerateTableData()
        console.log(res);
        this.loading = false
        this.$emit('confirm', res)
        this.dialogVisible = false
      },
      handleResponse({ response, responseOptions, isStatic }) {
        if (isStatic) {
          if (Array.isArray(response)) {
            this.$message.error('静态数据需传入对象格式，如：{"totalCount": 100, "data": []}')
            return
          }
          this.staticData = response
        }
        this.response = response
        this.responseOptions = responseOptions
      },
      copyPath() {
        // copyText(this.selectedPath, '路径已复制')
      },
      showInputResponseDialog() {
        this.$refs.inputDialog.open({type: 'object', data: this, key: 'response'})
      },
      handleInputResponse(value) {
        this.response = value
        this.responseOptions = getObjectTree(value)
      },
      handleClearResponse() {
        this.response = null
        this.responseOptions = []
      },
      handleDrop(event) {
        const { key } = event.target.dataset
        if (key) {
          const current = this.dynamicColumn.find(item => item.key === key)
          const path = getPathList(this.selectedPath)
          current.path = path.slice(1)
        }
      },
      getTip(key) {
        switch (key) {
          case 'dataList':
            return '表格数据字段，需传入的类型为数组'
          case 'totalCount':
            return '表格总行数字段，需传入数字类型'
          case 'key':
            return '表格中每列所展示的列数据编码'
          case 'name':
            return '表格中每列表头对应的中文名称'
          case 'value':
            return '表格中每列所展示的数据内容'
        }
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      }
    },
  }
</script>

