<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="选择数据源"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-loading.lock="loading">
      <n-form
        class="data-select-form"
        :model="queryForm"
        size="mini"
        inline
        label-with="80px"
        @submit.native.prevent
      >
        <n-form-item label="API编码">
          <el-input
            class="search-input"
            size="mini"
            v-model="queryForm.serviceCode"
            placeholder="请输入"
          >
          </el-input>
        </n-form-item>
        <n-form-item label="API名称">
          <el-input
            class="search-input"
            size="mini"
            v-model="queryForm.serviceName"
            placeholder="请输入"
          >
          </el-input>
        </n-form-item>
        <n-form-item label="所属应用">
          <el-input
            class="search-input"
            size="mini"
            v-model="queryForm.applicationCode"
            placeholder="请输入"
          >
          </el-input>
        </n-form-item>
        <el-button
          type="primary"
          size="mini"
          icon="el-icon-search"
          @click="handleSearch(false)"
        >
        </el-button>
        <el-button type="primary" size="mini" @click="handleSearch(true)">
          重置
        </el-button>
      </n-form>
      <div class="data-table">
        <n-table
          :data="dataList"
          border
          highlight-current-row
          @row-click="handleRowClick"
        >
          <n-table-column width="50px" label="选择">
            <template slot-scope="scope">
              <el-radio
                v-if="
                  selectedRow &&
                    selectedRow.serviceCode === scope.row.serviceCode
                "
              ></el-radio>
              <el-radio v-else :value='false'></el-radio>
            </template>
          </n-table-column>
          <n-table-column prop="serviceCode" label="API编码"></n-table-column>
          <n-table-column prop="serviceName" label="API名称"></n-table-column>
          <n-table-column
            prop="applicationCode"
            label="所属应用"
          ></n-table-column>
          <n-table-column prop="creator" label="创建者"></n-table-column>
          <n-table-column prop="createTime" label="创建时间"></n-table-column>
        </n-table>
        <div class="pagination">
          <pagination
            :page="queryForm.currentPage"
            :limit="queryForm.pageSize"
            :total="totalCount"
            @pagination="handlePageChange"
          />
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import API from '@/api/application'
  import Pagination from '@/components/Pagination'
  import request from '@/request/request'
  import { getCommonHeaders } from '@/utils'

  export default {
    name: 'GatewayDataSelect',
    components: {
      Pagination,
    },
    data() {
      return {
        dialogVisible: false,
        loading: false,
        totalCount: 0,
        queryForm: {
          applicationCode: '',
          currentPage: 1,
          pageSize: 10,
          serviceCode: '',
          serviceName: '',
          status: '1',
        },
        dataList: [],
        selectedRow: null,
      }
    },
    methods: {
      open(data) {
        this.selectedRow = data
        this.dialogVisible = true
        this.handleSearch(true)
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      confirm() {
        if (!this.selectedRow) {
          this.$message.error('请选择数据源')
          return
        }
        this.handleGetServeDetail()
      },
      handleGetServeDetail() {
        this.loading = true
        request({
          url: API.gatewayDataDetail,
          method: 'GET',
          params: {
            serviceCode: this.selectedRow.serviceCode,
          },
          headers: getCommonHeaders(),
        })
          .then((res) => {
            const { data } = res.data
            // const requestBody = JSON.parse(data.requestBody)
            const requestHeader = JSON.parse(data.requestHeader) || []
            const requestParams = JSON.parse(data.requestParams) || []
            const response = JSON.parse(data.responseText)
            const requestText = JSON.parse(data.requestText)
            const params = requestParams.map(item => ({key: item.key, value: item.value, test: item.value}))
            const body = Array.isArray(requestText) ? [] : Object.entries(requestText || {}).map(([key, value]) => ({key, value, test: value}))
            const headers = requestHeader.map(item => ({key: item.key, value: item.value, test: item.value}))
            const protocol = window.location.protocol
            const host = window.location.host
            let apiRequest = {
              url: `${protocol}//${host}/gateway${data.serviceCode}`,
              method: '',
              type: 4,
              paramsData: params.length ? params : [{key: '', value: undefined, test: undefined}],
              bodyData: body.length ? body : [{key: '', value: undefined, test: undefined}],
              headersData: headers.length ? headers : [{key: '', value: undefined, test: undefined}],
            }
            switch (data.requestMethod) {
              case 1:
                apiRequest.method = 'GET'
                break
              case 2:
                apiRequest.method = 'POST'
                break
              case 3:
                apiRequest.method = 'PUT'
                break
              case 4:
                apiRequest.method = 'DELETE'
                break
              case 5:
                apiRequest.method = 'PATCH'
                break
            }
            const result = {
              apiRequest,
              response,
              apiInfo: this.selectedRow,
              detail: data
            }
            this.$emit('confirm', result)
            this.dialogVisible = false
          })
          .catch((e) => {
            this.$message.error('数据解析错误')
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleSearch(reset) {
        this.loading = true
        if (reset) {
          this.queryForm = {
            applicationCode: '',
            currentPage: 1,
            pageSize: 10,
            serviceCode: '',
            serviceName: '',
            status: '1',
          }
        }
        request({
          url: API.gatewayDataList,
          method: 'POST',
          data: this.queryForm,
          headers: getCommonHeaders(),
        })
          .then((res) => {
            const { data } = res.data
            this.totalCount = data.total
            this.dataList = data.list
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleRowClick(row, column, event) {
        this.selectedRow = row
      },
      handlePageChange({ page, size }) {
        this.queryForm.currentPage = page
        this.queryForm.pageSize = size
        this.handleSearch(false)
      },
    },
  }
</script>

<style lang="less" scoped>
  .dialog-content::v-deep {
    .pagination {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 10px;
    }
  }
</style>
