<template>
  <div
    class="layout-placeholder"
    :class="{ 'select-mode': isShowType }"
    :data-zone="`${elementId}_${elementType}`"
  >
    <span class="empty-text" v-if="!isShowType">
      {{ emptyText }}
    </span>
    <div v-else class="select-layout">
      <div class="select-layout-title">选择页面布局</div>
      <div class="select-layout-desc">
        (默认流式布局，可直接拖拽组件进行使用)
      </div>
      <div class="select-layout-button">
        <div class="select-layout-button-item">
          <span class="item-text">流式布局</span>
          <div class="video" v-ripple @click="chooseType('normal')">
            <video
              autoplay="autoplay"
              loop="loop"
              muted
              :src="$getImgPrefix('/video/1.mp4')"
            ></video>
          </div>
        </div>
        <div class="select-layout-button-item">
          <span class="item-text">可拖拽布局</span>
          <div class="video" v-ripple @click="chooseType('dnd')">
            <video
              autoplay="autoplay"
              loop="loop"
              muted
              :src="$getImgPrefix('/video/2.mp4')"
            ></video>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { buildNewWidget, getParentNodeByElementId } from '@/utils/tree-utils'
import { getLastVersion } from '@/config/module'
import { getGlobalConfig, update } from '@/renderer/tools/history'
import { pageInject } from '@/renderer/tools/pageStore'
export default {
  name: 'LayoutPlaceholder',
  inject: ['dndOptions', ...pageInject],
  props: {
    emptyText: {
      type: String,
      default: '拖拽组件到这里',
    },
    showLayoutType: {
      type: Boolean,
      default: false,
    },
    elementId: {
      type: String,
      default: '',
    },
    elementType: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      layoutType: 'normal',
      isShowType: false,
    }
  },
  mounted() {
    this.isShowType = this.showLayoutType
  },
  methods: {
    chooseType(type) {
      if (type === 'normal') {
        this.isShowType = false
        return
      }
      if (type === 'dnd') {
        const oldVal = getGlobalConfig.call(this, [0, 4])
        const parentNode = getParentNodeByElementId(
          this.getStore().layoutData,
          this.elementId
        )
        const styleName = ['page'].includes(this.elementType)
          ? { minHeight: '100%' }
          : {}
        const widget = {
          elementId: '',
          elementType: 'grid-dnd',
          elementVersion: getLastVersion('grid-dnd'),
          elementName: '可拖拽布局',
          children: [],
          defaultProps: {
            styleName,
            isIgnoreDrag: true,
          },
        }
        const dropTargetId = ['slot', 'page'].includes(this.elementType)
          ? this.elementId
          : parentNode.elementId
        const newWidget = buildNewWidget.call(this, widget, dropTargetId, 'center')
        update({
          extra: {
            oldVal,
            val: getGlobalConfig.call(this, [0, 4])
          }
        })
        this.isShowType = false
        this.$nextTick(() => {
          if (this.dndOptions) {
            this.dndOptions.selectedId = newWidget.elementId
          }
        })
      }
    },
  },
}
</script>

