<template>
  <div class="style-config-params" v-if="config.params">
    <n-table
      :data="dynamicStyleConfigList"
      border
      style="width: 100%"
      header-cell-class-name="header-cell"
    >
      <n-table-column prop="name" label="作用部位" min-width="200px">
        <div class="variable-column" slot-scope="scope">
          <el-select v-model="scope.row.name" @change="handleNameChange($event, scope.row)">
            <el-option
              :disabled="dynamicStyleConfigList.some(item => item.name === option.name)"
              :label="option.name"
              :value="option.name"
              v-for="option in styleConfigList"
              :key="option.name"
            >
            </el-option>
          </el-select>
        </div>
      </n-table-column>
      <n-table-column
        min-width="200px"
        prop="query"
        label="CSS选择器"
      ></n-table-column>
      <n-table-column prop="styles" min-width="300px" label="值">
        <template slot-scope="scope">
          <div class="value-input">
            <el-tooltip
              v-if="scope.row.styles !== undefined"
              effect="dark"
              :content="formatTip(scope.row.styles)"
              placement="top"
            >
              <el-tag
                class="value-tag"
                closable
                disable-transitions
                @click="handleTagClick(scope.row, 'styles')"
                @close="scope.row.styles = undefined"
                :type="getTagType(scope.row.styles)"
              >
                {{ formatTag(getElementName(scope.row.styles), 12) }}
              </el-tag>
            </el-tooltip>
            <div v-else class="value-input-content">
              <el-button-group>
                <el-button
                  @click="showInputDialog('object', scope.row, 'styles')"
                  >对象
                </el-button>
                <el-button
                  @click="handleShowVariableSelect(scope.row, 'styles')"
                  >变量
                </el-button>
              </el-button-group>
            </div>
          </div>
        </template>
      </n-table-column>
      <n-table-column width="90" label="操作">
        <template slot-scope="scope">
          <div class="btns">
            <div
              class="add-btn-primary"
              v-if="isShowAdd"
              @click="handleAddConfig"
            >
              +
            </div>
            <div
              class="add-btn-primary"
              v-if="isShowDelete"
              @click="handleDeleteConfig(scope.$index)"
            >
              -
            </div>
          </div>
        </template>
      </n-table-column>
    </n-table>
    <input-dialog
      @confirm="handleInputResponse"
      ref="inputDialog"
    ></input-dialog>
    <variable-select
      ref="variableSelect"
      @confirm="handleVariableConfirm"
    ></variable-select>
  </div>
</template>

<script>
  import elementEditor from '@/mixins/elementEditor'
  import _ from 'lodash'
  import { styleConfig } from '@/config/code-var'
  export default {
    name: 'StyleConfigParams',
    mixins: [elementEditor],
    props: {
      elementId: {
        type: String,
      },
      config: {
        type: Object,
      },
      callbackId: {
        type: String,
      },
    },
    data() {
      return {
        dynamicStyleConfigList: [],
      }
    },
    computed: {
      styleConfigList() {
        const { styleConfigList } = this.getElementPropsById(this.elementId)
        return styleConfigList
      },
      isShowAdd() {
        return this.dynamicStyleConfigList.length < this.styleConfigList.length
      },
      isShowDelete() {
        return this.dynamicStyleConfigList.length > 1
      },
    },
    watch: {
      dynamicStyleConfigList: {
        deep: true,
        handler(n, o) {
          this.config.params.dynamicStyleConfigList = _.cloneDeep(n)
        },
      },
    },
    methods: {
      handleShowVariableSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.variableSelect.open({
          fullVariable: row[key],
          callbackId: this.callbackId,
        })
      },
      handleVariableConfirm(selectedVariable) {
        const { row, key, cb } = this.variableRowDetail
        if (key) {
          this.$set(row, key, selectedVariable)
        }
        cb?.(selectedVariable)
      },
      handleReset() {
        const root = this.styleConfigList[0]
        const configDefine = _.cloneDeep(styleConfig)
        configDefine.name = root.name
        configDefine.query = root.query
        configDefine.styles = undefined
        this.$set(this.config, 'params', {
          dynamicStyleConfigList: [configDefine]
        })
      },
      handleAddConfig() {
        const configDefine = _.cloneDeep(styleConfig)
        configDefine.name = ''
        configDefine.styles = undefined
        this.dynamicStyleConfigList.push(configDefine)
      },
      handleDeleteConfig(index) {
        this.dynamicStyleConfigList.splice(index, 1)
      },
      handleNameChange(name, row) {
        const currentConfig = this.styleConfigList.find(item => item.name === name) || {}
        row.query = currentConfig.query
      }
    },
    created() {
      if (!this.config.params) {
        this.handleReset()
      }
      this.dynamicStyleConfigList = _.cloneDeep(this.config.params.dynamicStyleConfigList)
    },
  }
</script>

<style lang="less" scoped>
  .style-config-params {

  }
</style>
