<template>
  <div class="widget-config-item">
    <div class="name">{{ name }}
      <el-tooltip v-if="options.tips" popper-class="tooltip-question" effect="dark" :content="options.tips" placement="top-start">
        <i class="el-icon-question"></i>
      </el-tooltip>
    </div>
    <div class="container">
      <el-button-group class='flex-button-group'>
        <el-button
          size='mini'
          class='config-item-button'
          v-for="item in radioOptions"
          :key="item.value"
          :type="getType(item.value)"
          @click="value = item.value"
        >
        <span>
          {{ item.label }}
        </span>
        <el-tooltip v-if="item.tips" popper-class="tooltip-question" effect="dark" :content="item.tips" placement="top-start">
          <i class="el-icon-question"></i>
        </el-tooltip>
        </el-button
        >
      </el-button-group>
    </div>
  </div>
</template>

<script>
  import CONFIG from './config.js'
  import { getNodeByElementId, treeUtil } from '@/utils/tree-utils'
  import { formTypes } from '@/config/widgets'
  import { globalEmit } from '@/renderer/tools/eventBus.js'
  import { PROPS } from '@/config/eventKey.js'
  import { pageInject } from '@/renderer/tools/pageStore/index.js'
  export default {
    inject: [...pageInject],
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      optionkey: {
        type: String,
      },
      vue: {
        type: String,
      },
      options: {
        type: Object,
        default: () => ({})
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = {
            ...this.elementProps,
            [this.propKey]: value,
          }
          if (this.isUpdateChildren) {
            switch (this.elementProps.elementType) {
              case 'form-container': {
                const current = getNodeByElementId(this.layoutData, this.elementProps.elementId)
                const formItems = treeUtil.findNodeAll(current.children, node => formTypes.includes(node.elementType))
                formItems.forEach(item => {
                  globalEmit.call(this, PROPS, {
                    elementId: item.elementId,
                    data: {
                      [this.optionkey]: value
                    },
                    pushHistory: false
                  })
                })
                break
              }
            }
          }
          this.$emit('update', res)
        },
      },
      radioOptions() {
        return CONFIG[this.optionkey]
      },
      isUpdateChildren() {
        return ['labelPosition'].includes(this.optionkey)
      }
    },
    methods: {
      getType(value) {
        return this.elementProps[this.propKey] === value ? 'primary' : 'default'
      },
    },
    watch: {
      value(val) {
        this.value = val
      }
    }
  }
</script>

