<template>
  <div class="widget-config-item">
    <div
      :class="{
        name: true,
        required: requiredArr.includes(name),
      }"
    >
      {{ name }}
      <el-tooltip v-if="tip" :content="tip" popper-class="tooltip-question" placement="top">
        <i style='margin-left: 5px;' class="el-icon-question"></i>
      </el-tooltip>
    </div>
    <n-form-item class="config-item" label="" :prop="propKey">
      <el-input :placeholder="placeholder" clearable v-model.trim="inputValue" @change='handleChange'></el-input>
<!--      <el-tooltip content="文本格式化" placement="top">-->
<!--        <i class="iconfont icon-qiehuan" @click="handleOpenFormat"></i>-->
<!--      </el-tooltip>-->
    </n-form-item>
<!--    <string-format ref="stringFormat"></string-format>-->
  </div>
</template>

<script>
  // import StringFormat from '@/components/StringFormat'
  import _ from 'lodash'
  import { UPDATE_DYNAMIC_PROPS } from '@/config/storeKey'
  import { getNodeByElementId, treeUtil } from '@/utils/tree-utils'
  import { formTypes } from '@/config/widgets'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    // components: { StringFormat },
    inject: [...pageInject],
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      tip: {
        type: String,
      },
      optionkey: {
        type: String,
      },
    },
    data() {
      return {
        requiredArr: [],
        inputValue: undefined
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
      },
      isUpdateChildren() {
        return ['labelWidth'].includes(this.optionkey)
      }
    },
    watch: {
      value: {
        deep: true,
        immediate: true,
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.inputValue = n
        }
      }
    },
    methods: {
      handleOpenFormat() {
        this.$refs.stringFormat.open({ content: this.value })
      },
      handleChange(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        if (this.isUpdateChildren) {
          switch (this.elementProps.elementType) {
            case 'form-container': {
              const current = getNodeByElementId(this.layoutData, this.elementProps.elementId)
              const formItems = treeUtil.findNodeAll(current.children, node => formTypes.includes(node.elementType))
              const needUpdateProps = formItems.map(item => ({
                [item.elementId]: {
                  [this.optionkey]: value
                }
              }))
              this._storeCommit(UPDATE_DYNAMIC_PROPS, {
                dynamicPropsList: needUpdateProps
              })
              break
            }
          }
        }
        this.$emit('update', res)
      }
    },
  }
</script>

