<template>
  <div class="widget-config-item">
    <n-form-item
      class="config-item"
      label="启用分页"
      prop="showPage"
      label-width='100px'
    >
      <el-switch v-model="showPage"></el-switch>
    </n-form-item>
    <template v-if='showPage'>
      <n-form-item
        class="config-item"
        label="前端分页"
        prop="isLocalPage"
        label-width='100px'
      >
        <el-switch v-model="isLocalPage"></el-switch>
      </n-form-item>
      <n-form-item class="config-item" label="布局" prop='paginationLayout' label-width='100px'>
        <el-select style='width: 100%;' v-model='paginationLayout'>
          <el-option v-for='option in layoutsOptions' :key='option.value' :label='option.label' :value='option.value'></el-option>
        </el-select>
      </n-form-item>
      <n-form-item class="config-item" label="对齐方式" prop='paginationAlign' label-width='100px'>
        <el-select style='width: 100%;' v-model='paginationAlign'>
          <el-option v-for='option in alignOptions' :key='option.value' :label='option.label' :value='option.value'></el-option>
        </el-select>
      </n-form-item>
      <n-form-item class="config-item" label="尺寸大小" prop='paginationSize' label-width='100px'>
        <el-select style='width: 100%;' v-model='paginationSize'>
          <el-option v-for='option in sizeOptions' :key='option.value' :label='option.label' :value='option.value'></el-option>
        </el-select>
      </n-form-item>
      <n-form-item
        class="config-item"
        label="显示背景色"
        prop="pagerBackground"
        label-width='100px'
      >
        <el-switch v-model="pagerBackground"></el-switch>
      </n-form-item>
      <n-form-item class="config-item" label="每页显示数" prop='size' label-width='100px'>
        <el-input-number
          style="width: 100%"
          v-model="size"
          :min="1"
          step-strictly
        ></el-input-number>
      </n-form-item>
    </template>
  </div>
</template>

<script>
  export default {
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        alignOptions: [
          {
            label: '左',
            value: 'flex-start',
          },
          {
            label: '中',
            value: 'center',
          },
          {
            label: '右',
            value: 'flex-end',
          },
        ],
        sizeOptions: [
          {
            label: '小',
            value: 'mini',
          },
          {
            label: '中',
            value: 'small',
          },
          {
            label: '大',
            value: 'medium',
          },
        ],
        layoutsOptions: [
          {
            label: '默认布局',
            value: 'default',
          },
          {
            label: '布局1',
            value: 'type1',
          },
          {
            label: '布局2',
            value: 'type2',
          },
          {
            label: '布局3',
            value: 'type3',
          },
        ],
      }
    },
    computed: {
      paginationAlign: {
        get() {
          return this.elementProps.paginationAlign
        },
        set(value) {
          const res = { ...this.elementProps, paginationAlign: value }
          this.$emit('update', res)
        },
      },
      isLocalPage: {
        get() {
          return this.elementProps.isLocalPage
        },
        set(value) {
          const res = { ...this.elementProps, isLocalPage: value }
          this.$emit('update', res)
        },
      },
      paginationSize: {
        get() {
          return this.elementProps.paginationSize
        },
        set(value) {
          const res = { ...this.elementProps, paginationSize: value }
          this.$emit('update', res)
        },
      },
      pagerBackground: {
        get() {
          return this.elementProps.pagerBackground
        },
        set(value) {
          const res = { ...this.elementProps, pagerBackground: value }
          this.$emit('update', res)
        },
      },
      showPage: {
        get() {
          return this.elementProps.showPage
        },
        set(value) {
          const res = { ...this.elementProps, showPage: value }
          this.$emit('update', res)
        },
      },
      paginationLayout: {
        get() {
          return this.elementProps.paginationLayout
        },
        set(value) {
          const res = { ...this.elementProps, paginationLayout: value }
          this.$emit('update', res)
        },
      },
      size: {
        get() {
          return this.elementProps.size
        },
        set(value) {
          const res = { ...this.elementProps, size: value }
          this.$emit('update', res)
        },
      },
    },
  }
</script>

<style lang="less" scoped>
  div.widget-config-item::v-deep {
    .name {
      font-family: PingFangSC-Medium;
      font-size: 12px;
      color: #555555;
      letter-spacing: 0;
      font-weight: 500;
      margin-bottom: 5px;
    }

    .required::before {
      content: '*';
      color: red;
    }

    div.config-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 8px;

      .el-form-item__content {
        flex: 1;
      }
    }
  }
</style>
