<template>
  <div class="option-config">
    表头分组
    <div class="config-btns-wrapper">
      <el-button type="primary" v-if="elementProps.tableHeaderGroup.length === 0" @click="handleAddRules">表头分组配置</el-button>
      <div v-else class="config-item">
        <el-button type="text" @click="handleEditRules">编辑表头分组</el-button>
        <i title="删除配置" class="el-icon-delete delete-icon" @click="handleDeleteRules" />
      </div>
    </div>
    <el-dialog
      custom-class="header-group-config"
      title="表头分组配置"
      :visible="dialogVisible"
      :close-on-click-modal="true"
      :modal-append-to-body="true"
      :append-to-body="true"
      :before-close="beforeClose"
      width="330px"
      top="0"
    >
      <div class="dialog-content">
        <div class="static-set-cont">
          <div>
            <!-- 树形编辑器开始 -->
            <n-tree
              ref="tree"
              class="option-tree"
              :data="treeData"
              node-key="id"
              icon-class="icon-right"
              draggable
              :expand-on-click-node="false"
              :allowDrop="allowDrop"
              @node-drop="handleDropChange"
            >
              <span class="custom-tree-node" slot-scope="{ node, data }">
                <span class="label">{{ node.label }}</span>
                <span>
                  <el-button
                    type="text"
                    size="mini"
                    @click="() => edit(node, data)"
                  >
                    <i class="el-icon-edit"></i>
                  </el-button>

                  <el-button
                    v-if="!data.isLeaf"
                    type="text"
                    size="mini"
                    @click="append(node, data)"
                  >
                    <i class="el-icon-plus"></i>
                  </el-button>
                  <el-button type="text" size="mini" @click="remove(node, data)">
                    <i class="el-icon-delete" style="color: red"></i>
                  </el-button>
                </span>
              </span>
            </n-tree>
            <!-- 树形编辑器结束 -->

            <el-button
              class="option-btn"
              type="text"
              @click="handleAddOption"
            >
              <i class="iconfont el-icon-circle-plus-outline"></i>
              添加分组
            </el-button>
          </div>

        </div>
        <el-dialog
          title="分组配置"
          :visible="popoverVisible"
          :close-on-click-modal="false"
          :modal-append-to-body="true"
          :append-to-body="true"
          :before-close="beforeSaveClose"
          width="350px"
          top="15vh"
        >
          <div class="variable-body">
            <n-form
              class="popover-config-form"
              ref="variableForm"
              :model="variableForm"
              :rules="rules"
              inline
              label-width="85px"
              @submit.native.prevent
            >
              <n-form-item label="分组标题" prop="label">
                <el-input v-model="variableForm.label"></el-input>
              </n-form-item>
              <n-form-item label="叶子节点" prop="isLeaf">
                <el-switch :disabled="!variableForm.isLeaf && variableForm.children && variableForm.children.length" v-model="variableForm.isLeaf" @change="variableForm.field = ''"></el-switch>
              </n-form-item>
              <n-form-item v-if="variableForm.isLeaf" label="数据列" prop="field">
                <el-select v-model="variableForm.field" @change="handleFieldChange">
                  <el-option
                    v-for="col in column"
                    :key="col.key"
                    :label="col.label"
                    :value="col.key"
                    :disabled="isDisabledField(col.key)"
                  ></el-option>
                </el-select>
              </n-form-item>
              <n-form-item v-if="!variableForm.isLeaf" label="对齐方式" prop="align">
                <el-button-group>
                  <el-button
                    :type="variableForm.align === 'left' ? 'primary' : ''"
                    @click="variableForm.align = 'left'"
                    >左
                  </el-button>
                  <el-button
                    :type="variableForm.align === 'center' ? 'primary' : ''"
                    @click="variableForm.align = 'center'"
                  >
                    中
                  </el-button>
                  <el-button
                    :type="variableForm.align === 'right' ? 'primary' : ''"
                    @click="variableForm.align = 'right'"
                  >
                    右
                  </el-button>
                </el-button-group>
              </n-form-item>
            </n-form>
          </div>
          <div slot="footer" class="dialog-footer">
            <el-button @click="popoverVisible = false">取 消</el-button>
            <el-button type="primary" @click="handleConfirm">确 定</el-button>
          </div>
        </el-dialog>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getValidators, required } from '@/utils/validateUtils'
import _ from 'lodash'
import treeTool from 'tree-tool'
import { Popover } from 'element-ui'
import { getRandomHexadecimal } from '@/utils/tree-utils'

const treeUtil = treeTool.createInstance({
  id: 'field',
  children: 'children',
  pid: 'parentField',
})

export default {
  components: {
    nPopover: Popover
  },
  props: {
    elementId: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      dialogVisible: false,
      variableForm: {
        label: '',
        isLeaf: false,
        field: '',
        align: 'left'
      },
      rules: {
        label: getValidators([
          {
            required: true,
            message: '请输入分组标题',
            include: required,
            trigger: 'change',
          },
        ]),
        field: getValidators([
          {
            required: true,
            message: '请选择数据列',
            include: required,
            trigger: 'change',
          },
        ]),
      },
      popoverVisible: false,
      editMode: null, // 编辑模式 ['root'  =>根节点添加 ; 'edit' => '节点编辑'; 'add' =>'节点添加子节点']
      currentData: null, // 当前选中的节点数据
      treeData: [], //树渲染所用的数据
      lastElementId: null, // 上一次的组件id, 用于判断是否更新
      loading: false,
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps.tableHeaderGroup
      },
      set(value) {
        this.$emit('update', value)
      }
    },
    column() {
      return this.elementProps.dataColumn
    }
  },
  methods: {
    async handleAddOption() {
      this.popoverVisible = true
      await this.$nextTick()
      this.$refs.variableForm.resetFields()
      this.variableForm = {
        label: '',
        children: [],
        isLeaf: false,
        field: '',
        align: 'left'
      }
      this.editMode = 'root'
      this.currentData = null
    },
    handleConfirm() {
      this.$refs.variableForm.validate((valid) => {
        if (!valid) {
          return
        }
        let tempStaticData = _.cloneDeep(this.value || [])
        let form = _.cloneDeep(this.variableForm)
        const { currentData } = this
        switch (this.editMode) {
          // 根节点添加
          case 'root':
            {
              form.id = getRandomHexadecimal(6)
              tempStaticData.push(form)
              this.$refs.tree.append(form)
            }
            break
          // 添加
          case 'add': {
            form.id = getRandomHexadecimal(6)
            const currentNode = treeUtil.findNode(tempStaticData, node => node.id === currentData.id)
            if (currentNode.children) {
              currentNode.children.push(form)
            } else {
              currentNode.children = [form]
            }
            this.$refs.tree.append(form, this.currentData.id)
            break
          }
          // 编辑
          case 'edit':
            treeUtil.forEach(tempStaticData, (item) => {
              if (item.id === currentData.id) {
                Object.keys(form).forEach((key) => {
                  if (key !== 'children') {
                    item[key] = form[key]
                  }
                })
              }
            })
            Object.keys(form).forEach((key) => {
              if (key !== 'children') {
                this.currentData[key] = form[key]
              }
            })
            break
          default:
        }
        this.popoverVisible = false
        this.value = _.cloneDeep(tempStaticData)
      })
    },
    async edit(node, data) {
      this.popoverVisible = true
      await this.$nextTick()
      this.$refs.variableForm.resetFields()
      this.variableForm = {
        id: data.id,
        field: data.field,
        label: data.label,
        children: data.children || [],
        isLeaf: data.isLeaf,
        align: data.align
      }
      this.currentData = data
      this.editMode = 'edit'
    },
    async append(node, data) {
      this.popoverVisible = true
      await this.$nextTick()
      this.$refs.variableForm.resetFields()
      this.variableForm = {
        label: '',
        children: [],
        isLeaf: false,
        field: '',
        align: 'left'
      }
      this.currentData = data
      this.editMode = 'add'
    },

    remove(node, data) {
      this.$refs.tree.remove(data.id)
      this.value = _.cloneDeep(this.treeData)
    },
    handleShownPopover() {
      this.$nextTick(() => {
        this.$refs.referenceBtn.$el.click()
      })
    },
    allowDrop(draggingNode, dropNode, type) {
      return !dropNode.data.isLeaf
    },
    handleDropChange(dragNode, dropNode, placement) {
      let tempStaticData = _.cloneDeep(this.value)
      const targetNode = treeUtil.findNode(tempStaticData, node => node.id === dropNode.key)
      const draggingNode = treeUtil.findNode(tempStaticData, node => node.id === dragNode.key)
      treeUtil.removeNode(tempStaticData, node => node.id === dragNode.key)
      switch (placement) {
        case 'inner': {
          if (targetNode.children) {
            targetNode.children.push(draggingNode)
          } else {
            targetNode.children = [draggingNode]
          }
          break
        }
        case 'before': {
          treeUtil.insertBefore(tempStaticData, draggingNode, targetNode)
          break
        }
        case 'after': {
          treeUtil.insertAfter(tempStaticData, targetNode, draggingNode)
          break
        }
      }
      this.value = _.cloneDeep(tempStaticData)
    },
    isDisabledField(key) {
      return !!treeUtil.findNode(this.treeData, node => node.field === key)
    },
    handleAddRules() {
      this.treeData = _.cloneDeep(this.value)
      this.dialogVisible = true
    },
    handleEditRules() {
      this.treeData = _.cloneDeep(this.value)
      this.dialogVisible = true
    },
    handleDeleteRules() {
      this.value = []
    },
    beforeClose(done) {
      this.dialogVisible = false
      done()
    },
    beforeSaveClose(done) {
      this.popoverVisible = false
      done()
    },
    handleFieldChange(field) {
      if (field) {
        this.variableForm.label = this.column.find(item => item.key === field)?.label
      }
    }
  },
}
</script>

