<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="选择数据源"
    :visible.sync="dialogVisible"
    width="1000px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <n-form
      class="data-select-form"
      label-position="top"
      :model='queryForm'
      size='mini'
      @submit.native.prevent
      v-loading.lock="loading"
    >
      <n-form-item>
        <div class="table-search">
          <el-select style='width: 200px; margin-right: 10px;' v-model="queryForm.condition.type" placeholder="请选择" @change="handleSearch">
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <el-input
            class="search-input"
            size="mini"
            v-model="queryForm.condition.name"
            placeholder="输入名称查询"
          >
          </el-input>
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-search"
            @click="handleSearch"
          >
          </el-button>
        </div>
      </n-form-item>
      <n-form-item>
        <n-table
          :data="dataList"
          border
          highlight-current-row
          @row-click="handleRowClick"
        >
          <n-table-column width="50px" label="选择">
            <template slot-scope="scope">
              <el-radio
                v-if="selectedRow && selectedRow.id === scope.row.id"
              ></el-radio>
              <el-radio v-else :value='false'></el-radio>
            </template>
          </n-table-column>
          <n-table-column prop="name" label="名称">
          </n-table-column>
          <n-table-column prop="description" label="描述">
          </n-table-column>
          <n-table-column prop="type" label="类型">
            <template slot-scope="scope">
              <span>{{getTypeLabel(scope.row.type)}}</span>
            </template>
          </n-table-column>
          <n-table-column prop="databaseName" label="数据库">
          </n-table-column>
        </n-table>
        <div class="pagination">
          <pagination
            :page="queryForm.pagination.pagenum"
            :limit="queryForm.pagination.pagesize"
            :total="totalCount"
            @pagination="handlePageChange"
          />
        </div>
      </n-form-item>
    </n-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import API from '@/api/application'
  import Pagination from '@/components/Pagination'

  export default {
    name: 'SqlDataSelect',
    components: {
      Pagination
    },
    data() {
      return {
        dialogVisible: false,
        loading: false,
        totalCount: 0,
        queryForm: {
          pagination: {
            pagenum: 1,
            pagesize: 10,
          },
          condition: {
            name: '',
            type: 'mysql',
            scene: 'data',
            // status: 1,
          }
        },
        typeList: [
          {
            label: 'Mysql',
            value: 'mysql'
          },
          {
            label: 'Oracle',
            value: 'oracle'
          },
          {
            label: 'ClickHouse',
            value: 'clickhouse'
          },
          {
            label: 'DB2',
            value: 'db2'
          },
          {
            label: 'OceanBase',
            value: 'oceanbase'
          },
        ],
        dataList: [],
        selectedRow: null
      }
    },
    methods: {
      open(data) {
        this.selectedRow = data
        this.dialogVisible = true
        this.handleSearch()
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      confirm() {
        if (!this.selectedRow) {
          this.$message.error('请选择数据源')
          return
        }
        this.$emit('confirm', this.selectedRow)
        this.dialogVisible = false
      },
      handleSearch() {
        this.loading = true
        this.$http({
          url: API.sqlDataSource,
          data: this.queryForm,
        })
          .then(({ data }) => {
            this.totalCount = data.rowCount
            this.dataList = data.records
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleRowClick(row, column, event) {
        this.selectedRow = row
      },
      handlePageChange({ page, size }) {
        this.queryForm.pagination.pagenum = page
        this.queryForm.pagination.pagesize = size
        this.handleSearch()
      },
      getTypeLabel(type) {
        return this.typeList.find(item => item.value === type)?.label || type
      }
    }
  }
</script>

<style lang="less" scoped>
  .data-select-form::v-deep {
    .table-search {
      flex: 1;
      display: flex;
      align-items: center;
      float: right;
      .search-input {
        margin-right: 10px;
        .el-input-group__prepend {
          min-width: 100px;
        }
      }
    }
    .pagination {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 10px;
    }
  }
</style>
