<template>
  <n-form-item class="config-item" :label="name" :prop="propKey">
    <el-select
      v-model="value"
      allow-create
      filterable
      default-first-option
      clearable
      placeholder="请选择"
    >
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      >
      </el-option>
    </el-select>
  </n-form-item>
</template>

<script>
  import CONFIG from './config.js'
  export default {
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      optionkey: {
        type: String,
      },
      vue: {
        type: String,
      },
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = {
            ...this.elementProps,
            [this.propKey]: value,
          }
          this.$emit('update', res)
        },
      },
      options() {
        return CONFIG[this.optionkey]
      },
    },
  }
</script>

<style lang="less" scoped>
  div.config-item::v-deep {
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 8px;
    margin-right: 0;
    .el-form-item__label {
      min-width: 100px;
    }
    .el-form-item__content {
      flex: 1;
      & > * {
        width: 100%;
      }
    }
  }
</style>
