<template>
  <div class="variable-input">
    <div v-if="elementProps.dynamicColumn" style="font-size:12px;color:#666;padding:10px;">备注：当表格数据的列编码和下方配置的列编码匹配时配置才生效</div>
    <div class="inner-content">
      <div class="title" v-popover:popover>
        <el-checkbox v-if="deleteMode" :indeterminate="isIndeterminate" v-model="isSelectAll" @change="handleCheckAllChange"></el-checkbox>
        <span class="col-title">列标题</span>
        <span class="key">列编码</span>
      </div>
      <draggable
        v-model="value"
        forceFallback="true"
        group="column"
        animation="300"
        handle=".el-icon-rank"
      >
        <transition-group>
          <div class="content" :style='{opacity: col.isHidden ? "0.5" : "1"}' v-for="(col, index) in value" :key="col.key">
            <el-checkbox v-if="deleteMode" v-model="selectedList" :label="col.key" @change="radioChange"></el-checkbox>
            <i class="icon el-icon-rank" />
            <span class="label">{{ col.label }}</span>
            <span class="key">{{ col.key }}</span>
            <div class="caozuo">
              <el-button type="text" @click="handleEditColumn(index, col)"
                >编辑
              </el-button>
              <el-button type="text" @click="handleDeleteColumn(col.key)"
                >删除
              </el-button>
            </div>
          </div>
        </transition-group>
      </draggable>
    </div>
    <template v-if="!deleteMode">
      <div style="display: flex; align-items: center;">
        <div
          class="add-btn-primary"
          @click="handleAddColumn"
        >
          +
        </div>
        <el-button style="margin-left: 20px;" size="mini" type="danger" @click="deleteMode = true">批量删除</el-button>
      </div>
    </template>
    <template v-else>
      <el-button size="mini" @click="deleteMode = false">取消</el-button>
      <el-button size="mini" type="danger" @click="doDelete">删除</el-button>
    </template>
    <template v-if="!elementProps.dynamicColumn">
      <el-divider></el-divider>
      <HeaderGroupConfig
        :elementId="elementId"
        :propKey="propKey"
        :elementProps="elementProps"
        @update="handleUpdateHeaderGroup"
      />
    </template>
    <el-popover
      width="300"
      trigger="manual"
      ref="popover"
      popper-class="has-modal-popper"
      placement="bottom"
      :visible-arrow="false"
      :title="variableForm.isAdd ? '新增' : '编辑'"
      v-model="popoverVisible"
      @after-enter="handleShowModal"
      @hide="handleHideModal"
    >
      <div class="variable-body">
        <n-form
          ref="variableForm"
          :model="variableForm"
          :rules="rules"
          label-width="95px"
          @submit.native.prevent
        >
          <n-form-item label="列标题" prop="label">
            <el-input v-model.trim="variableForm.label"></el-input>
          </n-form-item>
          <n-form-item label="列编码" prop="key">
            <el-input v-model.trim="variableForm.key"></el-input>
          </n-form-item>
          <n-form-item label="对齐方式" prop="align">
            <el-button-group>
              <el-button
                :type="variableForm.align === 'left' ? 'primary' : ''"
                @click="variableForm.align = 'left'"
                >左
              </el-button>
              <el-button
                :type="variableForm.align === 'center' ? 'primary' : ''"
                @click="variableForm.align = 'center'"
              >
                中
              </el-button>
              <el-button
                :type="variableForm.align === 'right' ? 'primary' : ''"
                @click="variableForm.align = 'right'"
              >
                右
              </el-button>
            </el-button-group>
          </n-form-item>
          <n-form-item label="列固定" prop="fixed">
            <el-button-group>
              <el-button
                :type="variableForm.fixed === undefined ? 'primary' : ''"
                @click="variableForm.fixed = undefined"
              >
                不固定
              </el-button>
              <el-button
                :type="variableForm.fixed === 'left' ? 'primary' : ''"
                @click="variableForm.fixed = 'left'"
                >左
              </el-button>
              <el-button
                :type="variableForm.fixed === 'right' ? 'primary' : ''"
                @click="variableForm.fixed = 'right'"
              >
                右
              </el-button>
            </el-button-group>
          </n-form-item>
          <n-form-item label="列宽度" prop="width">
            <el-input v-model.trim="variableForm.width"></el-input>
          </n-form-item>
          <n-form-item label="列最小宽度" prop="minWidth">
            <el-input v-model.trim="variableForm.minWidth"></el-input>
          </n-form-item>
          <n-form-item label="列格式化" prop="formatter">
            <el-button :disabled="variableForm.isUseSlot" size='mini' type='primary' @click='handleShowFormat' v-if='!variableForm.formatter'>配置</el-button>
            <div class='formatter-item' v-else>
              <el-button :disabled="variableForm.isUseSlot" type='text' @click='handleShowFormat'>{{ formatterLabel }}</el-button>
              <i style='margin-left: 10px;' class='iconfont el-icon-delete' @click='variableForm.formatter = undefined'></i>
            </div>
          </n-form-item>
          <n-form-item label="开启列排序" prop="sortable">
            <el-switch v-model="variableForm.sortable"></el-switch>
          </n-form-item>
          <n-form-item label="列隐藏" prop="isHidden">
            <el-switch v-model="variableForm.isHidden"></el-switch>
          </n-form-item>
          <n-form-item prop="isUseSlot">
            <template slot='label'>
              <span>开启列插槽</span>
              <el-tooltip content="开启列插槽可以自定义列的展示内容，但是列格式化会失效。注意：开启会影响性能" popper-class="tooltip-question" placement="top">
                <i style='margin-left: 5px;' class="el-icon-question"></i>
              </el-tooltip>
            </template>
            <el-switch v-model="variableForm.isUseSlot"></el-switch>
          </n-form-item>
        </n-form>
        <DataFormat @confirm='handleFormatterConfirm' ref='dataFormat'></DataFormat>
        <div style="text-align: right; margin: 0">
          <el-button type="primary" size="mini" @click="handleConfirm"
            >确认
          </el-button>
          <el-button size="mini" type="text" @click="popoverVisible = false"
            >取消
          </el-button>
        </div>
      </div>
    </el-popover>
  </div>
</template>

<script>
  import { getValidators, required } from '@/utils/validateUtils'
  import _ from 'lodash'
  import draggable from 'vuedraggable'
  import { treeUtil, getNodeByElementId, buildNewWidget } from '@/utils/tree-utils'
  import { DELETE_LAYOUT_DATA, UPDATE_DYNAMIC_PROPS } from '@/config/storeKey'
  import { getLastVersion } from '@/config/module'
  import DataFormat from '@/components/DataFormat'
  import { getGlobalConfig } from '@/renderer/tools/history'
  import { pageInject } from '@/renderer/tools/pageStore'
  import HeaderGroupConfig from './HeaderGroupConfig.vue';

  const columnData = {
    label: '',
    key: '',
    align: 'left',
    width: undefined,
    minWidth: 160,
    sortable: false,
    isHidden: false,
    isUseSlot: false,
    path: [],
    formatter: undefined,
    fixed: undefined,
}

  export default {
    components: {
      draggable,
      DataFormat,
      HeaderGroupConfig
    },
    inject: ['dndOptions', ...pageInject],
    props: {
      elementId: {
        type: String,
      },
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        variableForm: {},
        rules: {
          label: getValidators([
            {
              message: '请输入列标题',
              include: required,
              trigger: 'change',
              required: true
            },
          ]),
          key: getValidators([
            {
              message: '请输入列字段',
              include: required,
              trigger: 'change',
              required: true
            },
          ]),
        },
        popoverVisible: false,
        edtingIndex: undefined,
        deleteMode: false,
        isSelectAll: false,
        isIndeterminate: false,
        selectedList: []
      }
    },
    computed: {
      layoutData() {
        return this.getStore().layoutData
      },
      dynamicProps() {
        return this.getStore().dynamicProps
      },
      currentElement() {
        return getNodeByElementId(this.layoutData, this.elementProps.elementId)
      },
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
        set(value) {
          const res = { ...this.elementProps, [this.propKey]: value }
          this.$emit('update', res)
        },
      },
      formatterLabel() {
        const type = this.variableForm.formatter?.type
        switch (type) {
          case 'number':
            return '数值'
          case 'string':
            return '文本'
          case 'date':
            return '日期'
          case 'js':
            return '高级'
          default:
            return ''
        }
      }
    },
    methods: {
      errorToast() {
        this.$message.error('至少要保留一列')
      },
      doDelete() {
        if(this.selectedList.length === this.value.length) {
          return this.errorToast()
        }
        this.value = this.value.filter(item => !this.selectedList.includes(item.key))
        const deleteSlotIds = this.selectedList.filter(key => this.value.some(item => item.key === key && item.isUseSlot)).map(key => {
          const deleteElement = this.currentElement.children.find(c => this.getElementPropsById(c.elementId).slotName === key)
          return deleteElement.elementId
        })
        this._storeDispatch(DELETE_LAYOUT_DATA, { elementIdList: deleteSlotIds })
        this.dndOptions.selectedId = this.currentElement.elementId
        this.handleCheckAllChange(false)
        this.deleteMode = false
      },
      handleCheckAllChange(val) {
        this.selectedList = val ? this.value.map(item => item.key) : []
        this.isIndeterminate = false;
      },
      radioChange() {
        let checkedCount = this.selectedList.length;
        this.isSelectAll = checkedCount === this.value.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.value.length;
      },
      handleEditColumn(index, value) {
        this.popoverVisible = true
        this.$nextTick()
        this.$refs.variableForm.resetFields()
        this.variableForm = _.cloneDeep({ ...value, isAdd: false })
        this.edtingIndex = index
      },
      handleDeleteColumn(key) {
        if (this.value.length === 1) {
          return this.errorToast()
        }
        const current = this.value.find(item => item.key === key)
        if (current.isUseSlot) {
          const deleteKey = key
          const deleteElement = this.currentElement.children.find(c => this.getElementPropsById(c.elementId).slotName === deleteKey)
          this._storeDispatch(DELETE_LAYOUT_DATA, { elementId: deleteElement.elementId })
          this.dndOptions.selectedId = this.currentElement.elementId
        }

        this.value = this.value.filter(item => item.key !== key)
      },
      handleAddColumn() {
        this.popoverVisible = true
        this.$nextTick()
        this.$refs.variableForm.resetFields()
        this.variableForm = _.cloneDeep(columnData)
        this.variableForm.isAdd = true
        this.edtingIndex = undefined
      },
      handleConfirm() {
        this.$refs.variableForm.validate((valid) => {
          if (!valid) {
            return
          }
          const oldStore = getGlobalConfig.call(this)
          let tempValue = _.cloneDeep(this.value)
          const form = _.cloneDeep(this.variableForm)
          delete form.isAdd
          if (this.edtingIndex === undefined) {
            if (tempValue.some(item => item.key === form.key)) {
              this.$message.error('列编码不能重复！')
              return
            }
            if (form.isUseSlot) {
              const slotName = form.key
              const widget = {
                elementId: '',
                elementType: 'slot',
                elementVersion: getLastVersion('slot'),
                elementName: form.label,
                defaultProps: {
                  canDropInsideTypes: ['text', 'tag', 'href', 'button'],
                  slotName,
                  slotType: 'data',
                },
                children: [
                  {
                    elementId: '',
                    elementType: 'text',
                    elementVersion: getLastVersion('slot'),
                    elementName: '文本',
                    children: [],
                    defaultProps: {
                      useSlotProps: true,
                      prop: slotName
                    }
                  },
                ],
              }

              buildNewWidget.call(this, widget, this.currentElement.elementId, 'center')
            }

            tempValue.push(form)
            const res = {
              ...this.elementProps,
              [this.propKey]: tempValue,
            }
            this.$emit('update', res, { oldVal: oldStore, val: getGlobalConfig.call(this) })
          } else {
            const oldKey = tempValue[this.edtingIndex].key
            const newKey = form.key
            if (oldKey !== newKey && tempValue.some(item => item.key === newKey)) {
              this.$message.error('列编码不能重复！')
              return
            }
            if (form.isUseSlot) {
              const newLabel = form.label
              let current = this.currentElement.children.find(c => this.getElementPropsById(c.elementId).slotName === oldKey)
              if (!current) {
                const slotName = form.key
                const widget = {
                  elementId: '',
                  elementType: 'slot',
                  elementVersion: getLastVersion('slot'),
                  elementName: form.label,
                  defaultProps: {
                    slotName,
                    slotType: 'data',
                    canDropInsideTypes: ['text', 'tag', 'href', 'button'],
                  },
                  children: [
                    {
                      elementId: '',
                      elementType: 'text',
                      elementVersion: getLastVersion('slot'),
                      elementName: '文本',
                      children: [],
                      defaultProps: {
                        useSlotProps: true,
                        prop: slotName
                      }
                    },
                  ],
                }

                current = buildNewWidget.call(this, widget, this.currentElement.elementId, 'center')
              }
              const needUpdateProps = []

              if (oldKey !== newKey) {
                treeUtil.forEach(current.children, node => {
                  needUpdateProps.push({ [node.elementId]: {
                      prop: newKey,
                    }
                  })
                })
              }
              needUpdateProps.push({ [current.elementId]: {
                  slotName: newKey,
                  elementName: newLabel,
                }
              })
              this._storeCommit(UPDATE_DYNAMIC_PROPS, {
                dynamicPropsList: needUpdateProps
              })
            } else {
              const deleteKey = form.key
              const deleteElement = this.currentElement.children.find(c => this.getElementPropsById(c.elementId).slotName === deleteKey)
              if (deleteElement) {
                this._storeDispatch(DELETE_LAYOUT_DATA, { elementId: deleteElement.elementId })
                this.dndOptions.selectedId = this.currentElement.elementId
              }
            }

            tempValue[this.edtingIndex] = {...form, path: tempValue[this.edtingIndex].path}
            const res = {
              ...this.elementProps,
              [this.propKey]: tempValue,
            }
            this.$emit('update', res, { oldVal: oldStore, val: getGlobalConfig.call(this) })
          }
          this.popoverVisible = false
        })
      },
      handleShowFormat() {
        this.$refs.dataFormat.open({
          formatter: this.variableForm.formatter,
          defaultArguments: [
            {
              key: 'cellValue',
              value: undefined,
              test: undefined,
              desc: '单元格数据'
            },
            {
              key: 'rowData',
              value: undefined,
              test: undefined,
              desc: '行数据'
            },
          ]
        })
      },
      handleFormatterConfirm(formatter) {
        this.$set(this.variableForm, 'formatter', _.cloneDeep(formatter))
      },
      handleShowModal() {
        const popperElement = this.$refs.popover.popperElm
        popperElement.style.top = '80px'
        popperElement.style.right = '40px'
        popperElement.style.left = 'unset'
        popperElement.style.display = 'block'
        const modalElement = document.createElement('div')
        modalElement.classList.add('fullscreen-modal')
        modalElement.appendChild(popperElement)
        document.body.appendChild(modalElement)
      },
      handleHideModal() {
        const modalElement = document.querySelector('.fullscreen-modal')
        modalElement?.remove()
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      },
      handleUpdateHeaderGroup(data) {
        const res = { ...this.elementProps, tableHeaderGroup: data }
        this.$emit('update', res)
      }
    },
  }
</script>

