      justify-content: space-between;

      .el-checkbox {
        margin-left: 10px;
        .el-checkbox__label {
          display: none;
        }
      }

      &.is-show-detail {
        .arrow-icon {
          transform: rotate(90deg) !important;
        }
      }

      .label {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        font-size: 12px;
        height: 34px;
        line-height: 34px;
        width: 100px;

        .arrow-icon {
          display: block;
          margin-right: 10px;
          font-size: 12px;
          transform: rotate(0deg);
          transition: all 0.3s ease;
        }
      }

      .form-item-select {
        flex: 1;
        margin-bottom: 2px;
      }
    }

    .inner-content {
      background: #f7f8fa;
      border: 1px solid #e6e6e6;
      font-size: 12px;
      margin-bottom: 10px;
      .title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 32px;
        line-height: 32px;
        text-align: left;
        font-size: 12px;
        font-weight: 500;
        .el-checkbox {
          margin-left: 10px;
        }
        .col-title {
          width: 45%;
          padding-left: 25px;

          &::after {
            content: '|';
            color: #e6e6e6;
            padding-left: 30px;
          }
        }

        .key {
          text-indent: 10px;
          width: 55%;
        }
      }

      .content {
        justify-content: flex-start;
        height: 32px;
        line-height: 32px;
        background: #fff;
        border-bottom: 1px solid #e6e6e6;

        &:last-child {
          border-bottom: none;
        }

        .icon {
          cursor: move;
          font-weight: bold;
          padding: 0 10px;
        }

        .label {
          width: 22%;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          display: block;
        }

        .key {
          text-align: center;
          flex: 1;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          display: block;
        }

        .caozuo {
          padding: 0 10px;
        }

        .config {
          .iconfont {
            margin-left: 10px;

            &:hover {
              cursor: pointer;
              color: @drop-main-color;
            }
          }
        }
      }
    }

    .add {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-top: 8px;

      &:hover {
        cursor: pointer;
        color: @drop-main-color;
      }

      .iconfont {
        margin-right: 10px;
      }
    }
  }

  .variable-body::v-deep {
    .formatter-item {
      display: flex;
      align-items: center;
      height: 32px;
      .iconfont {
        &:hover {
          cursor: pointer;
          color: red;
        }
      }
    }
  }
</style>
