<template>
  <n-form-item class="config-item" :label="name">
    <el-button v-if='!dataInfo' type="primary" :loading='loading' @click="handleShowConfig">{{ btnText }}</el-button>
    <div v-else class='data-info-item'>
      <el-button type='text' :loading='loading' @click='handleShowConfig'>{{ dataInfo }}</el-button>
      <div class='data-tools' @click='handleDelete'>
        <i class='iconfont icon-delete'></i>
      </div>
    </div>
    <data-select ref="dataSelect" :use-type="useType" @confirm="handleConfirm"></data-select>
    <chart-data ref="chartData" v-bind="$props" @confirm="handleConfirm"></chart-data>
    <table-data ref='tableData' @confirm="handleConfirm"></table-data>
    <panel-data ref='panelData' @confirm="handleConfirm"></panel-data>
  </n-form-item>
</template>

<script>
  import dataSelect from '@/components/DataSelect/index'
  import chartData from '@/components/DataSelect/chartData'
  import tableData from '@/components/DataSelect/tableData'
  import panelData from '@/components/DataSelect/panelData'
  export default {
    components: {
      dataSelect,
      tableData,
      panelData,
      chartData
    },
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      optionkey: {
        type: String,
      },
      btnText: {
        type: String,
        default: '配置'
      },
      useType: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        loading: false,
      }
    },
    computed: {
      dataInfo() {
        const { apiRequest, sqlRequest, staticData } = this.elementProps
        switch (true) {
          case !!apiRequest:
            return 'API数据集'
          case !!sqlRequest:
            return 'SQL数据集'
          case !!staticData:
            return this.useType === 'options' ? false: '静态数据集'
          default:
            return undefined
        }
      }
    },
    methods: {
      handleShowConfig() {
        this.loading = true
        let refKey
        switch (this.optionkey) {
          case 'table':
            refKey = 'tableData'
            break
          case 'panel':
            refKey = 'panelData'
            break
          case 'chart':
            refKey = 'chartData'
            break
          default:
            refKey = 'dataSelect'
            break
        }
        setTimeout(() => {
          this.$refs[refKey].open({
            dataColumn: this.elementProps.dataColumn,
            widgetVariables: this.elementProps.widgetVariables,
            dataVariables: this.elementProps.dataVariables,
            apiRequest: this.elementProps.apiRequest,
            sqlRequest: this.elementProps.sqlRequest,
            staticData: this.elementProps.staticData,
            dynamicVariables: this.elementProps.dynamicVariables
          })
          this.loading = false
        })
      },
      handleConfirm(res) {
        this.$emit('update', {
          ...this.elementProps,
          ...res,
        })
      },
      handleDelete() {
        const res = {
          apiRequest: undefined,
          sqlRequest: undefined,
          staticData: undefined,
          widgetVariables: undefined,
          dataColumn: this.elementProps.dataColumn?.map(item => ({...item, path: []})),
          dataVariables: this.elementProps.dataVariables?.map(item => {
            if(this.optionkey === 'chart') {
              return { ...item, fields: [] }
            }
            return { ...item, path: [] }
          }),
        }
        this.$emit('update', {
          ...this.elementProps,
          ...res,
        })
      }
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  div.config-item::v-deep {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 8px;
    .data-info-item {
      display: flex;
      align-items: center;
      justify-content: center;
      .data-tools {
        margin-left: 10px;
        &:hover {
          cursor: pointer;
          color: red;
        }
      }
    }
    .el-form-item__label {
      min-width: 100px;
    }

    .el-form-item__content {
      flex: 1;
    }
  }
</style>
