<template>
  <div class="app-design-page">
    <div style="height: 50px; width: 100%; border-bottom: 1px solid #eeeeee">
      <page-header></page-header>
    </div>
    <splitpanes
      class="page-content default-theme"
      :class="{ 'error-log': errorLogVisible }"
      :dbl-click-splitter="false"
    >
      <pane
        :size="leftSize"
        class="left-panel"
        ref="leftPanel"
        :class="{ 'is-collapse': isCollapseLeft }"
      >
        <splitpanes
          class="default-theme"
          horizontal
          :dbl-click-splitter="false"
        >
          <pane size="35" class="tabs-container">
            <el-tabs v-model="currentPanel">
              <el-tab-pane label="页面" name="page">
                <page-panel @page-load="handlePageLoad"></page-panel>
              </el-tab-pane>
              <el-tab-pane
                label="组件层"
                name="tree"
                class="drop-zone widget-tab-pane"
                :data-zone="pageZone"
              >
                <tree-panel
                  v-if="currentPanel === 'tree' && treeData"
                  :data="treeData"
                ></tree-panel>
              </el-tab-pane>
            </el-tabs>
          </pane>
          <pane size="65" class="tabs-container">
            <widget-panel ref="widgetPanel"></widget-panel>
          </pane>
        </splitpanes>
      </pane>
      <pane :size="100 - leftSize - rightSize" class="main-wrapper">
        <div
          id="app-design-page-main"
          class="page-main"
          v-if="layoutData"
          v-loading="loading"
        >
          <nav class="nav">
            <div
              class="collapse-btn left"
              @click="isCollapseLeft = !isCollapseLeft"
            >
              <div class="arrow" :class="arrowClass(1)">
                <img :src="$getImgPrefix('/design/svg/arrow-deep.svg')" alt="" />
              </div>
            </div>
            <breadcrumb
              class="canvas-breadcrumb"
              :element-id="dndOptions.selectedId"
            ></breadcrumb>
            <div
              class="collapse-btn right"
              @click="isCollapseRight = !isCollapseRight"
            >
              <div class="arrow" :class="arrowClass(2)">
                <img :src="$getImgPrefix('/design/svg/arrow-deep.svg')" alt="" />
              </div>
            </div>
          </nav>
          <page-canvas
            @selectedChanged="handleSelectedChanged"
            v-if="asyncRender"
          ></page-canvas>
          <cover-wrapper
            v-if="asyncRender"
            ref="cover"
            @addToFavorite="handleAddToFavorite"
            @addToPublish="handleAddToPublish"
          ></cover-wrapper>
        </div>
        <div class="page-main empty" v-else></div>
      </pane>
      <pane
        :size="rightSize"
        class="setting-panel"
        :class="{
          'is-collapse': isCollapseRight,
          'style-panel': currentSettingPanel === 'style',
        }"
      >
        <div class="tabs-container">
          <n-tabs
            v-if="dndOptions.selectedId"
            v-model="currentSettingPanel"
            @tab-click="tabClick"
          >
            <n-tab-pane lazy label="属性" name="prop">
              <props-panel ref="prop"></props-panel>
            </n-tab-pane>
            <n-tab-pane lazy :disabled="hideStyle" label="样式" name="style">
              <style-panel ref="style" v-if="!hideStyle"></style-panel>
              <div v-else class="style-disabled-text">
                当前组件不支持设置样式
              </div>
            </n-tab-pane>
            <n-tab-pane lazy label="事件" name="event">
              <event-panel></event-panel>
            </n-tab-pane>
          </n-tabs>
          <div class="empty" v-else>请在左侧画布选中节点</div>
        </div>
        <!--        <div-->
        <!--          v-if="treeData"-->
        <!--          class="tree-panel-right drop-zone"-->
        <!--          v-show="isShowRightNodeTree"-->
        <!--          :data-zone="pageZone"-->
        <!--        >-->
        <!--          <tree-panel :data="treeData"></tree-panel>-->
        <!--        </div>-->
      </pane>
    </splitpanes>
    <FavoriteDialog
      ref="favoriteDialog"
      @success="handleRefreshComponentTemplate"
    ></FavoriteDialog>
    <template-save ref="templateSave"></template-save>
    <error-capture v-if="errorLogVisible" @update-panel="val => currentSettingPanel = val"></error-capture>
  </div>
</template>

<script>
  import Vue from 'vue'
  // 全局注册组件（所有被递归使用的组件）
  import LayoutPlaceholder from '@/components/LayoutPlaceholder/index'
  import VariableSelect from '@/components/VariableSelect'
  import AppVariables from '@/components/AppVariables'
  import InputDialog from '@/components/InputDialog'
  import ApiConfig from '@/components/ApiConfig'
  import SqlConfig from '@/components/SqlConfig'
  import HandlerDialog from '@/components/EventHandlerConfig'
  import DataConfigSave from '@/components/DataConfigSave'
  import EventSelect from '@/components/EventSelect'
  import FunctionSelect from '@/components/FunctionSelect/index'
  import JsConfig from '@/components/JsConfig'
  import DataConfigSelect from '@/components/DataConfigSelect/index'
  import DevopsConfig from '@/components/DevopsConfig/index'
  import MoreTypeDropdown from '@/components/MoreTypeDropdown'
  import UtilsSelect from '@/components/UtilsSelect'

  Vue.component('LayoutPlaceholder', LayoutPlaceholder)
  Vue.component('VariableSelect', VariableSelect)
  Vue.component('AppVariables', AppVariables)
  Vue.component('InputDialog', InputDialog)
  Vue.component('ApiConfig', ApiConfig)
  Vue.component('SqlConfig', SqlConfig)
  Vue.component('HandlerDialog', HandlerDialog)
  Vue.component('DataConfigSave', DataConfigSave)
  Vue.component('EventSelect', EventSelect)
  Vue.component('FunctionSelect', FunctionSelect)
  Vue.component('JsConfig', JsConfig)
  Vue.component('DataConfigSelect', DataConfigSelect)
  Vue.component('DevopsConfig', DevopsConfig)
  Vue.component('MoreTypeDropdown', MoreTypeDropdown)
  Vue.component('UtilsSelect', UtilsSelect)

  import { Splitpanes, Pane } from 'splitpanes'
  import 'splitpanes/dist/splitpanes.css'

  import { RESET_STORE } from '@/config/storeKey'

  import { treeUtil, getNodeByElementId } from '@/utils/tree-utils'
  import { dialogTypes } from '@/config/widgets'

  import _ from 'lodash'
  import breadcrumbControl from '@/mixins/breadcrumb-control'
  import echarts from '@/lib/echarts'
  import { FORCE_UPDATE } from '@/config/eventName'
  import { mapMutations } from 'vuex'
  import { loadCustomIconfont } from '@/utils'
  import trigger from '@/renderer/tools/trigger'
  import pageStore from '@/renderer/tools/pageStore'
  export default {
    name: 'AppDesign',
    components: {
      PageHeader: () => import('./components/PageHeader/index'),
      PagePanel: () => import('./components/PagePanel/index'),
      TreePanel: () => import('./components/TreePanel/index'),
      WidgetPanel: () => import('./components/WidgetPanel/index'),
      Breadcrumb: () => import('@/components/Breadcrumb/index'),
      PropsPanel: () => import('./components/ConfigPanel/PropsPanel'),
      StylePanel: () => import('./components/ConfigPanel/StylePanel/index'),
      EventPanel: () => import('./components/ConfigPanel/EventPanel/index'),
      PageCanvas: () => import('./components/PageCanvas/index'),
      CoverWrapper: () => import('@/components/CoverWrapper/index'),
      FavoriteDialog: () => import('./components/Favorite/edit-dialog'),
      TemplateSave: () => import('@/components/TemplateSave'),
      Splitpanes,
      Pane,
      errorCapture: () => import('@/components/errorCapture'),
    },
    mixins: [breadcrumbControl, pageStore],
    data() {
      return {
        currentPanel: 'page',
        currentSettingPanel: 'prop',
        dndOptions: {
          selectedId: '',
          dropZoneId: '',
          hoverId: '',
          placement: '',
          draggingWidget: null,
          resizingWidget: null,
          gridSize: 100,
          offsetLeft: 0,
          widgets: [],
          dndResizing: false,
          dndDragging: false,
          hasTree: false,
        },
        isCollapseLeft: false,
        isCollapseRight: false,
        // 动态组件加载时机
        asyncRender: false,
        loading: false,
        leftSize: 25,
        rightSize: 25,
      }
    },
    provide() {
      return {
        dndOptions: this.dndOptions,
      }
    },
    computed: {
      dynamicProps() {
        return this.storeData.dynamicProps
      },
      layoutData: {
        get() {
          const layoutData = this.storeData.layoutData
          return Object.keys(layoutData).length ? layoutData : null
        },
      },
      treeData() {
        const copy = this.layoutData ? [_.cloneDeep(this.layoutData)] : null
        if (!copy) {
          return null
        }
        treeUtil.forEach(copy, (node) => {
          const showChildren = []
          node.children.forEach((item) => {
            if (
              ['slot'].includes(item.elementType) &&
              (this.getElementPropsById(item.elementId).isVisible === false ||
                ['data'].includes(
                  this.getElementPropsById(item.elementId).slotType
                ))
            ) {
              return
            }
            showChildren.push(item)
          })
          node.children = showChildren
          node.elementName =
            this.getElementPropsById(node.elementId).elementName ||
            node.elementName
        })
        return copy
      },
      // isShowRightNodeTree() {
      //   return this.dndOptions.draggingWidget && this.currentPanel !== 'tree'
      // },
      pageZone() {
        return this.layoutData
          ? `${this.layoutData.elementId}_${this.layoutData.elementType}`
          : undefined
      },
      hideStyle() {
        const current = this.layoutData
          ? getNodeByElementId(this.layoutData, this.dndOptions.selectedId) || {}
          : {}
        return [...dialogTypes, 'timer'].includes(current.elementType)
      },
      errorLogVisible() {
        let settings = {}
        try {
          settings = JSON.parse(this.$store.state.CODE.baseInfo.VERSION_INFO.settings) || {}
        } catch (error) {
          settings = {}
        }
        return settings.edit
      }
    },
    watch: {
      isCollapseLeft(n) {
        const pageDom = document.querySelector('.app-design-page')
        const pageWidth = pageDom.getBoundingClientRect().width
        const leftSize = (250 / pageWidth) * 100
        this.leftSize = n ? 0 : leftSize
        setTimeout(() => {
          this.$refs.cover?.handleUpdateWrapperStyle()
        }, 300)
      },
      isCollapseRight(n) {
        const pageDom = document.querySelector('.app-design-page')
        const pageWidth = pageDom.getBoundingClientRect().width
        const rightSize = (360 / pageWidth) * 100
        this.rightSize = n ? 0 : rightSize
        setTimeout(() => {
          this.$refs.cover?.handleUpdateWrapperStyle()
        }, 300)
      },
      currentPanel(n) {
        this.dndOptions.hasTree = n === 'tree'
      },
    },
    methods: {
      tabClick(e) {
        // 刷新组件里的数据
        this.$refs[e.name]?.initProps()
      },
      handlePageLoad(isLoaded) {
        this.asyncRender = isLoaded
      },
      handleSelectedChanged() {
        this.$refs.cover?.handleUpdateWrapperStyle(true)
      },
      arrowClass(type) {
        const { isCollapseLeft: left, isCollapseRight: right } = this
        if (type === 1) {
          return {
            reverse: !left,
          }
        } else if (type === 2) {
          return {
            reverse: right,
          }
        }
        return {}
      },
      handleAddToFavorite(elementId) {
        this.$refs.favoriteDialog?.open({ elementId })
      },
      handleAddToPublish(elementId) {
        this.$refs.templateSave?.open({ elementId })
      },
      handleRefreshComponentTemplate() {
        this.$refs.widgetPanel?.handleGetTemplateList()
      },
      getElementPropsById(elementId) {
        const realId =
          elementId === 'app'
            ? this.storeData.layoutData.elementId
            : elementId
        return this.storeData.dynamicProps[realId] || {}
      },
      updatePanelSize() {
        const pageDom = document.querySelector('.app-design-page')
        const pageWidth = pageDom.getBoundingClientRect().width
        this.leftSize = (250 / pageWidth) * 100
        this.rightSize = (360 / pageWidth) * 100
      },
    },
    beforeDestroy() {
      this._storeCommit(RESET_STORE)
    },
    created() {
      loadCustomIconfont()
      echarts.init()
      if (process.env.NODE_ENV !== 'development') {
        this.eventFn = function (e) {
          const event = window.event || e
          event.returnValue = '确定离开当前页面吗？'
        }
        window.addEventListener('beforeunload', this.eventFn)
      }
      // 强制更新
      window.addEventListener(FORCE_UPDATE, () => {
        this.loading = true
        setTimeout(() => {
          this.asyncRender = false
          setTimeout(() => {
            this.asyncRender = true
            setTimeout(() => {
              this.loading = false
            }, 60)
          })
        }, 60)
      })
    },
    mounted() {
      this.$nextTick(() => {
        this.updatePanelSize()
      })
    },
    destroyed() {
      window.removeEventListener('beforeunload', this.eventFn)
    },
    errorCaptured(err, vm) {
      trigger.recordBug({
        target: vm,
        message: err,
        error: err,
      })
      return false
    },
  }
</script>

