<template>
  <div class="widget-config-item">
    <div
      :class="{
        name: true,
        required: requiredArr.includes(name),
      }"
    >
      {{ name }}
      <el-tooltip v-if="tip" :content="tip" popper-class="tooltip-question" placement="top">
        <i style='margin-left: 5px;' class="el-icon-question"></i>
      </el-tooltip>
    </div>
    <n-form-item class="config-item" label="" :prop="propKey">
      <el-input-number
        style="width: 100%"
        v-model="inputValue"
        @change='handleChange'
        :placeholder="placeholder"
        :min="options.min"
        :max="options.max"
      ></el-input-number>
    </n-form-item>
  </div>
</template>

<script>
  import _ from 'lodash'

  export default {
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
      options: {
        type: Object,
        default: () => ({ min: 0 })
      },
      tip: {
        type: String,
      },
    },
    data() {
      return {
        requiredArr: [],
        inputValue: undefined
      }
    },
    computed: {
      value: {
        get() {
          return this.elementProps[this.propKey]
        },
      }
    },
    watch: {
      value: {
        deep: true,
        immediate: true,
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.inputValue = n
        }
      }
    },
    methods: {
      handleChange(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      }
    }
  }
</script>

