  @import '~@/styles/index.less';
  div.widget-config-item::v-deep {
    display: flex;
    align-items: center;
    .name {
      font-family: PingFangSC-Medium;
      font-size: 12px;
      color: #555555;
      letter-spacing: 0;
      font-weight: 500;
      margin-bottom: 5px;
      padding-right: 10px;
      min-width: 100px;
    }
    .required::before {
      content: '*';
      color: red;
    }
    div.config-item {
      flex: 1;
