<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="选择操作"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-loading.lock="loading">
      <n-tabs
        v-model="queryForm.condition.isBuilt"
        type="card"
        @tab-click="handleClickTab"
      >
        <n-tab-pane label="用户操作" :name="0"></n-tab-pane>
        <n-tab-pane label="内置操作" :name="1"></n-tab-pane>
      </n-tabs>
      <n-form
        class="data-select-form"
        :model="queryForm"
        size="mini"
        inline
        label-with="80px"
        @submit.native.prevent
      >
        <n-form-item label="">
          <el-select
            style="width: 100px"
            v-model="searchType"
            @change="handleResetCondition"
          >
            <el-option label="操作名称" value="scriptName"></el-option>
            <el-option label="标签" value="labelName"></el-option>
          </el-select>
        </n-form-item>
        <n-form-item v-if="searchType === 'scriptName'" label="">
          <el-input
            class="search-input"
            size="mini"
            v-model="queryForm.condition.scriptName"
            placeholder="请输入操作名"
          >
          </el-input>
        </n-form-item>
        <n-form-item v-else label="">
          <el-select
            style="width: 178px"
            v-model="queryForm.condition.labelName"
            filterable
            default-first-option
            placeholder="请选择标签"
          >
            <el-option
              v-for="item in labelNameList"
              :key="item.labelTitle"
              :label="item.labelTitle"
              :value="item.labelTitle"
            ></el-option>
          </el-select>
        </n-form-item>
        <el-button
          type="primary"
          size="mini"
          icon="el-icon-search"
          @click="handleSearch(false)"
        >
        </el-button>
        <el-button type="primary" size="mini" @click="handleSearch(true)">
          重置
        </el-button>
      </n-form>
      <div class="data-table">
        <n-table
          :data="dataList"
          border
          highlight-current-row
          @row-click="handleRowClick"
          size="mini"
          height="410px"
        >
          <n-table-column width="50px" label="选择">
            <template slot-scope="scope">
              <el-radio
                v-if="
                  selectedRow && selectedRow.scriptName === scope.row.scriptName
                "
              ></el-radio>
              <el-radio v-else :value="false"></el-radio>
            </template>
          </n-table-column>
          <n-table-column
            prop="scriptId"
            label="操作ID"
            width="80px"
          ></n-table-column>
          <n-table-column
            prop="scriptName"
            label="操作名称"
            min-width="150px"
          ></n-table-column>
          <n-table-column prop="scriptLabels" label="标签" min-width="100px">
            <template slot-scope="scope">
              <el-tag
                style="margin: 2px"
                v-for="item in scope.row.scriptLabels"
                :key="item.labelId"
                >{{ item.labelTitle }}</el-tag
              >
            </template>
          </n-table-column>
          <n-table-column
            prop="scriptStatus"
            label="危险级别"
            width="80px"
          >
            <template slot-scope="scope">
              <span>{{ getScriptStatusLabel(scope.row.scriptStatus) }}</span>
            </template>
          </n-table-column>
          <n-table-column prop="scriptTypeName" label="语言"></n-table-column>
          <n-table-column
            prop="lastUpdateUserName"
            label="操作人"
          ></n-table-column>
          <n-table-column
            prop="status"
            label="状态"
            width="80px"
          >
            <template slot-scope="scope">
              <span>{{ getStatusLabel(scope.row.status) }}</span>
            </template>
          </n-table-column>
          <n-table-column prop="lastUpdateTime" label="修改时间" width="150px">
            <template slot-scope="scope">
              <span>{{ formatTime(scope.row.lastUpdateTime) }}</span>
            </template>
          </n-table-column>
        </n-table>
        <div class="pagination">
          <pagination
            :page="queryForm.pagination.pagenum"
            :limit="queryForm.pagination.pagesize"
            :total="totalCount"
            @pagination="handlePageChange"
          />
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import API from '@/api/application'
import Pagination from '@/components/Pagination'
import request from '@/request/request'
import { getCommonHeaders } from '@/utils'
import { formatTime } from '@/utils/format'

export default {
  name: 'scriptData',
  components: {
    Pagination,
  },
  data() {
    return {
      dialogVisible: false,
      loading: false,
      totalCount: 0,
      queryForm: {
        condition: { isBuilt: 0, scriptName: null, labelName: null },
        pagination: { pagenum: 1, pagesize: 100, sort: null },
      },
      dataList: [],
      selectedRow: null,
      labelNameList: [],
      searchType: 'scriptName',
    }
  },
  methods: {
    formatTime,
    open(data) {
      this.selectedRow = data
      this.dialogVisible = true
      this.handleGetLableList()
      this.handleSearch(true)
    },
    handleClose(done) {
      this.dialogVisible = false
      done()
    },
    cancel() {
      this.dialogVisible = false
    },
    confirm() {
      if (!this.selectedRow) {
        this.$message.error('请选择')
        return
      }
      this.handleGetServeDetail()
    },
    handleGetServeDetail() {
      this.loading = true
      request({
        url: API.atmScriptFullInfo,
        method: 'POST',
        data: {
          params: {
            scriptName: this.selectedRow.scriptName,
          },
        },
        headers: getCommonHeaders(),
      })
        .then(({ data }) => {
          const paramValueList = data.data.inputParams.map((item) => ({
            paramName: item.paramName,
            paramValue: item.defaultValue,
          }))
          const root = {
            params: {
              entity: {
                paramValueList,
                targetList: [{
                  'targetType': 91,
                  'targetName': 'linux_192.168.24.40',
                  'targetHost': '192.168.24.40',
                  'targetId': 129
                }],
                autoCommit: 1,
                sudo: false,
                sudoUser: '',
                layoutId: data.data.scriptId,
                taskSource: 1,
                execType: 0,
                accessType: 'local',
                timeout: 60,
                continueAfterFailure: 1,
              },
            },
          }

          const body = Object.entries(root).map(([key, value]) => ({
            key,
            value,
            test: value,
          }))
          const apiRequest = {
            url: '/atm/job/start',
            method: 'POST',
            type: 'script',
            paramsData: [{ key: '', value: undefined, test: undefined }],
            bodyData: body.length
              ? body
              : [{ key: '', value: undefined, test: undefined }],
            headersData: [{ key: '', value: undefined, test: undefined }],
          }
          const result = {
            apiRequest,
            apiDetail: data.data,
          }
          this.$emit('confirm', result)
          this.dialogVisible = false
        })
        .catch((e) => {
          this.$message.error('数据解析错误')
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleSearch(reset) {
      this.loading = true
      if (reset) {
        this.queryForm = {
          condition: { isBuilt: 0, scriptName: null, labelName: null },
          pagination: { pagenum: 1, pagesize: 100, sort: null },
        }
      }
      request({
        url: API.atmScriptList,
        method: 'POST',
        data: {
          params: this.queryForm,
        },
        headers: getCommonHeaders(),
      })
        .then(({ data }) => {
          this.totalCount = data.data.rowCount
          this.dataList = data.data.records
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleGetLableList() {
      request({
        url: API.atmCommonLabelList,
        method: 'POST',
        data: {
          params: {
            pagination: { pagenum: 1, pagesize: 999 },
          },
        },
        headers: getCommonHeaders(),
      }).then(({ data }) => {
        this.labelNameList = data.data.records
      })
    },
    handleRowClick(row, column, event) {
      this.selectedRow = row
    },
    handlePageChange({ page, size }) {
      this.queryForm.pagination.pagenum = page
      this.queryForm.pagination.pagesize = size
      this.handleSearch(false)
    },
    handleResetCondition(type) {
      switch (type) {
        case 'labelName': {
          this.handleGetLableList()
          break
        }
      }
      this.queryForm.condition.scriptName = null
      this.queryForm.condition.labelName = null
    },
    handleClickTab() {
      this.handleSearch(false)
    },
    getScriptStatusLabel(status) {
      switch(status) {
        case 1: return '危险'
        case 2: return '普通'
        case 3: return '普通'
      }
    },
    getStatusLabel(status) {
      switch(status) {
        case 0: return '已通过'
        case 1: return '下线'
      }
    },
  },
}
</script>

<style lang="less" scoped>
.dialog-content::v-deep {
  .data-select-form {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
  }
  .pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
  }
}
</style>
