<template>
  <div>
    <n-form-item class="config-item" label="默认开始日期">
      <default-time-picker
        :value.sync="value.start"
        :placeholder="placeholder"
        :picker-options="startPickerOptions"
      ></default-time-picker>
    </n-form-item>
    <n-form-item
      class="config-item"
      label="默认结束日期"
      :picker-options="pickerOptions"
    >
      <default-time-picker
        :value.sync="value.end"
        :placeholder="placeholder"
        :picker-options="endPickerOptions"
      ></default-time-picker>
    </n-form-item>
  </div>
</template>

<script>
import DefaultTimePicker from '@/components/defaultTimePicker'
export default {
  components: {
    DefaultTimePicker,
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
  },
  data() {
    return {
      startPickerOptions: {
        disabledDate: time => {
          const { type, value } = this.value.end
          const defaultValue = type === 0 ? new Date() : value
          return defaultValue && time.getTime() > defaultValue
        },
      },
      endPickerOptions: {
        disabledDate: time => {
          const { type, value } = this.value.start
          const defaultValue = type === 0 ? new Date() : value
          return defaultValue && time.getTime() < defaultValue
        },
      },
    }
  },
}
</script>

