<template>
  <div class="animation-config-params" v-if="config.params">
    <n-table
      :data="dynamicAnimationConfigList"
      border
      style="width: 100%"
      header-cell-class-name="header-cell"
    >
      <n-table-column prop="name" label="作用部位" min-width="200px">
        <div class="variable-column" slot-scope="scope">
          <el-select v-model="scope.row.name" @change="handleNameChange($event, scope.row)">
            <el-option
              :disabled="dynamicAnimationConfigList.some(item => item.name === option.name)"
              :label="option.name"
              :value="option.name"
              v-for="option in styleConfigList"
              :key="option.name"
            >
            </el-option>
          </el-select>
        </div>
      </n-table-column>
      <n-table-column
        min-width="200px"
        prop="query"
        label="CSS选择器"
      ></n-table-column>
      <n-table-column prop="animate" min-width="300px" label="值">
        <template slot-scope="scope">
          <div class="value-input">
            <el-tooltip
              v-if="scope.row.animate !== undefined"
              effect="dark"
              :content="scope.row.animate.animationName"
              placement="top"
            >
              <el-tag
                class="value-tag"
                closable
                disable-transitions
                @click="showAnimateSelect(scope.row)"
                @close="scope.row.animate = undefined"
              >
                {{ scope.row.animate.animationName }}
              </el-tag>
            </el-tooltip>
            <div v-else class="value-input-content">
              <el-button-group>
                <el-button
                  @click="showAnimateSelect(scope.row)"
                  >选择
                </el-button>
              </el-button-group>
            </div>
          </div>
        </template>
      </n-table-column>
      <n-table-column width="90" label="操作">
        <template slot-scope="scope">
          <div class="btns">
            <div
              class="add-btn-primary"
              v-if="isShowAdd"
              @click="handleAddConfig"
            >
              +
            </div>
            <div
              class="add-btn-primary"
              v-if="isShowDelete"
              @click="handleDeleteConfig(scope.$index)"
            >
              -
            </div>
          </div>
        </template>
      </n-table-column>
    </n-table>
    <AnimateEditDialog ref="animateEditDialog" :animation="[]" :isShowTrigger="false" @confirmAdd="handleAnimateConfirm" @confirmEdit="handleAnimateConfirm" />
  </div>
</template>

<script>
  import _ from 'lodash'
  import AnimateEditDialog from '@/pages/application/app-design/components/ConfigPanel/StylePanel/components/animate-edit-dialog.vue'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    name: 'StyleConfigParams',
    components: {
      AnimateEditDialog
    },
    inject: [...pageInject],
    props: {
      elementId: {
        type: String,
      },
      config: {
        type: Object,
      },
      callbackId: {
        type: String,
      },
    },
    data() {
      return {
        dynamicAnimationConfigList: [],
      }
    },
    computed: {
      styleConfigList() {
        const { styleConfigList } = this.getElementPropsById(this.elementId)
        return styleConfigList
      },
      isShowAdd() {
        return this.dynamicAnimationConfigList.length < this.styleConfigList.length
      },
      isShowDelete() {
        return this.dynamicAnimationConfigList.length > 1
      },
    },
    watch: {
      dynamicAnimationConfigList: {
        deep: true,
        handler(n, o) {
          this.config.params.dynamicAnimationConfigList = _.cloneDeep(n)
        },
      },
    },
    methods: {
      handleReset() {
        const root = this.styleConfigList[0]
        this.$set(this.config, 'params', {
          dynamicAnimationConfigList: [{
            name: root.name,
            query: root.query,
            animate: undefined
          }]
        })
      },
      handleAddConfig() {
        this.dynamicAnimationConfigList.push({
          name: '',
          query: '',
          animate: undefined
        })
      },
      handleDeleteConfig(index) {
        this.dynamicAnimationConfigList.splice(index, 1)
      },
      handleNameChange(name, row) {
        const currentConfig = this.styleConfigList.find(item => item.name === name) || {}
        row.query = currentConfig.query
      },
      showAnimateSelect(row) {
        this.currentRow = row
        this.$refs.animateEditDialog.open(row.animate)
      },
      handleAnimateConfirm(animate) {
        this.$set(this.currentRow, 'animate', animate)
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      },
    },
    created() {
      if (!this.config.params) {
        this.handleReset()
      }
      this.dynamicAnimationConfigList = _.cloneDeep(this.config.params.dynamicAnimationConfigList)
    },
  }
</script>

<style lang="less" scoped>
  .animation-config-params {

  }
</style>
