<template>
  <el-form>
    <el-form-item :label="name" class="widget-config-item">
      <el-button-group class="flex-button-group" v-model="value.mode">
        <el-button
          class="config-item-button"
          size="mini"
          :type="getType('preset')"
          @click="value.mode = 'preset'"
          >预设套色</el-button
        >
        <el-button
          class="config-item-button"
          size="mini"
          :type="getType('custom')"
          @click="value.mode = 'custom'"
          >自定义颜色</el-button
        >
      </el-button-group>
    </el-form-item>
    <el-form-item
      v-if="value.mode === 'custom'"
      label="自定义颜色"
      class="widget-config-item"
    >
      <el-input
        v-model="colorStr"
        placeholder="颜色请用英文逗号隔开"
        @change="inputChange"
      ></el-input>
    </el-form-item>
    <el-form-item v-else label="套色选择" class="widget-config-item">
      <div class="container">
        <div class="color-wall">
          <span
            class="color-item"
            v-for="color in showFilter(value.color)"
            :key="color"
            :style="{ backgroundColor: color }"
          ></span>
        </div>
        <el-popover placement="left-start" trigger="click">
          <div
            class="color-wall"
            v-for="(item, index) in options"
            :key="index"
            @click="itemClick(item)"
          >
            <span
              class="color-item"
              v-for="color in showFilter(item)"
              :key="color"
              :style="{ backgroundColor: color }"
            ></span>
          </div>

          <span ref="btn" class="el-icon-arrow-down" slot="reference"></span>
        </el-popover>
      </div>
    </el-form-item>
  </el-form>
</template>

<script>
import options from './option.js'
export default {
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
    },
    vue: {
      type: String,
    },
  },
  data() {
    return {
      options,
      colorStr: '',
    }
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = { ...this.elementProps, [this.propKey]: value }
        this.$emit('update', res)
      },
    },
  },
  methods: {
    getType(type) {
      return this.value.mode === type ? 'primary' : 'default'
    },
    inputChange() {
      this.value.color = this.colorStr.split(';')
    },
    itemClick(item) {
      this.value.color = item
      this.$refs.btn.click()
      this.initColorStr()
    },
    showFilter(value) {
      return value.slice(0, 6)
    },
    initColorStr(){
      this.colorStr = this.value.color.join(';')
    }
  },
  created() {
    if (!this.value.color.length) {
      this.value.color = options[0]
    }
    this.initColorStr()
  },
}
</script>

