<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="动画效果选择"
    :visible.sync="dialogVisible"
    width="1000px"
    top="5vh"
    :before-close="handleClose"
  >
    <div class="dialog-content">
      <div class="animation-group"
        v-for="animationGroup in animationList"
        :key="animationGroup.groupName"
       >
        <div class="animation-group-name">{{animationGroup.groupName}}</div>
        <div
          class="animation-item ani-item"
          :class="{'is-select': selected === animation.name}"
          v-for="animation in animationGroup.list"
          :key="animation.name"
          @click="handleSelect(animation)"
        >{{animation.name}}</div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type='primary' @click="confirm">确 认</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { animateCSS } from '@/utils'
import animationList from './animationList'
export default {
  name: 'AnimateSelect',
  data() {
    return {
      animationList,
      dialogVisible: false,
      selected: ''
    }
  },
  methods: {
    open(animationName) {
      this.selected = animationName || ''
      this.dialogVisible = true
    },
    handleClose(done) {
      this.dialogVisible = false
      done()
    },
    confirm() {
      if (!this.selected) {
        this.$message.error('请选择动画效果！')
        return
      }
      this.$emit('confirm', this.selected)
      this.dialogVisible = false
    },
    handleSelect(animation) {
      this.selected = animation.animationName
      this.$nextTick(() => {
        animateCSS({
          query: '.animation-item.ani-item.is-select',
          animationName: animation.animationName
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
@import '~@/styles/index.less';
.dialog-content {
  height: 530px;
  overflow-y: auto;
  overflow-x: hidden;
  .animation-group {
    width: 100%;
    padding: 10px;
    .animation-group-name {
      position: relative;
      padding-left: 10px;
      font-size: 20px;
      font-weight: bold;
      margin-bottom: 10px;
      &::before {
        position: absolute;
        left: 0;
        top: 0;
        content: '';
        width: 4px;
        height: 100%;
        background-color: @drop-main-color;
      }
    }
    .animation-item {
      display: inline-block;
      margin: 0 10px 10px 0;
      width: 140px;
      height: 40px;
      line-height: 40px;
      background-color: blanchedalmond;
      text-align: center;
      font-size: 14px;
      &:hover {
        cursor: pointer;
      }
      &.is-select {
        outline: 2px solid @drop-main-color;
      }
    }
  }
}
</style>