<template>
  <div>
    <n-form-item class="config-item select-page" label="选择页面">
      <Select
        v-model="value"
        :options="pageData"
        labelKey="elementName"
        valueKey="elementId"
        placeholder="请选择页面"
        @change="initSetting"
        :clearable="false"
      ></Select>
      <el-button v-if="value" type="primary" plain style="margin-left: 5px; width: 50px" size="mini" @click="jumpToPage">跳转</el-button>
    </n-form-item>
    <template v-if="value">
      <n-form-item class="config-item" label="参数配置">
        <div>
          <el-button
            v-if="propsData.length"
            type="primary"
            @click="setPropsInitVal"
            >设置初始值</el-button
          >
          <el-button icon="el-icon-refresh-right" @click="refreshSetting"
            >更新</el-button
          >
        </div>
      </n-form-item>
      <div class="props-wrap">
        <n-table border :data="propsData" style="width: 100%">
          <n-table-column
            v-for="column in columns1"
            :key="column.key"
            :prop="column.key"
            :label="column.name"
            width="auto"
          >
            <template slot-scope="scope">{{
              column.format
                ? column.format(scope.row, column.key)
                : scope.row[column.key] !== undefined
                ? scope.row[column.key]
                : '--'
            }}</template>
          </n-table-column>
        </n-table>
      </div>
    </template>
    <template v-if="value">
      <n-form-item class="config-item" style="margin-top: 10px;" label="暴露函数">
      </n-form-item>
      <div class="props-wrap">
        <n-table border :data="elementProps.fnsData" style="width: 100%">
          <n-table-column label="函数id" prop="fn" width="auto" ></n-table-column>
          <n-table-column label="函数名称" prop="name" width="auto" ></n-table-column>
        </n-table>
      </div>
    </template>
    <el-dialog
      custom-class="low-code-dialog"
      :close-on-click-modal="false"
      width="1000px"
      title="参数配置"
      class="props-setting-dialog"
      :visible.sync="paramsDialogVisible"
    >
      <n-table
        class="props-setting-table"
        border
        :data="dialogPropsData"
        style="width: 100%"
      >
        <n-table-column
          v-for="column in columns2"
          :key="column.key"
          :prop="column.key"
          :label="column.name"
          :width="column.width || auto"
        >
          <template slot-scope="scope">
            <template v-if="column.key === 'defaultValue'">
              <value-input
                :value="scope.row.defaultValue.value"
                :type="scope.row.defaultValue.type"
                @showInputDialog="
                  currentIndex = scope.$index
                  showInputDialog(...$event)
                "
                @remove="scope.row.defaultValue.value = undefined"
              ></value-input>
            </template>
            <template v-else-if="column.key === 'desc'">
              <el-input
                v-model="scope.row.desc"
                placeholder="请输入参数描述"
              ></el-input>
            </template>
            <template v-else>
              {{
                column.format
                  ? column.format(scope.row, column.key)
                  : scope.row[column.key] !== undefined
                  ? scope.row[column.key]
                  : '--'
              }}
            </template>
          </template>
        </n-table-column>
      </n-table>
      <input-dialog
        @confirm="handleInputResponse"
        ref="inputDialog"
      ></input-dialog>
      <div slot="footer" class="dialog-footer">
        <el-button @click="paramsDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirm">确 认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { appList } from '@/lib/business'
import API from '@/api/application'
import valueInput from '@/components/valueInput'
import elementEditor from '@/mixins/elementEditor'
import EventBus from '@/renderer/tools/eventBus'
import { BIND_EVENTS, SWITCH_PAGE } from '@/config/eventKey'
import _ from 'lodash'
import Select from '@/components/Select'
import { pageInject } from '@/renderer/tools/pageStore'

export default {
  inject: [...pageInject],
  mixins: [elementEditor],
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    optionkey: {
      type: String,
    },
    vue: {
      type: String,
    },
    tip: {
      type: String,
    },
  },
  data() {
    return {
      appList,
      componentsOptions: [],
      componentsLoading: false,
      appUrlError: false,
      columns1: [
        {
          name: '参数名',
          key: 'name',
        },
        {
          name: '参数描述',
          key: 'desc',
        },
        {
          name: '是否必传',
          key: 'required',
          format(row, key) {
            const value = row[key]
            return value !== undefined ? value : false
          },
        },
      ],
      columns2: [
        {
          name: '参数名',
          key: 'name',
        },
        {
          name: '参数描述',
          key: 'desc',
        },
        {
          name: '参数类型',
          key: 'type',
        },
        {
          name: '是否必传',
          key: 'required',
          format(row, key) {
            const value = row[key]
            return value !== undefined ? value : false
          },
        },
        {
          name: '默认值',
          key: 'default',
        },
        {
          name: '初始值',
          key: 'defaultValue',
          width: '260px',
        },
      ],
      paramsDialogVisible: false,
      currentIndex: -1,
      dialogPropsData: [],
      tips1: '按es module规范暴露组件的js脚本',
      tips2:
        '组件模块支持按需加载, 建议使用\n\nexport const xx = () => import(\'xxx\')',
    }
  },
  components: {
    valueInput,
    Select,
  },
  computed: {
    value: {
      get() {
        return this.elementProps[this.propKey]
      },
      set(value) {
        const res = {
          ...this.elementProps,
          [this.propKey]: value,
        }
        this.$emit('update', res)
      },
    },
    propsData: {
      get() {
        return this.elementProps['propsData']
      },
    },
    pageData() {
      const elementId = this.getStore().layoutData.elementId
      return this.$store.state.CODE.lowCodeStore.pageData.filter(
        (item) => item.elementType === 'page' && item.elementId !== elementId
      )
    },
    pageInfo() {
      return this.$store.state.CODE.lowCodeStore.pageData.find(
        (item) => item.elementId === this.value
      )
    },
  },
  watch: {},
  methods: {
    refreshSetting() {
      this.$confirm(
        '用此操作来更新页面容器的参数和事件,但会覆盖当前设置的默认值, 是否继续?',
        '提示',
        {
          closeOnClickModal: false,
          confirmButtonText: '继续',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).then(() => {
        this.initSetting(true)
        this.$message.success('已更新')
      })
    },
    setPropsInitVal() {
      this.dialogPropsData = _.cloneDeep(this.propsData)
      this.paramsDialogVisible = true
    },
    confirm() {
      const res = {
        ...this.elementProps,
        propsData: this.dialogPropsData,
        props: this.getProps(this.dialogPropsData),
      }
      this.$emit('update', res)
      this.paramsDialogVisible = false
    },
    async initSetting(pushHistory = false) {
      await this.$nextTick()

      // 解析参数表 和 解析events
      const { data } = await rootApp.$http({
        url: API.appPageDesignPageConfig,
        data: {
          pageDesignId: Number(this.pageInfo.pageDesignId),
        },
      })
      const { 2: dynamicProps } = data.reduce((obj, item) => {
        obj[item.type] = item.config
        return obj
      }, {})
      const { dynamicVariables = {}, eventsData = [], exportFunctions = [] } =
        JSON.parse(dynamicProps)[this.value]
      const propsData = Object.entries(dynamicVariables || {})
        .filter(([key, item]) => item.isEntry)
        .map(([key, item]) => ({
          name: key,
          type: '*',
          desc: item.desc,
          defaultValue: {
            type: '',
            value: item.value,
          },
        }))
        const fnsData = exportFunctions.map(item => ({ name: item.customName, fn: item.id }))
      const res = {
        ...this.elementProps,
        propsData,
        eventsData,
        props: this.getProps(propsData),
        fnsData
      }
      this.$emit('update', res, { pushHistory })
      this.$nextTick(() => {
        EventBus.$emit(BIND_EVENTS)
      })
    },
    // 根据propsData获取 props初始值
    getProps(propsData) {
      const result = propsData.reduce((obj, item) => {
        const { type, value } = item.defaultValue

        obj[item.name] =
          type === 'function'
            ? (() => {
                let val
                try {
                  // todo: js 沙箱机制
                  val = new Function(value)()
                } catch (error) {
                  val = value
                  console.warn(error)
                }
                return val
              })()
            : value
        return obj
      }, {})
      return result
    },
    handleInputResponse(value, type) {
      this.dialogPropsData[this.currentIndex].defaultValue = {
        value,
        type,
      }
    },
    jumpToPage() {
      const node = this.pageData.find(item => item.elementId === this.value)
      EventBus.$emit(SWITCH_PAGE, node)
    }
  },
}
</script>

<style lang="less" scoped>
div.config-item::v-deep {
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 8px;
  margin-right: 0;
  &.select-page {
    .el-form-item__content {
      display: flex;
    }
  }
  .el-form-item__content {
    flex: 1;
    & > * {
      width: 100%;
    }
  }
  .el-form-item__label {
    min-width: 100px;
    .el-tooltip.el-icon-question {
      margin-left: 5px;
      position: relative;
      z-index: 1;
    }
  }
  .props-wrap {
    overflow-x: auto;
    overflow-y: auto;
  }
}

.props-setting-dialog::v-deep {
  .el-table {
    user-select: text;
  }
}
</style>
