    <div v-if='!isHideSelect' class='dataset-select-wrapper'>
      <el-button size='mini' type="primary" @click="handleShowDataConfigSelect"
      >选择已存配置
      </el-button>
      <el-button size='mini' @click="handleShowDataConfigSave">配置另存为</el-button>
    </div>
    <div class="data-select-item no-border">
      <n-tabs
        class="data-content"
        :value="activeDataType"
        @tab-click="handleTabClick"
      >
        <n-tab-pane label="API" name="api">
          <api-config
            v-if="activeDataType === 'api'"
            :api-request.sync="apiRequest"
            @response="handleResponse"
          ></api-config>
        </n-tab-pane>
        <n-tab-pane v-if="!isHideSQL" label="SQL" name="sql">
          <sql-config
            v-if="activeDataType === 'sql'"
            :sql-request.sync="sqlRequest"
            :only-select="true"
            @response="handleResponse"
          ></sql-config>
        </n-tab-pane>
        <n-tab-pane v-if="!isHideStatic" label="静态" name="static">
          <static-config
            v-if="activeDataType === 'static'"
            :static-data.sync="staticData"
            @response="handleResponse"
          ></static-config>
        </n-tab-pane>
      </n-tabs>
    </div>
    <data-config-select
      ref="dataConfigSelect"
      @confirm="handleDataConfigConfirm"
    ></data-config-select>
    <data-config-save ref="dataConfigSave"></data-config-save>
  </div>
</template>

<script>
  import ApiConfig from '@/components/ApiConfig/index'
  import SqlConfig from '@/components/SqlConfig/index'
  import StaticConfig from '@/components/StaticConfig/index'
  import DataConfigSelect from '@/components/DataConfigSelect/index'
  import DataConfigSave from '@/components/DataConfigSave/index'
  import _ from 'lodash'

  export default {
    name: 'DataSetConfig',
    components: {
      ApiConfig,
      SqlConfig,
      StaticConfig,
      DataConfigSelect,
      DataConfigSave,
    },
    props: {
      isHideSQL: {
        type: Boolean,
        default: false,
      },
      isHideStatic: {
        type: Boolean,
        default: false,
      },
      isHideSelect: {
        type: Boolean,
        default: false,
      },
      request: {
        type: Object,
        default: () => ({
          apiRequest: undefined,
          sqlRequest: undefined,
          staticData: undefined,
          optionsMode: false
        }),
      },
      // 切换tab时是否把静态数据清掉
      clearStatic: {
        type: Boolean,
        default: true,
      },
    },
    data() {
      return {
        loading: false,
        dataSet: undefined,
        activeDataType: 'api',
        apiRequest: undefined,
        sqlRequest: undefined,
        staticData: undefined,
      }
    },
    watch: {
      activeDataType(n) {
        this.$emit('update:type', n)
      }
    },
    methods: {
      handleShowDataConfigSelect() {
        let type
        switch (this.activeDataType) {
          case 'api': {
            type = this.apiRequest.type
            break;
          }
          case 'sql': {
            type = this.sqlRequest.sqlType === 1 ? 2 : 3
            break;
          }
          case 'static': {
            type = 1
            break;
          }
        }
        this.$refs.dataConfigSelect.open({ type })
      },
      handleShowDataConfigSave() {
        let config
        let type
        switch (true) {
          case this.apiRequest !== undefined:
            config = {
              apiRequest: this.apiRequest,
            }
            type = this.apiRequest.type
            break
          case this.sqlRequest !== undefined:
            config = {
              sqlRequest: this.sqlRequest,
            }
            type = this.sqlRequest.sqlType === 1 ? 2 : 3
            break
          case this.staticData !== undefined:
            config = {
              staticData: this.staticData,
            }
            type = 1
            break
        }
        this.$refs.dataConfigSave.open({ config: JSON.stringify(config), type })
      },
      handleTabClick(event) {
        if (event.name === this.activeDataType) {
          return
        }
        this.activeDataType = event.name
        this.sqlRequest = undefined
        this.apiRequest = undefined
        // 切换时是否把静态数据清掉
        if(this.clearStatic) {
          this.staticData = undefined
        }
        this.$emit('tab-click', event)
      },
      handleResponse({ response, responseOptions, isStatic }) {
        this.$emit('response', { response, responseOptions, isStatic })
      },
      handleDataConfigConfirm(data) {
        this.dataSet = _.cloneDeep(data)
        const { apiRequest, sqlRequest, staticData } = JSON.parse(data.config)
        this.apiRequest = apiRequest
        this.sqlRequest = sqlRequest
        if(staticData) {
          this.staticData = staticData
        }
        switch (true) {
          case this.apiRequest !== undefined:
            this.activeDataType = 'api'
            break
          case this.sqlRequest !== undefined:
            this.activeDataType = 'sql'
            break
          case this.staticData !== undefined:
            this.activeDataType = 'static'
            break
        }
      },
      getResult() {
        // todo 校验数据
        return {
          apiRequest: this.apiRequest,
          sqlRequest: this.sqlRequest,
          staticData: this.staticData
        }
      }
    },
    created() {
      this.apiRequest = _.cloneDeep(this.request.apiRequest)
      this.sqlRequest = _.cloneDeep(this.request.sqlRequest)
      this.staticData = _.cloneDeep(this.request.staticData)
      switch (true) {
        case this.apiRequest !== undefined:
          this.activeDataType = 'api'
          break
        case this.sqlRequest !== undefined:
          this.activeDataType = 'sql'
          break
        case this.staticData !== undefined: {
          if (!this.request.optionsMode) {
            this.activeDataType = 'static'
          }
          break
        }
      }
    },
  }
</script>

