<template>
<div class='config-rich-input'>
  <div class='title'>{{ name }}</div>
  <el-button v-if='!hasValue' type='primary' size='mini' @click='handleInitDialog'>编辑</el-button>
  <div v-else class='has-value'>
    <el-button type='text' @click='handleInitDialog'>编辑默认值</el-button>
    <i class='iconfont icon-delete' @click='handleDelete'></i>
  </div>
  <el-dialog
    title="富文本编辑"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="1000px"
    :before-close="handleClose"
    custom-class="handler-dialog low-code-dialog"
    v-element-screen
  >
    <tinymce v-if='dialogVisible' v-model="value" :height="300" />
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</div>
</template>

<script>
  import Tinymce from '@/components/Tinymce'

  export default {
    name: 'configRichInput',
    components: {
      Tinymce
    },
    props: {
      propKey: {
        type: String,
      },
      name: {
        type: String,
      },
      placeholder: {
        type: String,
      },
      elementProps: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        dialogVisible: false,
        value: ''
      }
    },
    computed: {
      hasValue() {
        return !!this.elementProps[this.propKey]
      }
    },
    methods: {
      handleInitDialog() {
        this.value = this.elementProps[this.propKey]
        this.dialogVisible = true
      },
      cancel() {
        this.dialogVisible = false
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      confirm() {
        const res = { ...this.elementProps, [this.propKey]: this.value }
        this.$emit('update', res)
        this.dialogVisible = false
      },
      handleDelete() {
        const res = { ...this.elementProps, [this.propKey]: '' }
        this.$emit('update', res)
      }
    }
  }
</script>

<style lang='less' scoped>
  .config-rich-input::v-deep {
    display: flex;
    align-items: center;
    .title {
      width: 100px;
      font-size: 12px;
      color: #555555;
    }
    .has-value {
      .iconfont {
        font-size: 12px;
        margin-left: 10px;
        &:hover {
          cursor: pointer;
          color: red;
        }
      }
    }
  }
</style>
