<template>
  <el-dialog
    :custom-class="isShowTrigger ? 'animate-edit-dialog' : 'animate-edit-dialog center'"
    title=""
    :visible="dialogVisible"
    :close-on-click-modal="true"
    :modal-append-to-body="true"
    :append-to-body="true"
    :before-close="beforeClose"
    width="311px"
    top="0"
  >
    <div class="dialog-content">
      <div class="dialog-header">
        <div class="title">{{title}}</div>
      </div>
      <div class="dialog-body">
        <n-form
          label-position="left"
          ref="editForm"
          :rules="rules"
          :model="animate"
          @submit.native.prevent
          label-width="100px"
        >
          <n-form-item v-if="isShowTrigger" label="触发方式" prop="trigger">
            <el-select style="width: 100%" v-model="animate.trigger">
              <el-option
                v-for="trigger in triggers"
                :key="trigger.value"
                :label="trigger.label"
                :value="trigger.value"
                :disabled="isTriggerDisabled(trigger.value)"
              ></el-option>
            </el-select>
          </n-form-item>
          <n-form-item label="动画效果" prop="animationName">
            <el-tag v-if="animate.animationName" @click="handleShowAnimateList">{{animate.animationName}}</el-tag>
            <el-button v-else type="primary" size="mini" @click="handleShowAnimateList">选择动画</el-button>
          </n-form-item>
          <n-form-item label="延迟触发(s)" prop="delay">
            <el-input-number
              style="width: 100%"
              v-model="animate.delay"
              :min="0"
              :max="100"
              :step="0.5"
            ></el-input-number>
          </n-form-item>
          <n-form-item label="持续时间(s)" prop="duration">
            <el-input-number
              style="width: 100%"
              v-model="animate.duration"
              :min="0.1"
              :max="100"
              :step="0.5"
            ></el-input-number>
          </n-form-item>
          <!-- <n-form-item label="重复次数" prop="repeat">
            <el-input-number
              style="width: 100%"
              v-model="animate.repeat"
              :min="0"
              :max="10000"
              :step="1"
            ></el-input-number>
          </n-form-item> -->
        </n-form>
      </div>
      <div class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type='primary' @click="handleEditConfirm">确 认</el-button>
      </div>
    </div>
    <AnimateSelect ref="animateSelect" @confirm="handleAnimateSelectConfirm" />
  </el-dialog>
</template>

<script>
import _ from 'lodash';
import AnimateSelect from '@/components/AnimateSelect'
const animateDefine = {
  trigger: '',
  animationName: '',
  delay: 0,
  duration: 1,
  repeat: 1,
}
export default {
  name: 'AnimateEditDialog',
  components: {
    AnimateSelect
  },
  props: {
    animation: {
      type: Array,
      default: []
    },
    isRoot: {
      type: Boolean,
      default: true
    },
    isShowTrigger: {
      type: Boolean,
      default: true
    },
  },
  data() {
    return {
      dialogVisible: false,
      title: '',
      animate: {
        trigger: '',
        animationName: '',
        delay: 0,
        duration: 1,
        repeat: 1,
      },
      triggerOption: [
        {
          label: '点击',
          value: 'click'
        },
        {
          label: '悬停',
          value: 'hover'
        },
        {
          label: '显示',
          value: 'show'
        },
        {
          label: '隐藏',
          value: 'hide'
        },
      ]
    }
  },
  computed: {
    triggers() {
      return this.isRoot ? [
        {
          label: '点击',
          value: 'click'
        },
        {
          label: '悬停',
          value: 'hover'
        },
        {
          label: '显示',
          value: 'show'
        },
        {
          label: '隐藏',
          value: 'hide'
        },
      ] : [
        {
          label: '点击',
          value: 'click'
        },
        {
          label: '悬停',
          value: 'hover'
        },
      ]
    }
  },
  methods: {
    open(animate) {
      this.animate = _.cloneDeep(animate || animateDefine)
      this.title = animate ? '编辑动画' : '新增动画'
      this.dialogVisible = true
    },
    beforeClose(done) {
      this.dialogVisible = false
      done()
    },
    handleEditConfirm() {
      if (!this.animate.trigger && this.isShowTrigger) {
        this.$message.error('请选择触发方式！')
        return
      }
      if (!this.animate.animationName) {
        this.$message.error('请选择动画效果！')
        return
      }
      if (this.title === '新增动画') {
        this.$emit('confirmAdd', this.animate)
      } else {
        this.$emit('confirmEdit', this.animate)
      }
      this.dialogVisible = false
    },
    handleShowAnimateList() {
      this.$refs.animateSelect.open(this.animate.animationName)
    },
    handleAnimateSelectConfirm(animationName) {
      this.animate.animationName = animationName
    },
    isTriggerDisabled(trigger) {
      return this.animation.some(ani => ani.trigger === trigger) || (!this.isRoot && ['show', 'hide'].includes(trigger))
    }
  }
}
</script>

