  position: absolute;
  top: 150px;
  right: 50px;
  margin: 0;
  &.center {
    top: 50%;
    left: 50%;
    right: unset;
    transform: translate(-50%, -50%);
  }
  .el-dialog__header {
    display: none;
  }
  .el-dialog__body {
    padding: 0;
  }
  .dialog-content {
    width: 100%;
    padding: 10px;
    .dialog-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .dialog-body {
      margin-top: 10px;
      width: 100%;
    }
    .dialog-footer {
      margin-top: 10px;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 100%;
    }
  }
}
</style>
