<template>
  <div class="chart-field-selector">
    <el-row>
      <el-radio-group v-model="active">
        <el-radio-button label="response">响应体</el-radio-button>
        <el-radio-button label="preview" @click.native="init"
          >预览</el-radio-button
        >
      </el-radio-group>
      <div class="field-area chart-demo-box">
        <template v-if="active === 'response'">
          <template v-if="responseData">
            <vue-json-pretty
              class="json-pretty no-select"
              path="response"
              :data="responseData"
              :virtual="true"
              :itemHeight="24"
              selectableType="single"
              :showSelectController="true"
              :selectOnClickNode="true"
              v-model="selectedPath"
            >
            </vue-json-pretty>
          </template>
          <span v-else class="empty-tips">点击上方测试按钮</span>
        </template>

        <div v-show="active === 'preview'">
          <div v-if="responseData" id="antv-chart-preview-demo"></div>
          <span v-else class="empty-tips">{{
            '当前数据路径无数据' + '需要先选择响应体中的映射字段'
          }}</span>
        </div>
      </div>
    </el-row>
    <el-row>
      <div class="chart-field-title">数据（data）映射配置</div>
      <n-table class="data-path" :data="dataMapping" border>
        <n-table-column>
          <template slot="header">
            <div class="custom-header">
              <span class="required">*</span>data路径
            </div>
          </template>
          <template slot-scope="scope">
            <div class="drop-zone-path" :data-key="scope.$index">
              <n-tooltip
                v-if="scope.row.sourcePath && scope.row.sourcePath.length"
                :value="hidePrefix(scope.row.sourcePath)"
              >
                <el-tag closable @close="deleteSourcePath(scope.$index)" disable-transitions>{{
                  showLastPath(scope.row.sourcePath)
                }}</el-tag>
              </n-tooltip>
              <span v-else>从上方响应体中拖拽节点到此处</span>
            </div>
          </template>
        </n-table-column>
        <n-table-column>
          <template slot="header">
            <div class="custom-header">
              数据编辑 <n-tooltip value="非必填, 可对所选的数据内容进行编辑, 编辑保存后作为图表的显示数据内容"></n-tooltip>
            </div>
          </template>
          <template slot-scope="scope">
            <el-button
              type="primary"
              @click="jsFormatOpen(scope.row.formatCode, scope.$index)"
              >编辑</el-button
            >
            <el-button
              v-if="scope.row.formatCode"
              type="danger"
              @click="deleteJsFormat(scope.$index)"
              >删除</el-button
            >
          </template>
        </n-table-column>
        <n-table-column>
          <template slot="header">
            <div class="custom-header">
              <span class="required">*</span>字段路径<n-tooltip value="将选中的 “data路径” 映射到所选配置属性中"></n-tooltip>
            </div>
          </template>
          <template slot-scope="scope">
            <div style="display: flex">
              <el-input :value="scope.row.mappingPath ? scope.row.mappingPath.join('.') : scope.row.mappingPath" disabled></el-input>
              <el-popover
                placement="top"
                title="选择属性"
                trigger="click"
                ref="popover"
              >
                <n-cascader-panel
                  :value="scope.row.mappingPath"
                  :options="chartConfigSelectOption"
                  :props="{ checkStrictly: true }"
                  :show-all-levels="true"
                  clearable
                  @change="configSelectChange($event, scope.$index)"
                ></n-cascader-panel>
                <el-button
                  slot="reference"
                  style="margin-left: 10px"
                  type="primary"
                  >选择</el-button
                >
              </el-popover>
            </div>
          </template>
        </n-table-column>
        <n-table-column label="操作" width="100px">
          <template slot-scope="scope">
            <i class="el-icon-circle-plus addPath" @click="addMapping"></i>
            <i
              v-if="dataMapping.length > 1 && scope.$index !== 0"
              class="el-icon-remove addPath"
              style="color: #F56C6C;"
              @click="deleteMapping(scope.$index)"
            ></i>
          </template>
        </n-table-column>
      </n-table>
      <jsFormat
        title="数据编辑"
        :tips="tips"
        ref="jsFormat"
        @confirm="jsFormatConfirm"
        :default-arguments="defaultArguments"
      ></jsFormat>
    </el-row>
    <div class="dnd-cover" v-show="dndCoverStyle" :style="dndCoverStyle"></div>
  </div>
</template>

<script>
// func
import _ from 'lodash'
import { debounce } from '@/utils'
import jsFormat from '@/components/jsFormat'
// components
import draggable from 'vuedraggable'
import VueJsonPretty from '@/lib/vue-json-pretty'
import dnd from '@/mixins/dragAndDrop'
import { getObjectTree, getPathList } from '@/utils/format'
import trigger from '@/renderer/tools/trigger'
import { pageInject } from '@/renderer/tools/pageStore'
import { FUNCTION_FLAG } from '@/config'
const tips = `支持JS原生语法，使用“参数名”进行取值, 使用return返回结果，示例JS：
  return data`
export default {
  components: {
    draggable,
    VueJsonPretty,
    jsFormat,
  },
  inject: [...pageInject],
  mixins: [dnd],
  props: {
    data: {
      type: Object,
      require: true,
    },
    activeDataType: {
      type: String,
      require: true,
    },
    elementProps: {
      type: Object,
      require: true,
    },
    staticData: {
      type: Object,
      require: true,
    },
    dataMapping: {
      type: Array,
      require: true,
    },
  },
  data() {
    return {
      tips,
      dragVal: [],
      allField: [],
      xField: [],
      yField: [],
      soureData: [],
      active: 'response',
      editIndex: -1,
      selectedPath: '',
      defaultArguments: [
        {
          key: 'data',
          value: undefined,
          test: undefined,
          name: '源数据',
          desc: '源数据',
          definedTest: false,
        },
      ],
      chartInstance: null,
    }
  },
  mounted() {
    this.handleInitDnD()
  },
  beforeDestroy () {
    this.chartInstance?.destroy()
  },
  methods: {
    jsFormatOpen(id, index) {
      this.editIndex = index
      this.$refs.jsFormat.open({ id: id.replace(FUNCTION_FLAG, '') })
    },
    deleteJsFormat(index) {
      this.dataMapping[index].formatCode = ''
      this.init()
    },
    deleteSourcePath(index) {
      this.dataMapping[index].sourcePath = ''
    },
    configSelectChange(value, index) {
      this.dataMapping[index].mappingPath = _.cloneDeep(value)
      this.init()
    },
    jsFormatConfirm(id) {
      this.dataMapping[this.editIndex].formatCode = `${FUNCTION_FLAG}${id}`
      this.init()
    },
    handleDrop(event) {
      const { key } = event.target.dataset
      this.dataMapping[key].sourcePath = getPathList(this.selectedPath)
      this.init()
    },
    addMapping() {
      this.dataMapping.push({ sourcePath: [], mappingPath: [], formatCode: '' })
      this.dataMapping = _.cloneDeep(this.dataMapping)
    },
    deleteMapping(index) {
      this.dataMapping.splice(index, 1)
    },
    hidePrefix(path) {
      if (path instanceof Array) {
        return path.slice(1).join('/')
      }
    },
    showLastPath(path) {
      return path[path.length - 1]
    },
    init: debounce(async function () {
      await this.$nextTick()
      if (this.responseData) {
        import('@antv/g2plot').then((module) => {
          const { chartOption, dataMapping } = this
          const option = _.cloneDeep(chartOption)
          dataMapping.forEach(({ sourcePath, mappingPath, formatCode }) => {
            // 当前请求映射和配置映射都配置的情况下才去映射
            if (sourcePath.length && mappingPath.length) {
              let trueData
              const mockData = _.get(
                { response: this.responseData },
                sourcePath
              )
              if (formatCode) {
                trueData = trigger.getFuncValueByParams({
                  functionId: formatCode,
                  extraParams: [
                    {
                      key: 'data',
                      value: mockData,
                    },
                  ],
                  vueThis: this,
                })
              } else {
                trueData = mockData
              }
              _.set(option, mappingPath, trueData)
            }
          })
          if(this.chartInstance) {
            this.chartInstance.update(option)
          } else {
            this.chartInstance = new module[this.elementProps.type](
              'antv-chart-preview-demo',
              option
            )
            this.chartInstance.render()
          }
        })
      }
    }, 100),
  },

  computed: {
    responseData() {
      const { activeDataType, staticData } = this
      if (['api', 'sql'].includes(activeDataType)) {
        return this.data
      }
      return staticData
    },
    chartOption() {
      return trigger.getObjectVariables(
        this.elementProps.chartConfigId,
        [],
        this
      )
    },
    chartConfigSelectOption() {
      return getObjectTree(this.chartOption)
    },
  },

  watch: {},
}
</script>
