<template>
  <div class="option-config">
    <div
      v-if="popoverVisible"
      class="option-config-model"
      @click="popoverVisible = false"
    ></div>
    <div class="mode-select">
      <el-radio-group
        v-model="dataOrigin"
        fill="#3f92fe"
        @change="handleDataOriginChange"
      >
        <el-radio-button label="static">选项配置</el-radio-button>
        <el-radio-button label="dynamic">动态数据</el-radio-button>
      </el-radio-group>
    </div>
    <div class="static-set-cont" v-show="dataOrigin == 'static'">
      <div v-show="!sourceMode">
        <!-- 树形编辑器开始 -->
        <n-tree
          ref="tree"
          class="option-tree"
          :data="treeData"
          node-key="id"
          show-checkbox
          icon-class="icon-right"
          draggable
          :expand-on-click-node="false"
          :check-strictly="checkStrictly"
          @check-change="checkChange"
          @node-drop="handleDropChange"
        >
          <span class="custom-tree-node" slot-scope="{ node, data }">
            <span class="label">{{ node.label }}</span>
            <span>
              <el-button
                v-popover:popover
                type="text"
                size="mini"
                @click="() => edit(node, data)"
              >
                <i class="el-icon-edit"></i>
              </el-button>

              <el-button
                v-popover:popover
                type="text"
                size="mini"
                @click="append(node, data)"
              >
                <i class="el-icon-plus"></i>
              </el-button>
              <el-button type="text" size="mini" @click="remove(node, data)">
                <i class="el-icon-delete" style="color: red"></i>
              </el-button>
            </span>
          </span>
        </n-tree>
        <!-- 树形编辑器结束 -->

        <el-button
          class="option-btn"
          type="text"
          v-popover:popover
          @click="handleAddOption"
        >
          <i class="iconfont el-icon-circle-plus-outline"></i>
          添加选项
        </el-button>
        <el-button type="text" class="option-btn" @click="sourceMode = true">
          <i class="iconfont icon-jiaoben"></i>
          源码模式
        </el-button>
      </div>
      <div v-if="sourceMode" class="source-editor">
        <static-config
          show-cancel
          :static-data.sync="staticData"
          @response="handleStaticResponse"
          @cancel="cancel"
        />
      </div>
      <el-popover
        width="300"
        trigger="manual"
        ref="popover"
        v-model="popoverVisible"
      >
        <div class="variable-body">
          <n-form
            class="popover-config-form"
            ref="variableForm"
            :model="variableForm"
            :rules="rules"
            inline
            label-width="85px"
            @submit.native.prevent
          >
            <n-form-item label="默认选中">
              <el-switch v-model="variableForm.isDefault"></el-switch>
            </n-form-item>
            <n-form-item label="显示值" required prop="label">
              <el-input v-model="variableForm.label"></el-input>
            </n-form-item>
            <n-form-item label="选项值" required prop="value">
              <el-input clearable v-model="variableForm.value"></el-input>
            </n-form-item>
            <n-form-item v-if="['hasIcon'].includes(optionkey)" label="图标" prop="icon">
              <div class="icon_selector">
                <div class="col">
                  <span></span>
                  <i v-if="variableForm.icon" class="show-icon" :class="variableForm.icon"></i>
                  <n-popover
                    width="400"
                    placement="bottom"
                    trigger="click"
                    popper-class="top-popper"
                  >
                    <n-tabs v-model="iconType" type="card">
                      <n-tab-pane label="默认图标" name="default">
                        <div class="iconfont-list">
                          <i
                            v-for="(item, index) in icons"
                            :key="index"
                            :class="item"
                            @click="iconChange(item);handleShownPopover()"
                          ></i>
                        </div>
                      </n-tab-pane>
                      <n-tab-pane label="图标库选择" name="iconfont">
                        <div class="iconfont-list" v-loading="loading">
                          <i
                            v-for="(item, index) in iconfonts"
                            :key="index"
                            :class="item"
                            @click="iconChange(item);handleShownPopover()"
                          ></i>
                        </div>
                      </n-tab-pane>
                    </n-tabs>
                    <el-button
                      slot="reference"
                      circle
                      type="primary"
                      size="mini"
                      icon="el-icon-edit"
                      style="margin-right: 10px;"
                      ref="referenceBtn"
                    ></el-button>
                  </n-popover>

                  <el-button
                    v-if="variableForm.icon"
                    circle
                    type="danger"
                    size="mini"
                    icon="el-icon-delete"
                    @click.stop="iconChange('')"
                  ></el-button>
                </div>
              </div>
            </n-form-item>
            <n-form-item label="是否禁用" prop="disabled">
              <el-switch v-model="variableForm.disabled"></el-switch>
            </n-form-item>
          </n-form>
          <div style="text-align: right; margin: 0">
            <el-button type="primary" size="mini" @click="handleConfirm"
              >确认</el-button
            >
            <el-button size="mini" type="text" @click="popoverVisible = false"
              >取消</el-button
            >
          </div>
        </div>
      </el-popover>
      <select-reg-modal
        ref="selectRegModal"
        @confirm="handleSelectRegConfirm"
      />
    </div>
    <div v-show="dataOrigin == 'dynamic'" class="dynamic-btn">
      <data-selector
        name=""
        btnText="数据集配置"
        use-type="options"
        v-bind="$props"
        @update="dataSelectUpdate"
      ></data-selector>
    </div>
  </div>
</template>

<script>
import { getValidators, required } from '@/utils/validateUtils'
import { icons } from '../icon-select/setting'
import _ from 'lodash'
import draggable from 'vuedraggable'
import dataSelector from '../config-data'
import StaticConfig from '@/components/StaticConfig/index'
import treeTool from 'tree-tool'
import { Popover } from 'element-ui'
import { getRandomHexadecimal } from '@/utils/tree-utils'
import request from '@/request/request'

const treeUtil = treeTool.createInstance({
  id: 'id',
  children: 'children',
  pid: 'parentId',
})

export default {
  components: {
    draggable,
    dataSelector,
    StaticConfig,
    nPopover: Popover
  },
  props: {
    propKey: {
      type: String,
    },
    name: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    elementProps: {
      type: Object,
      default: () => ({}),
    },
    multiple: {
      type: Boolean,
      default: true,
    },
    optionkey: {
      type: String,
    },
  },
  data() {
    return {
      icons,
      dataOrigin: 'static',
      sourceMode: false,
      variableForm: {
        isDefault: false,
        label: '',
        value: undefined,
        icon: ''
      },
      rules: {
        label: getValidators([
          {
            required: true,
            message: '请输入显示值',
            include: required,
            trigger: 'change',
          },
        ]),
        value: getValidators([
          {
            required: true,
            message: '请输入选项值',
            include: required,
            trigger: 'change',
          },
        ]),
      },
      popoverVisible: false,
      editMode: null, // 编辑模式 ['root'  =>根节点添加 ; 'edit' => '节点编辑'; 'add' =>'节点添加子节点']
      currentData: null, // 当前选中的节点数据
      treeData: [], //树渲染所用的数据
      lastElementId: null, // 上一次的组件id, 用于判断是否更新
      loading: false,
      iconfonts: [],
      iconType: 'default'
    }
  },
  computed: {
    checkStrictly() {
      return this.elementProps['checkStrictly']
    },
    value() {
      return this.elementProps[this.propKey]
    },
    staticData: {
      get() {
        return this.elementProps['staticData']
      },
      set(value) {
        const res = { ...this.elementProps, staticData: _.cloneDeep(value) }
        this.$emit('update', res)
      },
    },
  },
  methods: {
    checkChange() {
      const keys = this.$refs.tree.getCheckedKeys()
      let tempStaticData = _.cloneDeep(this.staticData || [])
      treeUtil.forEach(tempStaticData, (item) => {
        item.isDefault = keys.includes(item.id)
      })
      this.updateStaticData(tempStaticData)
    },
    dataSelectUpdate(data) {
      this.$emit('update', {
        ...data,
        staticData: this.staticData,
      })
    },
    updateStaticData(staticData) {
      const res = {
        ...this.elementProps,
        staticData,
      }
      this.$emit('update', res)
    },
    handleDataOriginChange(v) {
      this.dataOrigin = v
    },
    async handleAddOption() {
      this.popoverVisible = true
      await this.$nextTick()
      this.$refs.variableForm.resetFields()
      this.variableForm = {
        isDefault: false,
        label: '',
        value: undefined,
        children: [],
        disabled: false,
        icon: ''
      }
      this.editMode = 'root'
      this.currentData = null
    },
    handleConfirm() {
      this.$refs.variableForm.validate((valid) => {
        if (!valid) {
          return
        }
        let tempStaticData = _.cloneDeep(this.staticData || [])
        let form = _.cloneDeep(this.variableForm)
        const { currentData } = this
        switch (this.editMode) {
          // 根节点添加
          case 'root':
            {
              form.id = getRandomHexadecimal(6)
              tempStaticData.push(form)
              this.$refs.tree.append(form)
            }
            break
          // 添加
          case 'add': {
            form.id = getRandomHexadecimal(6)
            const currentNode = treeUtil.findNode(tempStaticData, node => node.id === currentData.id)
            if (currentNode.children) {
              currentNode.children.push(form)
            } else {
              currentNode.children = [form]
            }
            this.$refs.tree.append(form, this.currentData.id)
            break
          }
          // 编辑
          case 'edit':
            treeUtil.forEach(tempStaticData, (item) => {
              if (item.id === currentData.id) {
                Object.keys(form).forEach((key) => {
                  if (key !== 'children') {
                    item[key] = form[key]
                  }
                })
              }
            })
            Object.keys(form).forEach((key) => {
              if (key !== 'children') {
                this.currentData[key] = form[key]
              }
            })
            break
          default:
        }
        this.updateStaticData(_.cloneDeep(tempStaticData))
        this.handleSetChecked(_.cloneDeep(tempStaticData))
        this.popoverVisible = false
      })
    },
    handleSetChecked(data) {
      this.$nextTick(() => {
        const keys = []
        treeUtil.forEach(data, (node) => {
          if (node.isDefault) {
            keys.push(node.id)
          }
        })
        this.$refs.tree.setCheckedKeys(keys)
      })
    },
    // 打开选择正则表达式弹窗
    handleSelectRegOpen() {
      this.$refs.selectRegModal.show()
    },
    handleSelectRegConfirm(data) {
      this.variableForm = { ...data }
      this.$refs.selectRegModal.close()
    },
    async handleStaticResponse({ response, responseOptions }) {
      if (!response) {
        return
      }
      if (!Array.isArray(response)) {
        this.$message.error('数据格式不正确')
        return
      }
      const res = response[0]
      if (!_.isObject(res)) {
        this.$message.error('数据格式不正确')
        return
      }
      this.treeData = _.cloneDeep(response)
      this.staticData = _.cloneDeep(response)
      this.sourceMode = false
    },
    cancel() {
      this.sourceMode = false
    },
    async edit(node, data) {
      this.popoverVisible = true
      await this.$nextTick()
      this.$refs.variableForm.resetFields()
      this.variableForm = {
        id: data.id,
        isDefault: data.isDefault,
        label: data.label,
        value: data.value,
        children: data.children || [],
        disabled: data.disabled,
        icon: data.icon || ''
      }
      this.currentData = data
      this.editMode = 'edit'
    },
    async append(node, data) {
      this.popoverVisible = true
      await this.$nextTick()
      this.$refs.variableForm.resetFields()
      this.variableForm = {
        isDefault: false,
        label: '',
        value: undefined,
        children: [],
        icon: ''
      }
      this.currentData = data
      this.editMode = 'add'
    },

    remove(node, data) {
      this.$refs.tree.remove(data.id)
      this.updateStaticData(this.treeData)
    },
    iconChange(val) {
      this.variableForm.icon = val
    },
    handleShownPopover() {
      this.$nextTick(() => {
        this.$refs.referenceBtn.$el.click()
      })
    },
    handleDropChange(dragNode, dropNode, placement) {
      let tempStaticData = _.cloneDeep(this.staticData || [])
      const targetNode = treeUtil.findNode(tempStaticData, node => node.id === dropNode.key)
      const draggingNode = treeUtil.findNode(tempStaticData, node => node.id === dragNode.key)
      treeUtil.removeNode(tempStaticData, node => node.id === dragNode.key)
      switch (placement) {
        case 'inner': {
          if (targetNode.children) {
            targetNode.children.push(draggingNode)
          } else {
            targetNode.children = [draggingNode]
          }
          break
        }
        case 'before': {
          treeUtil.insertBefore(tempStaticData, draggingNode, targetNode)
          break
        }
        case 'after': {
          treeUtil.insertAfter(tempStaticData, targetNode, draggingNode)
          break
        }
      }
      this.updateStaticData(_.cloneDeep(tempStaticData))
      this.handleSetChecked(_.cloneDeep(tempStaticData))
    },
    handleGetIconfont() {
      this.loading = true
      request({
        url: `${this.$store.state.CODE.lowCodeStore.appSourcePrefix}apps/${this.$route.query.id}/iconfont/iconfont.json`,
        method: 'GET',
        header: {
          'Content-Type': 'application/json',
        },
      })
        .then((res) => {
          const iconfontData = res.data
          this.iconfonts = iconfontData.glyphs.map(icon => `${iconfontData.font_family} ${iconfontData.css_prefix_text}${icon.font_class}`)
        })
        .finally(() => {
          this.loading = false
        })
    },
  },
  mounted() {
    this.handleGetIconfont()
    const { apiRequest, sqlRequest } = this.elementProps
    this.dataOrigin = apiRequest || sqlRequest ? 'dynamic' : 'static'
    this.handleSetChecked(this.treeData)
  },
  watch: {
    staticData: {
      deep: true,
      immediate: true,
      handler(val) {
        if (this.lastElementId !== this.elementProps.elementId) {
          this.lastElementId = this.elementProps.elementId
          this.treeData = _.cloneDeep(val)
        }
      },
    },
  },
}
</script>

